import React from 'react'

import get from 'lodash/get'

import { T9n } from 'react-sport'

import { EditTableName } from 'features/TableName/components/EditTableName'
import { RangeSelector } from './RangeSelector'
import { GearTable } from './GearTable'
import { DndParams } from './DndParams'
import { gearPopupComposed } from './composed'

import { TComposeInner } from './types'

// Попап окно с параметрами
const GearPopupComponent = ({
  currentStore,
  getTable,
  lexics: { getLexics },
  newPopup,
  onApply,
  onClose,
  playerProfile: { profile },
  table,
}: TComposeInner) => (
  <div className='popup popup-modern popup-modern_position-left popup-modern_active'>
    <div className='popup-inner'>
      <div className='popup-row-header'>
        <h3 className='popup-title'>
          <EditTableName
            table={getTable(table, newPopup)}
            newPopup={newPopup}
            currentStore={currentStore}
          />
          <span className='popup-title__icon' />
        </h3>
        <span className='popup-close' onClick={onClose} />
      </div>
      <div className='popup-row-container'>
        <GearTable
          currentStore={currentStore}
          ampluaId={get(profile, 'amplua.id', 0)}
        />
      </div>
      <div className='popup-column-aside'>
        <DndParams />
        <div className='popup-duration-settings'>
          <div className='popup-col-title'>
            <T9n t='boxscore_setting_all_ep' />
          </div>
          <RangeSelector />
        </div>
      </div>
      <div className='popup-row-footer'>
        <div className='popup-buttons'>
          <input
            onClick={onClose}
            type='button'
            value={getLexics('cancel')}
            className='popup-cancel'
          />
          <input
            onClick={onApply(table.position)}
            type='button'
            value={getLexics('ok')}
            className='popup-submit'
          />
        </div>
      </div>
    </div>
  </div>
)

export const GearPopup = gearPopupComposed(GearPopupComponent)
