import React from 'react'
import { Switch, Route } from 'react-router-dom'

import isEmpty from 'lodash/isEmpty'

import { EventPlayer as VideoPlayer, Popup } from 'react-sport'

import { StatsTabs } from 'features/components/common/stats-tabs/StatsTabs'
import { PlayerField } from 'features/components/common/player/PlayerField'
import { PlayerCard } from 'features/components/PlayerPage/PlayerCard'
import { PlayerStatsWrapper } from 'features/components/PlayerPage/PlayerStatsWrapper'
import MatchSelect from 'features/components/common/MatchSelect'
import VideoCuts from 'features/components/common/video-cuts/VideoCuts'
import Loader from 'features/components/common/loader/Loader'

import { PlayerPageMatches } from 'features/PlayerPageMatches/components'

import { playerPageComposed } from './PlayerPageComposed'

import 'features/components/common/MainPage.scss'
import 'features/components/common/PageCard.scss'
import 'features/components/common/Shot.scss'
import 'features/components/common/Zones.scss'

import 'features/components/App/magnific-popup.css'
import 'features/components/App/video-player.scss'
import 'features/components/common/player-card/player.scss'
import 'features/components/common/player-card/PlayerCard.scss'
import 'features/components/common/player-stats/PlayerStats.scss'
import 'features/components/common/player-stats/PlayerStatsDropdown.scss'

export type TProps = {
  getGearParams: (id: number | string) => void,
  match: {
    params: { playerId: string },
    url: string,
  },
  matches: {
    firstLoad: boolean,
  },
  playerProfile: { profile: {} | null },
  selectedMatches: {
    player: {
      [key: string]: Array<{}>,
    },
  },
}

const PlayerPageComponent = ({
  match: {
    params: { playerId },
  },
  match,
  matches: { firstLoad },
  playerProfile: { profile },
  selectedMatches,
}: TProps) => (
  <article className='player for-player'>
    {/*
      //@ts-ignore */}
    <Popup>
      {/*
        //@ts-ignore */}
      <VideoPlayer appName='volleyball' />
    </Popup>
    <section className='player-card'>
      <div className='container-fluid'>
        <div className='player-card-wrapper'>
          {profile && <PlayerCard />}
          <div className='player-card-responsive'>
            {!firstLoad ? (
              //@ts-ignore
              <MatchSelect
                page='player'
                pageId={playerId}
                selectedMatches={selectedMatches}
              />
            ) : (
              <Loader />
            )}
          </div>
          <div className='player-card-right'>
            {!isEmpty(profile) && !firstLoad ? (
              <PlayerStatsWrapper page='player' pageId={playerId} />
            ) : (
              <Loader />
            )}
          </div>
        </div>
      </div>
    </section>
    <section className='main-page__details'>
      {/*
        //@ts-ignore */}
      {!firstLoad ? <PlayerField pageId={playerId} /> : <Loader />}
      <div className='main-page__responsive'>
        <div className='main-page__content'>
          <div className='main-page__content-header'>
            <StatsTabs match={match} isStats />
          </div>
          {/*
          //@ts-ignore */}
          <Switch>
            {Boolean(selectedMatches.player[playerId]
            && profile)
            && (
              <Route
                path='/players/:playerId/'
                exact
                render={() => <PlayerPageMatches pageId={playerId} />
                }
              />
            )}
            <Route
              path='/players/:playerId/video'
              render={() => (
                //@ts-ignore
                <VideoCuts
                  page='player'
                  pageType='players'
                  playerId={playerId}
                />
              )}
            />
          </Switch>
        </div>
      </div>
    </section>
  </article>
)

//@ts-ignore
export const PlayerPage = playerPageComposed(PlayerPageComponent)
