import React from 'react'
import { inject, observer } from 'mobx-react'

import {
  compose,
  withHandlers,
  withState,

} from 'recompose'

import { T9n } from 'react-sport'

import { EditableInput } from './EditableInput'

type TProps = {
  editTableName: () => void,
  //@ts-ignore
  getTableName: (newPopup: string | null) => string,
  handleFocus: () => void,
  handleShowInput: () => boolean,
  handleTranslateName: (tableName: string | number) => string,
  newPopup?: boolean,
  showInput: boolean,
  table: {
    tableName: string | number,
    position: number,
  },
  currentStore: {},
}

// Компонент названия таблицы статистики
const EditTableNameComponent = ({
  currentStore,
  editTableName,
  getTableName,
  handleFocus,
  handleShowInput,
  handleTranslateName,
  newPopup,
  showInput,
  table,
}: TProps) => (
  <div>
    {showInput
      ? (
    //@ts-ignore
        <EditableInput
          tableName={handleTranslateName(table.tableName)}
          handleFocus={handleFocus}
          handleShowInput={handleShowInput}
          editTableName={editTableName}
          position={table.position}
          newPopup={newPopup}
          currentStore={currentStore}
        />
      )
        //@ts-ignore
      : <T9n t={getTableName(newPopup)} onClick={handleShowInput} />
    }
  </div>
)

const EditTableNameComposed = compose<TProps, any>(
  inject(
    'lexics',
    'teamTable',
    'playerTable',
    'teamPlayers',
    'matchTable',
    'tournamentMatchesTableStore',
    'gearPopupStore',
  ),
  withState('showInput', 'setShowInput', false),
  //@ts-ignore
  withHandlers({
    getTableName: ({
      gearPopupStore: { tempTableName },
      table: { tableName },
      //@ts-ignore
    }) => (newPopup) => {
      if (!newPopup) return tableName
      return tempTableName
    },
    /**
    * Функция редактирования имени таблицы
    * @param {number} position - номер таблицы
    * @param {string} tableName - название таблицы
    */
    editTableName: ({
      currentStore: {
        editTable,
        storeName,
      },
      gearPopupStore: { editTempTableName },
      newPopup,
      //@ts-ignore
    }) => (position, tableName) => {
      if (newPopup) {
        return editTempTableName(tableName)
      }
      return editTable(position, tableName, storeName)
    },
    // Показываем или input или блок с названием
    //@ts-ignore
    handleShowInput: ({ setShowInput }) => () => setShowInput(n => !n),
    // При фокусе выбираем весь текст в инпуте сразу
    //@ts-ignore
    handleFocus: () => e => e.target.select(),
    // Переводим на нужный язык value в инпуте при фокусе
    handleTranslateName: ({ lexics: { getLexics } }) => (
      //@ts-ignore
      tableName => getLexics(tableName) || tableName
    ),
  }),
  observer,
)
export const EditTableName = EditTableNameComposed(EditTableNameComponent)
