import React, { Fragment } from 'react'
import { Switch, Route } from 'react-router-dom'

import { EventPlayer as VideoPlayer, Popup } from 'react-sport'

import { StatsTabs } from 'features/components/common/stats-tabs/StatsTabs'
import TeamPlayers from 'features/TeamPage/components/TeamPlayers'
import VideoCuts from 'features/components/common/video-cuts/VideoCuts'
import Loader from 'features/components/common/loader/Loader'
import { PlayerField } from 'features/components/common/player/PlayerField'
import { TeamPageMatches } from 'features/TeamPageMatches'

import { teamPageComposed } from './teamPageComposed'
import { TeamPageHeader } from './TeamPageHeader'

import 'features/components/common/MainPage.scss'
import 'features/components/common/PageCard.scss'
import 'features/components/common/Shot.scss'
import 'features/components/common/Zones.scss'

import 'features/components/App/magnific-popup.css'
import 'features/components/App/video-player.scss'
import 'features/components/common/player-card/player.scss'
import 'features/components/common/player-card/PlayerCard.scss'
import 'features/components/common/player-stats/PlayerStats.scss'
import 'features/components/common/player-stats/PlayerStatsDropdown.scss'

type TProps = {
  lexics: { page: string | null },
  match: {
    params: {
      teamId: string,
    },
    url: string,
  },
  matches: {
    firstLoad: boolean,
  },
  selectedMatches: {
    team: {
      [key: string]: Array<{}>,
    },
  },
  teamProfile: { profile: {} },
  teamTable: {
    gearParams: {},
    tables: {},
  },
}

export const TeamPageComponent = ({
  match: {
    params: {
      teamId,
    },
  },
  match,
  matches: { firstLoad },
  selectedMatches,
  teamProfile: { profile },
}: TProps) => (
  <article className='player for-player'>
    {/*
      //@ts-ignore */}
    <Popup>
      {/*
        //@ts-ignore */}
      <VideoPlayer
        appName='volleyball'
      />
    </Popup>
    <TeamPageHeader
      profile={profile}
      firstLoad={firstLoad}
      teamId={teamId}
    />
    <section className='main-page__details'>
      {!firstLoad
      //@ts-ignore
        ? <PlayerField pageId={teamId} />
        : <Loader />
      }
      <div className='main-page__responsive'>
        <div className='main-page__content'>
          <div className='main-page__content-header'>
            <StatsTabs
              match={match}
              isStats
            />
          </div>
          <Switch>
            <Route
              path='/teams/:teamId/'
              exact
              render={() => <TeamPageMatches pageId={teamId} />}
            />
            <Route
              path='/teams/:teamId/video'
              render={props => (
                //@ts-ignore
                <VideoCuts
                  page='team'
                  pageType='teams'
                  playerId={teamId}
                  {...props}
                />
              )}
            />
            <Route
              path='/teams/:teamId/players'
              exact
              render={() => (
                <Fragment>
                  {/*
                    //@ts-ignore */}
                  {profile && selectedMatches && <TeamPlayers />}
                </Fragment>
              )}
            />
          </Switch>
        </div>
      </div>
    </section>
  </article>
)

//@ts-ignore
export const TeamPage = teamPageComposed(TeamPageComponent)
