import React from 'react'
import { Route } from 'react-router-dom'
import { inject, observer } from 'mobx-react'
import { DragDropContext } from 'react-dnd'

import HTML5Backend from 'react-dnd-html5-backend'
import { compose } from 'recompose'

import { Login } from 'react-sport'

import { loginLexic } from 'config/lexics'

import { Home } from 'features/components/Home/Home'
import { Header } from 'features/components/Header/Header'
import { PlayerPage } from 'features/components/PlayerPage/PlayerPage'
import { TeamPage } from 'features/TeamPage/components/TeamPage'
import MatchPage from 'features/components/MatchPage/MatchPage'
import { TournamentPage } from 'features/TournamentPage'

import './App.scss'

type TProps = {
  auth: {
    isAuth: boolean,
  },
  location: {
    pathname: string,
  },
}

const App = ({ auth: { isAuth }, location: { pathname } }: TProps) => (
  <div className='App'>
    {isAuth && pathname !== '/' && <Header />}
    <Route exact path='/' component={Home} />
    <Route path='/login' render={() => <Login loginLexics={loginLexic} />} />
    <Route path='/players/:playerId' component={PlayerPage} />
    <Route path='/teams/:teamId' component={TeamPage} />
    <Route path='/matches/:matchId' component={MatchPage} />
    <Route path='/tournaments/:tournamentId' component={TournamentPage} />
    <a className='download-link download-link_hidden' id='download-link'>
      <span />
    </a>
  </div>
)

const AppComposed = compose(
  inject('auth'),
  DragDropContext(HTML5Backend),
  observer,
  //@ts-ignore
)(App)

export default AppComposed
