import React, { Fragment } from 'react'
import { observer } from 'mobx-react'

import {
  compose,
  withHandlers,
} from 'recompose'
import map from 'lodash/map'
import reduce from 'lodash/reduce'
import concat from 'lodash/concat'

import { getSummaryStatValue } from 'module/utils/helpers/getSummaryStatValue'

import { HeaderStatsItem } from 'features/HeaderStatsItem'

import {
  TComposeInner,
  TPropsOuter,
  TWithHandlersInner,
  TStat,
} from './types'

/**
 * Отображение блока со статистикой в шапке профиля
 */
const HeaderStatsListComponent = ({
  getEvents,
  params,
  stats,
}: TComposeInner) => (
  <Fragment>
    {map(
      params,
      ({
        lexic_id,
        param_id,
        percent,
      }) => (
        <HeaderStatsItem
          key={param_id}
          paramId={param_id}
          lexicId={lexic_id}
          percent={percent}
          events={getEvents(param_id)}
          statValue={getSummaryStatValue(param_id, stats, 'average_for_match')}
        />
      ),
    )}
  </Fragment>
)

const headerStatsListComposed = compose<TComposeInner, TPropsOuter>(
  withHandlers<TPropsOuter, TWithHandlersInner>({
    getEvents: ({ stats }) => param_id => (
      reduce(
        stats,
        (acc: Array<number>, curr: TStat) => {
          if (curr.f_param === param_id && curr.f_match_event_ids_json) {
            return concat(acc, curr.f_match_event_ids_json)
          }
          return acc
        }, [],
      )
    ),
  }),
  observer,
)

export const HeaderStatsList = headerStatsListComposed(HeaderStatsListComponent)
