
import React from 'react'
import { observer, inject } from 'mobx-react'

import { compose, lifecycle } from 'recompose'
import get from 'lodash/get'
import isString from 'lodash/isString'
import isEmpty from 'lodash/isEmpty'

import { PlayingField } from 'react-sport'

import { Markers } from 'features/components/common/map/Markers'
import { MapFieldControls } from 'features/MapControls/components'

import { FieldZones } from './FieldZones'

import {
  TProps,
  TPrevProps,
  TComposeInner,
  TComposeOuter,
} from './types/mapFieldTypes'

export const MapFieldComponent = ({ markers: { select, setRect } }: TProps) => (
  <div className='main-page__map-wrapper'>
    <MapFieldControls />
    <div className='map-block__map-field-outer'>
      <div className='map-block__map-field-inner'>
        <div id='map' className='map-block__map-field-zones' />
        <FieldZones />
        <div className='main-page__map-shots'>
          {select && (
            <PlayingField.SelectionField
              wrapperClass='selection_field'
              width={360}
              height={700}
              onSelect={rect => setRect(rect || {}, 360, 700, 313, 631)}
            />
          )}
          <Markers />
        </div>
      </div>
    </div>
  </div>
)

const mapFieldComposed = compose<TComposeOuter, TComposeInner>(
  inject(
    'markers',
    'matches',
    'selectedMatches',
    'lexics',
    'zones',
  ),
  lifecycle<TPrevProps, {}>({
    componentDidUpdate(prevProps) {
      const {
        lexics: { page },
        markers: { fetch },
        matchId,
        pageId,
        selectedMatches,
        tab,
        zones,
      } = this.props
      const currentMatches = page === 'match'
        ? [matchId]
        : get(selectedMatches, [page, pageId])

      if (prevProps.tab === tab || isEmpty(currentMatches)) return null

      if (!isString(currentMatches)) {
        fetch(pageId, currentMatches)
        zones.fetch(pageId, currentMatches)
      }
    },
  }),
  observer,
)

export const MapField = mapFieldComposed(MapFieldComponent)
