// @ts-ignore
import { observable, computed } from 'mobx'
import toString from 'lodash/toString'
import isEmpty from 'lodash/isEmpty'

import moment from 'moment'

/**
 * Стор для видеонарезок, наследуется от стора видеонарезок из спорта
 */
// @ts-ignore
// @ts-ignore
const VideoCuts = markers => superclass =>
  class extends superclass {
    //@ts-ignore
    @observable groups = {};

    //@ts-ignore
    @observable markers = markers;

    /** получаем список событий, соответствующий фильтрам **/
    //@ts-ignore
    @computed get events() {
      //@ts-ignore
      const checked = []

      //@ts-ignore
      this.buttons.forEach(({ checkedFilters }) => {
        if (!isEmpty(checkedFilters)) {
          //@ts-ignore
          checkedFilters.forEach(filter => checked.push(filter))
        }
      })

      //@ts-ignore
      return this.markers.data.filter(({ prop }) =>
        //@ts-ignore
        //@ts-ignore
        prop.some(property =>
          //@ts-ignore
          checked.some(({ event_prop, event_value }) => {
            const value = event_value.split(',')
            return (
              parseInt(event_prop, 10) === property[0]
              && value.includes(toString(property[2]))
            )
          })))
    }

    /** Получаем общую длительность видео **/
    //@ts-ignore
    @computed get duration() {
      return moment.duration(
        this.events.length * this.range.sumRange,
        'seconds',
      )
    }
  }

export default VideoCuts
