
import * as React from 'react'
import { compose, withHandlers } from 'recompose'

import { T9n } from 'react-sport'

// @ts-ignore
import { tableStatsTypes } from 'config/tables'

// Хок для получения кнопок для выпадашки с типами значений для ячеек таблицы
// тип *  - т.к. хок универсальный и мы не знаем, какие пропсы в компонентах
export const getDropdownBtnsHoc = compose<any, any>(
  withHandlers({
    getDropdownBtns: () => ({
      //@ts-ignore
      onClick,
      //@ts-ignore
      position,
      //@ts-ignore
      summaryType,
    }) => {
      const types = summaryType === 'total' ? tableStatsTypes : tableStatsTypes.reverse()

      //@ts-ignore
      return types.map(type => (
        <T9n key={type} t={type} onClick={onClick(type, position)} />
      ))
    },
  }),
)
