import includes from 'lodash/includes'
import size from 'lodash/size'
import reduce from 'lodash/reduce'

import { percentageParams } from 'config/teamPopupParams'
import { getDigits } from 'module/utils/helpers/getDigits'
import { getFilteredStatsByParamId } from 'module/utils/helpers/getFilteredStatsByParamId'

/**
 * Получение значения статистики в итоговой строке в таблице
 * @param {number} param_id - id фильтра по которому считаем статистику
 * @param {array} stats - статистика по всем параметрам
 * @param {string} type - тип итоговой строки, среднее значение или сумма всех значений в столбце
 */
export const getSummaryStatValue = (
  param_id: number,
  stats: Array<{
    f_match_event_ids_json: Array<number> | null,
    f_param: number,
    value: number,
  }> = [],
  type: string,
) => {
  const filteredStats = getFilteredStatsByParamId(stats, param_id)

  const filteredStatsCount = size(
    reduce(filteredStats, (acc, curr) => {
      //@ts-ignore

      if (acc[curr.f_match]) return acc
      //@ts-ignore

      acc[curr.f_match] = curr
      return acc
    }, {}),
  )

  if (!filteredStatsCount) return '—'
  //@ts-ignore

  const value = reduce(filteredStats, (prev, { value }) => prev + value, 0)

  if (includes(percentageParams, param_id)) {
    return Number((value / filteredStatsCount).toFixed(getDigits(param_id)))
  }

  const types: {
    [key: string]: () => number,
  } = {
    average_for_match: () => (
      Number((value / filteredStatsCount)
        .toFixed(getDigits(param_id)))
    ),
    total: () => Number(value.toFixed(getDigits(param_id))),
  }

  return types[type]() || '—'
}
