type TArgs = {
  getMatchInfo: () => {
    teamName: string,
    oppTeamName: string,
    teamScore: number,
    oppTeamScore: number,
    activeTeam: string,
  },
  //@ts-ignore
  activeTab: string | null,
  page: string,
  simpleName: string,
  matchesCount: number,
  matchName: string,
}

// Получение тайтлов для карты ударов
const getMapShootsTitle = ({
  activeTab,
  getMatchInfo,
  matchesCount,
  matchName,
  page,
  simpleName,
}: TArgs) => () => {
  // Для страницы матча
  const getMatchMapShootsTitle = () => {
    const {
      activeTeam,
      oppTeamName,
      oppTeamScore,
      teamName,
      teamScore,
    } = getMatchInfo()
    return {
      playlist_title: `${activeTeam}`,
      playlist_subtitle: `${teamName} ${teamScore}:${oppTeamScore} ${oppTeamName} - ${activeTab}`,
    }
  }

  // Для страницы команды или игрока
  const getTeamOrPlayerMapShootsTitle = () => ({
    playlist_title: `${simpleName}`,
    playlist_subtitle: `${matchesCount} ${matchName} - ${activeTab}`,
  })

  const pages = {
    team: getTeamOrPlayerMapShootsTitle,
    player: getTeamOrPlayerMapShootsTitle,
    match: getMatchMapShootsTitle,
  }
  // запускаем для каждой страницы свою функцию
  //@ts-ignore
  if (!pages[page]) return null
  //@ts-ignore
  return pages[page]()
}

export default getMapShootsTitle
