import find from 'lodash/find'
import size from 'lodash/size'
import every from 'lodash/every'
import includes from 'lodash/includes'
import some from 'lodash/some'

import {
  playerExtraCheckMarkers,
  teamExtraCheckMarkers,
} from 'config/markers'

type TKeyFiltration = {
  c_match_event_prop: Array<number>,
  c_match_event_prop2?: Array<number> | null,
  c_match_event_prop_value: Array<number>,
  c_match_event_prop_value2?: Array<number> | null,
  f_player?: number,
  filterId: number,
  prop: Array<Array<number>>,
}

export const doubleKeyFiltration = ({
  c_match_event_prop,
  c_match_event_prop_value,
  c_match_event_prop_value2,
  c_match_event_prop2,
  prop,
}: TKeyFiltration) => {
  /**
   * Находим маркеры по группе и ключам, из списка выбранных фильтров,
   * достаем пропсы и из списка маркеров достаем маркеры со схожими пропсами
   * c_match_event_prop - группа из фильтра
   * c_match_event_prop_value - значения из фильтра
   * propGroup - группа
   * propValue - значения
   */
  if (c_match_event_prop_value && c_match_event_prop_value2) {
    const property1IsTrue = find(
      prop,
      ([propGroup, , propValue]) =>
        includes(c_match_event_prop, propGroup)
        && includes(c_match_event_prop_value, propValue),
    )

    const property2IsTrue = find(
      prop,
      ([propGroup, , propValue]) =>
        includes(c_match_event_prop2, propGroup)
        && includes(c_match_event_prop_value2, propValue),
    )
    return property1IsTrue && property2IsTrue
  }

  const property1IsTrue = find(prop, ([propGroup]) =>
    includes(c_match_event_prop, propGroup))

  const property2IsTrue = find(prop, ([propGroup]) =>
    includes(c_match_event_prop2, propGroup))
  return property1IsTrue && property2IsTrue
}

export const singleKeyFiltration = ({
  c_match_event_prop,
  c_match_event_prop_value,
  f_player,
  filterId,
  prop,
}: TKeyFiltration) => {
  /**
   * Функция, которая осуществляет доп. фильтрацию маркера
   * Работает так: если маркер не нуждается в доп. фильтрации, то возвращаем true
   * Фильтруем от обратного - если наше условие выполнилось, то маркер НЕ показываем
   * (не оставляем маркеры, которые подошли, а удаляем, которые не подошли)
   * Это сделано для того, чтобы была возможность исключать маркеры (напр. с ошибкой)
   * и наоборот оставлять подходящие под условие в одной функции
   */
  const additionalCheck = () => {
    // Для команды у нас всегда 4 элемента в prop[0], а для игрока по 3 элемента
    const extraCheckMarkers = size(prop[0]) === 4
      ? teamExtraCheckMarkers
      : playerExtraCheckMarkers
    if (!extraCheckMarkers[filterId]) return true
    const { group, values } = extraCheckMarkers[filterId]
    return !every(
      group,
      groupItem =>
        find(
          prop,
          ([propGroup, , propValue]) => {
            // если есть массив values, то проверяем по нему, иначе проверка идет по айди игрока
            const valueCheck = values
              ? includes(values, propValue)
              : propValue !== f_player

            return groupItem === propGroup && valueCheck
          },
        ) || !find(prop, ([propGroup]) => propGroup === groupItem),
    )
  }

  if (c_match_event_prop_value) {
    return (
      !!find(
        prop,
        ([propGroup, , propValue]) =>
          c_match_event_prop.includes(propGroup)
          && c_match_event_prop_value.includes(propValue),
      ) && additionalCheck()
    )
  }

  /**
   * Находим маркеры только по группе
   * c_match_event_prop - группа из фильтра
   * propGroup - айди группы маркера
   */
  return (
    some(c_match_event_prop, value =>
      find(prop, ([propGroup]) => propGroup === value)) && additionalCheck()
  )
}
