import { inject, observer } from 'mobx-react'
import { compose, withHandlers } from 'recompose'
import some from 'lodash/some'
import cx from 'classnames'
import { lexic } from 'config/lexics/filtersById'

type TPopupHoc = {
  filters: {
    tempSelectedFilters: {[key: number]: {}},
  },
}
// Для попапа с основной общей логикой для попап окон в блоке фильтров
export const PopupHOC = (wrappedComponent: any) =>
  compose(
    inject('filters'),
    withHandlers<TPopupHoc, {}>({
      getFilterLexics: () => (id: number) => lexic[id] || id,
      getItemClassName: ({
        filters: { tempSelectedFilters },
      }) => (id: number) =>
        cx('popup-column__item', {
          'popup-column__item_checked': tempSelectedFilters[id],
        }),
      getTitleClassName: ({ filters: { tempSelectedFilters } }) => (
        (ids: Array<number>) => (
          cx('popup-column__title', {
            'popup-column__title_checked': some(
              ids,
              id => tempSelectedFilters[id],
            ),
          })
        )
      ),
    }),
    observer,
  )(wrappedComponent)
