import React, { Fragment } from 'react'
import { inject, observer } from 'mobx-react'

import {
  compose,
  lifecycle,
  withProps,
} from 'recompose'
import map from 'lodash/map'

import {
  TWithPropsInner,
  TComposeInner,
  TWithPropsOuter,
  TLifeCycle,
} from './types'
import { filterPopupByTab, filterPopupByTabForPlayer } from './config'

import { FilterItem } from './FilterItem'


const MapFiltersComponent = ({ filtersList }: TWithPropsInner) => (
  <Fragment>
    {map(
      filtersList,
      ({
        data,
        style,
        title,
        type,
      }) => (
      //@ts-ignore
        <FilterItem
          key={title}
          type={type}
          title={title}
          data={data}
          style={style}
        />
      ),
    )}
  </Fragment>
)

const mapFiltersComposed = compose<TComposeInner, {}>(
  inject(
    'tabs',
    'filters',
    'lexics',
  ),
  withProps<TWithPropsInner, TWithPropsOuter>(({
    lexics: { page },
    tabs: { active },
  }) => ({
    filtersList: page === 'player'
      ? filterPopupByTabForPlayer[active]
      : filterPopupByTab[active],
  })),
  lifecycle<TLifeCycle, {}>({
    componentDidMount() {
      const { fetch, loaded } = this.props.filters
      if (!loaded) {
        fetch()
      }
    },
  }),
  observer,
)

export const MapFilters = mapFiltersComposed(MapFiltersComponent)
