import { withStateHandlers } from 'recompose'
import isEmpty from 'lodash/isEmpty'

type TWithStateHandlers = {
  param: {
    param_id?: number,
  },
  sortAsc: boolean,
}

/**
 * Получение отсортированных полей в таблице
 * @param {number} param - id параметра
 * @param {boolean} sortAsc - порядок сортировки (по возрастанию)
 */
export const setSortedIdHOC = withStateHandlers(
  ({ param = {}, sortAsc = true }: TWithStateHandlers) => ({
    param,
    sortAsc,
  }),
  {
    changeActiveParam: ({ param, sortAsc }) => ({
      newParam,
      storeAction,
      table,
    }) => {
      const clickOnNewParam = param.param_id !== newParam.param_id
      const sortIsNeedReset = Boolean(!clickOnNewParam && sortAsc)
      const clickCases = {
        default: {
          param: {},
          sortAsc: true,
        },
        newParamClicked: {
          param: newParam,
          sortAsc: false,
        },
        simpleClick: {
          param: newParam,
          sortAsc: !sortAsc,
        },
      }

      let result = null
      // При клике на другой столбец счетчик обнуляется и выделяется новый столбец
      if (!isEmpty(param) && clickOnNewParam) {
        result = clickCases.newParamClicked
      }
      // При клике третий раз на одном и том же столбце, сбрасываем сортировку
      if (sortIsNeedReset) {
        result = clickCases.default
      } else {
        result = clickCases.simpleClick
      }

      storeAction({
        param: result.param,
        sortAsc: result.sortAsc,
        table,
      })

      return result
    },
  },
)
