import React from 'react'
import { Link } from 'react-router-dom'
import { inject, observer } from 'mobx-react'

import cx from 'classnames'

import { compose, withProps } from 'recompose'
import {
  cmToFt,
  getLongSuffix,
  kgToPounds,
} from 'module/utils/helpers'

import Avatar from 'features/components/common/Avatar'
import InfoItem from 'features/components/common/InfoItem'

import {
  TProps,
  TWithPropsOuter,
  TWithPropsInner,
} from './types'

const PlayerCardComponent = ({
  playerProfile: {
    isFetching,
    profile,
  },
  suffix,
}: TProps) => (
  <div
    className={cx(
      'player-card-left',
      { 'player-card-left_loader': isFetching },
    )}
  >
    <div className='player-column player-column-avatar'>
      <div className='player-card-avatar'>
        {/*
          //@ts-ignore */}
        {profile && <Avatar image={profile.photo} />}
      </div>
    </div>
    {profile && (
      <div className='player-column player-column-name'>
        <h1 className='player-card-title'>
          <span>{profile[`short_name_${suffix}`]}</span>
        </h1>
        <div className='player-card-team'>
          {profile.team.id && (
            <Link target='_blank' to={`/teams/${profile.team.id}`}>
              {profile.team[`name_${suffix}`]}
            </Link>
          )}
          {profile.team.id && profile.national_team.id && ', '}
          {profile.national_team.id && (
            <Link target='_blank' to={`/teams/${profile.national_team.id}`}>
              {profile.national_team[`name_${suffix}`]}
            </Link>
          )}
        </div>
        <div className='player-card-labels'>
          {profile.citizenship.short && (
            <span className='player-label tooltip-trigger'>
              <i
                className={`flag-icon flag-icon-24x16 flag-icon-${profile.citizenship.short.toLowerCase()}`}
              />
              <div className='hover-tooltip'>
                <span>{profile.citizenship[`name_${suffix}`]}</span>
              </div>
            </span>
          )}
          {profile.age && (
            <InfoItem
              variants={[profile.age, profile.birthday]}
              tooltips={['header_card_age', 'header_card_birth_data']}
              t={['stat_year', '']}
            />
          )}
          {profile.height && (
            <InfoItem
              variants={[profile.height, cmToFt(profile.height)]}
              tooltips={['tp_stat_height_sm', 'tp_stat_height_foot']}
              t={['stat_sm', '']}
            />
          )}
          {profile.weight && (
            <InfoItem
              variants={[profile.weight, kgToPounds(profile.weight)]}
              tooltips={['tp_stat_weght_kg', 'tp_stat_weght_lbs']}
              t={['stat_kg', 'stat_pound']}
            />
          )}
          {profile.active_hand && (
            <span className='player-label'>{profile.active_hand} </span>
          )}
          {profile.amplua.id && (
            <span className='player-label'>
              {profile.amplua[`name_${suffix}`]}
            </span>
          )}
        </div>
      </div>
    )}
  </div>
)

const playerCardComposed = compose<TProps, {}>(
  inject('playerProfile', 'lexics'),
  observer,
  withProps<TWithPropsInner, TWithPropsOuter>(
    ({ lexics: { lang } }) => ({ suffix: getLongSuffix(lang) }),
  ),
  observer,
)

export const PlayerCard = playerCardComposed(PlayerCardComponent)
