import React from 'react'
import { inject, observer } from 'mobx-react'

import { compose, withHandlers } from 'recompose'
import find from 'lodash/find'
import map from 'lodash/map'

import { T9n } from 'react-sport'

import { getLongSuffix } from 'module/utils/helpers'

import { lexic } from 'config/lexics/filtersById'

import {
  TProps,
  TWithHandlersOuter,
  TWithHandlersInner,
  TPlayer,
  TComposeOuter,
  TComposeInner,
} from './types'

const FilterSubMenuComponent = ({
  checkedInGroup,
  getFullName,
  handleUncheck,
}: TProps) => (
  <ul className='checked-list'>
    {map(checkedInGroup, ({ id, player_id }) => (
      <li className='checked-list__item' key={id || player_id}>
        {player_id ? (
          <span className='checked-list__item-name'>
            {getFullName(player_id)}
          </span>
        ) : (
          <T9n className='checked-list__item-name' t={lexic[Number(id)]} />
        )}
        <i
          className='checked-list__item-remove'
          onClick={() => handleUncheck(id || `${player_id}_player`)}
        />
      </li>
    ))}
  </ul>
)

const filterSubMenuComposed = compose<TComposeInner, TComposeOuter>(
  inject('lexics'),
  withHandlers<TWithHandlersOuter, TWithHandlersInner>({
    getFullName: ({
      checkedInGroup,
      lexics: { lang },
    }) => (playerId) => {
      const suffix = getLongSuffix(lang)
      const player: TPlayer = find(checkedInGroup, { player_id: playerId }) || {}
      const fullName = `${player[`firstname_${suffix}`]} ${
        player[`lastname_${suffix}`]
      }`
      return fullName
    },
  }),
  observer,
)

export const FilterSubMenu = filterSubMenuComposed(FilterSubMenuComponent)
