import React from 'react'

import {
  TableHeaderSettingsButton,
  T9n,
  DownloadLinksComponent,
  SimpleDropdown,
} from 'react-sport'

// @ts-ignore
import { EditTableName } from 'features/TableName/components/EditTableName'
// @ts-ignore
import { tableHeaderButtons } from 'module/utils/helpers'
// @ts-ignore
import { tableTypes } from 'config/tables'
import { playersTableHeaderComposed } from './composed'

type TProps = {
  deleteTable: () => void,
  getClassName: (avatarIsVisible: boolean) => string,
  handlePopupAction: () => void,
  handleNewPopupAction: () => void,
  links: Array<any>,
  moveTableUp: () => void,
  moveTableDown: () => void,
  resetTable: () => void,
  setType: (value: string, position: number) => string,
  table: {
    position: number,
    avatarIsVisible: boolean,
    summaryType: string,
  },
  showAvatarToggle: (position: number) => void,
  teamPlayers: {
    tables: any,
  },
  saveReport: () => void,
  getDropdownBtns: (
    valueType: string,
    setType: (value: string, position: number) => string,
    position: number,
  ) => Array<any>,
}

const PlayersTableHeaderComponent = ({
  deleteTable,
  getClassName,
  getDropdownBtns,
  handleNewPopupAction,
  handlePopupAction,
  links,
  moveTableDown,
  moveTableUp,
  resetTable,
  saveReport,
  setType,
  showAvatarToggle,
  table,
  table: { avatarIsVisible, position, summaryType },
  teamPlayers,
}: TProps) => (
  <div className='stats-table__header'>
    <div className='stats-table__header-left'>
      {/*
      //@ts-ignore */}
      <SimpleDropdown placeholder={<T9n t='average_for_match' />}>
        {/*
          //@ts-ignore */}
        {getDropdownBtns({
          summaryType,
          position,
          onClick: setType,
        })}
      </SimpleDropdown>
      <a
        className={getClassName(avatarIsVisible)}
        onClick={() => showAvatarToggle(position)}
      >
        <T9n t='show_photo' />
      </a>
    </div>
    <div className='stats-table__name'>
      <EditTableName
        table={table}
        tableType={tableTypes.teamPlayers}
        currentStore={teamPlayers}
      />
    </div>
    <div className='stats-table__actions'>
      {/*
      //@ts-ignore */}
      <DownloadLinksComponent links={links} reportHandle={saveReport} />
      {tableHeaderButtons(
        handleNewPopupAction,
        deleteTable,
        moveTableUp,
        moveTableDown,
        resetTable,
        teamPlayers.tables,
      ).map(({ action, buttonType, t }) => (
        <TableHeaderSettingsButton
          onClick={action}
          buttonType={buttonType}
          key={t}
        >
          <T9n t={t} />
        </TableHeaderSettingsButton>
      ))}
      <a className='stats-table__action-link' onClick={handlePopupAction}>
        <i className='icon-settings' />
      </a>
    </div>
  </div>
)

export const PlayersTableHeader = playersTableHeaderComposed(
  PlayersTableHeaderComponent,
)
