import get from 'lodash/get'

type TArgs = {
  getMatchInfo: () => {
    teamName: string,
    oppTeamName: string,
    teamScore: number,
    oppTeamScore: number,
  },
  matchInfo: any,
  page: string,
  suffix: string,
  longSuffix: string,
  paramName: string | number,
  stores: {
    //@ts-ignore
    playerProfile: { profile: {} },
  },
  playerInfo: any,
}

// Получение тайтлов для ячеек таблиц со статистикой по матчам
const getTableMatchesTitle = ({
  getMatchInfo,
  longSuffix,
  matchInfo,
  page,
  paramName,
  playerInfo,
  stores,
  suffix,
}: TArgs) => () => {
  const oppTeamName: string = get(matchInfo, `name_opponent_${suffix}`, '')
  const teamName: string = get(matchInfo, `name_${suffix}`, '')
    || get(matchInfo, `name_${longSuffix}`, '')

  // Матчи с профиля команды
  const getTeamTableMatchesTitle = () => ({
    playlist_title: `${teamName}`,
    playlist_subtitle: `vs ${oppTeamName} - ${paramName}`,
    title: '',
  })

  // Матчи с профиля игрока
  const getPlayerTableMatchesTitle = () => {
    const { playerProfile: { profile } } = stores
    const firstName: string = get(profile, `firstname_${longSuffix}`)
    const lastName: string = get(profile, `lastname_${longSuffix}`)
    const playerName = `${firstName} ${lastName}`

    return {
      playlist_title: `${playerName} - ${teamName}`,
      playlist_subtitle: `vs ${oppTeamName} - ${paramName}`,
      title: '',
    }
  }

  // Таблица со статистикой с профиля матча
  const getStatsTableMatchesTitle = () => {
    const { oppTeamName, teamName } = getMatchInfo()
    return {
      playlist_title: `${playerInfo} - ${teamName}`,
      playlist_subtitle: `vs ${oppTeamName} - ${paramName}`,
      title: '',
    }
  }

  const pages = {
    team: getTeamTableMatchesTitle,
    player: getPlayerTableMatchesTitle,
    match: getStatsTableMatchesTitle,
  }
  //@ts-ignore
  if (!pages[page]) return null
  //@ts-ignore
  return pages[page]()
}

export default getTableMatchesTitle
