import React from 'react'
import { inject, observer } from 'mobx-react'

import { withHandlers, compose } from 'recompose'
import cx from 'classnames'
import toString from 'lodash/toString'

import { T9n } from 'react-sport'

import { getLongSuffix } from 'module/utils/helpers'

import { PlayerName } from 'features/components/common/map/PlayerName'

import {
  TComposeInner,
  TPropsOuter,
  TWithHandlersOuterEventToEpisode,
  TWithHandlersInnerEventToEpisode,
  TWithHandlersOuterHandleShootClick,
  TWithHandlersInnerHandleShootClick,
} from './types'

const GAME_PART_LEXIC = 8684

const ToolTipComponent = ({
  handleShootClick,
  left,
  lexics: { lang },
  point,
  point: {
    half,
    opponentTeam,
    result_lexic,
    score1,
    score2,
  },
  type,
  x,
  y,
}: TComposeInner) => (
  <div
    className={cx('shot', {
      [`shot_${type}`]: type,
      left,
    })}
    style={{ left: `${x}%`, top: `${y}%` }}
    onClick={handleShootClick(point)}
  >
    <div className='shot__tooltip'>
      <span className='shot__result'>
        <T9n t={toString(result_lexic)} />
      </span>
      {/*
        //@ts-ignore */}
      <PlayerName point={point} />
      <span className='shot__opponent-team'>
        @ {opponentTeam[`name_${getLongSuffix(lang)}`]}
      </span>
      <span className='shot__period-info'>
        <span className='shot__period-num'>
          {half} <T9n t={toString(GAME_PART_LEXIC)} />
        </span>
        <span className='shot__period-score'>{` ${score1} - ${score2}`}</span>
      </span>
    </div>
  </div>
)

const toolTipComposed = compose<TComposeInner, TPropsOuter>(
  inject(
    'videoPlayer',
    'lexics',
    'shootsRange',
  ),
  withHandlers<TWithHandlersOuterEventToEpisode, TWithHandlersInnerEventToEpisode>({
    eventToEpisode: ({
      lexics: {
        getLexics,
      },
    }) => ({
      f_match,
      half,
      id,
      result_lexic,
      second,
    }) => {
      const set = `${half} ${getLexics(GAME_PART_LEXIC)}`

      return {
        action: {
          name: getLexics(result_lexic),
          set,
        },
        eventId: Number(id),
        half,
        matchId: f_match,
        period: set,
        second: Math.round(second / 1000),
      }
    },
  }),
  withHandlers<TWithHandlersOuterHandleShootClick, TWithHandlersInnerHandleShootClick>({
    handleShootClick: ({
      eventToEpisode,
      videoPlayer,
    }) => event => () => {
      const data = [eventToEpisode(event)]
      videoPlayer.fetchVideo(data)
      videoPlayer.isOpened = true
    },
  }),
  observer,
)

export const ToolTip = toolTipComposed(ToolTipComponent)
