import React, { Fragment } from 'react'

import includes from 'lodash/includes'

import { Table } from './Table'
import { tableComposed } from './tableComposed'

import { TComposedInner } from './types'

// Основной блок таблицы со статистикой для страницы матча
export const TableMatchWrapper = tableComposed(
  ({
    activeId,
    activeTeams,
    currentStore: { tableStats },
    matchProfile: {
      data: { team1_id, team2_id },
    },
    pageId,
    param,
    setSortedId,
    sortAsc,
    table,
    teamsPlayersStats,
  }: TComposedInner) => (
    <div className='stats-table__wrapper'>
      <table className='stats-table__table'>
        <Fragment>
          {includes(activeTeams, 'showHomeTeam') && (
            <Table
              rows={teamsPlayersStats[team1_id]}
              tableStats={tableStats}
              table={table}
              setSortedId={setSortedId}
              pageId={pageId}
              sortAsc={sortAsc}
              activeId={activeId}
              param={param}
            />
          )}
          {includes(activeTeams, 'showOppTeam') && (
            <Table
              rows={teamsPlayersStats[team2_id]}
              tableStats={tableStats}
              table={table}
              setSortedId={setSortedId}
              pageId={pageId}
              sortAsc={sortAsc}
              activeId={activeId}
              param={param}
            />
          )}
        </Fragment>
      </table>
    </div>
  ),
)
