import React, { Fragment } from 'react'
import { inject, observer } from 'mobx-react'

import {
  lifecycle,
  compose,
  withProps,
  withHandlers,
} from 'recompose'

import map from 'lodash/map'
import head from 'lodash/head'

import { DropDownSimple } from 'features/DropDownSimple'
import { primarySportColor } from 'config/common'
import Loader from 'features/components/common/loader/Loader'

import './tournamentSeasonsSelect.scss'

type TProps = {
  tournamentSeasonSelect: {
    isLoaded: boolean,
    //@ts-ignore
    seasonsList: Array<{}>,
    selectedSeason?: {
      //@ts-ignore
      season_name?: string | null,
    },
  },
  settings: any,
  tournamentId: number,
  onSeasonSelect: (tournamentId: number, season_id: number) => () => void,
}

export const SeasonSelect = ({
  onSeasonSelect,
  settings,
  tournamentId,
  tournamentSeasonSelect: {
    isLoaded,
    seasonsList,
  },
}: TProps) => (
  <Fragment>
    <div className='season-select-container'>
      {isLoaded
        ? (
      //@ts-ignore
          <DropDownSimple
            //@ts-ignore
            placeholder={head(seasonsList).season_name}
            settings={settings}
          >
            {map(
              seasonsList,
              //@ts-ignore
              ({ season_id, season_name }) => (
                <div
                  key={season_id}
                  onClick={onSeasonSelect(tournamentId, season_id)}
                >
                  {season_name}
                </div>
              ),
            )}
          </DropDownSimple>
        )
        : <Loader />
      }
    </div>
  </Fragment>
)

const SeasonSelectComposed = compose(
  inject('lexics', 'tournamentSeasonSelect', 'tournamentHeaderStats'),
  //@ts-ignore
  withHandlers({
    onSeasonSelect: ({ tournamentSeasonSelect: { changeActiveSeason } }) =>
      //@ts-ignore
      (tournamentId, season_id) => () => changeActiveSeason(tournamentId, season_id),
  }),
  withProps(() => ({
    settings: {
      primaryColor: primarySportColor,
    },
  })),
  lifecycle({
    componentDidMount() {
      const {
        //@ts-ignore
        tournamentId,
        //@ts-ignore
        tournamentSeasonSelect: { fetchSeasons },
      } = this.props
      fetchSeasons(tournamentId)
    },
  }),
  observer,
  //@ts-ignore
)(SeasonSelect)

export default SeasonSelectComposed
