import { observable, computed } from 'mobx'
import reduce from 'lodash/reduce'
// @ts-ignore
import BasicStore from 'features/stores/BasicStore'
import FetchMapShoot from './FetchMapShoot'

class MapShootStore extends FetchMapShoot(BasicStore) {
  @observable error = null;

  @observable loaded = false;

  @observable data = {};

  @observable basicData = [];

  @observable basicLoaded = false;

  @computed get playersIds() {
    //@ts-ignore
    return reduce(
      this.data,
      (acc, { prop_player }) => {
        //@ts-ignore
        //@ts-ignore
        //@ts-ignore
        prop_player.forEach(({ player_id }) => acc.push(`${player_id}_player`))
        return acc
      },
      [],
    )
  }
}

export default MapShootStore
