
import { types } from 'mobx-state-tree'
import SelectedMatchesActions from './SelectedMatchesActions'

export const SelectedMatches = types.model('SelectedMatches', {
  opened: types.boolean,
  pageId: types.string,
  player: types.frozen({}),
  team: types.frozen({}),
  tempSelected: types.array(types.number),
  selectedPeriod: types.maybeNull(types.union(types.string, types.number)),
})

const SelectedMatchesStore = types.compose(
  SelectedMatches,
  SelectedMatchesActions,
)

export default SelectedMatchesStore
