import React from 'react'
import { inject, observer } from 'mobx-react'

import map from 'lodash/map'
import orderBy from 'lodash/orderBy'
import uniqBy from 'lodash/uniqBy'
import {
  withHandlers,
  compose,
  withState,
} from 'recompose'
import cx from 'classnames'

import { VideoLink } from 'react-sport'

import {
  TComposeInner,
  TComposeOuter,
  TWithState,
  TWithHandlersOuter,
  TWithHandlersInner,
} from './types'

const PlayVideoBtnComponent = ({
  getClassName,
  getRange,
  getShoots,
  handleVideoExternal,
  mapFieldFetching,
}: TComposeInner) => (
  <div className={getClassName()}>
    {/*
    //@ts-ignore */}
    <VideoLink
      shootsRange={getRange()}
      handleVideoExternal={handleVideoExternal}
      externalFetching={mapFieldFetching}
      shoots={getShoots()}
      store='shoots'
    />
  </div>
)

const playVideoBtnComposed = compose<TComposeInner, TComposeOuter>(
  inject(
    'videoLink',
    'lexics',
    'shootsRange',
    'markers',
    'selectedMatches',
  ),
  withState<TWithState, {}>(
    'mapFieldFetching',
    'toggleMapFieldFetching',
    false,
  ),
  withHandlers<TWithHandlersOuter, TWithHandlersInner>({
    getClassName: ({ markers: { loaded } }) => () =>
      cx('video__btn-container', { loading: !loaded }),

    getRange: ({
      shootsRange: {
        after,
        allPossession,
        before,
      },
    }) => () => (allPossession ? 0 : Number(before + after)),

    getShoots: ({
      markers: {
        list,
        loaded,
      },
    }) => () => ({
      activeData: uniqBy(list, 'id'),
      isFetching: !loaded,
    }),

    handleVideoExternal: ({
      lexics: { lang },
      shootsRange,
      tab,
      toggleMapFieldFetching,
      videoLink,
    }) => (shoots) => {
      toggleMapFieldFetching(true)
      const shots = map(orderBy(shoots, 'match_date', 'asc'), 'id')
      videoLink.request({
        events: shots,
        lang,
        range: shootsRange,
        tab,
        type: 'mapShoots',
      }).finally(() => toggleMapFieldFetching(false))
    },
  }),
  observer,
)

export const PlayVideoBtn = playVideoBtnComposed(PlayVideoBtnComponent)
