import React, { Fragment } from 'react'
import map from 'lodash/map'
import { T9n } from 'react-sport'
import { PopupHOC } from '../PopupHOC'
import { TPropsCommon } from '../types'

type TProps = TPropsCommon & {
  data: Array<number>,
}

// Обычный попап с фильтрами в столбик
const PopupSimpleComponent = ({
  data,
  filters: { handleTempCheck },
  getFilterLexics,
  getItemClassName,
  toggleOpenClose,
}: TProps) => (
  <Fragment>
    <div className='popup-column__wrapper'>
      <div className='popup-column__container'>
        <ul className='popup-column__list'>
          {map(data, id => (
            <li
              className={getItemClassName(id)}
              key={id}
              onClick={() => handleTempCheck(id)}
            >
              <T9n t={getFilterLexics(id)} />
            </li>
          ))}
        </ul>
      </div>
    </div>
    <button className='map-block__popup-button' onClick={toggleOpenClose}>
      OK
    </button>
  </Fragment>
)

export const PopupSimple = PopupHOC(PopupSimpleComponent)
