import React from 'react'
import PropTypes from 'prop-types'
import toString from 'lodash/toString'
import isEmpty from 'lodash/isEmpty'

import cx from 'classnames'
import { observer, inject } from 'mobx-react'
import { T9n } from 'react-sport'
import { compose, withHandlers, withState } from 'recompose'

import FilterPopup from './FilterPopup'
import Checked from './Checked'

// @ts-ignore
const FilterElement = (props) => {
  const {
    button: { lexical_formula },
    button,
    getClassName,
    handleClick,
    isOpened,
    onOutsideClick,
  } = props
  return (
    <li className={getClassName()}>
      <T9n
        t={toString(lexical_formula)}
        className='title'
        onClick={() => handleClick()}
      />
      <Checked button={button} />
      {isOpened && (
        <FilterPopup
          clickOutside={() => onOutsideClick(!isOpened)}
          {...props}
        />
      )}
    </li>
  )
}

const FilterElementComposed = compose(
  inject('playersVS', 'teamsVS'),
  withState('isOpened', 'onOutsideClick', false),
  //@ts-ignore
  withHandlers({
    getClassName: ({ button: { checkedFilters } }) => () =>
      cx('episodes-table-button', { active: !isEmpty(checkedFilters) }),
    handleClick: ({ isOpened, onOutsideClick }) => () =>
      onOutsideClick(!isOpened),
  }),
  observer,
)(FilterElement)

FilterElement.propTypes = {
  isOpened: PropTypes.bool,
  onOutsideClick: PropTypes.func.isRequired,
  getClassName: PropTypes.func.isRequired,
  handleClick: PropTypes.func.isRequired,
  button: PropTypes.shape({
    lexical_formula: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
    checkedFilters: PropTypes.array,
    checked: PropTypes.bool,
  }),
}

export default FilterElementComposed
