import { types, getRoot } from 'mobx-state-tree'
import { values } from 'mobx'

const TournamentSeasonSelectActions = types
  .model({})
  .volatile(() => ({
    selectedSeason: {},
    opened: false,
  }))
  .actions(self => ({
    changeActiveSeason: (tournamentId: number, id: number) => {
      //@ts-ignore
      self.selectedSeason = self.seasons.get(id)
      getRoot(self).tournamentMatches.fetchMatchesInfo(tournamentId, id)
    },
  }))
  .views(self => ({
    get seasonsList() {
      //@ts-ignore
      return values(self.seasons)
    },
  }))

export default TournamentSeasonSelectActions
