import React, { Fragment } from 'react'
import { inject } from 'mobx-react'

import { compose, withHandlers } from 'recompose'

import map from 'lodash/map'
import find from 'lodash/find'
import get from 'lodash/get'

import { TableUI } from 'react-sport'

// @ts-ignore
import { emptyStatToZero } from 'module/utils/helpers/emptyStatToZero'
import Loader from 'features/components/common/loader/Loader'
// @ts-ignore
import { THead, TBody } from 'features/Table'

import { TParam } from 'features/Table/models/tableModel'
import { Tr } from './Tr'

type TProps = {
  pageId: string,
  sortAsc: boolean,
  activeId: string,
  table: {
    selectedParams: Array<TParam>,
  },
  setSortedId: () => {},
  currentStore: {
    isLoaded: boolean,
  },
  param?: TParam,
  getPlayersList: () => Array<{}>,
};

// Основной блок таблицы со статистикой для страницы турнира
const TournamentPlayersComponent = ({
  activeId,
  currentStore: { isLoaded },
  getPlayersList,
  pageId,
  setSortedId,
  sortAsc,
  table,
}: TProps) => (
  <Fragment>
    <div className='stats-table__wrapper'>
      <table className='stats-table__table'>
        <TableUI.Thead>
          {/*
              // @ts-ignore */}
          <THead table={table} setSortedId={setSortedId} />
        </TableUI.Thead>
        {isLoaded ? (
          <Fragment>
            {/*
                //@ts-ignore */}
            <TBody
              selectedParams={table.selectedParams}
              rows={getPlayersList()}
              pageId={pageId}
              sortAsc={sortAsc}
              activeId={activeId}
              Tr={Tr}
            />
          </Fragment>
        ) : (
          <Loader />
        )}
      </table>
    </div>
  </Fragment>
)

const tournamentPlayersComposed = compose(
  inject('tournamentPlayersTableStore'),
  //@ts-ignore
  withHandlers({
    /**
     * Получение списка матчей, отсортированного по значению выбранного параметра
     * @param {boolean} sortAsc - флаг для сортировки (по убыванию или по возрастанию)
     */
    getPlayersList: ({
      param,
      tournamentPlayersTableStore: { players },
    }) => () =>
      map(players, (player) => {
        const sortedValue = get(
          find(player.params, { p: param.param_id }),
          'v',
          0,
        )
        return {
          ...player,
          param,
          sortBy: emptyStatToZero(sortedValue),
        }
      }),
  }),
)

// @ts-ignore
export const TournamentPlayers = tournamentPlayersComposed(
  //@ts-ignore
  TournamentPlayersComponent,
)
