import { flow } from 'mobx-state-tree'

import map from 'lodash/map'
import isEmpty from 'lodash/isEmpty'

import { callApi } from 'react-sport'

import { filterDBStats } from 'module/utils/helpers'

import { TeamPageMatchesActions } from './actions'

import { TParamExtended } from './model'

type TConfig = {
  body: string,
  method: string,
}

type TFetchTeamMatches = {
  matches: Array<number>,
  teamId: Array<number>,
}

export const TeamPageMatchesApiActions = TeamPageMatchesActions
  .actions(self => ({
    _onError(error: Error) {
      self.setError(error)
      self.setIsLoaded(false)
    },

    _onSuccessStats(json: { data: Array<TParamExtended> }) {
      const teamMatches = JSON.parse(window.localStorage.getItem('teamMatches') || '{}')

      self.setTableStats(filterDBStats(json.data))

      if (!isEmpty(teamMatches)) {
        self.setTables(teamMatches)
      } else {
        self.saveTables()
      }
      self.setError(null)
      self.setIsLoaded(true)
    },
  }))
  .actions(self => ({
    /** Запрос для получения статистики по параметрам
      * @param {array} matches - список матчей
      * @param {array} teamId - массив айди команд
      */
    fetchTableStats(matches: Array<number>, teamId: Array<number | string>) {
      const config: TConfig = {
        body: JSON.stringify({
          params: {
            _v_match_id: matches,
            _v_param_id: map(self.totalParams, 'param_id'),
            _v_team_id: teamId,
          },
          proc: 'ask_team_param_view',
        }),
        method: 'POST',
      }
      return callApi({
        config,
        onError: self._onError,
        onRequest: () => {
          self.setIsLoaded(false)
          self.setError(null)
        },
        onSuccess: self._onSuccessStats,
        url: self.url,
      })
    },
  }))
  .actions(self => ({
    fetchTeamMatches: flow(
      function* fetchTeamMatches({
        matches,
        teamId,
      }: TFetchTeamMatches) {
        try {
          // Делаем запрос для получения списка всех параметров для шестеренки
          yield self.getGearParamsList(0)
          // Делаем запрос для получения значений для команд
          yield self.fetchTableStats(matches, teamId)
        } catch (error) {
          self.setError(error)
        }
      },
    ),
  }))
