import { types } from 'mobx-state-tree'
import { toJS } from 'mobx'
import reduce from 'lodash/reduce'
import filter from 'lodash/filter'
import sortBy from 'lodash/sortBy'

import { callApi } from 'react-sport'

import routes from 'config/routes'

const url: string = `${routes.API_ROOT}/data`

export const tableApiActions = types.model({}).actions(self => ({
  getGearParamsList(ampluaId: number) {
    //@ts-ignore
    const config: TConfig = {
      method: 'POST',
      body: JSON.stringify({
        proc: 'ask_gear_params',
        params: {
          _v_amplua_id: ampluaId,
        },
      }),
    }

    return callApi({
      url,
      config,
      onRequest: () => {
        //@ts-ignore
        self.error = null
      },
      //@ts-ignore
      onSuccess: self._onSuccessGearParams,
      //@ts-ignore
      onError: self._onError,
    })
  },

  //@ts-ignore
  _onSuccessGearParams({ data }) {
    //@ts-ignore
    self.totalParams = data
    //@ts-ignore
    self.fetchGroups()
  },

  fetchGroups() {
    //@ts-ignore
    const config: TConfig = {
      method: 'POST',
      body: JSON.stringify({
        proc: 'ask_gear_group_view',
      }),
    }
    return callApi({
      url,
      config,
      onRequest: () => {},
      //@ts-ignore
      onSuccess: self._onSuccessGroups,
      //@ts-ignore
      onError: self._onError,
    })
  },

  _onSuccessGroups(json: {
    data: Array<{
      group_id: number,
      group_lexical_formula: number,
      group_name: string,
    }>,
  }) {
    //@ts-ignore
    self.gearParams = reduce(
      json.data,
      //@ts-ignore
      (prev, curr) => {
        const current = {
          params: sortBy(
            filter(
              //@ts-ignore
              toJS(self.totalParams),
              ({ gear_group_id }) => gear_group_id === curr.group_id,
            ),
            'sort_order',
          ),
          groupName: String(curr.group_lexical_formula),
        }
        return [...prev, current]
      },
      [],
    )
  },

  _onError(error: any) {
    //@ts-ignore
    self.error = error
  },
}))
