
import React from 'react'
import cx from 'classnames'

import { T9n } from 'react-sport'

import './styles.scss'

type TProps = {
  isFetching: boolean,
  onClick: () => void,
};

export const ShowMoreBtn = ({ isFetching, onClick }: TProps) => (
  <div
    className={cx('show-more-btn', { 'show-more-btn_loading': isFetching })}
    onClick={onClick}
  >
    {!isFetching && <T9n t='show_more' />}
  </div>
)
