import React from 'react'
import { observer, inject } from 'mobx-react'

import { compose, withProps } from 'recompose'
import get from 'lodash/get'
import isArray from 'lodash/isArray'
import cx from 'classnames'

import { MatchCheckBox } from 'features/components/common/MatchCheckBox'

import { InfinityLoad } from 'module/utils/InfinityLoad'
import { dateWithYear } from 'module/utils/helpers'

import { TLexics } from 'react-sport'

export const MatchSelectListComponent = ({
  lexics: { lang },
  matches: { isFetching },
  matchesList,
  selected,
  selectItem,
}: TComposeInner) => (
  <ul
    className={cx(
      'player-card-stats-dropdown-list',
      'player-card-stats-dropdown_dates',
      'player-card-stats-dropdown_checkboxes',
    )}
  >
    {matchesList.map(m => (
      <li
        key={m.f_match}
        className={cx('', {
          active: isArray(selected) && selected.includes(m.f_match),
        })}
        onClick={e => selectItem(e, m.f_match)}
      >
        <MatchCheckBox lang={lang} match={m} />
        <span className='date'>{dateWithYear(m.match_date)}</span>
      </li>
    ))}
    {isFetching && <InfinityLoad />}
  </ul>
)

type TComposeInner = TPropsOuter & TWithPropsInner & {
  lexics: TLexics,
  matches: {
    isFetching: boolean,
    list: {
      [key: string]: {},
    },
  },
}

type TPropsOuter = {
  pageId: string,
  selectItem: (e: React.SyntheticEvent<EventTarget>, matchId: number) => void,
  selected: string | Array<number>,
}

type TWithPropsOuter = {
  matches: {
    list: {
      [key: string]: {},
    },
  },
  pageId: string,
}

type TWithPropsInner = {
  matchesList: Array<{
    [key: string]: string | number,
    f_match: number,
    home_match: string,
    match_date: string,
    opponent_score: number,
    score: number,
  }>,
}

const matchSelectListComposed = compose<TComposeInner, TPropsOuter>(
  inject('matches', 'lexics'),
  observer,
  withProps<TWithPropsInner, TWithPropsOuter>(({
    matches: { list },
    pageId,
  }) => ({
    matchesList: get(list[pageId], 'matches', []),
  })),
)

export const MatchSelectList = matchSelectListComposed(MatchSelectListComponent)
