import { inject, observer } from 'mobx-react'
import { compose, withProps } from 'recompose'

import { getLongSuffix } from 'module/utils/helpers'

import { clickableClassNameHOC } from 'hocs/getClickableTdClassNameHoc'

import {
  TPropsInner,
  TPropsOuter,
  TWithPropsInner,
  TWithPropsOuter,
} from './types'

export const TrMatchComposed = compose<TPropsInner, TPropsOuter>(
  inject('videoLink', 'lexics'),
  observer,
  withProps<TWithPropsInner, TWithPropsOuter>(({
    lexics: { lang },
    rowInfo,
  }) => {
    const suffix = getLongSuffix(lang)
    const firstName = rowInfo[`firstname_${suffix}`]
    const lastName = rowInfo[`lastname_${suffix}`]

    return {
      playerName: `${firstName} ${lastName}`,
    }
  }),
  clickableClassNameHOC,
  observer,
)
