
import { types, flow } from 'mobx-state-tree'

import get from 'lodash/get'
import reduce from 'lodash/reduce'
import find from 'lodash/find'
import pick from 'lodash/pick'
import filter from 'lodash/filter'

import { callApi } from 'react-sport'
// @ts-ignore
import routes from 'config/routes'

type TConfig = {
  method: string,
  body: string,
};

type TFetch = {
  lang: string,
  matchId: string,
  params: Array<number>,
};

const url: string = `${routes.API_ROOT}/data`

export const PlayersCompareApiActions = types.model({}).actions(self => ({
  // Запрос при первом рендере компонента.
  basicFetch: flow(function* basicFetch({ lang, matchId, params }: TFetch) {
    try {
      // Запрашиваем список всех игроков для обеих команд
      //@ts-ignore
      yield self.fetchPlayers(matchId)

      // Предвыбираем по умолчанию первых игроков в каждой команде
      //@ts-ignore
      const defaultPlayers = reduce(
        //@ts-ignore
        self.playerList,
        (acc, curr) => {
          if (!find(acc, { f_team: curr.f_team })) {
            //@ts-ignore
            acc.push(curr)
          }
          return acc
        },
        [],
      )
      //@ts-ignore
      self.updateSelectedPlayers({
        lang,
        matchId,
        players: defaultPlayers,
      })

      // Запрашиваем данные по выбранным игрокам
      //@ts-ignore
      yield self.fetch({
        lang,
        matchId,
        params,
      })

      // Запрашиваем список всех параметров для шестеренки и групп
      //@ts-ignore
      yield self.getGearParamsList(1)
      // Ставим по умолчанию выбранные параметры (чтобы при открытии шестеренки они были выбраны)
      //@ts-ignore
      self.setDefaultSelectedParams()
    } catch (error) {
      //@ts-ignore
      self._onError(error)
    }
  }),

  fetch({ lang, matchId, params }: TFetch) {
    const config: TConfig = {
      method: 'POST',
      body: JSON.stringify({
        proc: 'scout_players_all_stat',
        params: {
          _p_match_id: +matchId,
          //@ts-ignore
          _p_player_arr: Object.values(self.selectedPlayers),
          _p_param_arr: params,
          _p_lang_id: Number(lang !== 'ru'),
        },
      }),
    }
    return callApi({
      url,
      config,
      //@ts-ignore
      onRequest: self._onRequest,
      //@ts-ignore
      onSuccess: self._onSuccess,
      //@ts-ignore
      onError: self._onError,
    })
  },

  _onRequest() {
    //@ts-ignore
    self.stats = []
    //@ts-ignore
    self.playersInfo = null
    //@ts-ignore
    self.isLoaded = false
  },

  _onSuccess(json: { data: Array<{}> } = { data: [] }) {
    const players = get(json.data, '[0].scout_players_all_stat') || []
    //@ts-ignore
    self.stats = get(
      //@ts-ignore
      find(players, 'params_info', {}),
      'params_info',
      [],
    )
    //@ts-ignore
    //@ts-ignore
    //@ts-ignore
    self.playersInfo = players.reduce((acc, current) => {
      acc[current.f_player] = pick(current, [
        'f_player',
        'f_team',
        'params',
        'photo_player',
        'photo_team',
      ])
      return acc
    }, {})
    //@ts-ignore
    self.error = null
    //@ts-ignore
    self.isLoaded = true
  },

  fetchPlayers(matchId: number | string) {
    const url: string = `${routes.API_ROOT}/data`
    const config: TConfig = {
      method: 'POST',
      body: JSON.stringify({
        proc: 'ask_players_list_match',
        params: {
          _v_match_id: matchId,
          _v_played: true,
        },
      }),
    }
    return callApi({
      url,
      config,
      onRequest: () => {},
      //@ts-ignore
      onSuccess: self._onSuccessPlayers,
      //@ts-ignore
      onError: self._onError,
    })
  },

  //@ts-ignore
  _onSuccessPlayers(json: { data?: Array<any> | null } = []) {
    //@ts-ignore
    self.playerList = filter(json.data, 'played')
  },

  _onError(error: any) {
    //@ts-ignore
    self.isLoaded = false
    //@ts-ignore
    self.error = error
  },
}))
