import * as React from 'react'
import {
  compose,
  withHandlers,
} from 'recompose'

import { HeaderWithOpp } from './HeaderWithOpp'
import { HeaderWithAvatar } from './HeaderWithAvatar'
import { HeaderWithTeams } from './HeaderWithTeams'

type TProps = {
  //@ts-ignore
  tableType: string | null,
  currentStore: {},
}

type TPropsComposed = {
  //@ts-ignore
  getTable: (type: string | null) => React.Node | null,
}

const TableHeaderComponent = ({
  getTable,
  tableType,
}: TProps & TPropsComposed) => getTable(tableType)

const tableHeaderComposed = compose<TProps & TPropsComposed, TProps>(
  withHandlers({
    //@ts-ignore
    getTable: props => (tableType) => {
      const tables = {
        //@ts-ignore
        teamMatches: <HeaderWithOpp {...props} />,
        //@ts-ignore

        matchPlayersTable: <HeaderWithTeams {...props} />,
        //@ts-ignore
        tournamentMatches: <HeaderWithOpp {...props} />,
        //@ts-ignore
        tournamentPlayers: <HeaderWithAvatar {...props} />,
      }
      //@ts-ignore
      return tables[tableType] || null
    },
  }),
)

export const TableHeader = tableHeaderComposed(TableHeaderComponent)
