import React from 'react'
import { Switch, Route } from 'react-router-dom'

import { tableTypes } from 'config/tables'

import { Tables } from 'features/Table'
import { StatsTabs } from 'features/components/common/stats-tabs/StatsTabs'
import VideoCuts from 'features/components/common/video-cuts/VideoCuts'
import { PlayerField } from 'features/components/common/player/PlayerField'

import { mainContentComposed } from './composed'


export const MainContentComponent = ({
  //@ts-ignore
  lexics: { page },
  //@ts-ignore
  match,
  //@ts-ignore
  matchId,
  matchTable: {
    //@ts-ignore
    isLoaded,
    //@ts-ignore
    tables,
  },
  //@ts-ignore
  pageId,
}) => (
  <section className='main-page__details'>
    {/*
      //@ts-ignore */}
    <PlayerField pageId={pageId} matchId={matchId} />
    <div className='main-page__responsive'>
      <div className='main-page__content'>
        <div className='main-page__content-header'>
          <StatsTabs match={match} isStats />
        </div>
        <Switch>
          <Route
            path='/matches/:matchId/'
            exact
            render={() =>
              (isLoaded ? (
                <Tables
                  tableType={tableTypes.matchPlayersTable}
                  tables={tables}
                  pageId={matchId}
                />
              ) : (
                <div className='loading' />
              ))
            }
          />
          <Route
            path='/matches/:matchId/video'
            render={props => (
              //@ts-ignore
              <VideoCuts
                page='match'
                pageType='teams'
                playerId={pageId}
                {...props}
              />
            )}
          />
        </Switch>
      </div>
    </div>
  </section>
)

// @ts-ignore
export const MainContent = mainContentComposed(MainContentComponent)
