import React from 'react'
import { inject, observer } from 'mobx-react'

import { compose, withProps } from 'recompose'
import cx from 'classnames'

import { T9n } from 'react-sport'

import { beta } from 'config/routes'
import { profilePaths } from 'config/common'

type TProps = {
  externalLink: string,
}

const UploadVideoBtnComponent = ({
  externalLink,
}: TProps) => (
  <div className='link-iconed-wrapper'>
    <a
      href={externalLink}
      className={cx(
        'link-iconed link-iconed-primary header-profile-link',
        'js-open-dropdown tooltip-trigger',
      )}
    >
      <div className='hover-tooltip'>
        <T9n t='header_upload_video' />
      </div>
      <i className='upload-video__icon' />
    </a>
  </div>
)

const injectedUploadVideoBtn = compose<TProps, {}>(
  inject('auth'),
  observer,
  withProps<TProps, {}>(() =>
    ({
      externalLink: `${beta}/${profilePaths.header_upload_video}`,
    })),
  observer,
)

export const UploadVideoBtn = injectedUploadVideoBtn(UploadVideoBtnComponent)
