import React from 'react'
import { observer } from 'mobx-react'
import { OutsideClick } from 'react-sport'
import { compose } from 'recompose'

import FilterCheckbox from './FilterCheckbox'

const FilterPopup = ({
  button: {
    //@ts-ignore
    setActive,
  },
  //@ts-ignore
  button,
  //@ts-ignore
  pageType,
}) => (
  <div className='episodes-table-button-popup'>
    <ul>
        //@ts-ignore
      {button.popupItems.map((filter, idx) => (
        //@ts-ignore
        <FilterCheckbox
          key={idx}
          setActive={setActive}
          filter={filter}
          pageType={pageType}
        />
      ))}
    </ul>
  </div>
)

const FilterPopupComposed = compose(
  OutsideClick,
  observer,
  //@ts-ignore
)(FilterPopup)

export default FilterPopupComposed
