
import React from 'react'
import { inject, observer } from 'mobx-react'

import { compose, withHandlers, withState } from 'recompose'

import { T9n } from 'react-sport'

import TeamsComparePopup from './TeamsComparePopup'
import { TTeamsCompareHeder } from '../types'

export const TeamsCompareHeder = ({
  handlePopupAction,
  popupOpened,
}: TTeamsCompareHeder) => (
  <div className='t-compare__header'>
    <T9n className='t-compare__title' t='teams_statistic' />
    <a className='t-compare__gear-options' onClick={handlePopupAction}>
      <i className='icon-settings' />
    </a>
    {popupOpened && <TeamsComparePopup handlePopupAction={handlePopupAction} />}
  </div>
)

const TeamsCompareHeaderComposed = compose(
  inject('lexics', 'videoLink', 'shootsRange', 'teamsCompareWidget'),
  withState('popupOpened', 'openPopup', false),
  //@ts-ignore
  withHandlers({
    //@ts-ignore
    handlePopupAction: ({ openPopup }) => () => openPopup(n => !n),
  }),
  observer,
  //@ts-ignore
)(TeamsCompareHeder)

export default TeamsCompareHeaderComposed
