import { filterDBStats } from 'module/utils/helpers'

import map from 'lodash/fp/map'
import values from 'lodash/fp/values'
import flatten from 'lodash/fp/flatten'
import pipe from 'lodash/fp/pipe'

import isEmpty from 'lodash/isEmpty'

import { callApi } from 'react-sport'

import routes from 'config/routes'

import { MatchPagePlayersFetchPlayers } from './fetchPlayers'

type TConfig = {
  body: string,
  method: string,
}

export const MatchPagePlayersFetchTableStats = MatchPagePlayersFetchPlayers.actions(self => ({
  _onSuccessStats(json: { data: typeof self.tableStats }) {
    //@ts-ignore
    self.tableStats = filterDBStats(json.data)
    if (
      isEmpty(self.tables)
      && window.localStorage.getItem('matchPagePlayers') === null
    ) {
      self.saveTables()
    } else {
      //@ts-ignore
      self.tables = JSON.parse(window.localStorage.getItem('matchPagePlayers'))
    }
    self.setIsLoaded(true)
    self.setError(null)
  },
}))
  .actions(self => ({
    fetchTableStats(matchId: number | string) {
      const url: string = `${routes.API_ROOT}/data`
      const config: TConfig = {
        body: JSON.stringify({
          params: {
            _v_match_id: [matchId],
            _v_param_id: map('param_id')(self.totalParams),
            _v_player_id:
          pipe(
            values,
            flatten,
            map('f_player'),
          )(self.playerList),
          },
          proc: 'ask_player_param_view',
        }),
        method: 'POST',
      }
      return callApi({
        config,
        onError: self._onError,
        onRequest: () => {
          self.setError(null)
          self.setIsLoaded(false)
        },
        onSuccess: self._onSuccessStats,
        url,
      })
    },
  }))
