import { types, getParent } from 'mobx-state-tree'

import reduce from 'lodash/reduce'
import size from 'lodash/size'
import includes from 'lodash/includes'

// @ts-ignore
import { percentageParams } from 'config/teamPopupParams'

const TournamentHeaderStatsActions = types
  .model({})
  .volatile(() => ({
    stats: {},
  }))
  .views(self => ({
    get matchesIds() {
      return getParent(self).tournamentMatches.matchesIds
    },

    get countAverageStatValue() {
      return reduce(
        self.stats,
        (result, { count, matches, value }, key) => {
          const denominator = includes(percentageParams, Number(key))
            ? count
            : size(matches)

          return {
            ...result,
            [key]: value / denominator,
          }
        },
        {},
      )
    },
  }))

export default TournamentHeaderStatsActions
