import React, { Fragment } from 'react'
import { observer } from 'mobx-react'

import { TableUI } from 'react-sport'

import { THead, TBody } from 'features/Table'

import { TrMatch } from '../Tr'

type TProps = {
  table: {},
  setSortedId: () => void,
  tableStats: Array<{}>,
  pageId: string,
  sortAsc: boolean,
  activeId: string,
  param: {},
  rows: Array<{}>,
}

export const Table = observer(
  ({
    activeId,
    pageId,
    param,
    rows,
    setSortedId,
    sortAsc,
    table,
    tableStats,
  }: TProps) => (
    <Fragment>
      <TableUI.Thead>
        {/*
        //@ts-ignore */}
        <THead table={table} setSortedId={setSortedId} />
      </TableUI.Thead>
      {/*
      //@ts-ignore */}
      <TBody
        //@ts-ignore
        selectedParams={table.selectedParams}
        rows={rows}
        tableStats={tableStats}
        pageId={pageId}
        sortAsc={sortAsc}
        activeId={activeId}
        param={param}
        Tr={TrMatch}
      />
    </Fragment>
  ),
)
