// @ts-ignore
import { action } from 'mobx'
import { utils as remoteUtils } from 'react-sport'

import Button from './Button'

// @ts-ignore
const VideoCutsMixin = superclass =>
  class extends superclass {
    /** Получение кнопок **/
    //@ts-ignore
    @action fetchButtons = () => {
      const url = `${this.rootUrl}/data`
      const config = {
        method: 'POST',
        body: JSON.stringify({
          proc: 'ask_filters_video_cutting_view',
        }),
      }

      remoteUtils.callApi({
        url,
        config,
        //@ts-ignore
        onRequest: () => {
          //@ts-ignore
          this.isFetching = true
        },
        onSuccess: json => this._onSuccessButtons(json),
        //@ts-ignore
        onError: (error) => {
          //@ts-ignore
          this.error = error
        },
      })
    };

    //@ts-ignore
    _onSuccessButtons = ({ data }) => {
      //@ts-ignore
      const newButtons = []
      //@ts-ignore
      const lexicIds = []

      /** Собираем лексику из кнопок и создаем для каждой кнопки класс **/
      //@ts-ignore
      data.forEach(({ value }) => {
        const { button, group } = value[0]
        //@ts-ignore
        //@ts-ignore
        button.forEach(({ button_lexic, ...buttonInfo }) => {
          buttonInfo.lexical_formula = button_lexic
          newButtons.push(new Button(buttonInfo, group))
          lexicIds.push(button_lexic)
          //@ts-ignore
          buttonInfo.popup.forEach(({ popup_lexic }) =>
            lexicIds.push(popup_lexic))
        })
        this.groups[group] = value[0]
      })
      //@ts-ignore
      this.isFetching = false

      //если объект лексики передан
      //то вытаскиваем всю лексику из кнопок и передаем ее
      //для запроса в объект лексики
      if (this.lexics) {
        const phrases = {}
        //@ts-ignore
        //@ts-ignore
        lexicIds.forEach((id) => { phrases[id] = id })
        this.lexics.lexicFetch(phrases)
      }

      //@ts-ignore
      //@ts-ignore
      this.buttons = newButtons
    };

    //@ts-ignore
    @action resetFilters = () => {
      //@ts-ignore
      this.buttons.forEach((button) => {
        button.uncheckAll()
      })
    };
  }

export default VideoCutsMixin
