import * as React from 'react'
import { compose, withHandlers, withProps, withState } from 'recompose'
import cx from 'classnames'
import { OutsideClick } from 'react-sport'
import './index.scss'
import DropDownItem from './DropDownItem'

/**
 * Компонент Dropdown - простой выпадающий список с возможностью кастомизации
 *
 * Работает так
 *  <Dropdown onClick={() => onClick()}>
 *    <ChildFirst/>
 *    <ChildSecond/>
 *  </Dropdown>
 *
 * @property {React, Component, Array} children - компоненты которые являются пунктами выпадашки
 * @property {function} onClick - действие при клике на пункт выпадашки
 * @property {function} handleClick - открытие/закрытие списка
 * @property {React, Component, string} placeholder - выбранный пункт выпадашки
 * @property {function} changePlaceholder - изменение заголовка
 * @property {object} settings - кастомизированные настройки выпадающего меню
  (основной цвет, отступы, шрифт и тд) не обязательно
  Прим. settings = {
    [key]: string
  }
 */

type TProps = {
  children: any,
  handleClick: () => void,
  //@ts-ignore
  placeholder: React.Node,
  //@ts-ignore
  statePlaceholder: React.Node,
  isOpened: boolean,
  inlineStyles: {
    placeHolder: {},
    listItem: {},
  },
  primaryColor: string,
  //@ts-ignore
  itemClick: (arg: React.Node) => () => void,
}

const DropDownSimple = ({
  children,
  handleClick,
  inlineStyles,
  isOpened,
  itemClick,
  placeholder,
  primaryColor,
  statePlaceholder,
}: TProps) => (
  <div
    className={cx('dropdown-container', {
      'dropdown-container__opened': isOpened,
    })}
  >
    {children && (
      <div className='dropdown-control' onClick={handleClick}>
        <span style={inlineStyles.placeHolder}>
          {statePlaceholder || placeholder}
        </span>
        <ul
          className={cx('dropdown-list', {
            'dropdown-list__hidden': !isOpened,
          })}
        >
          {React.Children.map(children, (child, i) => (
            <li
              key={i}
              className='dropdown-list__item'
              style={inlineStyles.listItem}
              onClick={itemClick(child)}
            >
              {/*
              //@ts-ignore */}
              <DropDownItem primaryColor={primaryColor} child={child} />
            </li>
          ))}
        </ul>
      </div>
    )}
  </div>
)

const DropDownSimpleComposed = compose(
  withState('isOpened', 'handleOpen', false),
  withState('statePlaceholder', 'changePlaceholder', ''),
  //@ts-ignore
  withHandlers({
    //@ts-ignore
    handleClick: ({ handleOpen }) => () => handleOpen(n => !n),
    clickOutside: ({ handleOpen }) => () => handleOpen(false),
    //@ts-ignore
    itemClick: ({ changePlaceholder, onClick }) => child => () => {
      changePlaceholder(React.cloneElement(child, { onClick: null }))
      if (child.props.onClick) child.props.onClick()
      if (onClick) onClick()
    },
  }),
  //@ts-ignore
  withProps(({ settings = {} }) => ({
    //@ts-ignore
    primaryColor: settings.primaryColor || '#e6e6e6',
    inlineStyles: {
      listItem: {
        //@ts-ignore
        minWidth: settings.minWidth || '150px',
        //@ts-ignore
        fontSize: settings.fontSize || '10px',
      },
      placeHolder: {
        //@ts-ignore
        paddingLeft: settings.padding || '10px',
      },
    },
  })),
  OutsideClick,
  //@ts-ignore
)(DropDownSimple)

export default DropDownSimpleComposed
