// @ts-ignore
import { observable, computed } from 'mobx'
import isEmpty from 'lodash/isEmpty'
import filter from 'lodash/filter'

// @ts-ignore
import stores from 'features/stores'

// @ts-ignore
const Markers = superclass =>
  class extends superclass {
    //@ts-ignore
    @observable loaded = false;

    //@ts-ignore
    @observable error = null;

    //@ts-ignore
    @observable data = [];

    //@ts-ignore
    @observable select = false;

    //@ts-ignore
    @observable directions = {
      to: true,
      from: true,
    };

    //@ts-ignore
    @computed get list() {
      //@ts-ignore
      const { checked } = stores.filters
      const store = this.getStore.bind(this)
      const markers = this.select
        //@ts-ignore
        ? filter(this.data, marker => marker.visible)
        : this.data
      if (isEmpty(checked)) return markers
      return this.getMarkers({
        checkedFilters: checked,
        markers,
        store,
      })
    }
  }

export default Markers
