// @ts-ignore
import { action } from 'mobx'
import { utils as remoteUtils } from 'react-sport'

// @ts-ignore
import routes from 'config/routes'

// @ts-ignore
const FetchShotType = superclass =>
  class extends superclass {
    //@ts-ignore
    @action fetch = () => {
      const url = `${routes.API_ROOT}/data`
      const config = {
        method: 'POST',
        body: JSON.stringify({
          proc: 'ask_type_of_actions_view',
        }),
      }

      remoteUtils.callApi({
        url,
        config,
        onRequest: this._onRequest.bind(this),
        onSuccess: this._onSuccess.bind(this),
        onError: this._onError.bind(this),
      })
    };

    //@ts-ignore
    _onSuccess({ data }) {
      //@ts-ignore
      this.data = data
      //@ts-ignore
      this.loaded = true
      //@ts-ignore
      this.error = false
    }

    _onRequest() {
      //@ts-ignore
      this.loaded = false
      //@ts-ignore
      this.error = false
    }

    //@ts-ignore
    _onError(error) {
      //@ts-ignore
      this.error = error
      //@ts-ignore
      this.loaded = false
    }
  }

export default FetchShotType
