import stores from 'features/stores'

import get from 'lodash/get'

import { MatchPagePlayersModel } from './matchPlayersStore'

export const MatchPagePlayersViews = MatchPagePlayersModel
  .views(() => ({
    get teamsId() {
      const { team1_id, team2_id } = get(stores, 'matchProfile.data', {})

      return [team1_id, team2_id]
    },
  }))
