import { inject, observer } from 'mobx-react'
import {
  compose,
  withHandlers,
} from 'recompose'
import isEmpty from 'lodash/isEmpty'
import reduce from 'lodash/reduce'
import filter from 'lodash/filter'
import forEach from 'lodash/forEach'

import { clickableClassNameHOC } from 'hocs/getClickableTdClassNameHoc'

import { TFoot as TFootComponent } from 'features/Table'

import { TParam } from 'features/Table/models/tableModel'

type TProps = {
  activeId: string,
  getActualMatchesList: (
    page: string,
    pageId: string,
    tableStats: Array<{}>,
    opps?: boolean,
  ) => Array<any>,
  //@ts-ignore
  getClassName: (param: TParam, activeId: string) => void,
  getExtLink: (args: {
    lexical_formula: string,
    param_id: number,
  }) => void,
  param?: TParam,
  showOpponentStats: boolean,
  page?: string,
  pageId: string,
  selectedMatches: {},
  setType: (arg: string) => void,
  table: {
    selectedParams: Array<TParam>,
  },
  tableStats: Array<{}>,
}

export const tfootComposed = compose<any, TProps>(
  inject(
    'shootsRange',
    'lexics',
    'videoLink',
    'tournamentMatchesTableStore',
  ),
  //@ts-ignore
  withHandlers({
    /**
    * Функция для открытия попап окна с видео повторами
    * @param {number} param_id - id параметра
    * @param {string} lexical_formula - название параметра
    * @param {array} tableStats - массив всех параметров
    * @param {number} id - id игрока или команды
    * @param {boolean} forOpp - флаг для фильтрации. Ищем для своей команды или для команд соперника
    */
    getExtLink: ({
      lexics: { lang },
      shootsRange,
      tableStats,
      videoLink: { request },
    }) => ({ lexical_formula, param_id }: {
      lexical_formula: string,
      param_id: number,
    }) => {
      // Фильтруем сначала по команде
      const teamTableStats = filter(
        tableStats,
        { f_param: param_id },
      )
      if (isEmpty(teamTableStats)) return null

      // Выдергиваем только ивенты из массива статистики для конкретной команды
      // и делаем из них просто общий массив [number]
      const filteredEvents = reduce(
        teamTableStats,
        (acc, { f_match_event_ids_json: events }) => {
          if (!events) return acc
          //@ts-ignore
          forEach(events, event => acc.push(event))
          return acc
        }, [],
      )

      return request({
        events: filteredEvents,
        lang,
        lexical_formula,
        range: shootsRange,
        type: 'tablesFooter',
      })
    },
    /**
    * Функция установки выбранного типа итогового значения в таблице
    * @param {string} type - типа итогового значения
    */
    setType: ({
      tournamentMatchesTableStore: { changeValueType },
    }) => (type: string, position: number) => changeValueType(type, position),
    /**
    * Функция фильтрации статистики по всем матчам только по выбранным матчам.
    И для команды соперника или нашей
    * @param {string} page - игрок или команда
    * @param {string} pageId - id игрока/команды
    * @param {array} tableStats - массив параметров
    */
    getActualMatchesList: ({ tableStats }) => () => tableStats,
  }),
  clickableClassNameHOC,
  observer,
)

export const TFoot = tfootComposed(TFootComponent)
