import React, { Fragment } from 'react'
import { Switch, Route, Redirect } from 'react-router-dom'

import Loader from 'features/components/common/loader/Loader'
import { TournamentMatchesTable } from 'features/TournamentPageMatchesTable'
import { TournamentPlayersTable } from 'features/TournamentPagePlayersTable'
import { StatsTabs } from 'features/components/common/stats-tabs/StatsTabs'

import TournamentPageHeader from './TournamentPageHeader'
import { tournamentPageComposed } from './composed'

import './tournamentPage.scss'

type TProps = {
  match: {
    url: string,
    params: {
      tournamentId: string,
    },
  },
  tournamentMatches: {
    isLoaded: boolean,
  },
  tournamentsProfile: {
    isLoaded: boolean,
    profile: {},
  },
};

const TournamentPageComponent = ({
  match: { params, ...match },
  tournamentMatches: { isLoaded },
  tournamentsProfile: { isLoaded: profileLoaded, profile },
}: TProps) => (
  <Fragment>
    {profileLoaded ? (
      <TournamentPageHeader
        tournamentId={Number(params.tournamentId)}
        profile={profile}
        isLoaded={isLoaded}
      />
    ) : (
      <Loader />
    )}
    <section className='main-page__details'>
      <div className='main-page__responsive'>
        <div className='main-page__content'>
          <div className='main-page__content-header'>
            <StatsTabs match={match} isStats />
          </div>
          <Switch>
            <Route
              path='/tournaments/:tournamentId/'
              exact
              render={() => (
                <Redirect to={`/tournaments/${params.tournamentId}/matches`} />
              )}
            />
            <Route
              path='/tournaments/:tournamentId/matches'
              exact
              render={() =>
                (isLoaded ? (
                //@ts-ignore
                  <TournamentMatchesTable
                    tournamentId={Number(params.tournamentId)}
                  />
                ) : (
                  <Loader />
                ))
              }
            />
            <Route
              path='/tournaments/:tournamentId/players'
              exact
              render={() =>
                (isLoaded ? (
                //@ts-ignore
                  <TournamentPlayersTable
                    tournamentId={Number(params.tournamentId)}
                  />
                ) : (
                  <Loader />
                ))
              }
            />
          </Switch>
        </div>
      </div>
    </section>
  </Fragment>
)

// @ts-ignore
export const TournamentPage = tournamentPageComposed(TournamentPageComponent)
