import React from 'react'
import { inject, observer } from 'mobx-react'

import {
  withHandlers,
  compose,
} from 'recompose'
import isArray from 'lodash/isArray'
import get from 'lodash/get'
import { toJS } from 'mobx'

import Loader from 'features/components/common/loader/Loader'
import { Marker } from './MarkerItem'

type TProps = {
  getMarkers: () => Array<{}>,
  preparePoint: (marker: {}) => {},
}

//@ts-ignore
type TMarkerType = boolean | { pos_y: number | null }

/**
 * Компонент Markers - является контейнером маркеров
 *
 * @property {function} getMarkers - функция получения списка маркеров
 * @property {function} preparePoint - в функцию передается маркер и она возврощает
 * позицию маркера и делает некую фильтрацию
 *
 */

const MarkersComponent = ({
  getMarkers,
  preparePoint,
}: TProps) => (
  isArray(toJS(getMarkers()))
    ? getMarkers().map((marker, idx) => (
      //@ts-ignore
      <Marker
        key={idx}
        marker={preparePoint(marker)}
      />
    ))
    : getMarkers()
)

const markersComposed = compose(
  inject('markers', 'tabs', 'zones'),
  withHandlers({
    //функция получения типа удара
    //@ts-ignore
    getActionTypes: () => ({ action_type }, to, from) => {
      const type = { fromType: '', toType: '' }
      switch (action_type) {
        case 'Positive':
          if (to) {
            type.toType = 'point'
          }
          break
        case 'Negative':
          if (from && to) {
            type.fromType = 'miss'
            type.toType = 'out'
          } else if (from) {
            type.fromType = 'miss'
          } else {
            type.toType = 'out'
          }
          break
        default: break
      }
      return type
    },

    /*
    * функция получения счета, счет первой и второй команды достаются
    * в маркерах по специальным ключам
    */
    //@ts-ignore
    getScore: () => ({ prop }, key) => prop.find(property => property[0] === key),
    //@ts-ignore
    getList: () => (list, data) => (
      //@ts-ignore
      list.filter(({ prop }) => prop.find(([property, value]) => (
        Object.entries(data).some(([key, values]) => {
          //@ts-ignore
          if (key === 'any') return values.includes(property)
          //@ts-ignore
          return parseInt(key, 10) === property && values.includes(value)
        })
      )))
    ),
  }),
  //@ts-ignore
  withHandlers({
    /*
    * в функцию передается маркер и она возврощает
    * позицию маркера и делает некую фильтрацию
    */
    preparePoint: ({
      getActionTypes,
      getScore,
      markers: {
        directions,
      },
      tabs: { active },
      zones: {
        checked,
      },
      //@ts-ignore
    }) => (point) => {
      //получаем позиции маркера
      let { from, middle, to } = point

      //условия для показа точки по середине
      const middleCondition = directions.from
        && directions.to
        && !point.dontShowOpponentSide
        && middle

      //устанавливаем координаты для точек откуда, куда и блок, если есть точка по середине
      from = directions.from && from
      middle = middleCondition && middle
      to = (directions.to && !point.dontShowOpponentSide)
        && to

      //получаем тип удара, для точек откуда и куда
      const { fromType, toType } = getActionTypes(point, to, from)

      //получаем счет игры из маркера
      const score1 = getScore(point, 136)
      const score2 = getScore(point, 137)
      //функция которая скрывает маркер, если он находится под разметкой зоны
      // для вкладки 'подачи' скрываем маркеры под зонами противника
      // для остальных - под своими зонами
      //@ts-ignore
      const hideMarker = (marker) => {
        const tabCheck = active === 'serve'
          ? (get(marker, 'pos_y') > 50)
          : (get(marker, 'pos_y') < 50)
        return tabCheck && marker.pos_y
      }
      //@ts-ignore
      const checkPosition = (markerType: TMarkerType) => {
        if (!markerType) return null
        //@ts-ignore
        if (checked === 'without_zone') return markerType.pos_y
        return hideMarker(markerType)
      }

      const checkZone = (
        markerType: TMarkerType,
        directionType: string,
      ) => ({
        //@ts-ignore
        ...markerType,
        type: directionType,
        pos_y: checkPosition(markerType),
      })

      return {
        point: {
          ...point,
          score1: get(score1, 2, 0),
          score2: get(score2, 2, 0),
        },
        from: checkZone(from, fromType),
        to: checkZone(to, toType),
        middle,
      }
    },
    //получение маркеров
    getMarkers: ({
      markers: {
        list,
        loaded,
      },
    }) => () => {
      if (!loaded) return <Loader />
      return list
    },
  }),
  observer,
)

//@ts-ignore
export const Markers = markersComposed(MarkersComponent)
