import React, { Fragment } from 'react'
import { inject, observer } from 'mobx-react'

import get from 'lodash/get'
import { compose, withProps } from 'recompose'
import { getLongSuffix } from 'module/utils/helpers'

import Avatar from 'features/components/common/Avatar'

type TProps = {
  suffix: string,
  //@ts-ignore
  countryName: string | null,
  profile: {
    photo?: string,
  },
}
const TournamentPageInfo = ({
  countryName,
  profile: {
    photo,
    ...profile
  },
  suffix,
}: TProps) => (
  <Fragment>
    <div className='tournament-header-left'>
      <div className='tournament-avatar'>
        <Avatar image={photo} name='tournament' />
      </div>
      <div className='tournament-name'>
        <h1 className='tournament-header-title'>
          <span>
            {/*
              //@ts-ignore */}
            {profile[`name_${suffix}`]}
          </span>
        </h1>
        <div className='tournament-header-team'>
          <a>{countryName}</a>
        </div>
      </div>
    </div>
  </Fragment>
)

const TournamentPageInfoComposed = compose(
  inject('lexics'),
  observer,
  //@ts-ignore
  withProps(({ lexics: { lang }, profile }) => {
    const suffix = getLongSuffix(lang)
    return {
      suffix,
      countryName: get(profile, `country.name_${suffix}`),
    }
  }),
  //@ts-ignore
)(TournamentPageInfo)

export default TournamentPageInfoComposed
