import React, { Fragment } from 'react'

import { T9n, utils } from 'react-sport'

import { TOuterProps } from './types'
import PlayersStats from '../PlayersStats'
import { playersInfoComposed } from './composed'

const {
  helpers: { onProfileImageError },
} = utils

const PlayersInfoComponent = ({
  getClassName,
  getExtLink,
  getImage,
  getStatValue,
  playersCompareWidget: { sortedStats },
  selectedPlayers,
  team1,
  team2,
}: TOuterProps) => (
  <Fragment>
    <div className='t-compare__control-row'>
      <div className='t-compare__logo-container t-compare__logo-container_player'>
        <img
          className='t-compare__logo t-compare__logo_player'
          //@ts-ignore
          src={getImage(selectedPlayers[team1])('photo_team')}
          alt='logoTeam'
          onError={e => onProfileImageError(e, 'team')}
        />
        <img
          className='t-compare__logo'
          //@ts-ignore
          src={getImage(selectedPlayers[team1])('photo_player')}
          alt='logoPlayer'
          onError={onProfileImageError}
        />
      </div>

      <div className='t-compare__controls t-compare__controls_player'>
        <T9n className='t-compare__toggler' t='match' />
      </div>

      <div className='t-compare__logo-container t-compare__logo-container_player'>
        <img
          className='t-compare__logo t-compare__logo_player t-compare__logo_player-right'
          //@ts-ignore
          src={getImage(selectedPlayers[team2])('photo_team')}
          alt='logoTeam'
          onError={e => onProfileImageError(e, 'team')}
        />
        <img
          className='t-compare__logo'
          //@ts-ignore
          src={getImage(selectedPlayers[team2])('photo_player')}
          alt='logoPlayer'
          onError={onProfileImageError}
        />
      </div>
    </div>

    <PlayersStats
      //@ts-ignore
      stats={sortedStats}
      team1={team1}
      team2={team2}
      getStatValue={getStatValue}
      getClassName={getClassName}
      getExtLink={getExtLink}
      selectedPlayers={selectedPlayers}
    />
  </Fragment>
)

export const PlayersInfo = playersInfoComposed(PlayersInfoComponent)
