import React from 'react'

import map from 'lodash/map'

import { Td } from 'features/Table/components/Td'

import { TPropsTr } from './types'

import { trComposed } from './composed'
import { LeftSideTr } from '../LeftSideTr'

// Строка со статистикой по матчу
export const Tr = trComposed(
  //@ts-ignore
  ({
    activeId,
    getClassName,
    getPost,
    matchLinks: { isFetching, links },
    matchPopupOpened,
    pageId,
    rowInfo,
    selectedParams,
    showOpponentStats,
    tableStats,
    tableType,
    toggleClose,
  }: TPropsTr) => (
    <tr className='stats-table__table-row'>
      {/*
      //@ts-ignore */}
      <LeftSideTr
        showOpponentStats={showOpponentStats}
        rowInfo={rowInfo}
        pageId={pageId}
        handleClick={getPost}
        matchPopupOpened={matchPopupOpened}
        isFetching={isFetching}
        links={links}
        toggleClose={toggleClose}
      />
      {map(selectedParams, param => (
        <td
          key={`${param.param_id}_${param.gear_group_id}`}
          className={getClassName(param, activeId)}
        >
          <table>
            <tbody>
              <tr>
                <Td
                  id={Number(pageId)}
                  param={param}
                  tableStats={tableStats.home}
                  rowInfo={rowInfo}
                  tableType={tableType}
                />
              </tr>
            </tbody>
          </table>
          {showOpponentStats && (
            <table>
              <tbody>
                <tr>
                  <Td
                    id={rowInfo.f_team_opponent}
                    param={param}
                    tableStats={tableStats.guest}
                    rowInfo={rowInfo}
                    tableType={tableType}
                  />
                </tr>
              </tbody>
            </table>
          )}
        </td>
      ))}
    </tr>
  ),
)
