import React from 'react'
import { observer, inject } from 'mobx-react'

import {
  withHandlers,
  compose,
  lifecycle,
} from 'recompose'
import cx from 'classnames'

import { T9n } from 'react-sport'

import { LinksList } from 'features/components/common/stats-tabs/LinksList'

import {
  TProps,
  TComposeInner,
  TWithHandlers,
  TPrevProps,
} from './types'

import './StatsTabs.scss'

export const StatsTabsComponent = ({
  handleClick,
  isStats,
  lexics: { page },
  match,
  tabs: {
    active,
    list,
  },
}: TComposeInner) => (
  <ul className='stats-tabs'>
    {isStats
      ? <LinksList match={match} page={page} />
      : list.map(tab => (
        <li key={tab} className='stats-tabs__item'>
          <a
            href=''
            className={cx({ active: active === tab })}
            onClick={handleClick(tab)}
          >
            <T9n t={tab} />
          </a>
        </li>
      ))}
  </ul>
)

const statsTabComposed = compose<TComposeInner, TProps>(
  inject(
    'lexics',
    'tabs',
    'markers',
    'zones',
    'filters',
  ),
  withHandlers<TWithHandlers, {}>({
    handleClick: ({
      filters: { clear },
      markers: {
        loaded,
        selectToggle,
      },
      tabs,
      zones: {
        resetToDefaultDirections,
        setChecked,
      },
    }) => (id: number) => (e: React.FormEvent<HTMLInputElement>) => {
      e.preventDefault()
      if (loaded) {
        tabs.changeActive(id)
        selectToggle(false)
        setChecked('without_zone')
        resetToDefaultDirections()
        clear()
      }
    },
  }),
  lifecycle<TPrevProps, TPrevProps>({
    componentDidMount() {
      const {
        markers: { setDefaultDirections },
      } = this.props
      setDefaultDirections()
    },
  }),
  observer,
)

export const StatsTabs = statsTabComposed(StatsTabsComponent)
