import React from 'react'

import { DownloadLinksComponent } from 'react-sport'

import { GearPopup } from 'features/GearPopup'
import { TableHeader } from 'features/TableHeader/components/'
import { EditTableName } from 'features/TableName/components/EditTableName'

import { tableComposed } from './composed'
import { TableContent } from '../TableContent'
import { TableActions } from '../TableActions'

import 'features/Table/styles/StatsTable.scss'
import 'features/Table/styles/TablePopup.scss'

import { TPropsInner } from './types'

// Таблица со статистикой по выбранным матчам
const TableComponent = ({
  activeId,
  currentStore,
  getClassName,
  getLinks,
  handleNewPopupAction,
  handlePopupAction,
  newPopupOpened,
  pageId,
  param,
  popupOpened,
  saveReport,
  setSortedId,
  sortAsc,
  table,
  tableType,
}: TPropsInner) => (
  <div className='stats-table'>
    <div className='stats-table__header'>
      <div className='stats-table__header-left'>
        <TableHeader
          table={table}
            //@ts-ignore
          tableType={tableType}
          currentStore={currentStore}
          getClassName={getClassName}
        />
      </div>
      <div className='stats-table__name'>
        <EditTableName table={table} currentStore={currentStore} />
      </div>
      <div className='stats-table__actions'>
        {/*
          //@ts-ignore */}
        <DownloadLinksComponent links={getLinks()} reportHandle={saveReport} />
        {/*
          //@ts-ignore */}
        <TableActions
          currentStore={currentStore}
          handleNewPopupAction={handleNewPopupAction}
          handlePopupAction={handlePopupAction}
          table={table}
        />
      </div>
    </div>
    <TableContent
      activeId={activeId}
      currentStore={currentStore}
      pageId={pageId}
      param={param}
      setSortedId={setSortedId}
      sortAsc={sortAsc}
      table={table}
      tableType={tableType}
    />
    {(popupOpened || newPopupOpened) && (
      <GearPopup
        table={table}
        handlePopupAction={handlePopupAction}
        handleNewPopupAction={handleNewPopupAction}
        popupOpened={popupOpened}
        newPopupOpened={newPopupOpened}
        currentStore={currentStore}
        newPopup={newPopupOpened}
      />
    )}
  </div>
)

export const Table = tableComposed(TableComponent)
