import { types } from 'mobx-state-tree'

import filter from 'lodash/fp/filter'
import sortBy from 'lodash/fp/sortBy'
import flow from 'lodash/fp/flow'

// @ts-ignore
import { saveTablesToLocalStorage } from 'module/utils/helpers'
// @ts-ignore
import { defaultTableName } from 'config/tables'

export const TournamentPagePlayersActions = types.model({}).actions(self => ({
  saveTables() {
    const defaultTable = {
      position: 1,
      selectedParams: flow(
        filter('is_default'),
        sortBy(['gear_group_id', 'sort_order']),
        //@ts-ignore
      )(self.totalParams),
      tableName: defaultTableName,
      summaryType: 'total',
    }
    //@ts-ignore
    self.tables = {
      1: defaultTable,
    }
    //@ts-ignore
    saveTablesToLocalStorage({
      name: 'tournamentPlayersTableStore',
      //@ts-ignore
      tables: self.tables,
    })
  },

  toggleShowAvatar() {
    //@ts-ignore
    //@ts-ignore
    self.avatarIsVisible = !self.avatarIsVisible
  },
}))
