import { utils } from 'react-sport'
import { action } from 'mobx'
import routes from 'config/routes'
import get from 'lodash/get'
import map from 'lodash/map'
import filter from 'lodash/filter'
import uniqBy from 'lodash/uniqBy'
import concat from 'lodash/concat'


import m from 'moment'

import BasicStore from 'features/stores/BasicStore'

const procedures = {
  player: 'scout_player_matches_list',
  team: 'scout_team_match_period_1',
}

const fields = {
  player: '_p_player_id',
  team: '_p_team_id',
}

interface IMatchesApiStore {
  fetch: (
    page: string,
    pageId: string | number,
    offset: number,
    limit: number,
    //@ts-ignore
    dateFrom?: string,
    //@ts-ignore
    dateTo?: string,
    //@ts-ignore
    setTempSelected?: (filteredMatches: Array<number>) => void,
    //@ts-ignore
    setOpened?: (pageId: string | number) => void,
  ) => void | null,
  addOffset: (pageId: string | number) => void,
  changeActive: () => void,
}

// Стор для загруженных матчей
export default class MatchesApiStore extends BasicStore
  implements IMatchesApiStore {
  //@ts-ignore
  @action fetch = (
    //@ts-ignore
    page,
    //@ts-ignore
    pageId,
    //@ts-ignore
    offset,
    //@ts-ignore
    limit,
    // При выборе всего года или сезона в выпадающем меню
    dateFrom = null,
    dateTo = null,
    setTempSelected = null,
    setOpened = null,
  ) => {
    //@ts-ignore
    if (get(this.list[pageId], 'next') === false) {
      // Если загружены все матчи, то не делаем запрос далее,
      // просто выбираем из общего списка матчи за год или за сезон
      // и закрываем меню
      if (dateFrom && setTempSelected && setOpened) {
        //@ts-ignore
        const checkPeriod = matchDate => m(matchDate).isBetween(dateFrom, dateTo)
        // фильтруем матчи, даты которых входят в нужный диапазон
        const filteredMatches = map(
          //@ts-ignore
          filter(this.list[pageId].matches, ({ match_date }) => checkPeriod(match_date)),
          'f_match',
        )
        //@ts-ignore
        setTempSelected(filteredMatches)
        //@ts-ignore
        setOpened(pageId)
      }
      return null
    }
    const url = `${routes.API_ROOT}/data`
    const config = {
      method: 'POST',
      body: JSON.stringify({
        //@ts-ignore
        proc: procedures[page],
        params: {
          //@ts-ignore
          [fields[page]]: pageId,
          _p_date_from: dateFrom,
          _p_date_to: dateTo,
          _p_offset: offset,
          _p_limit: limit,
        },
      }),
    }
    utils.callApi({
      url,
      config,
      onRequest: () => {
        //@ts-ignore
        this.isFetching = true
      },
      onSuccess: json => this._onSuccess(
        json,
        page,
        pageId,
        //@ts-ignore
        setTempSelected,
        //@ts-ignore
        setOpened,
      ),
      onError: this._onError,
    })
  }

  _onSuccess = (
    json: {
      data: Array<any>,
    },
    page: string,
    pageId: string | number,
    //@ts-ignore
    setTempSelected?: (filteredMatches: Array<number>) => void,
    //@ts-ignore
    setOpened?: (pageId: string | number) => void,
  ) => {
    //@ts-ignore
    const data = json.data[0] ? json.data[0][procedures[page]] : []

    if (setTempSelected && setOpened) {
      setTempSelected(map(data, 'f_match'))
      setOpened(pageId)
    }

    const pageProfile = get(this, ['list', pageId])
    const matches = get(pageProfile, 'matches', [])
    const offset = get(pageProfile, 'offset', 0)
    //@ts-ignore
    this.list[pageId] = {
      matches: data ? uniqBy(concat(matches, data), 'f_match') : matches,
      offset: data
        ? offset + 20
        : offset,
      next: !!data,
    }
    //@ts-ignore
    this.error = null
    //@ts-ignore
    this.isFetching = false
    //@ts-ignore
    if (this.firstLoad) {
      //@ts-ignore
      this.firstLoad = false
    }
  }

  _onError = (error: any) => {
    //@ts-ignore
    this.isFetching = false
    //@ts-ignore
    this.error = error
  }

  //@ts-ignore
  @action addOffset = (pageId, limit = 20) => {
    //@ts-ignore
    const offset = this.list[pageId] ? this.list[pageId].offset : 0
    this.fetch(this.getStore('lexics').page, pageId, offset, limit)
  }

  //@ts-ignore
  @action changeActive = (active) => {
    //@ts-ignore
    this.active = active
  }
}
