import React from 'react'
import PropTypes from 'prop-types'
import { observer } from 'mobx-react'
import { T9n } from 'react-sport'
import orderByLodash from 'lodash/orderBy'

import FilterElement from './FilterElement'

/** Компонент для таблицы с кнопками
 * @param {String} playerId id игрока
 * @param {String} className часть строки css класса
 * @param {Array} orderBy массив из кнопок
 * @param {String} title заголовок таблицы
 * @param {Array} checked массив чекнутых кнопок
 * @param {Function} uncheckAllPlayers сброс выбранных игроков
 */

const TableButtons = ({
  //@ts-ignore
  checked,
  //@ts-ignore
  className,
  //@ts-ignore
  orderBy,
  //@ts-ignore
  pageType,
  //@ts-ignore
  playerId,
  //@ts-ignore
  title,
  //@ts-ignore
  uncheckAllPlayers,
}) => (
  <div className={`episodes-table-column ${className}`}>
    <div className='episodes-table-column-title'>
      <T9n t={title} />
    </div>
    <ul>
      {orderByLodash(orderBy, ['sort_order'], ['asc']).map((btn, idx) => (
        //@ts-ignore
        <FilterElement
          key={idx}
          button={btn}
          playerId={playerId}
          checked={checked}
          uncheckAllPlayers={uncheckAllPlayers}
          pageType={pageType}
        />
      ))}
    </ul>
  </div>
)

TableButtons.propTypes = {
  pageType: PropTypes.oneOf(['players', 'teams']).isRequired,
  playerId: PropTypes.oneOfType([PropTypes.string, PropTypes.number])
    .isRequired,
  className: PropTypes.string.isRequired,
  orderBy: PropTypes.array.isRequired,
  title: PropTypes.string.isRequired,
  checked: PropTypes.array.isRequired,
  uncheckAllPlayers: PropTypes.func.isRequired,
}

export default observer(TableButtons)
