import {
  compose,
  withHandlers,
  withState,
} from 'recompose'
import { inject } from 'mobx-react'
import { TLexics } from 'react-sport'

type TToggleClose = {
  matchPopupOpened: boolean,
  setOpened: (arg: boolean) => void,
}

type TGetPost = {
  lexics: TLexics,
  matchLinks: {
    fetch: (
      f_match: number,
      pageId: number,
      page: string
    ) => void,
  },
  matchPopupOpened: boolean,
  setOpened: (arg: boolean) => void,

}
/*
  Хок для компонента c выпадашкой, в которой содержится информация о матче.
  При клике на матч делается запрос на получение ссылок с бэка.
  в компоненте который оборачиваестя хоком появляется метод toggleClose
  для открытия/закрытие попап окна со списком ссылок
*/
export const withMatchLinksGetter = compose(
  inject('lexics', 'matchLinks'),
  withState('matchPopupOpened', 'setOpened', false),

  withHandlers({
    getPost: ({
      lexics: { page },
      matchLinks: { fetch },
      matchPopupOpened,
      setOpened,
    }: TGetPost) => (f_match: number, pageId: number) => () => {
      fetch(f_match, pageId, page)
      setOpened(!matchPopupOpened)
    },

    toggleClose: ({ matchPopupOpened, setOpened }: TToggleClose) => () =>
      setOpened(!matchPopupOpened),
  }),
)
