import { types } from 'mobx-state-tree'

import set from 'lodash/set'
import reduce from 'lodash/reduce'
import uniq from 'lodash/uniq'
import get from 'lodash/get'
import map from 'lodash/map'

import { callApi } from 'react-sport'

import routes from 'config/routes'

import { team_params } from 'config/params'
import { filterDBStats } from 'module/utils/helpers'

type TConfig = {
  method: string,
  body: string,
}

const TournamentHeaderStatsApiActions = types.model({}).actions(self => ({
  fetchHeaderParams() {
    const url: string = `${routes.API_ROOT}/data`
    const config: TConfig = {
      method: 'POST',
      body: JSON.stringify({
        proc: 'ask_tournament_param_view',
        params: {
          //@ts-ignore
          _v_match_id: self.matchesIds,
          _v_param_id: map(team_params, 'param_id'),
        },
      }),
    }
    return callApi({
      url,
      config,
      onRequest: () => {
        //@ts-ignore
        self.loaded = false
        //@ts-ignore
        self.error = null
      },
      //@ts-ignore
      onSuccess: (json: { data: Array<{}> }) => self._onSuccesHeaderParams(json),
      //@ts-ignore
      onError: (error: any) => self._onError(error),
    })
  },

  _onSuccesHeaderParams(json: { data: Array<{}> }) {
    // Фильтруем ответ. Нужно отсеить параметры для части поля и тд
    const filteredStats = filterDBStats(json.data)
    //@ts-ignore
    self.stats = reduce(
      filteredStats,
      (acc, { f_match, f_match_event_ids_json, f_param, value }) => {
        //@ts-ignore
        if (acc[f_param]) {
          const allEvents = get(acc, `${f_param}.events`) || []
          const matches = get(acc, `${f_param}.matches`) || []
          const currentEvents = f_match_event_ids_json || []

          //@ts-ignore
          acc[f_param] = {
            //@ts-ignore
            count: acc[f_param].count + 1,
            //@ts-ignore
            value: acc[f_param].value + value,
            matches: [...matches, f_match],
            events: [...allEvents, ...currentEvents],
          }
        } else {
          //@ts-ignore
          acc[f_param] = {
            count: 1,
            matches: [f_match],
            value,
            events: f_match_event_ids_json,
          }
        }
        //@ts-ignore
        const { matches } = acc[f_param]
        set(acc, `${f_param}.matches`, uniq(matches))

        return acc
      }, {},
    )
    //@ts-ignore
    self.loaded = true
  },

  _onError(error: any) {
    //@ts-ignore
    self.error = error
  },

}))

export default TournamentHeaderStatsApiActions
