import React from 'react'
import PropTypes from 'prop-types'
import { observer } from 'mobx-react'
import { T9n } from 'react-sport'
import isEmpty from 'lodash/isEmpty'
import toString from 'lodash/toString'

const Checked = ({
  button: {
    //@ts-ignore
    checkedFilters,
  },
}) => (
  <span className='text'>
    {!isEmpty(checkedFilters) ? (
      checkedFilters.length < 3 ? (
      //@ts-ignore
        checkedFilters.map((filter, idx) => (
          <T9n key={idx} t={toString(filter.lexicalFormula)} />
        ))
      ) : (
        <span>{checkedFilters.length} items</span>
      )
    ) : (
      <span className='text' />
    )}
  </span>
)

Checked.propTypes = {
  button: PropTypes.shape({
    checkedFilters: PropTypes.oneOfType([PropTypes.array, PropTypes.object]),
  }),
}

export default observer(Checked)
