import React from 'react'
import PropTypes from 'prop-types'
import get from 'lodash/get'
import { toJS } from 'mobx'
import { inject, observer } from 'mobx-react'
import { compose, withHandlers } from 'recompose'

// @ts-ignore
import Loader from 'features/components/common/loader/Loader'
import VideoCuts from './VideoCutsContainer'
import './VideoCuts.scss'

// @ts-ignore
const VideoCutsContainer = (props) => {
  const {
    getSelectedMatches,
    matches: { isFetching },
    page,
  } = props
  if (page !== 'match' && isFetching) return <Loader />
  return <VideoCuts selected={getSelectedMatches()} {...props} />
}

VideoCutsContainer.propTypes = {
  matches: PropTypes.shape({
    isFetching: PropTypes.bool,
  }),
  getSelectedMatches: PropTypes.func,
}

const VideoCutsComposed = compose(
  inject('lexics', 'selectedMatches', 'matches'),
  //@ts-ignore
  withHandlers({
    getSelectedMatches: ({
      matchId,
      page,
      playerId,
      selectedMatches,
    }) => () => {
      if (page === 'match') return [matchId]
      //@ts-ignore
      return toJS(get(selectedMatches, [page, playerId]))
    },
  }),
  observer,
)(VideoCutsContainer)

export default VideoCutsComposed
