import { types } from 'mobx-state-tree'
import { toJS } from 'mobx'

import filter from 'lodash/filter'
import isEqual from 'lodash/isEqual'
import map from 'lodash/map'

// @ts-ignore
import { indexLexic } from 'config/lexics'

export const gearPopupActions = types.model({}).actions(self => ({
  //@ts-ignore
  addToTempSelected(param) {
    //@ts-ignore
    //@ts-ignore
    self.tempSelected = [...self.tempSelected, toJS(param)]
  },

  //@ts-ignore
  removeFromTempSelected(param) {
    //@ts-ignore
    self.tempSelected = filter(
      //@ts-ignore
      self.tempSelected,
      item => !isEqual(param, item),
    )
  },

  eraseTempSelected() {
    //@ts-ignore
    self.tempSelected = []
  },

  //@ts-ignore
  fillTempSelected(params) {
    //@ts-ignore
    self.tempSelected = map(params, param => toJS(param))
  },
  /** Редактирование названия таблицы
   * @param {string} name - название таблицы
   * @param {bool} erase - возврат к значению по-умолчанию
   */
  editTempTableName(name: string, erase: boolean = false) {
    if (erase) {
      //@ts-ignore
      self.tempTableName = indexLexic.new_table
    } else {
      //@ts-ignore
      self.tempTableName = name
    }
  },
}))
