import { types } from 'mobx-state-tree'
import { callApi } from 'react-sport'
// @ts-ignore
import routes from 'config/routes'

const TeamProfileApiActions = types.model({}).actions(self => ({
  fetch(teamId: string | number) {
    const url = `${routes.API_ROOT}/data`
    const config = {
      method: 'POST',
      body: JSON.stringify({
        proc: 'scout_team_inf',
        params: {
          _p_team_id: teamId,
        },
      }),
    }
    callApi({
      url,
      config,
      onRequest: () => {
        //@ts-ignore
        self.profile = null
        //@ts-ignore
        self.isLoaded = false
      },
      //@ts-ignore
      onSuccess: self._onSuccess,
      //@ts-ignore
      onError: self._onError,
    })
  },

  _onSuccess(json: {
    data: Array<{
      scout_team_inf: Array<{}>,
    }>,
  }) {
    //@ts-ignore
    self.isLoaded = true
    //@ts-ignore
    self.profile = json.data[0].scout_team_inf[0] || null
    //@ts-ignore
    self.error = null
  },

  _onError(error: any) {
    //@ts-ignore
    self.error = error
  },
}))

export default TeamProfileApiActions
