import { inject, observer } from 'mobx-react'

import {
  withHandlers,
  compose,

} from 'recompose'
import cx from 'classnames'
import find from 'lodash/find'
import get from 'lodash/get'

import { getLongSuffix } from 'module/utils/helpers'

import { TOuterProps, TInnerProps } from './types'

export const playersInfoComposed = compose<TOuterProps, TInnerProps>(
  inject(
    'lexics',
    'videoLink',
    'shootsRange',
    'matchTable',
    'playersCompareWidget',
  ),
  //@ts-ignore
  withHandlers({
    getPlayerParams: ({
      playersCompareWidget: { playersInfo },
      //@ts-ignore
    }) => playerId => get(playersInfo, [playerId, 'params'], []),
  }),
  //@ts-ignore
  withHandlers({
    getImage: ({
      playersCompareWidget: { playersInfo },
      //@ts-ignore
    }) => playerId => key => get(playersInfo, [playerId, key], ''),
    //@ts-ignore
    getStatValue: ({ getPlayerParams }) => (param, playerId) => {
      const value = get(
        find(getPlayerParams(playerId), { param }), 'value',
      )
      return value ? value.toFixed() : '-'
    },
    //@ts-ignore
    getClassName: ({ getPlayerParams }) => (param, playerId) => {
      const hasVideo = get(
        find(getPlayerParams(playerId), { param }),
        'f_match_event_ids',
      )

      return cx(
        't-compare__stats-num',
        { 't-compare__clickable-cell': hasVideo },
      )
    },

    getExtLink: ({
      getPlayerParams,
      lexics: { lang },
      playersCompareWidget: { playerList },
      shootsRange,
      videoLink: { request },
    }) => (
      //@ts-ignore
      param,
      //@ts-ignore
      param_name,
      //@ts-ignore
      playerId,
    ) => {
      //@ts-ignore
      const events: Array<any> = get(
        find(getPlayerParams(playerId), { param }),
        'f_match_event_ids',
      )
      const player = get(
        find(playerList, { f_player: playerId }),
        'prop_player[0]',
        {},
      )
      const suffix = getLongSuffix(lang)

      if (events) {
        request({
          events,
          range: shootsRange,
          lang,
          playerInfo: `${player[`firstname_${suffix}`]} ${player[`lastname_${suffix}`]}`,
          lexical_formula: param_name,
          type: 'widgets',
          widgetType: 'playersCompare',
        })
      }
    },
  }),
  observer,
)
