import { action } from 'mobx'

import get from 'lodash/get'
import values from 'lodash/values'
import map from 'lodash/map'
import size from 'lodash/size'

import routes from 'config/routes'
import BasicStore from 'features/stores/BasicStore'
import stores from 'features/stores'

import { getParamObj } from './filterParams'

interface ITableReportsApiStore {
  getReport: (
    e: {},
    key: string,
    tableType?: string
  ) => Promise<void | null>,
}

//TODO переписать на MST и декомпозировать
export default class TableReportsApiStore extends BasicStore
  implements ITableReportsApiStore {
  //@ts-ignore
  getReportInfo = (tableType?: string, page: string, pageId: number) => {
    // Общие методы для доступа к информации об игроке, команде или матче
    const commonTable = get(this.getStore(`${page}Table`), 'tables')
    const commonMatches = get(this.getStore('selectedMatches'), [page, pageId])
    const commonShowOpponentStats = get(
      this.getStore(`${page}Table`), 'showOpponentStats',
    )

    const tableReportOptions = {
      matchPlayersTable: {
        matches: [pageId],
        tables: commonTable,
      },
      playerMatches: {
        matches: commonMatches,
        showOpponentStats: commonShowOpponentStats,
        tables: commonTable,
      },
      teamMatches: {
        matches: commonMatches,
        showOpponentStats: commonShowOpponentStats,
        tables: commonTable,
      },

      tournamentMatches: {
        matches: map(get(stores, 'tournamentMatches.matches', []), 'f_match'),
        showOpponentStats: get(stores, 'tournamentMatchesTableStore.tables'),
        tables: get(stores, 'tournamentMatchesTableStore.tables'),
      },
      tournamentPlayers: {}, //тут пока заглушки TODO
      tournamentTeams: {}, //тут пока заглушки TODO
    }
    //@ts-ignore
    return get(tableReportOptions, tableType, {})
  }

  //@ts-ignore
  @action getReport = (e: {}, key: string, tableType?: string) => {
    const url: string = `${routes.API_ROOT}/report/tables_report`
    const {
      getLexics,
      lang,
      page,
    }: {
      getLexics: (tableName: string | number) => string | null,
      lang: string,
      page: string,
    } = this.getStore('lexics')
    const currentProfile: string = `${page}Profile`

    const pageId: number = get(this.getStore(currentProfile).profile, 'id')
      || +get(this.getStore(currentProfile), 'matchId')

    const {
      matches,
      showOpponentStats,
      tables,
    } = this.getReportInfo(tableType, page, pageId)

    // Если матчей не выбрано, то не делаем запрос на отчет
    if (!matches.length) return null

    const setSortBy = (sortAsc: boolean, activeId: number) => ({
      flag: sortAsc ? 'ASC' : 'DESC',
      param: activeId,
    })

    const prepareMatchPlayersTable = () => {
      const { team1_id, team2_id } = get(this.getStore('matchProfile'), 'data', {})
      const preparedTables = map(values(tables), ({
        activeId,
        selectedParams,
        sortAsc,
        tableName,
        team,
      }) => {
        const table: {
          name: string,
          params: Array<number>,
          sortby: {},
          team?: number,
        } = {
          name: getLexics(tableName) || tableName,
          params: map(selectedParams, 'param_id'),
          sortby: setSortBy(sortAsc, activeId),
        }
        // Передаем в отчет поле team только в случае если выбрана одна команда
        if (size(team) === 1) {
          const teamsSelection: {
            [key: string]: number,
          } = {
            showHomeTeam: team1_id,
            showOppTeam: team2_id,
          }

          table.team = teamsSelection[team[0]]
        }

        return table
      })

      return {
        lang_id: lang,
        matches,
        tables: preparedTables,
        teams: [team1_id, team2_id],
        type: key,
        [`${page}_id`]: pageId,
      }
    }

    const prepareDefaultTable = () => ({
      lang_id: lang,
      matches,
      tables: map(values(tables), ({
        activeId,
        selectedParams,
        sortAsc,
        summaryType,
        tableName,
      }: any) => ({
        is_show_opponent: Number(showOpponentStats),
        is_total: Boolean(summaryType === 'total'),
        name: getLexics(tableName) || tableName,
        params: map(selectedParams, ({ param_id }) => getParamObj(param_id)),
        sortby: setSortBy(sortAsc, activeId),
      })),
      type: key,
      [`${page}_id`]: pageId,
    })

    // Для отчета по матчу нужна другая структура,
    // в зависимости от типа таблицы вызываем нужную
    // функцию для формирования конфига для запроса
    const prepareConfig = {
      default: prepareDefaultTable,
      matchPlayersTable: prepareMatchPlayersTable,
    }

    const getConfig = (tableType: string = '') => (
      get(prepareConfig, tableType)
      || get(prepareConfig, 'default')
    )

    const config: {
      params: {},
      uri: string,
    } = {
      params: getConfig(tableType)(),
      uri: `request_${page}_stat_scout`,
    }

    //@ts-ignore
    this[`${key}IsFetching`] = true

    return fetch(url, {
      body: JSON.stringify(config),
      method: 'POST',
    })
      .then((response: any) => {
        //@ts-ignore
        this[`${key}IsFetching`] = false
        return response.blob()
      })
      .catch((error: {}) => {
        //@ts-ignore
        this.error = error
        //@ts-ignore
        this[`${key}IsFetching`] = false
      })
  }
}
