
import { inject, observer } from 'mobx-react'

import { compose, withHandlers, lifecycle } from 'recompose'

type TInnerProps = {
  handlePopupAction: () => void,
};

type TOuterProps = {
  matchProfile: {},
  teamProfile: {},
  matchTable: {},
  lexics: {},
  playersCompareWidget: {},
  gearPopupStore: {},
  onApply: () => void,
  onClose: () => void,
};

export const PlayersComparePupupComposed = compose<
TOuterProps & TInnerProps,
TInnerProps
>(
  inject(
    'matchProfile',
    'teamProfile',
    'matchTable',
    'lexics',
    'playersCompareWidget',
    'gearPopupStore',
  ),
  //@ts-ignore
  withHandlers({
    /**
     * Запись в стор выбранных параметров в попапе шестеренки
     * @param {func} saveTempParams - функция сохранения параметров
     * @param {func} handlePopupAction - функция закрытия попапа
     * @param {string} lang - выбранный язык
     * @param {string} matchId - id матча
     */
    onApply: ({
      gearPopupStore: { tempSelected },
      handlePopupAction,
      lexics: { lang },
      matchProfile: { matchId },
      playersCompareWidget: { saveTempParams },
    }) => () => {
      saveTempParams({
        lang,
        matchId,
        params: tempSelected,
      })
      handlePopupAction()
    },
    /**
     * Закрытие попапа шестеренки
     * @param {func} editTempSelected - функция очищения выбранных параметров
     * @param {func} handlePopupAction - функция закрытия попапа
     *
     */
    onClose: ({
      gearPopupStore: { eraseTempSelected },
      handlePopupAction,
    }) => () => {
      eraseTempSelected()
      handlePopupAction()
    },
  }),
  lifecycle({
    componentDidMount() {
      const {
        //@ts-ignore
        gearPopupStore: { fillTempSelected },
        //@ts-ignore
        playersCompareWidget: { selectedParams },
      } = this.props
      fillTempSelected(selectedParams)
    },
  }),
  observer,
)
