import React, { Fragment } from 'react'
import { inject, observer } from 'mobx-react'
import { compose, withHandlers } from 'recompose'
import map from 'lodash/map'
import filter from 'lodash/filter'

import { TableUI } from 'react-sport'

import { getSummaryStatValue } from 'module/utils/helpers/getSummaryStatValue'
import { emptyStatToZero } from 'module/utils/helpers/emptyStatToZero'

import Loader from 'features/components/common/loader/Loader'
import { THead, TBody } from 'features/Table'
import { ShowMoreBtn } from 'features/ShowMoreBtn'

import { TParam } from 'features/Table/models/tableModel'
import { TFoot } from './HOCs/TFootHOC'
import { Tr } from './Tr'

import './styles.scss'

type TProps = {
  pageId: string,
  sortAsc: boolean,
  activeId: string,
  table: {
    selectedParams: Array<TParam>,
  },
  setSortedId: () => {},
  getMatchesList: () => Array<{}>,
  currentStore: {
    isLoaded: boolean,
    showOpponentStats: boolean,
    stats: Array<{}>,
    addOffset: () => void,
    partFetching: boolean,
    canFetch: boolean,
  },
  param?: TParam,
}

// Основной блок таблицы со статистикой для страницы турнира
const TournamentTableComponent = ({
  activeId,
  currentStore: {
    addOffset,
    canFetch,
    isLoaded,
    partFetching,
    showOpponentStats,
    stats,
  },
  getMatchesList,
  pageId,
  setSortedId,
  sortAsc,
  table,
}: TProps) => (

  <Fragment>
    <div className='stats-table__wrapper'>

      <table className='stats-table__table'>
        <TableUI.Thead>
          {/*
          //@ts-ignore */}
          <THead table={table} setSortedId={setSortedId} />
        </TableUI.Thead>
        {isLoaded ? (
          <Fragment>
            {/*
            //@ts-ignore */}
            <TBody
              selectedParams={table.selectedParams}
              rows={getMatchesList()}
              tableStats={stats}
              pageId={pageId}
              sortAsc={sortAsc}
              activeId={activeId}
              showOpponentStats={showOpponentStats}
              Tr={Tr}
            />
            {/*
            //@ts-ignore */}
            <TFoot
              table={table}
              tableStats={stats}
              pageId={pageId}
              activeId={activeId}
              showOpponentStats={showOpponentStats}
            />
          </Fragment>
        ) : (
          <Loader />
        )}
      </table>
    </div>
    {canFetch && (
      <div className='show-btn-container'>
        <ShowMoreBtn onClick={addOffset} isFetching={partFetching} />
      </div>
    )}
  </Fragment>
)

const tournamentTableComposed = compose(
  inject('tournamentMatchesTableStore'),
  //@ts-ignore
  withHandlers({
    /**
     * Получение списка матчей, отсортированного по значению выбранного параметра
     * @param {boolean} sortAsc - флаг для сортировки (по убыванию или по возрастанию)
     */
    getMatchesList: ({
      param,
      tournamentMatchesTableStore: { actualMatchesList, stats },
    }) => () =>
      map(actualMatchesList, (match) => {
        const sortedValue = getSummaryStatValue(
          param.param_id,
          filter(stats, { f_match: match.f_match }),
          'total',
        ).toString()
        return {
          ...match,
          param,
          sortBy: emptyStatToZero(sortedValue),
        }
      }),
  }),
  observer,
)

//@ts-ignore
export const TournamentTable = tournamentTableComposed(TournamentTableComponent)
