import { types } from 'mobx-state-tree'
import TournamentPageApiActions from './TournamentPageApiActions'

export const TournamentPage = types.model('TournamentProfile', {
  profile: types.maybeNull(types.frozen({})),
  isLoaded: false,
  error: types.maybeNull(types.frozen({})),
})

const TournamentPageStore = types.compose(
  TournamentPage,
  TournamentPageApiActions,
)

export default TournamentPageStore
