import { types, flow } from 'mobx-state-tree'

import { utils } from 'react-sport'

import get from 'lodash/get'
import find from 'lodash/find'
import reduce from 'lodash/reduce'
import pick from 'lodash/pick'

import stores from 'features/stores'

import routes from 'config/routes'

type TConfig = {
  method: string,
  body: string,
}

export const TeamsCompareApiActions = types.model({})
  .actions(self => ({
    // Запрос при первом рендере компонента.
    // Нам нужны все параметры для шестеренки
    // После ответа делаем запрос на получение данных для виджета
    basicFetch: flow(function* basicFetch(
      //@ts-ignore
      lang,
      //@ts-ignore
      matchId,
      //@ts-ignore
      paramsList,
    ) {
      // Делаем запрос данных для таблицы
      //@ts-ignore
      yield self.fetchStats(lang, matchId, paramsList)
      // Делаем запрос всех параметров (для шестеренки)
      //@ts-ignore
      yield self.getGearParamsList(1)
      // Устанавливаем выбранные параметры по-умолчанию
      //@ts-ignore
      self.setDefaultSelectedParams()
    }),
    // Запрос к API для получения статистики команд
    fetchStats(
      lang: string,
      matchId: string,
      params: Array<number>,
    ) {
      const matchInfo = get(stores, 'matchProfile.data', {})
      const teams = [matchInfo.team1_id, matchInfo.team2_id]
      const url: string = `${routes.API_ROOT}/data`
      const config: TConfig = {
        method: 'POST',
        body: JSON.stringify({
          proc: 'scout_teams_all_stat',
          params: {
            _p_match_id: Number(matchId),
            _p_season_id: null,
            _p_team_arr: teams,
            _p_param_arr: params,
            _p_lang_id: Number(lang !== 'ru'),
          },
        }),
      }
      return utils.callApi({
        url,
        config,
        //@ts-ignore
        onRequest: self._onRequest,
        //@ts-ignore
        onSuccess: self._onSuccess,
        //@ts-ignore
        onError: self._onError,
      })
    },

    _onRequest() {
      //@ts-ignore
      self.stats = []
      //@ts-ignore
      self.teamsInfo = null
      //@ts-ignore
      self.teamsInfoLoaded = false
    },

    //@ts-ignore
    _onSuccess(json: { data?: Array<{}> }) {
      const teams = get(json.data, '[0].scout_teams_all_stat', [])
      //@ts-ignore
      self.stats = get(find(teams, 'params_info', {}), 'params_info')
      //@ts-ignore
      self.teamsInfo = reduce(teams, (acc, current) => {
        //@ts-ignore
        acc[current.f_team] = pick(
          current,
          ['f_team', 'params', 'photo_team'],
        )
        return acc
      }, {})
      //@ts-ignore
      self.error = null
      //@ts-ignore
      self.teamsInfoLoaded = true
    },

    _onError(error: any) {
      //@ts-ignore
      self.error = error
      //@ts-ignore
      self.teamsInfoLoaded = false
    },
  }))
