import { types, Instance } from 'mobx-state-tree'

import { TableStore } from 'features/Table/models/tableModel'

const ParamExtended = types.model('Param', {
  f_match: types.number,
  f_match_event_ids: types.maybeNull(types.array(types.number)),
  f_match_event_ids_json: types.maybeNull(types.array(types.number)),
  f_option_0: types.number,
  f_option_1: types.maybeNull(types.number),
  f_option_2: types.maybeNull(types.number),
  f_option_3: types.maybeNull(types.number),
  f_option_4: types.maybeNull(types.number),
  f_option_5: types.maybeNull(types.number),
  f_option_6: types.maybeNull(types.number),
  f_param: types.maybeNull(types.number),
  f_player: types.maybeNull(types.number),
  f_team: types.maybeNull(types.number),
  f_variant_zone: types.maybeNull(types.number),
  id: types.string,
  param_name: types.string,
  value: types.maybeNull(types.number),
})

export const PlayerMatchesModel = types.compose(
  types.model('PlayerMatchesModel', {
    error: types.maybeNull(types.frozen({})),
    isLoaded: false,
    storeName: 'playerMatches',
    tableStats: types.array(ParamExtended),
    tables: types.frozen({}),
    url: types.string,
  }),
  // общие методы и свойства таблиц
  TableStore,
)

export type TParamExtended = Instance<typeof ParamExtended>
