import { types } from 'mobx-state-tree'

import size from 'lodash/size'

// @ts-ignore
import { saveTablesToLocalStorage } from 'module/utils/helpers'

export const addTable = types.model({}).actions(self => ({
  /**
   * Добавляем новую таблицу
   * @param {string} name - название таблицы
   * @param {string} localStorageName - ключ для записи в localStorage
   */
  addTable(
    name: string,
    localStorageName: string,
    selectedParams: Array<{}>,
  ) {
    //@ts-ignore
    const position: number = size(self.tables) + 1
    //@ts-ignore
    self.tables = {
      //@ts-ignore
      ...self.tables,
      [position]: {
        position,
        selectedParams,
        tableName: name,
        summaryType: 'average_for_match',
      },
    }
    //@ts-ignore
    saveTablesToLocalStorage({ name: localStorageName, tables: self.tables })
  },
}))
