
import { compose, withHandlers, withProps, lifecycle } from 'recompose'
import get from 'lodash/get'
import { inject, observer } from 'mobx-react'
import {
  indexLexic,
  matchLexic,
  filtersLexic,
  markersLexic,
  tableParamsLexic,
  tableParamsLexicShort,
  tableParamsNumLexic,
  videoCutsLexic,
  //@ts-ignore
} from 'config/lexics'

const MatchPageComposed = compose(
  inject('lexics', 'matchProfile'),
  //@ts-ignore
  withHandlers({
    //@ts-ignore
    getMatchProfile: ({ matchProfile: { fetch } }) => (matchId) => {
      fetch(matchId)
    },

    //@ts-ignore
    //@ts-ignore
    editTempSelected: ({ matchTable: { editTempSelected } }) => (
      //@ts-ignore
      action,
      //@ts-ignore
      param,
    ) => {
      editTempSelected(action, param)
    },
  }),
  //@ts-ignore
  withProps(({ match }) => {
    const matchId = get(match, 'params.matchId')
    return {
      matchId,
    }
  }),
  lifecycle({
    componentDidMount() {
      const {
        //@ts-ignore
        getMatchProfile,
        //@ts-ignore
        lexics,
        //@ts-ignore
        matchId,
      } = this.props
      lexics.appPage = {
        page: 'match',
        phrases: {
          ...indexLexic,
          ...matchLexic,
          ...filtersLexic,
          ...markersLexic,
          ...tableParamsLexic,
          ...tableParamsLexicShort,
          ...tableParamsNumLexic,
          ...videoCutsLexic,
        },
      }
      getMatchProfile(matchId)
    },
  }),
  observer,
)

export default MatchPageComposed
