import React from 'react'
import { inject, observer } from 'mobx-react'
import { compose, withHandlers } from 'recompose'

import get from 'lodash/get'
import find from 'lodash/find'
import orderBy from 'lodash/orderBy'

import PlayerTr from './PlayerTr'

type TProps = {
  avatarIsVisible: boolean,
  selectedParams: Array<{}>,
  activeId: string,
  getPlayersList: (
    sortAsc: boolean,
    activeId: string,
    position: number
  ) => Array<{ f_player: number }>,
  sortAsc: boolean,
  position: number,
};

const PlayersList = ({
  activeId,
  avatarIsVisible,
  getPlayersList,
  position,
  selectedParams,
  sortAsc,
}: TProps) => (
  <tbody>
    {getPlayersList(sortAsc, activeId, position).map(player => (
      <PlayerTr
        key={player.f_player}
        player={player}
        activeId={activeId}
        selectedParams={selectedParams}
        avatarIsVisible={avatarIsVisible}
        position={position}
      />
    ))}
  </tbody>
)

const PlayersListComposed = compose<any, TProps>(
  inject('teamPlayers'),
  //@ts-ignore
  withHandlers({
    /**
     * Получение списка игроков, отсортированного по значению выбранного параметра
     * @param {boolean} sortAsc - флаг для сортировки (по убыванию или по возрастанию)
     */
    getPlayersList: ({ teamPlayers: { players, tables } }) => (
      sortAsc: boolean,
      activeId: string,
      position: number,
    ) => {
      if (!activeId) return players
      const field: string = get(tables, `${position}.summaryType`) === 'average_for_match'
        ? 'averageValue'
        : 'value'
      const id: string = activeId.split('_')[0]
      //@ts-ignore
      const value = player =>
        get(find(player.params, { param: Number(id) }), field, 0)
      const sortType = sortAsc ? 'asc' : 'desc'
      return orderBy(players, value, sortType)
    },
  }),
  observer,
)(PlayersList)

export default PlayersListComposed
