import React, { Fragment } from 'react'
import { inject, observer } from 'mobx-react'

import {
  compose,
  withProps,
  withHandlers,
} from 'recompose'
import map from 'lodash/map'
import filter from 'lodash/filter'

import { getLongSuffix } from 'module/utils/helpers'
import { PopupHOC } from '../PopupHOC'

import {
  TProps,
  TWithPropsInner,
  TWithHandlersOuter,
  TWithPropsOuter,
  TPlayer,
  TComposeOuter,
} from './types'

// Попап со списком игроков разбитым по командам
const PopupWithHomePlayersComponent = ({
  filters: { handleTempCheck },
  getFullName,
  getItemClassName,
  playersList,
  toggleOpenClose,
  type,
}: TProps) => (
  <Fragment>
    <div className='popup-column__wrapper'>
      <ul className='popup-column__list'>
        {map(playersList, ({ f_player: playerId, prop_player: player }) => (
          <li
            className={getItemClassName(`${playerId}_player`)}
            key={playerId}
            onClick={() => handleTempCheck(`${playerId}_player`, type)}
          >
            <span>{getFullName(player[0])}</span>
          </li>
        ))}
      </ul>
    </div>
    <button className='map-block__popup-button' onClick={toggleOpenClose}>
      OK
    </button>
  </Fragment>
)

const popupWitHomePlayersComposed = compose<TComposeOuter, TProps>(
  inject(
    'playerInfo',
    'lexics',
    'playerProfile',
  ),
  observer,
  withProps<TWithPropsOuter, TWithPropsInner>(
    ({
      lexics: { lang },
      playerInfo: { basicData },
      playerProfile: { profile },
    }) => ({
      playersList: profile
        ? filter(
          basicData, ({ f_player }: {f_player: number}) => f_player !== profile.id,
        )
        : basicData,
      suffix: getLongSuffix(lang),
    }),
  ),
  withHandlers<TWithPropsOuter, TWithHandlersOuter>({
    getFullName: ({ suffix }) => (player: TPlayer) =>
      `${player[`firstname_${suffix}`]} ${player[`lastname_${suffix}`]}`,
  }),
  PopupHOC,
  observer,
)

export const PopupWithHomePlayers = popupWitHomePlayersComposed(PopupWithHomePlayersComponent)
