import React from 'react'
// @ts-ignore
import { SeasonSelect } from 'features/TournamentSeasonSelect'
// @ts-ignore
import { TournamentHeaderStats } from 'features/TournamentHeaderStats'
// @ts-ignore
import Loader from 'features/components/common/loader/Loader'
import TournamentPageInfo from './TournamentPageInfo'

type TProps = {
  tournamentId: number,
  profile: {},
  isLoaded: boolean,
}

const TournamentPageHeader = ({ isLoaded, profile, tournamentId }: TProps) => (
  <section className='tournament-header'>
    <div className='tournament-header-wrapper'>
      <div className='tournament-header__profile-info'>
        {/*
        //@ts-ignore */}
        <TournamentPageInfo profile={profile} />
      </div>
      <div className='tournament-header__season-select'>
        {/*
        //@ts-ignore */}
        {profile && <SeasonSelect tournamentId={tournamentId} />}
      </div>
      <div className='tournament-header__stats'>
        {isLoaded ? <TournamentHeaderStats /> : <Loader />}
      </div>
    </div>
  </section>
)

export default TournamentPageHeader
