import React, { Fragment } from 'react'
import { observer } from 'mobx-react'
import { compose, withHandlers } from 'recompose'

import { T9n, SimpleDropdown } from 'react-sport'

// @ts-ignore
import { getDropdownBtnsHoc } from 'hocs/getDropdownBtnsHoc'

type TProps = {
  getClassName: (value: boolean) => string,
  currentStore: {
    avatarIsVisible: boolean,
    toggleShowAvatar: () => void,
    changeValueType: (type: string, position: number) => void,
  },
  getDropdownBtns: (options: {}) => Array<any>,
  table: {
    position: number,
    summaryType: string,
  },
  setTypeHandler: () => void,
}

// Компонент левой части шапки таблицы, где есть статистика соперника
const HeaderWithAvatarComponent = observer(
  ({
    currentStore: { avatarIsVisible, toggleShowAvatar },
    getClassName,
    getDropdownBtns,
    setTypeHandler,
    table: { position, summaryType },
  }: TProps) => (
    <Fragment>
      {/*
      //@ts-ignore */}
      <SimpleDropdown placeholder={<T9n t='total' />}>
        {getDropdownBtns({
          summaryType,
          position,
          onClick: setTypeHandler,
        })}
      </SimpleDropdown>
      <a className={getClassName(avatarIsVisible)} onClick={toggleShowAvatar}>
        <T9n t='show_photo' />
      </a>
    </Fragment>
  ),
)

const HeaderWithAvatarComposed = compose<any, TProps>(
  //@ts-ignore
  withHandlers({
    setTypeHandler: ({ currentStore: { changeValueType } }) =>
      //@ts-ignore
      //@ts-ignore
      (type, position) => () => changeValueType(type, position),
  }),
  getDropdownBtnsHoc,
)

export const HeaderWithAvatar = HeaderWithAvatarComposed(
  HeaderWithAvatarComponent,
)
