// @ts-ignore
import { action } from 'mobx'
import { utils as remoteUtils } from 'react-sport'
import get from 'lodash/get'

// @ts-ignore
import routes from 'config/routes'

// @ts-ignore
// @ts-ignore
export const getToolTipResult = (markers, resultList) => {
  //@ts-ignore
  const markersWithResult = []
  //@ts-ignore
  markers.forEach((marker) => {
    const { prop } = marker
    const result = resultList.find((
      {
        //@ts-ignore
        //@ts-ignore
        //@ts-ignore
        prop_id,
        //@ts-ignore
        prop_value_id,
      }, //@ts-ignore
    ) =>
      //@ts-ignore
      prop.find(
        //@ts-ignore
        property => property[0] === prop_id && property[2] === prop_value_id,
      ))
    if (result) {
      marker.result_lexic = result.lexical_formula
      markersWithResult.push(marker)
    } else {
      markersWithResult.push(marker)
    }
  })
  //@ts-ignore
  return markersWithResult
}

// @ts-ignore
const ToolTipActions = superclass =>
  class extends superclass {
    //@ts-ignore
    @action fetch = () => {
      const url = `${routes.API_ROOT}/data`
      const config = {
        method: 'POST',
        body: JSON.stringify({
          proc: 'scout_event_prop_action_result',
          params: {
            _p_action_result: true,
          },
        }),
      }

      remoteUtils.callApi({
        url,
        config,
        onRequest: this._onRequest.bind(this),
        onSuccess: this._onSuccess.bind(this),
        onError: this._onError.bind(this),
      })
    };

    //@ts-ignore
    _onSuccess({ data }) {
      //@ts-ignore
      this.loaded = true
      //@ts-ignore
      this.error = false
      //@ts-ignore
      this.data = get(data, '[0].scout_event_prop_action_result', [])
    }

    _onRequest() {
      //@ts-ignore
      this.loaded = false
      //@ts-ignore
      this.error = false
    }

    //@ts-ignore
    _onError(error) {
      //@ts-ignore
      this.error = error
      //@ts-ignore
      this.loaded = false
    }
  }

export default ToolTipActions
