import { observable, action } from 'mobx'
// @ts-ignore
import { middleOptions } from 'config/markers'
// @ts-ignore
import { setCoord, getPoints } from 'module/utils/helpers'

//функция проверки наличия точки по середине
// @ts-ignore
// @ts-ignore
// @ts-ignore
const getMiddlePoint = ({ prop }) =>
  prop.find(
    //@ts-ignore
    //@ts-ignore
    ([key, property]) => key === 24 && middleOptions.includes(property),
  )

class Marker {
  @observable visible = true;

  //@ts-ignore
  //@ts-ignore
  constructor(props, store) {
    Object.assign(this, props)
    const { active } = store('tabs')
    //@ts-ignore
    const withMiddlePoint = getMiddlePoint(this)
    //получаем позицию маркера
    const {
      //@ts-ignore
      pos_from_x,
      //@ts-ignore
      pos_from_y,
      //@ts-ignore
      pos_middle_x,
      //@ts-ignore
      pos_middle_y,
      //@ts-ignore
      pos_to_x,
      //@ts-ignore
      pos_to_y,
      //@ts-ignore
    } = getPoints(active, this, withMiddlePoint)
    //устанавливаем координаты для точек откуда, куда и блок, если есть точка по середине
    //@ts-ignore
    this.from = setCoord(pos_from_x[2], pos_from_y[2])
    //@ts-ignore
    this.middle = withMiddlePoint && setCoord(pos_middle_x[2], pos_middle_y[2])
    //@ts-ignore
    this.to = setCoord(pos_to_x[2], pos_to_y[2])
  }

  //@ts-ignore
  @action setVisible = (value) => {
    this.visible = value
  };
}

export default Marker
