import { observer } from 'mobx-react'

import filter from 'lodash/filter'
import includes from 'lodash/includes'
import { compose, withProps } from 'recompose'

import { getStatValue } from 'module/utils/helpers/getStatValue'
import { getSummaryStatValue } from 'module/utils/helpers/getSummaryStatValue'
import { getLongSuffix } from 'module/utils/helpers'

import { getExtLinkHOC } from 'hocs/getExtLinkHOC'

import { playListTypes } from 'config/playListTypes'

import {
  TPropsInner,
  TPropsOuter,
  TWithPropsInner,
  TWithPropsOuter,
  TStat,
} from './types'

import { getStatsFilterOptions } from '../../helpers/getStatsFilterOptions'

export const tdComposed = compose<TPropsInner, TPropsOuter>(
  getExtLinkHOC,
  observer,
  withProps<TWithPropsInner, TWithPropsOuter>(({
    getExtLink,
    id,
    lexics: { lang },
    param,
    rowInfo,
    tableStats,
    tableType,
    type = 'average_for_match',
  }) => {
    const preparedStats = filter<TStat>(
      tableStats,
      getStatsFilterOptions({
        matchId: rowInfo.f_match,
        param,
        playerId: id,
        tableType,
      }),
    )
    const prepareExtLinkArgs = (lang: string) => {
      const defaultOptions: {} = {
        id,
        match: rowInfo,
        param,
        playListType: playListTypes.matches,
        stats: preparedStats,
      }

      if (includes(['playerMatches', 'matchPlayersTable'], tableType)) {
        const suffix = getLongSuffix(lang)
        const firstName = rowInfo[`firstname_${suffix}`]
        const lastName = rowInfo[`lastname_${suffix}`]
        const playerName = `${firstName} ${lastName}`

        return {
          ...defaultOptions,
          playerInfo: playerName,
        }
      }

      return defaultOptions
    }

    return {
      handleClick: getExtLink(prepareExtLinkArgs(lang)),
      value: type === 'total'
        ? getSummaryStatValue(param.param_id, preparedStats, 'total')
        : getStatValue(param, preparedStats),
    }
  }),
  observer,
)
