
// @ts-ignore
import { action, computed } from 'mobx'

interface IRangeActions {
  rangePopup: () => void,
  setAllPossession: () => void,
  setRange: () => void,
  setBefore: (value: number) => number | void,
  setAfter: (value: number) => number | void,
}

// @ts-ignore
const RangeActions = (superclass: Class<any>) =>
  class extends superclass implements IRangeActions {
    /** Функция закрытия попап окна
     */
    //@ts-ignore
    @action rangePopup = () => {
      //@ts-ignore
      //@ts-ignore
      if (this.before === '' || this.before < 1) {
        this.setBefore(6)
      }
      //@ts-ignore
      //@ts-ignore
      if (this.after === '' || this.after < 1) {
        this.setAfter(6)
      }
      //@ts-ignore
      //@ts-ignore
      this.isOpened = !this.isOpened
    };

    /** Показать все владение (чекбокс)
     */
    //@ts-ignore
    @action setAllPossession = () => {
      //@ts-ignore
      //@ts-ignore
      this.allPossession = !this.allPossession
    };

    /** Показать диапазон (чекбокс)
     */
    //@ts-ignore
    @action setRange = () => {
      //@ts-ignore
      //@ts-ignore
      this.range = !this.range
    };

    /** Выставить секунды до события
     * @param {number} before - секунды до момента
     */
    //@ts-ignore
    @action setBefore = (before: number | string) => {
      //@ts-ignore
      this.before = before
      // TODO переписать на общий
      // Выставляем секунды во внутреннем плеере тоже
      //@ts-ignore
      this.getStore('videoPlayer').range.setVideoStart(before)
    };

    /** Выставить секунды после события
     * @param {number} after - секунды до момента
     */
    //@ts-ignore
    @action setAfter = (after: number | string) => {
      //@ts-ignore
      this.after = after
      // TODO переписать на общий
      // Выставляем секунды во внутреннем плеере тоже
      //@ts-ignore
      this.getStore('videoPlayer').range.setVideoEnd(after)
    };

    /** Считаем всю длительность владения
     */
    //@ts-ignore
    @computed get possessionLength() {
      //@ts-ignore
      //@ts-ignore
      return this.after + this.before
    }
  }

export default RangeActions
