import { types } from 'mobx-state-tree'
import TournamentSeasonSelectApiActions from './TournamentSeasonSelectApiActions'
import TournamentSeasonSelectActions from './TournamentSeasonSelectActions'

const Season = types.model('Season', {
  season_id: types.maybeNull(types.number),
  season_name: types.maybeNull(types.string),
})

export const TournamentSeasons = types.model('TournamentSeasons', {
  seasons: types.map(Season),
  isLoaded: false,
  error: types.maybeNull(types.frozen({})),
})

const TournamentSeasonsStore = types.compose(
  TournamentSeasons,
  TournamentSeasonSelectApiActions,
  TournamentSeasonSelectActions,
)

export default TournamentSeasonsStore
