
import { types } from 'mobx-state-tree'
import TeamPlayersApiActions from './TeamPlayersApiActions'
import TeamPlayersActions from './TeamPlayersActions'

// Модель параметра для игрока
const PlayerParam = types.model('PlayerParam', {
  data_type: types.string,
  f_match_event_ids: types.maybeNull(types.number),
  matches_count: types.maybeNull(types.number),
  option: types.number,
  param: types.number,
  value: types.number,
  averageValue: types.number,
})

// Модель параметра(шестеренка)
const Param = types.model('Param', {
  clickable: types.number,
  f_option_3: types.number,
  full_lexical_formula: types.string,
  gear_group_id: types.number,
  gear_subgroup_id: types.number,
  is_default: types.maybeNull(types.string),
  lexical_formula: types.string,
  name: types.string,
  param1_id: types.maybeNull(types.array(types.string)),
  param2_id: types.maybeNull(types.array(types.string)),
  param_id: types.identifierNumber,
  short_lexical_formula: types.string,
  sort_order: types.number,
})

// Модель параметров, разбитых на логические группы (основные, подачи и тд)
const GearParam = types.model('GearParam', {
  groupName: types.maybeNull(types.string),
  params: types.array(Param),
})

//Модель игрока
const Player = types.model('Player', {
  f_player: types.number,
  matches_count: types.number,
  nickname_eng: types.maybeNull(types.string),
  nickname_rus: types.maybeNull(types.string),
  params: types.maybeNull(types.array(PlayerParam)),
  shirt_num: types.string,
  short_name_eng: types.string,
  short_name_rus: types.string,
})

// Модель группы с названием
const ParamGroup = types.model('ParamGroup', {
  group_id: types.number,
  group_lexical_formula: types.number,
  group_name: types.string,
})

export const TeamPlayers = types.model('TeamPlayers', {
  totalParams: types.array(Param),
  gearParams: types.array(GearParam),
  tables: types.frozen({
    position: types.number,
    selectedParams: types.array(Param),
    tableName: types.string,
    summaryType: types.string,
    avatarIsVisible: types.boolean,
  }),
  tempSelected: types.array(Param),
  players: types.array(Player),
  tempTableName: types.union(types.string, types.number, types.undefined),
  isFetching: types.boolean,
  error: types.frozen({}),
  paramGroups: types.array(ParamGroup),
  events: types.array(types.number),
})

const TeamPlayersStore = types.compose(
  TeamPlayers,
  TeamPlayersActions,
  TeamPlayersApiActions,
)

export default TeamPlayersStore
