import { types } from 'mobx-state-tree'

import size from 'lodash/size'

import { saveTablesToLocalStorage } from 'module/utils/helpers'

export const moveTable = types.model({}).actions(self => ({
  /** Перемещение таблицы вверх/вниз
* @param {number} position - порядковый номер таблицы
* @param {string} type - тип перемещения (вверх или вниз)
* @param {string} localStorageName - ключ для записи в localStorage
  */
  moveTable(
    position: number,
    type: string,
    localStorageName: string,
  ) {
    //@ts-ignore
    const tableCount: number = size(self.tables)
    //@ts-ignore
    const currentTable: {} = self.tables[position]
    //@ts-ignore
    const prevTable: {} = self.tables[position - 1]
    //@ts-ignore
    const nextTable: {} = self.tables[position + 1]

    if (
      (type === 'up' && position === 1)
      || (type === 'down' && position === tableCount)
    ) return null

    if (type === 'up') {
      //@ts-ignore
      self.tables = {
        //@ts-ignore
        ...self.tables,
        [position - 1]: {
          ...currentTable,
          position: position - 1,
        },
        [position]: {
          ...prevTable,
          position,
        },
      }
    }

    if (type === 'down') {
      //@ts-ignore
      self.tables = {
        //@ts-ignore
        ...self.tables,
        [position]: {
          ...nextTable,
          position,
        },
        [position + 1]: {
          ...currentTable,
          position: position + 1,
        },
      }
    }
    //@ts-ignore
    saveTablesToLocalStorage({ name: localStorageName, tables: self.tables })
  },
}))
