
import React from 'react'
import { inject, observer } from 'mobx-react'

import { compose, withHandlers, withState } from 'recompose'

import { T9n } from 'react-sport'

import { PlayersComparePopup } from './PlayersComparePopup'

type TProps = {
  handlePopupAction: () => void,
  popupOpened: boolean,
};

const PlayerCompareHeader = ({ handlePopupAction, popupOpened }: TProps) => (
  <div className='t-compare__header'>
    <T9n className='t-compare__title' t='players_table' />
    <a className='t-compare__gear-options' onClick={handlePopupAction}>
      <i className='icon-settings' />
    </a>
    {popupOpened && (
      <PlayersComparePopup handlePopupAction={handlePopupAction} />
    )}
  </div>
)

const PlayerCompareHeaderComposed = compose(
  inject('lexics', 'videoLink', 'shootsRange', 'matchTable'),
  withState('popupOpened', 'openPopup', false),
  //@ts-ignore
  withHandlers({
    //@ts-ignore
    handlePopupAction: ({ openPopup }) => () => openPopup(n => !n),
  }),
  observer,
  //@ts-ignore
)(PlayerCompareHeader)

export default PlayerCompareHeaderComposed
