import React from 'react'

type TProps = {
  stats: Array<{
    param: number,
    //@ts-ignore
    param_name: string | null,
    //@ts-ignore
    param_name_short: string | null,
  }>,
  selectedPlayers: {},
  team1: number,
  team2: number,
  getStatValue: (param: number, playerId: number) => number | string,
  getClassName: (param: number, playerId: number) => string,
  getExtLink: (
    param: number,
    param_name: string,
    playerId: number
  ) => void,
}

export const PlayersStats = ({
  getClassName,
  getExtLink,
  getStatValue,
  selectedPlayers,
  stats,
  team1,
  team2,
}: TProps) => (
  <div className='t-compare__stats-container'>
    {stats.map(({
      param,
      param_name,
      param_name_short,
    }, index) => (
      <div
        key={`${param}_${param_name_short}_${index}`}
        className='t-compare__stats-row'
      >
        <span
          //@ts-ignore
          className={getClassName(param, selectedPlayers[team1])}
              //@ts-ignore
          onClick={() => getExtLink(param, param_name, selectedPlayers[team1])}
        >
          {/*
          //@ts-ignore */}
          {getStatValue(param, selectedPlayers[team1])}
        </span>
        <span className='t-compare__stats-name'>{param_name}</span>
        <span
          //@ts-ignore
          className={getClassName(param, selectedPlayers[team2])}
              //@ts-ignore
          onClick={() => getExtLink(param, param_name, selectedPlayers[team2])}
        >
          {/*
          //@ts-ignore */}
          {getStatValue(param, selectedPlayers[team2])}
        </span>
      </div>
    ))}
  </div>
)

export default PlayersStats
