import { observer, inject } from 'mobx-react'

import {
  compose,
  withState,
  withHandlers,
} from 'recompose'

import {
  TRangeSettingsHocInner,
  TWithHandlersOuter1,
  TWithHandlersInner1,
  TWithHandlersOuter2,
  TWithHandlersInner2,
} from './types'

export const rangeSettingsHoc = compose<TRangeSettingsHocInner, any>(
  inject('shootsRange'),
  withState(
    'rangeBefore',
    'setRangeBefore',
    ({ shootsRange: { before } }) => before,
  ),
  withState(
    'rangeAfter',
    'setRangeAfter',
    ({ shootsRange: { after } }) => after,
  ),
  withHandlers<TWithHandlersOuter1, TWithHandlersInner1>({
    setRangeState: ({
      setRangeAfter,
      setRangeBefore,
    }) => (name, value) => {
      const setNewStateRange = name === 'start' ? setRangeBefore : setRangeAfter
      setNewStateRange(value)
    },
  }),
  withHandlers<TWithHandlersOuter2, TWithHandlersInner2>({
    onBlurHandler: ({
      rangeAfter,
      rangeBefore,
      shootsRange: { setAfter, setBefore },
    }) => () => {
    // Записываем в стор выбранный диапазон
      setBefore(rangeBefore)
      setAfter(rangeAfter)
    },
    onChangeHandler: ({ setRangeState }) => (e) => {
      const { name, value } = e.target
      const newValue = value === '' ? value : parseInt(value, 10)
      setRangeState(name, newValue)
    },
  }),
  observer,
)
