import React from 'react'
import { inject, observer } from 'mobx-react'

import { compose, lifecycle } from 'recompose'

import {
  T9n,
  Langs,
  TLexics,
  LiveSearchUI,
} from 'react-sport'

import { indexLexic } from 'config/lexics'

import { ProfilePopup } from 'features/ProfilePopup'
import { UploadVideoBtn } from 'features/UploadVideoBtn'

type TPropsLifecycle = {
  lexics: TLexics,
}

type TComposeInner = {
  auth: {
    logout: () => void,
  },
  lexics: TLexics,
}

export const HomeComponent = ({ auth: { logout } }: TComposeInner) => (
  <section className='home'>
    <div className='header'>
      <div className='header-table' data-block='sharedHeaderTable'>
        <div className='header-row'>
          <div className='header-column-left'>
            <div className='container-fluid'>
              <div className='row'>
                <div className='logo' />
              </div>
            </div>
          </div>
          <p className='home__text'>
            <T9n t='enter_team_or_player' />
          </p>
          <div className='header-column-responsive' id='header-live-search'>
            <LiveSearchUI.LiveSearch />
          </div>
          <div className='header-column-right'>
            <UploadVideoBtn />
            <ProfilePopup logout={logout} />
            <div className='lang-wrapper'>
              <Langs />
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
)

const homeComposed = compose<TComposeInner, {}>(
  inject('auth', 'lexics'),
  lifecycle<TPropsLifecycle, {}>({
    componentDidMount() {
      const { lexics } = this.props
      lexics.appPage = { page: 'index', phrases: indexLexic }
    },
  }),
  observer,
)

export const Home = homeComposed(HomeComponent)
