import { compose, withHandlers, withState, withProps } from 'recompose'
import { inject, observer } from 'mobx-react'

import { setSortedIdHOC } from 'hocs'

type TProps = {
  activeId: string | void,
  deleteTable: () => void,
  handlePopupAction: () => void,
  handleNewPopupAction: () => void,
  links: Array<any>,
  moveTableUp: () => void,
  moveTableDown: () => void,
  newPopupOpened: () => boolean,
  popupOpened: () => boolean,
  resetTable: () => void,
  setSortedId: () => void,
  setType: (value: string) => string,
  teamPlayers: {
    isFetching: boolean,
    gearParams: Array<{}>,
    editTempSelected: () => void,
    tempSelected: Array<{}>,
  },
};

export const playersTableComposed = compose<any, TProps>(
  inject('lexics', 'teamPlayers', 'tableReportsMST'),
  withState('popupOpened', 'openPopup', false),
  withState('newPopupOpened', 'openNewPopup', false),
  // Хок хендлера сортировки
  setSortedIdHOC,
  observer,
  //@ts-ignore
  withProps(
    //@ts-ignore
    ({
      param: { gear_group_id, param_id },
      tableReportsMST: { pdfIsFetching, xlsIsFetching },
    }) => ({
      activeId: `${param_id}_${gear_group_id}`,
      // Пока действий на клик нет, то не нужен и loader, ставим false.
      links: [{ xls: xlsIsFetching }, { pdf: pdfIsFetching }],
    }),
  ),
  //@ts-ignore
  withHandlers({
    //@ts-ignore
    handlePopupAction: ({ openPopup }) => () => openPopup(n => !n),
    //@ts-ignore
    handleNewPopupAction: ({ openNewPopup }) => () => openNewPopup(n => !n),
    // Дополняем метод записью в сторе для таблицы
    // Устанавливаем активный параметр - по нему идет сортировка в таблице
    handleSetActiveParam: ({
      changeActiveParam,
      table,
      teamPlayers: { setActiveParam },
      //@ts-ignore
    }) => (param) => {
      // Запускаем функцию из setSortedIdHOC.
      changeActiveParam({
        newParam: param,
        storeAction: setActiveParam,
        table,
      })
    },
  }),
  observer,
)
