import React from 'react'
import map from 'lodash/map'

import { Td } from 'features/Table/components/Td'

import { TPropsTr } from './types'

import { LeftSideTr } from '../LeftSideTr'
import { TrComposed } from './composed'

// Строка со статистикой по матчу
export const Tr = TrComposed(
  ({
    activeId,
    getClassName,
    getPost,
    matchLinks: { isFetching, links },
    matchPopupOpened,
    pageId,
    rowInfo,
    selectedParams,
    showOpponentStats,
    tableStats,
    toggleClose,
  }: TPropsTr) => (
    <tr className='stats-table__table-row'>
      {/*
        //@ts-ignore */}
      <LeftSideTr
        showOpponentStats={showOpponentStats}
        rowInfo={rowInfo}
        pageId={pageId}
        handleClick={getPost}
        matchPopupOpened={matchPopupOpened}
        isFetching={isFetching}
        links={links}
        toggleClose={toggleClose}
      />
      {map(selectedParams, param => (
        <td
          key={`${param.param_id}_${param.gear_group_id}`}
          className={getClassName(param, activeId)}
        >
          {showOpponentStats ? (
            <table>
              <tbody>
                {map([rowInfo.team_id, rowInfo.opp_team_id], team => (
                  <tr key={team}>
                    <Td
                      id={team}
                      param={param}
                      tableStats={tableStats}
                      rowInfo={rowInfo}
                      tableType='tournamentMatches'
                    />
                  </tr>
                ))}
              </tbody>
            </table>
          ) : (
            <table>
              <tbody>
                <tr>
                  <Td
                    param={param}
                    tableStats={tableStats}
                    rowInfo={rowInfo}
                    type='total'
                    tableType='tournamentMatchesTotal'
                  />
                </tr>
              </tbody>
            </table>
          )}
        </td>
      ))}
    </tr>
  ),
)
