import React from 'react'
import { inject, observer } from 'mobx-react'

import { withHandlers, compose } from 'recompose'
import map from 'lodash/map'
import get from 'lodash/get'

import cx from 'classnames'

import { T9n, OutsideClick } from 'react-sport'

import SelectLabel from 'features/components/common/SelectLabel'
import { MatchSelectList } from 'features/components/common/MatchSelectList'

import names from 'config/matchesSelection'

import {
  TComposeInner,
  TPropsOuter,
  TWithHandlersOuter,
  TWithHandlersInner,
} from './types'

const MatchSelectContainerComponent = ({
  dropDownClick,
  extendedSelectItem,
  getSelected,
  matches: { isFetching },
  pageId,
  scrollHandle,
  selectedMatches,
  selectItem,
}: TComposeInner) => (
  <div className='player-card-stats-dropdown'>
    <div className='player-card-dropdown'>
      <a
        className='selectBox selectBox-dropdown'
        //@ts-ignore
        tabIndex='0'
        title=''
        onClick={dropDownClick}
      >
        <span className='selectBox-label'>
          {/*
            //@ts-ignore */}
          <SelectLabel selected={getSelected()} />
        </span>
        <span className='selectBox-arrow' />
      </a>
      {selectedMatches.opened && (
        <div
          onScroll={scrollHandle}
          className={cx('player-card-stats-dropdown-inner', 'loading-active', {
            'loading-active_global': isFetching,
          })}
        >
          <ul
            className={cx(
              'player-card-stats-dropdown-list',
              'player-card-stats-dropdown_dates',
              'player-card-stats-dropdown_checkboxes',
            )}
          >
            {map(names, (name, key) => (
              <li key={key} onClick={e => extendedSelectItem(e, key)}>
                {/*
                  //@ts-ignore */}
                <T9n t={names[key]} />
              </li>
            ))}
          </ul>
          <hr className='player-card-stats-dropdown-list-hr' />
          <MatchSelectList
            pageId={pageId}
            selectItem={selectItem}
            selected={getSelected()}
          />
        </div>
      )}
    </div>
  </div>
)

const matchSelectContainerComposed = compose<TComposeInner, TPropsOuter>(
  inject(
    'lexics',
    'matches',
    'selectedMatches',
  ),
  OutsideClick,
  withHandlers<TWithHandlersOuter, TWithHandlersInner>({
    getSelected: ({
      lexics: { page },
      pageId,
      selectedMatches,
    }) => () => {
      const selected = selectedMatches.opened
        ? selectedMatches.tempSelected
        : selectedMatches.selectedPeriod
        || get(selectedMatches[page], `${pageId}`, [])

      return selected ? selected.slice() : []
    },
  }),
  observer,
)

export const MatchSelectContainer = matchSelectContainerComposed(MatchSelectContainerComponent)
