import React from 'react'
import { inject, observer } from 'mobx-react'
import { compose, withProps } from 'recompose'
// @ts-ignore
import { getLongSuffix, isNationalTeam } from 'module/utils/helpers'
import cx from 'classnames'

// @ts-ignore
import Avatar from 'features/components/common/Avatar'

type TProps = {
  teamProfile: {
    profile: {
      country_id: number,
      team_type: number,
      photo: string,
    },
    isLoaded: boolean,
  },
  suffix: string,
}

export const TeamCard = ({
  suffix,
  teamProfile: { isLoaded, profile },
}: TProps) => {
  const { country_id, photo, team_type } = profile
  return (
    <div
      className={cx({
        'player-card-left': isLoaded,
        'player-card-left player-card-left_loader': !isLoaded,
      })}
    >
      <div className='player-column player-column-avatar'>
        <div
          className={cx('player-card-avatar', {
            'player-card-avatar_national': isNationalTeam(team_type),
          })}
        >
          <Avatar image={photo} name='team' />
        </div>
      </div>
      <div className='player-column player-column-name'>
        <h1 className='player-card-title'>
          <span>
            {/*
            //@ts-ignore */}
            {profile[`short_name_${suffix}`] || profile[`name_${suffix}`]}
          </span>
        </h1>
        <div className='player-card-team'>
          {/*
          //@ts-ignore */}
          {country_id && <a>{profile[`country_name_${suffix}`]}</a>}
        </div>
      </div>
    </div>
  )
}

const TeamCardComposed = compose(
  inject('teamProfile', 'lexics'),
  observer,
  //@ts-ignore
  withProps(({ lexics: { lang } }) => ({
    suffix: getLongSuffix(lang),
  })),
  //@ts-ignore
)(TeamCard)

export default TeamCardComposed
