import { types } from 'mobx-state-tree'
import has from 'lodash/has'
import every from 'lodash/every'
import forEach from 'lodash/forEach'
import find from 'lodash/find'
import get from 'lodash/get'
import reduce from 'lodash/reduce'

import { toJS } from 'mobx'
// @ts-ignore
import stores from 'features/stores'

const FilterActions = types
  .model({})
  .volatile(() => ({
    tempSelectedFilters: {},
  }))
  .actions(self => ({
    clear() {
      //@ts-ignore
      self.checked = {}
    },

    //@ts-ignore
    toggleOpenMenu(type) {
      //@ts-ignore
      //@ts-ignore
      self.opened = !self.opened
      if (type === 'reset') {
        self.tempSelectedFilters = {}
        return null
      }
      //@ts-ignore
      if (self.opened) {
        //@ts-ignore
        self.tempSelectedFilters = self.checked
      } else {
        //@ts-ignore
        self.checked = self.tempSelectedFilters
        self.tempSelectedFilters = {}
      }
      return null
    },

    handleTempCheck(id: number | string, popupType?: string) {
      const tempClone = { ...self.tempSelectedFilters }
      //@ts-ignore
      const filterInfo = toJS(self.findFilterInfo(id, popupType))
      if (has(tempClone, id)) {
        //@ts-ignore
        delete tempClone[id]
      } else if (filterInfo) {
        //@ts-ignore
        tempClone[id] = filterInfo
      }
      self.tempSelectedFilters = tempClone
      return null
    },

    //@ts-ignore
    handleAllTempCheck(ids, popupType?: string) {
      const tempClone = { ...self.tempSelectedFilters }
      if (every(ids, id => has(tempClone, id))) {
        forEach(ids, (id) => {
          //@ts-ignore
          delete tempClone[id]
        })
      } else {
        forEach(ids, (id) => {
          //@ts-ignore
          const filterInfo = toJS(self.findFilterInfo(id, popupType))
          if (filterInfo) {
            //@ts-ignore
            tempClone[id] = filterInfo
          }
        })
      }
      self.tempSelectedFilters = tempClone
    },

    handleUncheck(id: number | string) {
      //@ts-ignore
      const checkedCloned = { ...self.checked }
      delete checkedCloned[id]
      //@ts-ignore
      self.checked = checkedCloned
    },
  }))
  .views(self => ({
    findFilterInfo(id: number | string, popupType?: string) {
      //@ts-ignore
      if (!popupType) return find(self.data, { id })

      //@ts-ignore
      const getIdFromString = id => Number(id.split('_')[0])
      const players = popupType === 'popupWithHomePlayers'
        //@ts-ignore
        ? self.homePlayers
        //@ts-ignore
        : self.teamPlayers
      return find(players, { player_id: getIdFromString(id) })
    },

    get teamPlayers() {
      const players = get(stores, 'mapShoot.data')
      return reduce(
        players,
        (acc, { prop_player }) => {
          //@ts-ignore
          //@ts-ignore
          prop_player.forEach(player => acc.push(player))
          return acc
        },
        [],
      )
    },

    get homePlayers() {
      const players = get(stores, 'playerInfo.basicData')
      //@ts-ignore
      return reduce(
        players,
        //@ts-ignore
        (acc, { f_player, prop_player }) => {
          const newObj = prop_player[0]
          newObj.player_id = f_player
          return [...acc, newObj]
        },
        [],
      )
    },
  }))

export default FilterActions
