import { inject, observer } from 'mobx-react'
import { compose, withHandlers, lifecycle } from 'recompose'
import {
  indexLexic,
  teamLexic,
  filtersLexic,
  markersLexic,
  tableParamsLexic,
  tableParamsLexicShort,
  tableParamsNumLexic,
  videoCutsLexic,
  //@ts-ignore
} from 'config/lexics'

export const teamPageComposed = compose(
  inject(
    'lexics',
    'matches',
    'teamProfile',
    'teamTable',
    'selectedMatches',
  ),
  //@ts-ignore
  withHandlers({
    //@ts-ignore
    getTeamProfile: ({ teamProfile: { fetch } }) => (id) => {
      fetch(id)
    },
  }),
  lifecycle(
    {
      componentDidMount() {
        const {
          getTeamProfile,
          lexics,
          match: {
            params: { teamId },
          },
          matches,
        } = this.props
        lexics.appPage = {
          page: 'team',
          phrases: {
            ...indexLexic,
            ...teamLexic,
            ...filtersLexic,
            ...markersLexic,
            ...tableParamsLexic,
            ...tableParamsLexicShort,
            ...tableParamsNumLexic,
            ...videoCutsLexic,
          },
        }
        getTeamProfile(teamId)
        matches.fetch('team', teamId, 0, 20)
      },
      //@ts-ignore
    },
    //@ts-ignore
    {
      //@ts-ignore
      componentDidUpdate(prevProps) {
        const {
          getTeamProfile,
          match: {
            params: { teamId },
          },
        } = this.props
        if (prevProps.teamId !== teamId) {
          getTeamProfile(teamId)
        }
      },
    },
  ),
  observer,
)
