
import React, { Fragment } from 'react'
import { T9n } from 'react-sport'
import map from 'lodash/map'

type TProps = {
  headerVideoButtons: Array<{
    link: string,
    id: string,
    t: string,
  }>,
};

const VideoLinks = ({ headerVideoButtons }: TProps) => (
  <Fragment>
    {map(headerVideoButtons, ({ id, link, t }) => (
      <a key={id} href={link} className='match-header__play-button'>
        <T9n t={t} />
      </a>
    ))}
  </Fragment>
)

export default VideoLinks
