import React from 'react'
import { DragSource, DropTarget } from 'react-dnd'
import { compose, pure, withHandlers } from 'recompose'
import { findDOMNode } from 'react-dom'

import { T9n } from 'react-sport'

import {
  THover,
  TTargetProps,
  TMonitor,
  TComposedInner,
  TPropsOuter,
  TWithHandlersOuter,
  TWithHandlersInner,
  TConnectDropTarget,
  TConnectDragSource,
  TMonitorDragSource,
} from './types'

const cardSource = {
  beginDrag({ id, index }: { id: number | string, index: number }) {
    return { id, index }
  },
}

const cardTarget: THover = {
  hover(
    props: TTargetProps,
    monitor: TMonitor,
    component: React.ReactNode,
  ): number | boolean {
    // Это будущий индекс той карточки, которую перетягиваем
    const dragIndex = monitor.getItem().index
    // Это индекс той карточки, над которой находимся
    const hoverIndex = props.index

    // Если карточка находится над самой собой (над бывшим своим расположением),
    // то ничего не высчитываем
    if (dragIndex === hoverIndex) return false

    const hoverBoundingRect: { bottom: number, top: number } = findDOMNode(
      //@ts-ignore
      component,
    ).getBoundingClientRect()
    const hoverMiddleY = (hoverBoundingRect.bottom - hoverBoundingRect.top) / 2
    const clientOffset = monitor.getClientOffset()
    const hoverClientY = clientOffset.y - hoverBoundingRect.top

    // Меняем расположение карточек, только если перетаскиваемая пересекла половину следующей
    if (dragIndex < hoverIndex && hoverClientY < hoverMiddleY) return false
    if (dragIndex > hoverIndex && hoverClientY > hoverMiddleY) return false
    // Меняем расположение карточек
    props.moveCard(dragIndex, hoverIndex)
    monitor.getItem().index = hoverIndex
    return monitor.getItem().index
  },
}

const DndItemComponent = ({
  connectDragSource,
  connectDropTarget,
  isDragging,
  item,
  removeParam,
}: TComposedInner) =>
  connectDragSource(
    connectDropTarget(
      <li style={{ opacity: Number(!isDragging) }}>
        <T9n t={item.full_lexical_formula} />
        <span className='remover' onClick={removeParam(item)}>
          &times;
        </span>
      </li>,
    ),
  )

const dndItemComposed = compose<TComposedInner, TPropsOuter>(
  withHandlers<TWithHandlersOuter, TWithHandlersInner>({
    removeParam: ({ removeFromTempSelected }) => param => () => {
      removeFromTempSelected(param)
    },
  }),
  pure,

  DropTarget(
    'item',
    //@ts-ignore
    cardTarget,
    (connect: TConnectDropTarget) => ({
      connectDropTarget: connect.dropTarget(),
    }),
  ),

  DragSource(
    'item',
    cardSource,
    (connect: TConnectDragSource, monitor: TMonitorDragSource) => ({
      connectDragSource: connect.dragSource(),
      isDragging: monitor.isDragging(),
    }),
  ),
)

export const DndItem = dndItemComposed(DndItemComponent)
