import React from 'react'
import { inject, observer } from 'mobx-react'

import { compose, lifecycle } from 'recompose'
import get from 'lodash/get'

import { tableTypes } from 'config/tables'

import { Tables } from 'features/Table'
import Loader from 'features/components/common/loader/Loader'

type TProps = {
  pageId: string,
}

type TPropsInner = TProps & {
  playerTable: {
    isLoaded: boolean,
    tables: {},
  },
}

type TLifecycle = TProps & {
  playerProfile: {},
  playerTable: {
    fetchPlayerMatches : (param: {
      ampluaId: number,
      matches: Array<number>,
      playerId: string,
    }) => void,
  },
  selectedMatches: {},
}

const PlayerPageMatchesComponent = ({
  pageId,
  playerTable: {
    isLoaded,
    tables,
  },
}: TPropsInner) => (
  <div>
    {isLoaded
      ? (
        <Tables
          tableType={tableTypes.playerMatches}
          tables={tables}
          pageId={pageId}
        />
      )
      : <Loader />}
  </div>
)

const playerPageMatchesComposed = compose<TPropsInner, TProps>(
  inject(
    'playerTable',
    'selectedMatches',
    'playerProfile',
  ),
  lifecycle<TLifecycle, TProps>({
    componentDidMount() {
      const {
        pageId,
        playerProfile,
        playerTable: { fetchPlayerMatches },
        selectedMatches,
      } = this.props
      const matches = get(selectedMatches, ['player', pageId])
      const ampluaId = get(playerProfile, ['profile', 'amplua', 'id'])

      fetchPlayerMatches({
        ampluaId,
        matches,
        playerId: pageId,
      })
    },
  }),
  observer,
)

export const PlayerPageMatches = playerPageMatchesComposed(PlayerPageMatchesComponent)
