
import { inject, observer } from 'mobx-react'

import {
  compose,
  withHandlers,
  withState,
  withProps,
} from 'recompose'

import cx from 'classnames'
import get from 'lodash/get'

import { setSortedIdHOC } from 'hocs'

import {
  TPropsInner,
  TPropsOuter,
  TWithPropsInner,
  TWithPropsOuter,
  TStoreObj,
  TWithHandlersOuter,
  TWithHandlersInner,
  TProfileNames,
} from './types'

export const tableComposed = compose<TPropsInner, TPropsOuter>(
  inject(
    'matchProfile',
    'teamTable',
    'playerTable',
    'matchTable',
    'lexics',
    'tableReports',
    'teamProfile',
    'playerProfile',
    'tournamentMatchesTableStore',
    'tournamentsProfile',
    'teamPlayers',
    'tournamentPlayersTableStore',
  ),
  withState('popupOpened', 'openPopup', false),
  withState('newPopupOpened', 'openNewPopup', false),
  // Хок хендлера сортировки
  setSortedIdHOC,

  withProps<TWithPropsInner, TWithPropsOuter>(
    ({
      matchTable,
      param: {
        gear_group_id,
        param_id,
      },
      playerTable,
      tableType = 'default',
      teamPlayers,
      teamTable,
      tournamentMatchesTableStore,
      tournamentPlayersTableStore,
    }) => {
      // Используем нужный стор для использования методов
      const stores: TStoreObj = {
        matchPlayersTable: matchTable,
        playerMatches: playerTable,
        teamMatches: teamTable,
        teamPlayers,
        tournamentMatches: tournamentMatchesTableStore,
        tournamentPlayers: tournamentPlayersTableStore,
      }

      return {
        activeId: `${param_id}_${gear_group_id}`,
        currentStore: stores[tableType],
      }
    },
  ),

  withHandlers<TWithHandlersOuter, TWithHandlersInner>({
    getClassName: ({ currentStore: { showOpponentStats } }) => checked =>
      cx('team-stats-header-opponents-toggler', {
        checked: showOpponentStats || checked,
      }),

    getLinks: ({ tableReports: { pdfIsFetching, xlsIsFetching } }) => () => [
      { xls: xlsIsFetching },
      { pdf: pdfIsFetching },
    ],

    handleNewPopupAction: ({ openNewPopup }) => () => openNewPopup(n => !n),

    handlePopupAction: ({ openPopup }) => () => openPopup(n => !n),

    saveReport: ({
      lexics: { page },
      matchProfile,
      playerProfile,
      tableReports: { getReport },
      tableType,
      teamProfile,
      tournamentsProfile,
    }) => (e, key) => {
      const profilesNames: TProfileNames = {
        match: get(matchProfile, ['data', 'match_date'], ''),
        player: get(playerProfile, 'profile.short_name_eng'),
        team: get(teamProfile, 'profile.name_eng'),
        tournaments: get(tournamentsProfile, 'profile.name_eng'),
      }
      const name = profilesNames[page]
      getReport(e, key, tableType)
        .then((blob) => {
          const link: any = document.getElementById('download-link')
          const date = new Date()
          link.href = URL.createObjectURL(blob)
          link.download = `${date.getDate()}_${date.getMonth()
            + 1}_${date.getFullYear()}_matches_${name}.${key}`
          link.click()
        })
    },

    setSortedId: ({
      changeActiveParam,
      currentStore: { setActiveParam },
      table,
    }) => (param) => {
      // Запускаем функцию из setSortedIdHOC.
      changeActiveParam({
        newParam: param,
        storeAction: setActiveParam,
        table,
      })
    },

  }),
  observer,
)
