import React from 'react'
import { Link } from 'react-router-dom'
import { inject, observer } from 'mobx-react'

import { compose } from 'recompose'

import { Langs, LiveSearchUI } from 'react-sport'

import { ProfilePopup } from 'features/ProfilePopup'
import { UploadVideoBtn } from 'features/UploadVideoBtn'

type TProps = {
  auth: {
    logout: () => void,
  },
}

const HeaderComponent = ({ auth: { logout } }: TProps) => (
  <div>
    <header className='header'>
      <div className='header-table' data-block='sharedHeaderTable'>
        <div className='header-row'>
          <div className='header-column-left'>
            <div className='container-fluid'>
              <div className='row'>
                <Link className='logo' to='/' />
              </div>
            </div>
          </div>
          <div className='header-column-responsive' id='header-live-search'>
            <LiveSearchUI.LiveSearch />
          </div>
          <div className='header-column-right'>
            <UploadVideoBtn />
            <ProfilePopup logout={logout} />
            <div className='lang-wrapper'>
              <Langs />
            </div>
          </div>
        </div>
      </div>
    </header>
  </div>
)

const headerComposed = compose<TProps, {}>(
  inject('lexics', 'auth'),
  observer,
)

export const Header = headerComposed(HeaderComponent)
