import { types } from 'mobx-state-tree'
// @ts-ignore
import routes from 'config/routes'

type TOptions = {
  lang_id: string,
  team: number,
  matches: Array<any>,
  tables: {},
};

const TableReportsApiActions = types.model({}).actions(self => ({
  //@ts-ignore
  //@ts-ignore
  //@ts-ignore
  fetchData(e, key, options) {
    const url: string = `${routes.API_ROOT}/report/tables_report`
    const { lang_id, matches, tables, team }: TOptions = options
    const body = {
      uri: 'request_players_stat_scout',
      test: true,
      params: {
        type: key,
        lang_id,
        team,
        matches,
        tables,
      },
    }
    //@ts-ignore
    self.changeFetchingStatus(key)
    return fetch(url, { method: 'POST', body: JSON.stringify(body) })
      .then((response: any) => {
        //@ts-ignore
        self.changeFetchingStatus(key)
        return response.blob()
      })
      .catch((error: {}) => {
        //@ts-ignore
        self.onError(error)
        //@ts-ignore
        self.changeFetchingStatus(key)
      })
  },
  //@ts-ignore
  changeFetchingStatus(key) {
    //@ts-ignore
    //@ts-ignore
    self[`${key}IsFetching`] = !self[`${key}IsFetching`]
  },
  //@ts-ignore
  onError(error) {
    //@ts-ignore
    self.error = error
  },
}))

export default TableReportsApiActions
