// @ts-ignore
import { action, toJS } from 'mobx'

import forIn from 'lodash/forIn'
import find from 'lodash/find'
import min from 'lodash/min'
import max from 'lodash/max'
import inRange from 'lodash/inRange'
import get from 'lodash/get'
import toPairs from 'lodash/toPairs'

import { defaultDirections } from 'config/tabsConfig'

type TMarker = {
  setVisible: (arg: boolean) => void,
}

type TRect = {
  x1: number,
  x2: number,
  y1: number,
  y2 : number,
}

const Actions = (superclass: any) =>
  class extends superclass {
    //@ts-ignore
    @action handleDirection = (name, value) => {
      this.directions[name] = value
    };

    setDefaultDirections = () => {
      const { active }: {active: string} = this.getStore('tabs')
      const defaultDirectionsActive: {from: boolean, to: boolean } = get(
        defaultDirections,
        active,
      )

      return forIn(defaultDirectionsActive, (value, key) =>
        this.handleDirection(key, value))
    };

    //@ts-ignore
    @action selectToggle = (value: boolean) => {
      //@ts-ignore
      this.select = value
    };

    //@ts-ignore
    @action setAllVisible = () => {
      this.data.forEach(({ setVisible }: TMarker) => setVisible(true))
    }

    setRect = (
      {
        x1,
        x2,
        y1,
        y2,
      } : TRect,
      rectWidth: number,
      rectLength: number,
      fieldWidth: number,
      fieldLength: number,
    ) => {
      if (x1 === x2 || y1 === y2) {
        return this.setAllVisible()
      }
      const xStart = min([x1, x2]) || 0
      const xEnd = max([x1, x2]) || 0
      const yStart = min([y1, y2]) || 0
      const yEnd = max([y1, y2]) || 0
      // для корректного подсчета нужно перевести в абсолютные значения
      const xStartAbs = (xStart * rectWidth) / 100
      const xEndInAbs = (xEnd * rectWidth) / 100
      const yStartInAbs = (yStart * rectLength) / 100
      const yEndInAbs = (yEnd * rectLength) / 100

      const markersType = find(
        toPairs(this.directions),
        (direction: Array<string | boolean>) => direction[1] === true,
      ) || ['to', true]

      this.data.forEach((marker: TMarker) => {
        const { pos_x, pos_y } = get(marker, get(markersType, '[0]'))
        // координаты переводим в абсолютные значения
        // (24 и 30 - это отступы у поля, которые необходимо учитывать)
        const xCoordAbs = (pos_x * fieldWidth) / 100 + 24
        const yCoordAbs = (pos_y * fieldLength) / 100 + 30
        if (
          !inRange(xCoordAbs, xStartAbs, xEndInAbs)
          || !inRange(yCoordAbs, yStartInAbs, yEndInAbs)
        ) {
          marker.setVisible(false)
        }
      })
    };

    //@ts-ignore
    @action deleteMarkers = () => {
      //@ts-ignore
      this.data = []
    };
  }

export default Actions
