import React, { Fragment } from 'react'

import map from 'lodash/map'

import { T9n } from 'react-sport'

import { PopupHOC } from '../PopupHOC'

import { TPropsCommon } from '../types'

type TProps = TPropsCommon & {
  data: Array<{
    columnTitleId: number | false,
    ids: Array<number>,
    zoneId: number,
    zoneNumber: number,
  }>,
}

// Попап с фильтрами, которые разбиты по группам (в колонку) с заголовком у каждой группы
// Если нет заголовка, то отображаем просто список фильтров
const ColumnsPopupComponent = ({
  data,
  filters: { handleAllTempCheck, handleTempCheck },
  getFilterLexics,
  getItemClassName,
  getTitleClassName,
  toggleOpenClose,
}: TProps) => (
  <Fragment>
    <div className='popup-column__wrapper'>
      {map(data, ({ columnTitleId, ids }, idx) => (
        <div className='popup-column__container' key={columnTitleId || idx}>
          {columnTitleId && (
            <div
              className={getTitleClassName(ids)}
              onClick={() => handleAllTempCheck(ids)}
            >
              <T9n t={getFilterLexics(columnTitleId)} />
            </div>
          )}
          <ul className='popup-column__list'>
            {map(ids, id => (
              <li
                className={getItemClassName(id)}
                key={id}
                onClick={() => handleTempCheck(id)}
              >
                <T9n t={getFilterLexics(id)} />
              </li>
            ))}
          </ul>
        </div>
      ))}
    </div>
    <button className='map-block__popup-button' onClick={toggleOpenClose}>
        OK
    </button>
  </Fragment>
)

export const ColumnsPopup = PopupHOC(ColumnsPopupComponent)
