import filter from 'lodash/filter'
import isEmpty from 'lodash/isEmpty'
import reduce from 'lodash/reduce'
import set from 'lodash/set'

import { saveTablesToLocalStorage } from 'module/utils/helpers'

import { TeamMatchesModel, TParamExtended } from './model'

type TTable = {
  position: number,
  selectedParams: Array<{ is_default: string | null }>,
  summaryType: string,
  tableName: string | number,
}

export const TeamPageMatchesActions = TeamMatchesModel
  .actions(self => ({
    setTableStats(tableStats: Array<TParamExtended>) {
      //@ts-ignore
      self.tableStats = tableStats
    },
    setTables(tables:{}) {
      self.tables = tables
    },
  }))
  .actions(self => ({
    // Записываем в стор таблицы после получения параметров
    saveTables() {
      const filteredParams = filter(self.gearParams, ({ params }) => params.length)

      const updatedTables = reduce(filteredParams, (acc, curr, index) => {
        const position = index + 1
        const table: TTable = {
          position,
          selectedParams: filter(curr.params, { is_default: 'default' }),
          summaryType: 'average_for_match',
          tableName: String(curr.groupName),
        }

        if (!isEmpty(table.selectedParams)) {
          set(acc, position, table)
        }

        return acc
      }, {})

      self.setTables(updatedTables)

      saveTablesToLocalStorage({
        name: 'teamMatches',
        tables: self.tables,
      })
    },

    toggleShowOpponent() {
      self.showOpponentStats = !self.showOpponentStats
    },
  }))
