import { types } from 'mobx-state-tree'
import { values } from 'mobx'

import orderBy from 'lodash/orderBy'
import get from 'lodash/get'
import head from 'lodash/head'

import { callApi } from 'react-sport'
// @ts-ignore
import routes from 'config/routes'

const TournamentPageApiActions = types.model({}).actions(self => ({
  fetchSeasons(tournamentId: number) {
    const url = `${routes.API_ROOT}/data`
    const config = {
      method: 'POST',
      body: JSON.stringify({
        proc: 'scout_tournament_season_list',
        params: {
          _p_tournament_id: tournamentId,
        },
      }),
    }

    return callApi({
      url,
      config,
      onRequest: () => {
        //@ts-ignore
        self.isLoaded = false
        //@ts-ignore
        self.error = null
      },
      //@ts-ignore
      onSuccess: json => self._onSuccessSeasons(json, tournamentId),
      //@ts-ignore
      onError: self._onError,
    })
  },

  _onSuccessSeasons(
    json: {
      data: Array<{
        scout_tournament_season_list: Array<{}>,
      }>,
    },
    tournamentId: number,
  ) {
    const apiResponse = orderBy(
      get(json, 'data[0].scout_tournament_season_list', []),
      ['season_id'],
      ['desc'],
    )
    //@ts-ignore
    apiResponse.forEach(season => self.seasons.set(season.season_id, season))
    //@ts-ignore
    const defaultSeasonId = head(values(self.seasons)).season_id
    //@ts-ignore
    self.changeActiveSeason(tournamentId, defaultSeasonId)
    //@ts-ignore
    self.error = null
    //@ts-ignore
    self.isLoaded = true
  },

  _onError(error: any) {
    //@ts-ignore
    self.error = error
  },
}))

export default TournamentPageApiActions
