import { types, getParent } from 'mobx-state-tree'

import map from 'lodash/map'
import reduce from 'lodash/reduce'
import values from 'lodash/values'
import flatten from 'lodash/flatten'
import take from 'lodash/take'
import chunk from 'lodash/chunk'

import { matchesToShow } from '../config'

export const TournamentMatchesTableViews = types
  .model({})
  .volatile(() => ({
    // offSet - кол-во частей запрашиваемых матчей.
    //Т.к. их очень много, запрашивать будем по частям
    offset: 1,
    canFetch: true,
    showOpponentStats: false,
    partFetching: false,
  }))
  .views(self => ({
    get summarySelectedParamsFromTables() {
      const paramsSummary = reduce(
        //@ts-ignore
        values(self.tables),
        (acc, curr) => {
          const currentTableSelectedParams = map(
            curr.selectedParams,
            'param_id',
          )
          //@ts-ignore
          acc.push(currentTableSelectedParams)
          return acc
        },
        [],
      )
      return flatten(paramsSummary)
    },

    get defaultParams() {
      return reduce(
        //@ts-ignore
        self.totalParams,
        (acc, { is_default, param_id }) => {
          if (is_default) {
            //@ts-ignore
            acc.push(param_id)
          }
          return acc
        },
        [],
      )
    },

    get actualMatchesList() {
      return take(
        map(getParent(self).tournamentMatches.matches),
        self.offset * matchesToShow,
      )
    },

    // Считаем кол-во матчей для запроса
    get matchesToFetch() {
      const matchesTotal = getParent(self).tournamentMatches.matchesIds // Общее коли-во матчей
      // Разбиваем общее кол-во матчей на части
      const chunkedMatchesList = chunk(matchesTotal, matchesToShow)
      return chunkedMatchesList[self.offset - 1]
    },
  }))
