import React from 'react'
import { Link } from 'react-router-dom'

import map from 'lodash/map'

import { Td } from 'features/Table/components/Td'

import { TrMatchComposed } from './composed'

import { TPropsInner } from './types'

// Строка со статистикой по матчу
const TrMatchComponent = ({
  activeId,
  getClassName,
  playerName,
  rowInfo,
  selectedParams,
  tableStats,
}: TPropsInner) => (
  <tr className='stats-table__table-row'>
    <td className='left-side-tr'>
      <Link target='_blank' to={`/players/${rowInfo.f_player}`}>
        <div className='stats-cell'>
          <div className='player-number'>{rowInfo.num}</div>
          <div className='player-name'>{playerName}</div>
        </div>
      </Link>
    </td>
    {map(selectedParams, param => (
      <td
        key={`${param.param_id}_${param.gear_group_id}`}
        className={getClassName(param, activeId)}
      >
        <table>
          <tbody>
            <tr>
              <Td
                id={rowInfo.f_player}
                param={param}
                tableStats={tableStats}
                rowInfo={rowInfo}
                tableType='matchPlayersTable'
              />
            </tr>
          </tbody>
        </table>
      </td>
    ))}
  </tr>
)

export const TrMatch = TrMatchComposed(TrMatchComponent)
