import React from 'react'
import { Link } from 'react-router-dom'

import { compose, withHandlers } from 'recompose'
import cx from 'classnames'
import map from 'lodash/map'

import { T9n, OutsideClick } from 'react-sport'

type TProps = {
  isFetching: boolean,
  links: {} | Array<any>,
  f_match: number,
};

const MatchPopupComponent = ({ f_match, isFetching, links }: TProps) => (
  <div
    className={cx(
      'popup-container',
      'popup-matches-episodes',
      'popup-matches-episodes__position-bottom',
      { 'popup-container__loading': isFetching },
    )}
  >
    {map(links, ({ link }, k) => (
      <a
        className='ui-button'
        target='_blank'
        rel='noopener noreferrer'
        href={link}
        key={k}
      >
        <i className='icon-play_volleyball_circle' />
        <T9n className='ui-button__text' t={`match_popup_${k}`} />
      </a>
    ))}
    <Link className='ui-button' target='_blank' to={`/matches/${f_match}`}>
      <i className='icon-play_volleyball_circle' />
      <T9n t='match_popup_details' />
    </Link>
  </div>
)

const MatchPopupComposed = compose(
  //@ts-ignore
  withHandlers({
    clickOutside: ({ toggleClose }) => () => {
      toggleClose()
    },
  }),
  OutsideClick,
)

// @ts-ignore
export const MatchPopup = MatchPopupComposed(MatchPopupComponent)
