import React, { Fragment } from 'react'
import { NavLink } from 'react-router-dom'
import { observer } from 'mobx-react'

import { compose, withProps } from 'recompose'
import includes from 'lodash/includes'
import filter from 'lodash/filter'
import map from 'lodash/map'

import { T9n } from 'react-sport'

import { mainPageTabs } from 'config/tabsConfig'
import {
  TProps,
  TPropsOuter,
  TComposedInner,
  TWithPropsInner,
  TWithPropsOuter,
} from './types'

export const LinksListComponent = ({ match: { url }, tabs }: TProps) => (
  <Fragment>
    {map(tabs, ({ exact, lexics, link }) => (
      <li className='stats-tabs__item' key={link}>
        <NavLink
          to={`${url}${link}`}
          activeClassName='active'
          exact={exact}
        >
          <T9n t={lexics} />
        </NavLink>
      </li>
    ))}
  </Fragment>
)

const linksListComposed = compose<TComposedInner, TPropsOuter>(
  observer,
  withProps<TWithPropsInner, TWithPropsOuter>(({ page }) => {
    const tabs = filter(mainPageTabs, ({ pages }) => includes(pages, page))
    return { tabs }
  }),
)

export const LinksList = linksListComposed(LinksListComponent)
