import { observer, inject } from 'mobx-react'

import {
  dateWithYear,
  getSuffix,
  getAbbr,
  //@ts-ignore
} from 'module/utils/helpers'

import { compose, withProps } from 'recompose'
import get from 'lodash/get'

export const leftSideTrComposed = compose(
  inject('lexics'),
  observer,
  //@ts-ignore
  withProps(
    //@ts-ignore
    ({
      handleClick,
      lexics: { lang },
      pageId,
      rowInfo: {
        f_match,
        home_match,
        match_date,
        opponent_score,
        score,
        ...rowInfo
      },
      showOpponentStats,
    }) => {
      const suffix = getSuffix(lang)
      const oppTeamName = get(rowInfo, `name_opponent_${suffix}`)
      const teamName = get(rowInfo, `name_${suffix}`)

      return {
        tdClassName: home_match === '@' ? 'opponent' : '',
        rowspan: '',
        handleClick: handleClick(f_match, pageId),
        titleClassName: 'match-score',
        showOpponent: showOpponentStats,
        ownerTeamName: getAbbr(teamName),
        oppTeamName: getAbbr(oppTeamName),
        titleScore: `${score}:${opponent_score}`,
        shortNameOpponent: oppTeamName,
        matchDate: dateWithYear(match_date),
        match: {
          has_video: false,
          home_match,
          f_match,
        },
      }
    },
  ),
)
