import { types } from 'mobx-state-tree'

import size from 'lodash/size'
import keys from 'lodash/keys'
import omit from 'lodash/omit'
import reduce from 'lodash/reduce'

// @ts-ignore
import { saveTablesToLocalStorage } from 'module/utils/helpers'

export const deleteTable = types.model({}).actions(self => ({
  /** удаления таблицы
   * @param {number} position - порядковый номер таблицы
   * @param {string} localStorageName - ключ для записи в localStorage
   */
  deleteTable(position: number, localStorageName: string) {
    //@ts-ignore
    const tablesList: Array<any> = keys(self.tables)
    // Удаляем текущую таблицу
    //@ts-ignore
    const omitedTables = omit(self.tables, position)
    // // Перезаписываем все таблицы, позиция которых после удаленной. Передвигаем весь список
    const updatedTables = reduce(
      omitedTables,
      (acc, curr, key) => {
        //@ts-ignore
        if (key > position) {
          //@ts-ignore
          //@ts-ignore
          acc[key - 1] = {
            //@ts-ignore
            ...self.tables[key],
            //@ts-ignore
            position: key - 1,
          }
        }
        //@ts-ignore
        //@ts-ignore
        acc[key] = self.tables[key]
        return acc
      },
      {},
    )

    // Удаляем последнюю таблицу, дубль при перемещении всех наверх
    //@ts-ignore
    self.tables = omit(updatedTables, size(tablesList))

    //@ts-ignore
    saveTablesToLocalStorage({ name: localStorageName, tables: self.tables })
  },
}))
