const stores = {}
const Markers = ComposeMarkers(BasicStore)
const markers = new Markers(stores)
const lexics = new remoteStore.Lexics(routes.API_ROOT, langsList)
const Cuts = VideoCuts(remoteStore.VideoSummaries, markers)

const mstRootStore = types
  .model('rootStore', {
    tournamentSeasonSelect: types.optional(TournamentSeasonSelectStore, {}),
    tournamentsProfile: types.optional(TournamentPageStore, {}),
    tournamentHeaderStats: types.optional(TournamentHeaderStatsStore, {}),
    tournamentMatches: types.optional(TournamentMatchesStore, {}),
    tournamentMatchesTableStore: types.optional(
      TournamentMatchesTableStore,
      //@ts-ignore
      {},
    ),
    tournamentPlayersTableStore: types.optional(
      TournamentPlayersTableStore,
      //@ts-ignore
      {},
    ),
    gearPopupStore: types.optional(GearPopupStore, {}),
    teamsCompareWidget: types.optional(TeamsCompareStore, {}),
  })
  .create()

Object.assign(stores, {
  playersCompareWidget,
  teamPlayers,
  tableReportsMST,
  matchProfile,
  teamProfile,
  selectedMatches,
  videoLink,
  filters,
  // TODO заглушка для стора
  tutorials: {},
  tabs: new Tabs(stores),
  matches: new Matches(stores),
  matchTable,
  liveSearch: remoteStore.LiveSearch.create({
    rootUrl: routes.API_ROOT,
    sport: 'volleyball',
  }),
  //@ts-ignore
  infoItem: new InfoItem(stores),
  matchLinks: new MatchLinks(stores),
  playerProfile: new PlayerProfile(stores),
  playerStats: new PlayerStats(stores),
  teamStats: new TeamStats(stores),
  teamTable: teamMatchesStore,
  playerTable: playerMatchesStore,
  //@ts-ignore
  zones: new Zones(stores),
  //@ts-ignore
  shotTypes: new ShotTypes(stores),
  //@ts-ignore
  mapShoot: new MapShoot(stores),
  //@ts-ignore
  playerInfo: new PlayerInfo(stores),
  //@ts-ignore
  tooltips: new ToolTips(stores),
  tableReports: new TableReports(stores),
  //@ts-ignore
  shootsRange: new ShootsRange(stores),
  videoPlayer: new remoteStore.VideoPlayer(routes.API_ROOT, 50000000),
  header: new remoteStore.Header({
    profilePopup: profilePaths,
  }),
  recent: new remoteStore.Recent(routes.API_ROOT),
  searchString: new remoteStore.SearchString(routes.API_ROOT),
  auth: new remoteStore.Auth(routes.API_ROOT, history),
  //@ts-ignore
  playersVS: new Cuts(routes.API_ROOT, 'player', lexics),
  //@ts-ignore
  teamsVS: new Cuts(routes.API_ROOT, 'team', lexics),
  lexics,
  markers,
  ...mstRootStore,
})

export default stores
