
import React from 'react'
import { inject, observer } from 'mobx-react'

import { compose, withHandlers } from 'recompose'
import get from 'lodash/get'

import { takeScreenshot, utils } from 'react-sport'

import {
  TProps,
  TWithHandlersOuter,
  TWithHandlersInner,
} from './types'

const {
  helpers: { getLongSuffix, getSuffix },
} = utils

const SaveScreenShotComponent = ({ handleDownload }: TProps) => (
  <button className='map-block__controls-button'>
    <i className='icon-download' onClick={handleDownload} />
  </button>
)

export const saveScreenShotComposed = compose<TProps, any>(
  inject(
    'zones',
    'lexics',
    'tabs',
    'matches',
    'markers',
    'matchProfile',
    'playerProfile',
    'teamProfile',
  ),
  withHandlers<TWithHandlersOuter, TWithHandlersInner>({
    handleDownload: ({
      lexics: { getLexics, lang, page },
      markers: { selectToggle },
      matches: { active: currentTeam },
      matchProfile: { data },
      playerProfile,
      tabs: { active },
      teamProfile,
    }) => (e) => {
      // Снимаем выделение зоны.
      // TODO подумать, как реализовать когда у нас два элемента SVG сохраняются в одной картинке
      selectToggle(false)
      const field = document.getElementsByClassName(
        'map-block__map-field-outer',
      )[0]
      const elementBackgroundSVG = document.getElementsByClassName(
        'map-block__zone-map',
      )[0]

      const suffix = getSuffix(lang)
      const longSuffix = getLongSuffix(lang)
      const pages = {
        match: {
          body: get(data, `${currentTeam}_name_${suffix}`, ''),
          extra: `${get(data, 'match_date', '')}.png`,
        },
        player: {
          body:
            `${get(playerProfile, `profile.firstname_${longSuffix}`, '')}`
            + ` ${get(playerProfile, `profile.lastname_${longSuffix}`, '')}`,
        },
        team: {
          body: get(teamProfile, `profile.name_${longSuffix}`, ''),
        },
      }
      const pageInfo = get(pages, page, {})
      const picName = `${getLexics(active)}`
        + ` ${get(pageInfo, 'body', '')}`
        + ` ${get(pageInfo, 'extra', '')}`

      takeScreenshot({
        callback: () => {},
        e,
        elementBackgroundSVG,
        elementToChangeBackground: 'map',
        field,
        picName,
      })
    },
  }),
  observer,
)

export const SaveScreenShot = saveScreenShotComposed(SaveScreenShotComponent)
