import { types } from 'mobx-state-tree'
import TournamentHeaderStatsApiActions from './TournamentHeaderStatsApiActions'
import TournamentHeaderStatsActions from './TournamentHeaderStatsActions'

const Param = types.model('Param', {
  p: types.number,
  o: types.number,
  v: types.number,
})

const GearParam = types.model('GearParam', {
  f_option_3: types.number,
  full_lexical_formula: types.string,
  gear_group_id: types.number,
  gear_subgroup_id: types.number,
  is_default: types.maybeNull(types.string),
  lexical_formula: types.string,
  name: types.string,
  param1_id: types.maybeNull(types.array(types.string)),
  param2_id: types.maybeNull(types.array(types.string)),
  param_id: types.number,
  short_lexical_formula: types.string,
  sort_order: types.number,
})

const Match = types.model('Match', {
  f_match: types.number,
  match_date: types.string,
  match_status: types.number,
  home_match: types.string,
  win_lose: types.string,
  name_ru: types.string,
  name_en: types.string,
  name_opponent_ru: types.string,
  name_opponent_en: types.string,
  score: types.number,
  opponent_score: types.number,
  team_id: types.number,
  opp_team_id: types.number,
  params: types.maybeNull(types.array(Param)),
  params_opp: types.maybeNull(types.array(Param)),
})

export const TournamentHeaderStats = types.model('TournamentHeaderStats', {
  matches: types.map(Match),
  loaded: false,
  error: types.maybeNull(types.frozen({})),
  gearParams: types.maybeNull(types.array(GearParam)),
})

const TournamentHeaderStatsStore = types.compose(
  TournamentHeaderStats,
  TournamentHeaderStatsApiActions,
  TournamentHeaderStatsActions,
)

export default TournamentHeaderStatsStore
