import React from 'react'
import { observer, inject } from 'mobx-react'

import map from 'lodash/map'
import {
  compose,
  lifecycle,
  withProps,
} from 'recompose'

import { getSuffix } from 'module/utils/helpers'

import { TeamsCompareInfo } from '../TeamsCompareInfo'
import TeamsCompareHeader from '../TeamsCompareHeader'

import defaultParams from '../../config'

import {
  TPropsInner,
  TWithPropsInner,
  TWithPropsOuter,
  TLifecycle,
} from './types'

export const TeamsCompareComponent = ({
  matchProfile: {
    data: {
      team1_id,
      team2_id,
      ...data
    },
  },
  suffix,
  teamsCompareWidget: { stats, teamsInfoLoaded },
}: TPropsInner) => (
  <div className='t-compare'>
    <TeamsCompareHeader />
    <div className='t-compare__container'>
      <div className='t-compare__teams'>
        <div className='t-compare__team'>
          <span className='t-compare__team-name'>
            {data[`team1_name_${suffix}`]}
          </span>
        </div>
        <div className='t-compare__team'>
          <span className='t-compare__team-name'>
            {data[`team2_name_${suffix}`]}
          </span>
        </div>
      </div>
      {teamsInfoLoaded ? (
        <TeamsCompareInfo
          team1={team1_id}
          team2={team2_id}
          stats={stats}
        />
      ) : (
        <div className='loading' />
      )}
    </div>
  </div>
)

const TeamsCompareComposed = compose<TPropsInner, {}>(
  inject(
    'lexics',
    'teamsCompareWidget',
    'matchProfile',
  ),
  observer,
  withProps<TWithPropsInner, TWithPropsOuter>(
    ({ lexics: { lang } }) => ({
      lang,
      suffix: getSuffix(lang),
    }),
  ),
  lifecycle<TLifecycle>({
    componentDidMount() {
      const {
        lang: currentLang,
        matchProfile: { matchId },
        teamsCompareWidget: { basicFetch },
      } = this.props
      basicFetch(currentLang, matchId, defaultParams)
    },
    componentDidUpdate(prevProps) {
      const {
        lang: currentLang,
        matchProfile: { matchId },
        teamsCompareWidget: { fetchStats, selectedParams },
      } = this.props

      if (prevProps.lang !== currentLang) {
        const paramsIds = map(selectedParams, 'param_id')
        fetchStats(currentLang, matchId, paramsIds)
      }
    },
  }),
  observer,
)

export const TeamsCompare = TeamsCompareComposed(TeamsCompareComponent)
