import { types } from 'mobx-state-tree'

import map from 'lodash/map'
import size from 'lodash/size'
import isEmpty from 'lodash/isEmpty'
import compact from 'lodash/compact'
import forEach from 'lodash/forEach'
import filter from 'lodash/filter'
import includes from 'lodash/includes'

type TUpdateSelectedPlayers = {
  lang: string,
  matchId: string,
  players: Array<{
    f_team: number,
    f_player: number,
  } | null>,
};

type TSaveTempParams = {
  lang: string,
  matchId: string,
  params: Array<{}>,
};

export const PlayersCompareActions = types.model({}).actions(self => ({
  // Обновляем список игроков при выборе нового игрока из раскрывающегося списка
  updateSelectedPlayers({ lang, matchId, players }: TUpdateSelectedPlayers) {
    if (isEmpty(compact(players))) return null
    // При инициализации стора у нас нет игроков,
    // но при изменении пользователем игрока учитываем предыдущее состояние
    //@ts-ignore
    const newSelectedPlayers = { ...self.selectedPlayers }
    // При обновлении одного игрока из команды мы меняем только нужного, второй остается в объекте
    forEach(
      players,
      //@ts-ignore
      //@ts-ignore
      ({ f_player, f_team }) => { newSelectedPlayers[f_team] = f_player },
    )
    // Заменяем в сторе обновленный список игроков для дальнейшего запроса к API
    //@ts-ignore
    self.selectedPlayers = newSelectedPlayers
    if (size(players) === 1) {
      //@ts-ignore
      self.fetch({
        lang,
        matchId,
        //@ts-ignore
        params: self.getParamsIds,
      })
    }
  },

  // Сохранение временно выбранных параметров (при закрытии шестеренки)
  saveTempParams({ lang, matchId, params }: TSaveTempParams) {
    //@ts-ignore
    self.selectedParams = params
    //@ts-ignore
    self.fetch({
      lang,
      matchId,
      //@ts-ignore
      params: self.getParamsIds,
    })
  },

  //Сохраняем все параметры в шестеренке и предвыбираем дефолтные (при первом открытии)
  setDefaultSelectedParams() {
    // По умолчанию предвыбираем дефолтные параметры
    //@ts-ignore
    const defaultIds = map(self.stats, 'param')
    //@ts-ignore
    self.selectedParams = filter(
      //@ts-ignore
      self.totalParams,
      ({ param_id }) => includes(defaultIds, param_id),
    )
  },
}))
