import React from 'react'
import { inject, observer } from 'mobx-react'

import { compose, lifecycle } from 'recompose'
import get from 'lodash/get'
import reduce from 'lodash/reduce'
import includes from 'lodash/includes'

import { tableTypes } from 'config/tables'
import { withSpinnerWhileLoading } from 'hocs'

import { Tables } from 'features/Table'

import {
  TPropsInner,
  TProps,
  TLifecycle,
  TParam,
} from './types'

const TeamPageMatchesComponent = ({
  pageId,
  teamTable: {
    tables,
  },
}: TPropsInner) => (
  <div>
    <Tables
      tableType={tableTypes.teamMatches}
      tables={tables}
      pageId={pageId}
    />
  </div>
)

const teamPageMatchesComposed = compose<TPropsInner, TProps>(
  inject(
    'teamTable',
    'selectedMatches',
    'matches',
  ),
  withSpinnerWhileLoading(
    ({ pageId, selectedMatches }) => !(selectedMatches.team[pageId]),
  ),
  lifecycle<TLifecycle>({
    componentDidMount() {
      const {
        matches: { list },
        pageId,
        selectedMatches,
        teamTable: { fetchTeamMatches },
      } = this.props

      // Необходимо подготовить id команд соперников из выбранных матчей
      const selectedMatchesIds = get(selectedMatches, ['team', pageId])
      const allMatches = get(list, [pageId, 'matches'])
      const opponentsIds = reduce(
        allMatches,
        (acc: Array<number>, curr: TParam) => {
          if (includes(selectedMatchesIds, curr.f_match)) {
            acc.push(Number(curr.f_team_opponent))
          }
          return acc
        }, [],
      )
      fetchTeamMatches({
        matches: selectedMatchesIds,
        teamId: [Number(pageId), ...opponentsIds],
      })
    },
  }),
  withSpinnerWhileLoading(
    ({ teamTable }) => !(teamTable.isLoaded),
  ),
  observer,
)

export const TeamPageMatches = teamPageMatchesComposed(TeamPageMatchesComponent)
