import React from 'react'
import { inject, observer } from 'mobx-react'

import {
  compose,
  withHandlers,
  withProps,
} from 'recompose'
import cx from 'classnames'
import map from 'lodash/map'

import { PopupHOC } from '../PopupHOC'

import {
  TProps,
  TWithHandlersOuter,
  TWithHandlersInner,
  TComposeInner,
  TWithPropsInner,
  TWithPropsOuter,
} from './types'

// Попап с зонами поля
const ZonesPopupComponent = ({
  data,
  fieldClassName,
  filters: { handleTempCheck },
  getZoneClassName,
  toggleOpenClose,
}: TProps) => (
  <div className='map-block__popup'>
    <ul className={fieldClassName}>
      {map(data, ({ zoneId, zoneNumber }) => (
        <li
          className={getZoneClassName(zoneId)}
          key={zoneId}
          onClick={() => handleTempCheck(zoneId)}
        >
          {zoneNumber}
        </li>
      ))}
    </ul>
    <button className='map-block__popup-button' onClick={toggleOpenClose}>
        OK
    </button>
  </div>
)

const zonesPopupComposed = compose<TComposeInner, {}>(
  inject('filters'),
  observer,
  withProps<TWithPropsInner, TWithPropsOuter>(({
    style,
  }) => ({
    fieldClassName: cx('map-block__popup-zone-list', {
      'map-block__popup-zone-list_home': style === 'home',
    }),
  })),
  withHandlers<TWithHandlersOuter, TWithHandlersInner>({
    getZoneClassName: ({ filters: { tempSelectedFilters } }) => id =>
      cx('map-block__popup-zone', {
        'map-block__popup-zone_active': tempSelectedFilters[id],
      }),
  }),
  PopupHOC,
)

export const ZonesPopup = zonesPopupComposed(ZonesPopupComponent)
