import { observer, inject } from 'mobx-react'

import get from 'lodash/get'
import find from 'lodash/find'
import reduce from 'lodash/reduce'
import map from 'lodash/map'
import includes from 'lodash/includes'
import {
  compose,
  withProps,
} from 'recompose'

import { withMatchLinksGetter } from 'hocs'
import { clickableClassNameHOC } from 'hocs/getClickableTdClassNameHoc'

import {
  TWithPropsInner,
  TWithPropsOuter,
} from './types'

import { TStat } from '../types'

export const trComposed = compose(
  inject(
    'videoLink',
    'lexics',
    'shootsRange',
    'matchLinks',
    'matches',
  ),
  withMatchLinksGetter,
  observer,
  withProps<TWithPropsInner, TWithPropsOuter>(({
    lexics: { page },
    matches: { list },
    pageId,
    rowInfo: { f_match },
    selectedParams,
    tableStats,
  }) => {
    const selectedParamsIds = map(selectedParams, 'param_id')
    const matchesList = get(list, [pageId, 'matches'], [])

    // Разделяем всю статистику по командам
    const tableStatsReduced = reduce(tableStats, (
      acc: {[key: string]: Array<TStat>},
      curr: TStat,
    ) => {
      if (
        !includes(selectedParamsIds, curr.f_param)
        || curr.f_match !== f_match
      ) {
        return acc
      }
      //f_player или f_team
      if (get(curr, `f_${page}`) === Number(pageId)) {
        acc.home.push(curr)
        return acc
      }

      acc.guest.push(curr)
      return acc
    }, {
      guest: [],
      home: [],
    })

    return {
      rowInfo: find(matchesList, { f_match }),
      tableStats: tableStatsReduced,
      tableType: page === 'team' ? 'teamMatches' : 'playerMatches',
    }
  }),
  clickableClassNameHOC,
  observer,
)
