import { types } from 'mobx-state-tree'

// @ts-ignore
import { TableStore } from 'features/Table/models/tableModel'

import { TournamentPagePlayersApiActions } from './apiActions'
import { TournamentPagePlayersActions } from './actions'
import { TournamentPagePlayersViews } from './views'

const Param = types.model('Param', {
  p: types.number,
  o: types.number,
  v: types.number,
})

const Player = types.model('Player', {
  f_player: types.number,
  shirt_num: types.maybeNull(types.string),
  name_rus: types.string,
  name_eng: types.string,
  f_team: types.maybeNull(types.number),
  pos1: types.maybeNull(types.number),
  params: types.maybeNull(types.array(Param)),
})

export const TournamentPagePlayersTable = types.model('TournamentPlayers', {
  storeName: 'tournamentPlayersTableStore',
  isLoaded: false,
  error: types.maybeNull(types.frozen({})),
  tables: types.frozen({}),
  players: types.array(Player),
})

export const TournamentPlayersTableStore = types.compose(
  // общие методы и свойства таблиц
  TableStore,
  // Собственные методы и свойства
  TournamentPagePlayersTable,
  TournamentPagePlayersApiActions,
  TournamentPagePlayersActions,
  TournamentPagePlayersViews,
)
