import React from 'react'
import { inject, observer } from 'mobx-react'

import { compose, withHandlers } from 'recompose'

import map from 'lodash/map'

import { T9n } from 'react-sport'

import update from 'module/utils/update'
import { DndItem } from '../DndItem'

import {
  TComposeInner,
  TWithHandlersOuter,
  TWithHandlersInner,
} from './types'

export const DndParamsComponent = ({
  gearPopupStore: { removeFromTempSelected, tempSelected },
  getId,
  moveHandler,
}: TComposeInner) => (
  <div className='popup-col-inner'>
    <div className='popup-col-title'>
      <T9n t='boxscore_selected_params' />
      <span className='found-param-counter'>
        {tempSelected && tempSelected.length}
      </span>
    </div>
    <div className='popup-col-comment' />
    <div className='ul-checked-wrapper'>
      <ul>
        {map(tempSelected, (item, index) => (
          <DndItem
            index={index}
            id={getId(item)}
            key={getId(item)}
            item={item}
            removeFromTempSelected={removeFromTempSelected}
            moveCard={moveHandler}
          />
        ))}
      </ul>
    </div>
  </div>
)

export const DndParamsComposed = compose<TComposeInner, {}>(
  inject('gearPopupStore'),
  withHandlers<TWithHandlersOuter, TWithHandlersInner>({
    getId: () => ({
      gear_group_id,
      short_lexical_formula,
    }) => `${short_lexical_formula}_${gear_group_id}`,

    moveHandler: ({
      gearPopupStore: { fillTempSelected, tempSelected },
    }) => (dragIndex, hoverIndex) => {
      // Заменяем обновленный (после перестановки) список карточек с параметрами в правом меню
      const updatedTempParamsList = update(tempSelected, {
        $splice: [[dragIndex, 1], [hoverIndex, 0, tempSelected[dragIndex]]],
      })
      fillTempSelected(updatedTempParamsList)
    },
  }),
  observer,
)

export const DndParams = DndParamsComposed(DndParamsComponent)
