import React from 'react'

import { TableUI } from 'react-sport'

import { GearPopup } from 'features/GearPopup'
import { THead } from 'features/Table'

import PlayersList from '../PlayersList'
import { playersTableComposed } from './composed'
import { PlayersTableHeader } from './PlayersTableHeader'

type TProps = {
  activeId: string | void,
  handleNewPopupAction: () => void,
  handlePopupAction: () => void,
  handleSetActiveParam: () => void,
  lexics: {
    page: string,
  },
  links: Array<any>,
  newPopupOpened: boolean,
  popupOpened: boolean,
  sortAsc: boolean,
  table: {
    avatarIsVisible: boolean,
    position: number,
    selectedParams: Array<{}>,
    summaryType: string,
  },
  teamPlayers: {
    editTempSelected: () => void,
    gearParams: Array<{}>,
    isFetching: boolean,
    tables: any,
    tempSelected: Array<{}>,
  },
}

/** Компонент Таблица со списком игроков в команде
* @param {string} activeId - выбранный айди параметра по которому сортируем
* @param {func} handlePopupAction - функция для отображение попап шестеренки
* @param {func} handleNewPopupAction - функция для отображение попап шестеренки для новой таблицы
* @param {func} newPopupOpened - открыт попап с новой таблицей
* @param {func} popupOpened - открыт попап с редактированием параметров для таблицы
* @param {array} links - ссылки для использования для скачивания отчета
* @param {func} setSortedId - при клике на шапку в таблице указываем
айди по которому будем сортировать игроков
* @param {object} table - объект таблицы со свойствами
* @param {bool} sortAsc - сортируем по убыванию или по возрастанию значения
* @param {bool} isFetching - флаг для отображения лоадера
*/
const PlayersTableComponent = ({
  activeId,
  handleNewPopupAction,
  handlePopupAction,
  handleSetActiveParam,
  links,
  newPopupOpened,
  popupOpened,
  sortAsc,
  table: { avatarIsVisible, position, selectedParams },
  table,
  teamPlayers,
  teamPlayers: { isFetching },
}: TProps) => (
  <div className='stats-table'>
    <PlayersTableHeader
      table={table}
      links={links}
      handlePopupAction={handlePopupAction}
      handleNewPopupAction={handleNewPopupAction}
    />
    {(popupOpened || newPopupOpened) && (
      <GearPopup
        table={table}
        handlePopupAction={handlePopupAction}
        handleNewPopupAction={handleNewPopupAction}
        popupOpened={popupOpened}
        newPopupOpened={newPopupOpened}
        currentStore={teamPlayers}
        newPopup={newPopupOpened}
      />
    )}
    <div className='stats-table__wrapper'>
      <table className='stats-table__table'>
        <TableUI.Thead>
          {/*
              //@ts-ignore */}
          <THead table={table} setSortedId={handleSetActiveParam} />
        </TableUI.Thead>
        {isFetching
          ? <tbody className='loading' />
          : (
            <PlayersList
              selectedParams={selectedParams}
                //@ts-ignore
              activeId={activeId}
              sortAsc={sortAsc}
              avatarIsVisible={avatarIsVisible}
              position={position}
            />
          )
        }
      </table>
    </div>
  </div>
)

export const PlayersTable = playersTableComposed(PlayersTableComponent)
