
import React from 'react'
import { lifecycle, compose } from 'recompose'
import { inject, observer } from 'mobx-react'

import { Tables } from 'features/Table'
import Loader from 'features/components/common/loader/Loader'

import { tableTypes } from 'config/tables'
import { TLexics } from 'react-sport'

type TProps = {
  lexics: TLexics,
  tournamentId: number,
  tournamentMatchesTableStore: {
    isLoaded: boolean,
    tables: {},
  },
};

const TournamentMatches = ({
  tournamentId,
  tournamentMatchesTableStore: { isLoaded, tables },
}: TProps) =>
  (isLoaded ? (
    <Tables
      tableType={tableTypes.tournamentMatches}
      tables={tables}
      pageId={String(tournamentId)}
    />
  ) : (
    <Loader />
  ))

export const TournamentMatchesComposed = compose(
  inject('lexics', 'tournamentMatchesTableStore'),
  lifecycle({
    componentDidMount() {
      //@ts-ignore
      const {
        //@ts-ignore
        tournamentMatchesTableStore: { fetchTableInfo },
      } = this.props
      fetchTableInfo()
    },
  }),
  observer,
  //@ts-ignore
)(TournamentMatches)
