
import find from 'lodash/find'
import get from 'lodash/get'

type TArgs = {
  teamPlayers: {},
  paramName: string | number,
  playerInfo: any,
  longSuffix: string,
};

// Получение тайтлов для игроков в команде на вкладке ИГРОКИ
const getTeamPlayersTitle = ({
  longSuffix,
  paramName,
  playerInfo,
  teamPlayers,
}: TArgs) => () => {
  const player = find(
    get(teamPlayers, 'players'),
    ({ f_player }) => f_player === playerInfo,
  )
  const playerName = get(player, `short_name_${longSuffix}`, '')

  return {
    playlist_title: `${playerName}`,
    playlist_subtitle: `${paramName}`,
  }
}

export default getTeamPlayersTitle
