import React, { Fragment } from 'react'
import {
  withHandlers,
  compose,
  withProps,
} from 'recompose'

import get from 'lodash/get'
import isEmpty from 'lodash/isEmpty'
import cx from 'classnames'

// TODO переписать получение координат для маркеров, чтобы разобраться в типах
type TProps = {
  positionsInfo: {
    from: {
      //@ts-ignore
      pos_x?: number | boolean | null,
      //@ts-ignore
      pos_y?: number | boolean | null,
    },
    middle: {
      //@ts-ignore
      pos_x: number | null,
      //@ts-ignore
      pos_y: number | null,
      //@ts-ignore
    } | string | null,
  },
  pointers: {
    pointer1?: {
      width: number,
      rotate: number,
    },
    pointer2?: {
      width: number,
      rotate: number,
    },
  },
  getPointerClass: () => string,
  getTransform: (value: number) => string,
}

const PointerComponent = ({
  getPointerClass,
  getTransform,
  pointers,
  pointers: {
    pointer1,
    pointer2,
  },
  positionsInfo: {
    from,
    middle,
  },
}: TProps) => {
  if (isEmpty(pointers)) return null

  return (
    <Fragment>
      <div
        className={getPointerClass()}
        style={{
          //@ts-ignore
          width: `calc(${pointer1.width}%)`,
          left: `${from.pos_x}%`,
          top: `${from.pos_y}%`,
          //@ts-ignore
          transform: getTransform(pointer1.rotate),
        }}
      />
      {(get(middle, 'pos_x') && get(middle, 'pos_y')) && (
        <div
          className={getPointerClass()}
          style={{
            //@ts-ignore
            width: `calc(${pointer2.width}%)`,
            //@ts-ignore
            left: `${middle.pos_x}%`,
            //@ts-ignore
            top: `${middle.pos_y}%`,
            //@ts-ignore
            transform: getTransform(pointer2.rotate),
          }}
        />
      )}
    </Fragment>
  )
}

const pointerComposed = compose(
  withHandlers({
    getDirections: () => ({
      beginning: {
        //@ts-ignore
        pos_x: firstX,
        //@ts-ignore
        pos_y: firstY,
      },
      ending: {
        //@ts-ignore
        pos_x: lastX,
        //@ts-ignore
        pos_y: lastY,
      },
    }) => {
      // Т.к. длина вектора выражается в %,
      // а % (в координатах точек) берутся от разных длин (длина поля в 2 раза длиннее ширины)
      // То для математических действий надо привести их к одной системе координат
      // Y надо умножить на 2 (Y - ширина поля)
      const cat1 = Math.abs(firstY - lastY) * 2 //Первый катет
      const cat2 = Math.abs(firstX - lastX) // Второй катет
      const width = Math.sqrt((cat2 ** 2) + (cat1 ** 2)) // гипотенуза

      let angle = Math.asin(cat1 / width) * 180 / Math.PI // вычисляем угол поворота вектора

      // Учитываем нахожение точек начала и конца
      if (lastX >= firstX && lastY < firstY) {
        angle = 360 - angle
      }

      if (lastX < firstX && lastY >= firstY) {
        angle = 180 - angle
      }

      if (lastX < firstX && lastY < firstY) {
        angle += 180
      }

      return {
        width,
        rotate: angle,
      }
    },
  }),
  //@ts-ignore
  withHandlers({
    getPointers: (({
      getDirections,
      positionsInfo: {
        from,
        middle,
        to,
      },
    }) => () => {
      if (!get(from, 'pos_x', false)
        || !get(from, 'pos_y', false)
        || !get(to, 'pos_x', false)
        || !get(to, 'pos_y', false)) {
        return {}
      }

      if ((get(middle, 'pos_x') && get(middle, 'pos_y'))) {
        const pointer1 = getDirections({
          beginning: from,
          ending: middle,
        })
        const pointer2 = getDirections({
          beginning: middle,
          ending: to,
        })
        return {
          pointer1,
          pointer2,
        }
      }

      const pointer1 = getDirections({
        beginning: from,
        ending: to,
      })
      return { pointer1 }
    }),
    getPointerClass: ({
      positionsInfo: {
        from,
        middle,
        to,
      },
    }) => () => {
      const pointClass = get(from, 'type') || get(to, 'type')

      return cx(
        'pointer',
        {
          middle: (get(middle, 'pos_x') && get(middle, 'pos_y')),
          [`vector_${pointClass}`]: pointClass,
        },
      )
    },
  }),
  withHandlers({
    //@ts-ignore
    getTransform: () => angle => `rotate(${angle}deg)`,
  }),
  //@ts-ignore
  withProps(({ getPointers }) => ({
    pointers: getPointers(),
  })),
)

//@ts-ignore
export const Pointer = pointerComposed(PointerComponent)
