import React from 'react'
import { inject, observer } from 'mobx-react'

import {
  compose,
  lifecycle,
} from 'recompose'
import get from 'lodash/get'

import { team_params } from 'config/params'

import Loader from 'features/components/common/loader/Loader'
import { HeaderStatsList } from 'features/HeaderStatsList'

import {
  TComposeInner,
  TPropsOuter,
  TLifecycle,
} from './types'
/**
 * Отображение блока со статистикой в шапке профиля
 */
export const TeamStatsWrapperComponent = ({
  teamStats: {
    isFetching,
    stats,
  },
}: TComposeInner) => (
  <div className='player-card-stats-list'>
    {isFetching ? (
      <Loader />
    ) : (
      <HeaderStatsList
        params={team_params}
        stats={stats}
      />
    )}
  </div>
)

const teamStatsComposed = compose<TComposeInner, TPropsOuter>(
  inject('teamStats', 'selectedMatches'),
  lifecycle<TLifecycle, {}>({
    componentDidMount() {
      const {
        page,
        pageId,
        selectedMatches,
        teamStats: { fetch },
      } = this.props
      /**
       * Получение данных по выбранным матчам.
       * @param {number} pageId - id профиля команды
       * @param {array} selectedMatches[page][pageId] - id матчей для запроса
       */
      fetch(pageId, get(selectedMatches, [page, pageId], []))
    },
  }),
  observer,
)

export const TeamStatsWrapper = teamStatsComposed(TeamStatsWrapperComponent)
