
type TArgs = {
  getMatchInfo: () => {
    teamName: string,
    oppTeamName: string,
    teamScore: number,
    oppTeamScore: number,
  },
  playerInfo: any,
  paramName: string | number,
};

// Получение тайтлов для виджетов
const getWidgetsTitle = ({ getMatchInfo, paramName, playerInfo }: TArgs) => (
  widgetType: string,
) => {
  // Виджет сравнения игроков из 2 команд
  const getPlayersCompareWidgetsTitle = () => {
    const { oppTeamName, oppTeamScore, teamName, teamScore } = getMatchInfo()
    const title = `${teamName} ${teamScore}:${oppTeamScore} ${oppTeamName} - ${playerInfo}`
    return {
      playlist_title: title,
      playlist_subtitle: `${paramName}`,
      title,
    }
  }

  const getTeamsCompareWidgterTitle = () => {
    const { oppTeamName, oppTeamScore, teamName, teamScore } = getMatchInfo()
    const title = `${teamName} ${teamScore}:${oppTeamScore} ${oppTeamName}`
    return {
      playlist_title: title,
      playlist_subtitle: `${paramName}`,
      title,
    }
  }

  // Все виджеты тут
  const widgetTypes = {
    playersCompare: getPlayersCompareWidgetsTitle,
    teamsCompare: getTeamsCompareWidgterTitle,
  }

  //@ts-ignore
  return widgetTypes[widgetType]()
}

export default getWidgetsTitle
