import React from 'react'
import map from 'lodash/map'

import { TableUI } from 'react-sport'
import { LeftSideTr } from '../LeftSideTr'
import { TrComposed } from './composed'

// import { TPropsTr } from './types'

type TProps = {
  activeId: string,
  //@ts-ignore
  getClassName: (param: {}, activeId: string) => void,
  rowInfo: {},
  pageId: string,
  selectedParams: Array<{}>,
  getStat: (param: number) => string,
}

// Строка со статистикой по игроку
export const Tr = TrComposed(
  ({
    activeId,
    getClassName,
    getStat,
    pageId,
    rowInfo,
    selectedParams,
  }: TProps) => (
    <tr className='stats-table__table-row'>
      {/*
          //@ts-ignore */}
      <LeftSideTr rowInfo={rowInfo} pageId={pageId} />
      {map(selectedParams, param => (
        <TableUI.Td
          //@ts-ignore
          key={`${param.param_id}_${param.gear_group_id}`}
            //@ts-ignore
          className={getClassName(param, activeId)}
            //@ts-ignore
          value={getStat(param.param_id)}
          onClick={() => { }}
        />
      ))}
    </tr>
  ),
)
