
import React from 'react'
import { inject, observer } from 'mobx-react'

import {
  lifecycle,
  withHandlers,
  compose,
} from 'recompose'
import map from 'lodash/map'
import get from 'lodash/get'
import includes from 'lodash/includes'
import filter from 'lodash/filter'
import slice from 'lodash/slice'

import {
  getStartYear,
  getNow,
  getStartSeason,
  getEndOfSeason,
} from 'module/utils/dateHelpers'

import { MatchSelectContainer } from 'features/components/common/MatchSelectContainer'

type TProps = {
  pageId: string,
  clickOutside: () => void,
  dropDownClick: (e: {}) => void,
  selectItem: (e: {}, id: number | string) => void,
  extendedSelectItem: (e: {}, period: string | number) => void,
  scrollHandle: (e: {}) => void,
};

const MatchSelect = ({
  clickOutside,
  dropDownClick,
  extendedSelectItem,
  pageId,
  scrollHandle,
  selectItem,
}: TProps) => (
  <MatchSelectContainer
    pageId={pageId}
    clickOutside={clickOutside}
    dropDownClick={dropDownClick}
    selectItem={selectItem}
    extendedSelectItem={extendedSelectItem}
    scrollHandle={scrollHandle}
  />
)

const MatchSelectComposed = compose<any, TProps>(
  inject('lexics', 'matches', 'selectedMatches'),
  lifecycle({
    componentDidMount() {
      //@ts-ignore
      const {
      //@ts-ignore
        matches: { list },
        //@ts-ignore
        pageId,
        //@ts-ignore
        selectedMatches,
      } = this.props
      const matches = get(list, [pageId, 'matches'], [])
      const matchesIds = map(matches, 'f_match')
      selectedMatches.setSelected(slice(matchesIds, 0, 3), pageId)
    },
  }),
  //@ts-ignore
  withHandlers({
    handleInfiniteLoad: ({ matches, pageId }) => () => matches.addOffset(pageId),
  }),
  //@ts-ignore
  withHandlers({
    //@ts-ignore
    scrollHandle: ({ handleInfiniteLoad }) => (e) => {
      const { offsetHeight, scrollHeight, scrollTop } = e.target
      if (scrollTop - scrollHeight + offsetHeight === 1) {
        handleInfiniteLoad()
      }
    },
    //@ts-ignore
    dropDownClick: ({ pageId, selectedMatches }) => (e) => {
      e.preventDefault()
      selectedMatches.setOpened(pageId)
    },
    clickOutside: ({ pageId, selectedMatches }) => () => {
      if (selectedMatches.opened) {
        selectedMatches.setOpened(pageId)
      }
    },
    //@ts-ignore
    //@ts-ignore
    selectItem: ({ lexics, pageId, selectedMatches }) => (e, id) => {
      e.preventDefault()
      selectedMatches.setPeriod(null)
      let ids: Array<number> = selectedMatches.opened
        ? selectedMatches.tempSelected
        : get(selectedMatches[lexics.page], `${pageId}`, [])
      //@ts-ignore
      ids = includes(['year', 'season'], ids) ? [] : ids
      ids = ids ? ids.slice() : []

      if (!includes(ids, id)) {
        ids.push(id)
      } else {
        ids = filter(ids, item => item !== id)
      }
      selectedMatches.setTempSelected(ids, pageId)
    },
    extendedSelectItem: ({
      lexics: { page },
      matches: { fetch, list },
      pageId,
      selectedMatches: { setOpened, setPeriod, setTempSelected },
      //@ts-ignore
      //@ts-ignore
    }) => (e, period) => {
      e.preventDefault()
      const matches: Array<{ f_match: number }> = get(list[pageId], 'matches')
      if (includes(['year', 'season'], period)) {
        const isYear: boolean = period === 'year'
        const dateFrom: string = isYear ? getStartYear() : getStartSeason()
        const dateTo: string = isYear ? getNow() : getEndOfSeason()
        setPeriod(period)
        fetch(
          page,
          pageId,
          0,
          null,
          dateFrom,
          dateTo,
          setTempSelected,
          setOpened,
        )
      } else {
        const ids = matches.reduce((result, { f_match }, idx) => {
          if (idx < period) {
            //@ts-ignore
            result.push(f_match)
          }
          return result
        }, [])
        setPeriod()
        setTempSelected(ids, pageId)
        setOpened(pageId)
      }
    },
  }),
  observer,
)(MatchSelect)

export default MatchSelectComposed
