import { observable, computed, action } from 'mobx'
import find from 'lodash/find'
import isEmpty from 'lodash/isEmpty'

import PopupItem from './PopupItem'

/**
 * Класс кнопок из видеонарезок, через который задаются нужные параметры для кнопок
 *
 * @property {string} group - имя группы, к которой принадлежит кнопка
 *
 */
class Button {
  @observable checked = false;

  @observable popupItems = [];

  /** Дополнительно передается название группы, к которой принадлежит кнопка **/
  //@ts-ignore
  //@ts-ignore
  constructor(props, group) {
    Object.assign(this, props)
    //@ts-ignore
    this.group = group

    /** Здесь достаются id лексики кнопки и попап кнопок **/
    //@ts-ignore
    //@ts-ignore
    this.popup.forEach((popup) => {
      popup.lexical_formula = popup.popup_lexic
    })
    //@ts-ignore
    //@ts-ignore
    this.popupItems = this.popup.map(item => new PopupItem(item))
  }

  /** Кнопка активна, если хотя бы один из элементов относящихся к этой кнопке активен **/
  @computed get activeItem() {
    const active = find(this.popupItems, { checked: true })
    return active || null
  }

  /** Получаем активные элементы из кнопки **/
  @computed get activeItems() {
    //@ts-ignore
    return this.popupItems.filter(item => item.checked)
  }

  /** Получаем все активные фильтры **/
  @computed get checkedFilters() {
    //@ts-ignore
    const filters = []

    if (!isEmpty(this.activeItems)) {
      this.activeItems.forEach(({ checkedFilters }) => {
        //@ts-ignore
        //@ts-ignore
        checkedFilters.forEach(filter => filters.push(filter))
      })
    }

    //@ts-ignore
    return filters
  }

  //@ts-ignore
  @action setActive = item => (this.activeItem = item);

  /** Проходим по всем элементам и делаем их не активными **/
  @action uncheckAll = () => {
    this.popupItems.forEach((popupItem) => {
      //@ts-ignore
      popupItem.setChecked(false)
    })
  };

  /** Устанавлием активную кнопку **/
  //@ts-ignore
  //@ts-ignore
  //@ts-ignore
  set activeItem({ c_match_event_prop, c_match_event_prop_value, popup }) {
    const oldActive = find(this.popupItems, { checked: true })
    const currentBtn = find(this.popupItems, { popup })

    /** если кнопка, на которую нажали уже активна, то делаем её не активной **/
    //@ts-ignore
    //@ts-ignore
    //@ts-ignore
    //@ts-ignore
    //@ts-ignore
    if (currentBtn.checked) return currentBtn.setChecked(false)

    /** Делаем не активным, все другие кнопки **/
    //@ts-ignore
    if (oldActive) {
      //@ts-ignore
      oldActive.checked = false
    }

    const newActive = find(this.popupItems, {
      c_match_event_prop,
      c_match_event_prop_value,
    })

    //@ts-ignore
    //@ts-ignore
    newActive.setChecked(true)
  }
}
export default Button
