import { observer, inject } from 'mobx-react'
import {
  compose,
  withHandlers,
  lifecycle,
} from 'recompose'

import {
  indexLexic,
  playerLexic,
  filtersLexic,
  markersLexic,
  tableParamsLexic,
  tableParamsLexicShort,
  tableParamsNumLexic,
  videoCutsLexic,
} from 'config/lexics'

export const playerPageComposed = compose(
  inject(
    'lexics',
    'matches',
    'playerProfile',
    'selectedMatches',
    'playerTable',
  ),
  //@ts-ignore
  withHandlers({
    //@ts-ignore
    getPlayerProfile: ({ playerProfile }) => (id) => {
      playerProfile.fetch(id)
    },
    //@ts-ignore
    getGearParams: ({ playerTable }) => (id) => {
      playerTable.fetchParams(id)
    },
  }),
  lifecycle(
    {
      componentDidMount() {
        const {
          getPlayerProfile,
          lexics,
          match: {
            params: { playerId },
          },
          matches: { fetch },
        } = this.props
        lexics.appPage = {
          page: 'player',
          phrases: {
            ...indexLexic,
            ...playerLexic,
            ...filtersLexic,
            ...markersLexic,
            ...tableParamsLexic,
            ...tableParamsLexicShort,
            ...tableParamsNumLexic,
            ...videoCutsLexic,
          },
        }
        getPlayerProfile(playerId)
        fetch('player', playerId, 0, 20)
      },
    },
    //@ts-ignore
    {
      //@ts-ignore
      componentDidUpdate(prevProps) {
        const {
          getPlayerProfile,
          match: {
            params: { playerId },
          },
        } = this.props
        if (prevProps.playerId !== playerId) {
          getPlayerProfile(playerId)
        }
      },
    },
  ),
  observer,
)
