

type TArgs = {
  paramName: string | number,
  simpleName: string,
  matchesCount: number,
  matchName: string,
};

// Получение тайтлов для футера таблиц со статистикой по матчам
const getTablesFooterTitle = ({
  matchesCount,
  matchName,
  paramName,
  simpleName,
}: TArgs) => () => ({
  playlist_title: `${simpleName}`,
  playlist_subtitle: `${matchesCount} ${matchName} - ${paramName}`,
})

export default getTablesFooterTitle
