import React from 'react'
import { compose } from 'recompose'

import { OutsideClick } from 'react-sport'
import { ColumnsPopup } from './FilterPopup/ColumnsPopup'
import { ZonesPopup } from './FilterPopup/ZonesPopup'
import { PopupSimple } from './FilterPopup/PopupSimple'
import { PopupWithPlayers } from './FilterPopup/PopupWithPlayers'
import { PopupWithHomePlayers } from './FilterPopup/PopupWithHomePlayers'
import './FilterPopup/filterPopup.scss'

const FilterMenuComponent = ({ type, ...props }: {type: string}) => (
  <div className='map-block__menu'>
    {type === 'popupWithColumns' && <ColumnsPopup {...props} />}
    {type === 'popupWithZones' && <ZonesPopup {...props} />}
    {type === 'popupSimple' && <PopupSimple {...props} />}
    {type === 'popupWithPlayers' && (
      <PopupWithPlayers type={type} {...props} />
    )}
    {type === 'popupWithHomePlayers' && (
      //@ts-ignore
      <PopupWithHomePlayers type={type} {...props} />
    )}
  </div>
)

const filterMenuComposed = compose<{type: string}, {}>(
  OutsideClick,
)

export const FilterMenu = filterMenuComposed(FilterMenuComponent)
