
import React, { Children } from 'react'
import { observer } from 'mobx-react'

import {
  withHandlers,
  withState,
  withProps,
  compose,
} from 'recompose'
import cx from 'classnames'
import includes from 'lodash/includes'

import { OutsideClick } from 'react-sport'

import {
  TPropsInner,
  TPropsOuter,
  TWithHandlersOuter,
  TWithHandlersInner,
  TWithPropsInner,
  TWithPropsOuter,
} from './types'

export const ControlDropDownComponent = ({
  children,
  className,
  isVisible,
  menuType,
  title,
  toggleOpenMenu,
  withTitle,
}: TPropsInner) => (
  <div className={className} onClick={toggleOpenMenu}>
    <button className='map-block__controls-button'>
      {withTitle ? (
        <span className='map-block__controls-button__title'>{title}</span>
      ) : (
        <i className={`icon-select-${menuType}`} />
      )}
    </button>
    {isVisible && (
      <ul className='map-block__select-list'>
        {Children.map(children, (child, i) => (
          <li key={i} className='map-block__select-item'>
            <a>{child}</a>
          </li>
        ))}
      </ul>
    )}
  </div>
)

const controlDropDownComposed = compose<TPropsInner, TPropsOuter>(
  withState('isVisible', 'toggleIsVisible', false),
  withHandlers<TWithHandlersOuter, TWithHandlersInner>({
    clickOutside: ({ toggleIsVisible }) => () => toggleIsVisible(false),
    toggleOpenMenu: ({ isVisible, toggleIsVisible }) => () => toggleIsVisible(!isVisible),
  }),
  withProps<TWithPropsInner, TWithPropsOuter>(({ isVisible, menuType }) => ({
    className: cx('map-block__select', {
      'map-block__select_open': isVisible,
    }),
    withTitle: includes(['teamSelect'], menuType),
  })),
  OutsideClick,
  observer,
)

export const ControlDropDown = controlDropDownComposed(ControlDropDownComponent)
