import { types } from 'mobx-state-tree'

import get from 'lodash/get'

import { callApi } from 'react-sport'

// @ts-ignore
import routes from 'config/routes'

const TournamentPageApiActions = types.model({}).actions(self => ({
  fetchTournamentProfile(tournamentId: number) {
    const url = `${routes.API_ROOT}/data`
    const config = {
      method: 'POST',
      body: JSON.stringify({
        proc: 'scout_tournament_inf',
        params: {
          _p_tournament_id: tournamentId,
        },
      }),
    }

    return callApi({
      url,
      config,
      onRequest: () => {
        //@ts-ignore
        self.profile = {}
        //@ts-ignore
        self.isLoaded = false
      },
      //@ts-ignore
      onSuccess: self._onSuccess,
      //@ts-ignore
      onError: self._onError,
    })
  },

  _onSuccess(json: {
    data: Array<{
      scout_tournament_inf: {},
    }>,
  }) {
    //@ts-ignore
    self.profile = get(json, 'data[0].scout_tournament_inf', {})
    //@ts-ignore
    self.error = null
    //@ts-ignore
    self.isLoaded = true
  },

  _onError(error: any) {
    //@ts-ignore
    self.error = error
  },
}))

export default TournamentPageApiActions
