type TActions = { c_action: any };

export const actionTypes = {
  pass: ({ c_action }: TActions) => c_action === 400100,
  attack: ({ c_action }: TActions) => c_action === 500100,
  block: ({ c_action }: TActions) => c_action === 600100,
  serve: ({ c_action }: TActions) => c_action === 200100,
  attack_reception: ({ c_action }: TActions) => c_action === 300100,
  serve_reception: ({ c_action }: TActions) => c_action === 30010,
}

