// @ts-ignore
import { action } from 'mobx'
import isPlainObject from 'lodash/isPlainObject'
import get from 'lodash/get'
import isEmpty from 'lodash/isEmpty'
import uniq from 'lodash/uniq'
import pickBy from 'lodash/pickBy'
import some from 'lodash/some'
import filter from 'lodash/filter'

import { groupFilters } from 'module/utils/helpers'

import { fetchProps } from 'config/markers'

import {
  TCheckMarkerArgs,
  TGetMarkersArgs,
  TFilter,
} from './types'
import { doubleKeyFiltration, singleKeyFiltration } from './KeysFiltration'

//TODO Переписать функции фильтрации
/**
 * checkMarker - проверка маркеров на соответствие фильтрам
 * @property {object} filter - добавление
 * @property {object} prop - параметры маркера, по которым будет проходить проверка
 * @property {number} player_id - id игрока, нужен в случае если нету 4х параметров ниже
 * @property {array} c_match_event_prop - основной ключ маркера, по которому будет поиск
 * @property {array} c_match_event_prop2 - дополнительный ключ, если есть,
 * то проверяется на соответствие обоим ключам
 * @property {array} c_match_event_prop_value - параметр поиска для первого ключа
 * @property {array} c_match_event_prop_value2 - параметр поиска для второго ключа
 *
 * возврощает значение bool
 */

const checkMarker = ({
  c_match_event_prop,
  c_match_event_prop_value,
  c_match_event_prop_value2,
  c_match_event_prop2,
  f_player,
  filter,
  filterId,
  player_id,
  prop,
  store,
}: TCheckMarkerArgs) => {
  if (!isPlainObject(filter)) return null
  if (c_match_event_prop && c_match_event_prop2) {
    return doubleKeyFiltration({
      c_match_event_prop,
      c_match_event_prop_value,
      c_match_event_prop_value2,
      c_match_event_prop2,
      filterId,
      prop,
    })
  }
  if (c_match_event_prop) {
    return singleKeyFiltration({
      c_match_event_prop,
      c_match_event_prop_value,
      f_player,
      filterId,
      prop,
    })
  }
  if (filter.fetchProps === 'mapShoot') {
    const mapShoot = get(fetchProps, [store('tabs').active, 'mapShoot'])
    const mapForCheck = prop.filter(property => mapShoot.includes(property[0]))
    return (
      mapForCheck && mapForCheck.some(property => property[2] === player_id)
    )
  }
  if (
    store('lexics').page === 'player'
    || store('tabs').active === 'block'
    || (store('tabs').active === 'attack' && filter.fetchProps === 'playerInfo')
  ) {
    const playerInfo = get(fetchProps, [store('tabs').active, 'playerInfo'])
    const mapForCheck = prop.filter(property =>
      playerInfo.includes(property[0]))
    return (
      mapForCheck && mapForCheck.some(property => property[2] === player_id)
    )
  }
  return player_id === f_player
}

/**
 * Логика фильтрации следующая:
 * Приходят несколько фильтров и маркеры, если маркеры в одной группе,
 * то надо отдавать маркеры которые совпадают хотя бы с одним фильтром,
 * Если же маркеры из разных групп, то надо отдавать маркеры которые соответствуют всем фильтрам.
 * Но есть момент когда приходят маркеры из одной и из разных групп,
 * в этом случае маркеры разбиваются по группам,
 * и сначала делаем фильтрацию по группам, а после между группами
 */
const getMarkers = ({
  checkedFilters,
  markers,
  store,
}: TGetMarkersArgs) => {
  const groupedFilters: {
    [key: string]: Array<{}>,
  } = groupFilters(checkedFilters)
  const markersFilteredByGroup: {[key: string]: {}} = {}

  /**
   * Выше группируем фильтры и в данном цикле отфильтровываем маркеры, после
   * забиваем тоже по группам.
   * Здесь фильтрация происходит внутри одной группы, то есть как описал выше
   * возвращаются маркера соответствующие любому фильтру из списка.
   */
  Object.entries(groupedFilters).forEach(([groupName, filters]) => {
    markersFilteredByGroup[groupName] = filter(markers, ({ f_player, prop }) =>
      some(filters, (filter: TFilter) => {
        const {
          c_match_event_prop,
          c_match_event_prop_value,
          c_match_event_prop_value2,
          c_match_event_prop2,
          id: filterId,
          player_id,
        }: TFilter = filter

        return checkMarker({
          c_match_event_prop,
          c_match_event_prop_value,
          c_match_event_prop_value2,
          c_match_event_prop2,
          f_player,
          filter,
          filterId,
          player_id,
          prop,
          store,
        })
      }))
  })

  const markersToArray = Object.entries(markersFilteredByGroup)

  /**
   * если групп меньше 2х, то фильтрации между групп не требуется, поэтому возвращаем
   * отфильтрованные выше маркеры
   */
  if (markersToArray.length < 2) {
    return !isEmpty(markersFilteredByGroup) ? markersToArray[0][1] : []
  }

  /**
   * Здесь делаем фильтрацию маркеров между разных групп.
   */
  let result: Array<number> = []
  markersToArray.forEach(([markersGroup, markers]) => {
    /**
     * Отбираем фильтры, из других групп
     */
    const neededFilters = pickBy(
      groupedFilters,
      (group: Array<{}>) => ('c_match_event_prop' in group[0]
        //@ts-ignore
        ? !group[0].c_match_event_prop.includes(parseInt(markersGroup, 10))
        //@ts-ignore
        : group[0].groupName !== markersGroup),
    )
    /**
     * Здесь делаем фильтрацию маркеров, на соответствие фильтрам из других групп
     */
    const linkedMarkers = filter(markers, ({ f_player, prop }) =>
      Object.values(neededFilters).every(filters =>
      //@ts-ignore
        some(filters, (filter: TFilter) => {
          const {
            c_match_event_prop,
            c_match_event_prop_value,
            c_match_event_prop_value2,
            c_match_event_prop2,
            id: filterId,
            player_id,
          }: TFilter = filter

          return checkMarker({
            c_match_event_prop,
            c_match_event_prop_value,
            c_match_event_prop_value2,
            c_match_event_prop2,
            f_player,
            filter,
            filterId,
            player_id,
            prop,
            store,
          })
        })))

    result = [...result, ...linkedMarkers]
  })
  // Убираем дубли, если появились в процессе добавления нескольких фильтров
  // Маркер может совпадать с несколькими фильтрами и добавиться несколько раз
  return uniq(result)
}

const MarkersFiltration = (superclass: any) =>
  class extends superclass {
    //@ts-ignore
    @action getMarkers = (props: TGetMarkersArgs) => getMarkers(props);
  }

export default MarkersFiltration
