import { observable, computed, action } from 'mobx'

/**
 * Класс попап-элементов в кнопках из видеонарезок
 */
class PopupItem {
  @observable checked = false;

  @observable items = [];

  //@ts-ignore
  constructor(props) {
    Object.assign(this, props)
  }

  /** Если на попап-элемент активен, то достаем свойства, по которым далее фильтруем **/
  @computed get checkedFilters() {
    const filters = []
    if (this.checked) {
      filters.push({
        //@ts-ignore
        event_prop: this.c_match_event_prop,
        //@ts-ignore
        event_value: this.c_match_event_prop_value,
        //@ts-ignore
        lexicalFormula: this.lexical_formula,
      })
    }
    return filters
  }

  //@ts-ignore
  @action setChecked = value => (this.checked = value);
}

export default PopupItem
