import React from 'react'
import { observer } from 'mobx-react'

import includes from 'lodash/includes'
import { compose, withProps } from 'recompose'
import cx from 'classnames'

import {
  TPropsInner,
  TPropsOuter,
  TWithPropsInner,
  TWithPropsOuter,
} from './types'

export const TeamCheckboxComponent = ({
  ckeckboxClassName,
  onClick,
  status,
  teamName,
}: TPropsInner) => (
  <a
    className={ckeckboxClassName}
    onClick={onClick(status)}
  >
    {teamName}
  </a>
)

const teamCheckbox = compose<TPropsInner, TPropsOuter>(
  observer,
  withProps<TWithPropsInner, TWithPropsOuter>(({ status, team }) => ({
    ckeckboxClassName: cx('team-stats-header-opponents-toggler', {
      checked: includes(team, status),
    }),
  })),
)
export const TeamCheckbox = teamCheckbox(TeamCheckboxComponent)
