import * as React from 'react'

import { TableUI, utils } from 'react-sport'

import { leftSideTrComposed } from './composed'

type TProps = {
  rowspan: string,
  //@ts-ignore
  shirtNumber: string | null,
  playerName: string,
  avatarUrl: string,
  playerProfileLink: string,
  avatarIsVisible: boolean,
}
//@ts-ignore
export const LeftSideTr = leftSideTrComposed(({
  avatarIsVisible,
  avatarUrl,
  playerName,
  playerProfileLink,
  shirtNumber,
}: TProps) => (
  <TableUI.LeftSideTrPlayersWrapper>
    {avatarIsVisible && (
      <TableUI.ResultsAvatar
        src={avatarUrl}
        onError={utils.helpers.onProfileImageError}
      />
    )}
    <TableUI.PlayerNumber>
      {shirtNumber}
    </TableUI.PlayerNumber>
    {/*
        //@ts-ignore */}
    <TableUI.PlayerName href={playerProfileLink}>
      {playerName}
    </TableUI.PlayerName>
  </TableUI.LeftSideTrPlayersWrapper>
))
