import React, { Fragment } from 'react'
import {
  lifecycle,
  compose,
  withHandlers,
} from 'recompose'
import { inject, observer } from 'mobx-react'

import get from 'lodash/get'
import map from 'lodash/map'

import Loader from 'features/components/common/loader/Loader'
import { HeaderStatsItem } from 'features/HeaderStatsItem'

import { team_params } from 'config/params'
import './index.scss'

type TProps = {
  getEvents: (param_id: number) => Array<number>,
  getStat: (param_id: number) => number | string,
  tournamentHeaderStats: { loaded: boolean },
};

const TournamentHeaderStats = ({
  getEvents,
  getStat,
  tournamentHeaderStats: { loaded },
}: TProps) => (
  <Fragment>
    {loaded ? (
      <div className='header-stats-container'>
        {map(team_params, ({ lexic_id, param_id, percent }) => (
          <HeaderStatsItem
            key={param_id}
            paramId={param_id}
            lexicId={lexic_id}
            percent={percent}
            events={getEvents(param_id)}
            statValue={getStat(param_id)}
          />
        ))}
      </div>
    ) : (
      <Loader />
    )}
  </Fragment>
)

const TournamentHeaderStatsComposed = compose(
  inject('tournamentHeaderStats', 'tournamentSeasonSelect'),
  observer,
  //@ts-ignore
  withHandlers({
    getStat: ({ tournamentHeaderStats: { countAverageStatValue } }) =>
      //@ts-ignore
      param_id => get(countAverageStatValue, param_id, 0).toFixed(1),

    getEvents: ({ tournamentHeaderStats: { stats } }) =>
      //@ts-ignore
      param_id => get(stats[param_id], 'events', []),
  }),
  lifecycle({
    componentDidMount() {
      const {
        //@ts-ignore
        tournamentHeaderStats: { fetchHeaderParams },
      } = this.props
      fetchHeaderParams()
    },
  }),
  observer,
  //@ts-ignore
)(TournamentHeaderStats)

export default TournamentHeaderStatsComposed
