import { observer } from 'mobx-react'
import { compose, withProps } from 'recompose'

import orderBy from 'lodash/orderBy'

import {
  TPropsOuter,
  TWithPropsInner,
  TWithPropsOuter,
  TPropsInner,
} from './types'

export const tBodyComposed = compose<TPropsInner, TPropsOuter>(
  observer,
  withProps<TWithPropsInner, TWithPropsOuter>(({ rows, sortAsc }) => {
    const sortType = sortAsc ? 'asc' : 'desc'

    return {
      rowsList: orderBy(
        rows,
        [row => Number(row.sortBy)],
        sortType,
      ),
    }
  }),
)
