import { flow } from 'mobx-state-tree'

import map from 'lodash/map'
import get from 'lodash/get'

import { callApi } from 'react-sport'

import { filterDBStats } from 'module/utils/helpers'
import stores from 'features/stores'

import { TParamExtended } from './PlayerPageMatchesStore'

import { PlayerPageMatchesActions } from './actions'

type TConfig = {
  body: string,
  method: string,
}

type TFetchPlayerMatches = {
  ampluaId: number,
  matches: Array<number>,
  playerId: number,
}

export const PlayerPageMatchesApiActions = PlayerPageMatchesActions
  .actions(self => ({
    _onError(error: {}) {
      self.error = error
      self.isLoaded = false
    },

    _onSuccessStats(json: { data: Array<TParamExtended> }) {
      //@ts-ignore
      self.tableStats = filterDBStats(json.data)
      const playerMatches = get(
        JSON.parse(window.localStorage.getItem('playerMatches') || '{}'),
        get(stores, 'playerProfile.profile.amplua.id'),
      )
      if (playerMatches) {
        self.tables = playerMatches
      } else {
        self.saveTables('playerMatches')
      }
      self.error = {}
      self.isLoaded = true
    },
  }))
  .actions(self => ({
    /** Запрос для получения статистики по параметрам
      * @param {array} matches - список матчей
      * @param {string} teamId - айди игрока
      */
    fetchTableStats(matches: Array<number>, playerId: number | string) {
      const config: TConfig = {
        body: JSON.stringify({
          params: {
            _v_match_id: matches,
            _v_param_id: map(self.totalParams, 'param_id'),
            _v_player_id: [playerId],
          },
          proc: 'ask_player_param_view',
        }),
        method: 'POST',
      }
      return callApi({
        config,
        onError: self._onError,
        onRequest: () => {
          self.isLoaded = false
          self.error = {}
        },
        onSuccess: self._onSuccessStats,
        url: self.url,
      })
    },
  }))
  .actions(self => ({
    fetchPlayerMatches: flow(
      function* fetchPlayerMatches({
        ampluaId,
        matches,
        playerId,
      }: TFetchPlayerMatches) {
        try {
          // Делаем запрос для получения списка всех параметров
          yield self.getGearParamsList(ampluaId)
          // Делаем запрос для получения списка всех групп параметров
          yield self.fetchGroups()
          // Делаем запрос для получения значений для игроков
          yield self.fetchTableStats(matches, playerId)
        } catch (error) {
          self._onError(error)
        }
      },
    ),
  }))
