import { types } from 'mobx-state-tree'

const getPlainError = (error: Error | null) =>
  (error
    ? {
      message: error.message,
      name: error.name,
      stack: error.stack,
    }
    : null)

const Error = types.model({
  message: types.string,
  name: '',
  stack: types.maybe(types.string),
})

export const ErrorModel = types
  .model({
    error: types.maybeNull(Error),
  })
  .actions(self => ({
    setError(error: Error | null) {
      self.error = getPlainError(error)
    },
  }))
