import React from 'react'
import PropTypes from 'prop-types'
import cx from 'classnames'
import get from 'lodash/get'
import toString from 'lodash/toString'
import isEmpty from 'lodash/isEmpty'
import filter from 'lodash/filter'
import map from 'lodash/map'


import { T9n, Range, VideoCutPlayers } from 'react-sport'
import { observer, inject } from 'mobx-react'
import { compose, lifecycle, withProps, withHandlers } from 'recompose'

import TableButtons from './TableButtons'

/** Компонент для создания видеонарезок
 * @param {Object} selected Выбранные матчи
 * @param {String} pageType Типа страницы
 * @param {Object} playersVS стор для видеонарезок игроков
 * @param {Object} videoSummaries стор для видеонарезок по выбору либо команды, либо игроки
 * @param {Function} getCheckedBtn получает список чекнутых кнопок
 * @param {Function} handleVideoExternal отправляет запрос
 * @param {Array} partners список игроков
 * @param {Array} opponents список игроков
 * @param {Object} duration длительность общего количества нарезок
 * @param {Number} seconds количество секунд
 */
// @ts-ignore
const VideoCuts = (props) => {
  const {
    getCheckedBtn,
    getGroupedBtns,
    handleVideoExternal,
    selected,
    totalTime,
    videoSummaries: {
      isFetching,
      players: { playersChecked },
      range,
      reloadCount,
      resetFilters,
    },
    videoSummaries,
  } = props
  return (
    <div className='block-content clearfix'>
      <div className='episodes-table'>
        <div className='episodes-table-header'>
          <div className='episodes-table-header-title'>
            <a
              onClick={() => reloadCount(selected)}
              className='episodes-table__reload'
            >
              <i className='icon-reload' />
              <T9n className='episodes-table__text' t='video_reload' />
            </a>
            <div className='episodes-table-header-count'>
              <div className='episodes-table-header-count__row'>
                <div
                  style={{ cursor: 'pointer' }}
                  className={cx('episodes-table-header-title-play', {
                    'episodes-table-header-title-play_loader': isFetching,
                  })}
                  onClick={handleVideoExternal}
                >
                  <span className='icon' />
                  <span className='episodes-table-header-count__num'>
                    {get(videoSummaries, ['events', 'length'], 0)}
                  </span>
                </div>
                <span className='episodes-table-header-count__num episodes-table-header-count__num_light'>
                  {totalTime}
                </span>
              </div>
            </div>
            <div className='episodes-table-header-buttons'>
              <a onClick={resetFilters} className='episodes-table__reset'>
                <i className='icon-reset' />
                <T9n className='episodes-table__text' t='video_reset_filters' />
              </a>
              <Range range={range} />
            </div>
          </div>
        </div>
        <div className='episodes-table__container'>
          <div className='episodes-table-content clearfix'>
            <div className='episodes-table-column__title'>
              <h3 className='episodes-table-column__heading'>
                <T9n t='video_actions' />
              </h3>
              <T9n
                className='episodes-table-column__subheading'
                t='video_select_all_actions'
              />
            </div>
            {getGroupedBtns()
              //@ts-ignore
              && map(getGroupedBtns(), ({ buttons, lexic_id }, idx) => (
                <TableButtons
                  key={idx}
                  orderBy={buttons}
                  className='episodes-table-column_group'
                  title={toString(lexic_id)}
                  checked={getCheckedBtn()}
                  {...props}
                />
              ))}
          </div>
          <div className='episodes-table__summary'>
            <VideoCutPlayers
              name='partner'
              playersCount={get(playersChecked, ['partners', 'length'])}
              checked={getCheckedBtn()}
              {...props}
            />
            <VideoCutPlayers
              name='opponent'
              playersCount={get(playersChecked, ['opponents', 'length'])}
              checked={getCheckedBtn()}
              {...props}
            />
          </div>
        </div>
      </div>
    </div>
  )
}

const VideoCutsCompose = compose(
  inject('playersVS', 'teamsVS', 'shootsRange', 'lexics', 'markers'),
  //@ts-ignore
  withProps(({ pageType, playerId, ...props }) => {
    const videoSummaries = props[`${pageType}VS`]
    const minutes = get(videoSummaries, ['duration', 'minutes'])
    const seconds = get(videoSummaries, ['duration', 'seconds'])
    const totalTime = !isEmpty(videoSummaries.events)
      && `${minutes()}:${seconds() > 9 ? seconds() : `0${seconds()}`}`
    return {
      playersChecked: get(videoSummaries, ['players', 'playersChecked']),
      players: videoSummaries.players,
      pageId: playerId,
      uncheckAllPlayers: () => videoSummaries.players.uncheckAll(),
      videoSummaries,
      totalTime,
    }
  }),
  //@ts-ignore
  withHandlers({
    /** Проходим по блокам атаки и защиты, и собираем чекнутые **/
    getCheckedBtn: ({ videoSummaries }) => () => {
      const { buttons } = videoSummaries
      //@ts-ignore
      return filter(buttons, ({ checkedFilters }) => !isEmpty(checkedFilters))
    },
  }),
  //@ts-ignore
  withHandlers({
    /** Группируем кнопки **/
    getGroupedBtns: ({ videoSummaries }) => () => {
      const { buttons, groups } = videoSummaries
      //@ts-ignore
      return map(Object.values(groups), ({ group, group_lexical_formula }) => ({
        lexic_id: group_lexical_formula,
        buttons: filter(buttons, ['group', group]),
      }))
    },

    handleVideoExternal: ({
      lexics: { lang },
      shootsRange,
      videoSummaries,
    }) => () => {
      const events = map(videoSummaries.events, 'id')
      if (isEmpty(events)) return null

      videoSummaries.request({
        range: shootsRange,
        events,
        lang,
      })
    },
  }),
  lifecycle({
    componentWillUnmount() {
      //@ts-ignore
      const { videoSummaries } = this.props
      videoSummaries.resetFilters()
      videoSummaries.changeCount(0)
    },

    componentDidMount() {
      //@ts-ignore
      const { videoSummaries } = this.props
      videoSummaries.fetchButtons()
    },
  }),
  observer,
)(VideoCuts)

VideoCuts.propTypes = {
  pageType: PropTypes.oneOf(['players', 'teams']).isRequired,
  selected: PropTypes.array,
  playersVS: PropTypes.object.isRequired,
  teamsVS: PropTypes.object.isRequired,
  playerId: PropTypes.string.isRequired,
  videoSummaries: PropTypes.object.isRequired,
  getCheckedBtn: PropTypes.func.isRequired,
  getGroupedBtns: PropTypes.func,
  handleVideoExternal: PropTypes.func.isRequired,
  duration: PropTypes.object.isRequired,
  partners: PropTypes.array,
  opponents: PropTypes.array,
  totalTime: PropTypes.string,
  seconds: PropTypes.number.isRequired,
}

export default VideoCutsCompose
