import React, { Fragment } from 'react'

import map from 'lodash/map'

import { Table } from './Table'
import { TParam } from '../models/tableModel'

type TProps = {
  pageId: string,
  tableType?: string,
  tables: {
    [key: number]: {
      position: number,
      selectedParams: Array<TParam>,
    },
  },
}

export const Tables = ({
  pageId,
  tables,
  tableType,
}: TProps) => (
  <Fragment>
    {map(
      tables,
      table => (
        <Table
          tableType={tableType}
          key={table.position}
          pageId={pageId}
          table={table}
        />
      ),
    )}
  </Fragment>
)
