import React, { Fragment } from 'react'
import { inject, observer } from 'mobx-react'

import { compose, withHandlers } from 'recompose'
import get from 'lodash/get'
import filter from 'lodash/filter'
import map from 'lodash/map'

import { TableUI } from 'react-sport'

import { getStatValue } from 'module/utils/helpers/getStatValue'
import { emptyStatToZero } from 'module/utils/helpers/emptyStatToZero'

import { THead, TBody } from 'features/Table'

import { TFoot } from './HOCs/TFootHOC'
import { Tr } from './Tr'

import {
  TProps,
  TPropsOuter,
  TWithHandlersOuter,
  TWithHandlersInner,
} from './types'

export const CommonTableComponent = ({
  activeId,
  currentStore,
  getMatchesList,
  lexics: { page },
  pageId,
  param,
  setSortedId,
  sortAsc,
  table,
}: TProps) => (
  <div className='stats-table__wrapper'>
    <table className='stats-table__table'>
      <TableUI.Thead>
        {/*
        //@ts-ignore */}
        <THead table={table} setSortedId={setSortedId} />
      </TableUI.Thead>
      {/* Тут пока используем в т.ч. isFetching, т.к. не все сторы переписаны на один стиль */}
      {currentStore.isLoaded || !currentStore.isFetching ? (
        <Fragment>
          {/*
          //@ts-ignore */}
          <TBody
            selectedParams={table.selectedParams}
            rows={getMatchesList(pageId, param)}
            pageId={pageId}
            tableStats={currentStore.tableStats}
            sortAsc={sortAsc}
            activeId={activeId}
            showOpponentStats={currentStore.showOpponentStats}
            Tr={Tr}
          />
          {/*
          //@ts-ignore */}
          <TFoot
            table={table}
            tableStats={currentStore.tableStats}
            pageId={pageId}
            page={page}
            activeId={activeId}
            showOpponentStats={currentStore.showOpponentStats}
          />
        </Fragment>
      ) : (
        <tbody className='loader-wrapper' />
      )}
    </table>
  </div>
)

const CommonTableComposed = compose<TProps, TPropsOuter>(
  inject('lexics', 'selectedMatches'),
  withHandlers<TWithHandlersOuter, TWithHandlersInner>({
    getMatchesList: ({
      currentStore: { tableStats },
      lexics: { page },
      selectedMatches,
    }) => (pageId, param) => {
      const selectedMatchesList = get(selectedMatches, `${page}.${pageId}`, [])
      const tableStatsFiltered = (match: number) => (
        filter(
          tableStats,
          ({
            f_match,
            f_param,
            f_player,
            f_team,
          }) => {
            const pageType = page === 'player' ? f_player : f_team
            return (
              f_match === match
            && pageType === +pageId
            && f_param === param.param_id
            )
          },
        )
      )

      return map(selectedMatchesList, (match) => {
        const sortedValue = getStatValue(param, tableStatsFiltered(match))
        return {
          f_match: match,
          param,
          sortBy: emptyStatToZero(sortedValue),
        }
      })
    },
  }),
  observer,
)

export const CommonTable = CommonTableComposed(CommonTableComponent)
