import { inject, observer } from 'mobx-react'
import { compose, withHandlers } from 'recompose'

import map from 'lodash/map'
import values from 'lodash/values'
import reduce from 'lodash/reduce'

import m from 'moment'
import cx from 'classnames'

// @ts-ignore
import { getDropdownBtnsHoc } from 'hocs/getDropdownBtnsHoc'

// TODO убрать тип any при рефакторинге стора игроков на профиле команды
export const playersTableHeaderComposed = compose<any, any>(
  inject(
    'lexics',
    'teamPlayers',
    'selectedMatches',
    'tableReportsMST',
    'teamProfile',
  ),
  //@ts-ignore
  withHandlers({
    //@ts-ignore
    getClassName: () => checked => cx('team-stats-show-avatar', { checked }),
    deleteTable: ({
      table: { position },
      teamPlayers: { deleteTable },
    }) => () => deleteTable(position),
    /**
     * Поднятие таблицы в списке
     */
    moveTableUp: ({
      table: { position },
      teamPlayers: { moveTable },
    }) => () => {
      if (position === 1) return null
      return moveTable(position, 'up')
    },
    /**
     * Спуск таблицы вниз по списку
     */
    moveTableDown: ({
      table: { position },
      teamPlayers: { moveTable },
    }) => () => moveTable(position, 'down'),
    /**
     * Функция возврата таблице значений по-умолчанию
     */
    resetTable: ({ table: { position }, teamPlayers: { resetTable } }) => () =>
      resetTable(position),
    showAvatarToggle: ({ teamPlayers: { showAvatarToggle } }) =>
      //@ts-ignore
      position => showAvatarToggle(position),
    setType: ({ teamPlayers: { changeValueType } }) => (
      type: string,
      position: number,
    ) => () => {
      changeValueType(type, position)
    },
    saveReport: ({
      lexics: { getLexics, lang },
      selectedMatches: { team },
      tableReportsMST: { fetchData },
      teamPlayers,
      teamProfile: { profile },
      //@ts-ignore
      //@ts-ignore
    }) => (e, key) => {
      const { id, name_eng } = profile
      const tables = reduce(
        values(teamPlayers.tables),
        (
          acc,
          {
            activeId,
            avatarIsVisible,
            selectedParams,
            sortAsc,
            summaryType,
            tableName,
            //@ts-ignore
          },
        ) =>
          //@ts-ignore
          [
            ...acc,
            {
              name: getLexics(tableName) || tableName,
              params: map(selectedParams, 'param_id'),
              is_total: Number(summaryType === 'total'),
              is_avatar: Number(avatarIsVisible && key === 'pdf'),
              sortby: {
                param: activeId,
                flag: sortAsc ? 'ASC' : 'DESC',
              },
            },
          ],
        [],
      )
      const options = {
        lang_id: lang,
        team: id,
        matches: team[id],
        tables,
      }
      fetchData(e, key, options)
        //@ts-ignore
        .then((response) => {
          if (response) {
            const link: any = document.getElementById('download-link')
            link.href = URL.createObjectURL(response)
            link.download = `${m().format(
              'DD-MM-YY',
            )}_${name_eng}_players.${key}`
            link.click()
          }
        })
    },
  }),
  // Получаем список кнопок для выпадащего меню
  getDropdownBtnsHoc,
  observer,
)
