import { utils } from 'react-sport'
import { action } from 'mobx'
import routes from 'config/routes'
import concat from 'lodash/concat'
import map from 'lodash/map'
import BasicStore from 'features/stores/BasicStore'
import { team_params, additional_params } from 'config/params'

import { filterDBStats } from 'module/utils/helpers'


interface ITeamStatsApiStore {
  fetch: (
    teamId: number | string,
    //@ts-ignore
    selectedMatches: Array<number> | string | null,
  ) => void,
  eraseHeaderStats: () => void,
}

// Действия со стором статистики, получение от БД данных по статистике команды.
export default class TeamStatsApiStore extends BasicStore
  implements ITeamStatsApiStore {
  @action
  //@ts-ignore
  fetch = (teamId, selectedMatches) => {
    const url: string = `${routes.API_ROOT}/data`
    const teamParams: Array<number> = map(team_params, 'param_id')
    const config: {
      method: string,
      body: string,
    } = {
      method: 'POST',
      body: JSON.stringify({
        proc: 'ask_team_param_view',
        params: {
          _v_match_id: selectedMatches,
          _v_team_id: [teamId],
          _v_param_id: concat(teamParams, additional_params),
        },
      }),
    }
    utils.callApi({
      url,
      config,
      onRequest: () => {
        //@ts-ignore
        this.stats = []
        //@ts-ignore
        this.isFetching = true
      },
      //@ts-ignore
      onSuccess: (json: { data?: Array<{}> }) => this._onSuccess(json, teamId),
      onError: (error: any) => this._onError(error),
    })
  }

  //@ts-ignore
  _onSuccess = (json: { data?: Array<{}> }) => {
    //@ts-ignore
    this.isFetching = false
    // Нужна статистика только по целому матчу f_option_0 = 0
    // Фильтруем ответ от сервера, удаляем ненужные значения и записываем в стор
    //@ts-ignore
    this.stats = filterDBStats(json.data)
    //@ts-ignore
    this.error = null
  }

  //@ts-ignore
  _onError = (error: {}) => {
    //@ts-ignore
    this.isFetching = false
    //@ts-ignore
    this.error = error
  }

  @action eraseHeaderStats = () => {
    //@ts-ignore
    this.stats = []
  }
}
