import {
  compose,
  withState,
  withHandlers,
} from 'recompose'

import { TdComponent } from 'features/Table/components/Td'
import {
  TPropsInner,
  TPropsOuter,
  TWIthHandlersOuter,
  TWithHandlersInner,
} from './types'

const tdComposed = compose<TPropsInner, TPropsOuter>(
  withState('linkIsFetching', 'toggleLinkIsFetching', false),
  withHandlers<TWIthHandlersOuter, TWithHandlersInner>({
    handleClick: ({
      handleClick,
      param,
      player,
      toggleLinkIsFetching,
    }) => () => {
      toggleLinkIsFetching(true)
      handleClick(
        param.param_id,
        param.lexical_formula,
        player.f_player,
      )
        .then(() => toggleLinkIsFetching(false))
    },
  }),
)

export const Td = tdComposed(TdComponent)
