import React, { Fragment } from 'react'

import cx from 'classnames'
import map from 'lodash/map'
import get from 'lodash/get'
import includes from 'lodash/includes'

import { T9n } from 'react-sport'

import { gearColumnsList } from 'config/teamPopupParams'

import { PopupItem } from 'features/GearPopup'
import { gearTableComposed } from './composed'

import { TComposeInner } from './types'

/**
 * Компонент внутри попапа. Основной список чекбоксов
 */
const GearTableComponent = ({
  ampluaId,
  getCurrentGroupInfo,
  isActive,
  lastElemsInGear,
  massCheck,
}: TComposeInner) => (
  <Fragment>
    {map(gearColumnsList[ampluaId], ({ className, num }) => (
      <div key={num} className={`popup-column-${className}`}>
        <div className='popup-col-title' onClick={massCheck(num)}>
          <T9n
            className={cx(
              'checkbox-label',
              { 'checkbox-label_active': isActive(num) },
            )}
            t={get(getCurrentGroupInfo(num), 'groupName', '')}
          />
        </div>
        <div className={`popup-column-${className}-table`}>
          {map(
            get(getCurrentGroupInfo(num), 'params'), param => (
              <PopupItem
                key={param.param_id}
                last={includes(lastElemsInGear, param.param_id)}
                param={param}
              />
            ),
          )}
        </div>
      </div>
    ))}
  </Fragment>
)

export const GearTable = gearTableComposed(GearTableComponent)
