import React from 'react'
import { observer } from 'mobx-react'

import map from 'lodash/map'
import get from 'lodash/get'

import { compose, withHandlers } from 'recompose'

import { T9n, SimpleDropdown } from 'react-sport'

import { getSummaryStatValue } from 'module/utils/helpers/getSummaryStatValue'

type TProps = {
  activeId?: string,
  getActualMatchesList: (params: {
    pageId: string,
    forOpps?: boolean,
  }) => Array<any>,
  getClassName: (param: {}, activeId: string) => void,
  getExtLink: (
    param: {},
    id: number,
    forOpp: boolean,
  ) => void,
  //@ts-ignore
  param?: { param1_id: {}, param2_id: {} },
  showOpponentStats?: boolean,
  page?: string,
  pageId: string,
  selectedMatches?: {},
  setSummaryType: (summaryType: string, position: number) => () => void,
  table: {
    selectedParams: Array<{
      param_id: number,
      gear_group_id: number,
      param1_id?: {},
      param2_id?: {},
    }>,
    position: number,
    summaryType?: string,
  },
}

// Футер с выбранными параметрами в таблице статистики
export const TFootComponent = ({
  activeId,
  getActualMatchesList,
  getClassName,
  getExtLink,
  page,
  pageId,
  selectedMatches,
  setSummaryType,
  showOpponentStats,
  table: {
    position,
    selectedParams,
    summaryType,
  },
}: TProps) => (
  <tfoot>
    <tr className='overall'>
      <td className='overall__summary'>
        <div>
          {/*
            //@ts-ignore */}
          <SimpleDropdown placeholder={<T9n t='average_for_match' />}>
            {map(['average_for_match', 'total'], summaryType => (
              <T9n
                key={summaryType}
                t={summaryType}
                onClick={setSummaryType(summaryType, position)}
              />
            ))}
          </SimpleDropdown>
        </div>
      </td>
      {get(selectedMatches, `${page}.${pageId}`, [])
          && map(selectedParams, param => (
            <td
              //@ts-ignore
              className={getClassName(param, activeId)}
              key={`${param.param_id}_${param.gear_group_id}`}
            >
              <table>
                <tbody>
                  <tr onClick={() =>
                    getExtLink(
                      param,
                      +pageId,
                      false,
                    )}
                  >
                    <td>
                      {getSummaryStatValue(
                        param.param_id,
                        getActualMatchesList({
                          pageId,
                          forOpps: false,
                        }),
                        //@ts-ignore
                        summaryType,
                      )}
                    </td>
                  </tr>
                  {showOpponentStats && page === 'team'
                    && (
                      <tr onClick={() =>
                        getExtLink(
                          param,
                          +pageId,
                          true,
                        )}
                      >
                        <td>
                          {getSummaryStatValue(
                            param.param_id,
                            getActualMatchesList({
                              pageId,
                              forOpps: true,
                            }),
                            //@ts-ignore
                            summaryType,
                          )}
                        </td>
                      </tr>
                    )
                  }
                </tbody>
              </table>
            </td>
          ))}
    </tr>
  </tfoot>
)

const tFootComposed = compose(
  //@ts-ignore
  withHandlers({
    setSummaryType: ({ setType }) => (
      summaryType: string, position: number,
    ) => () => setType(summaryType, position),
  }),
  observer,
)

//@ts-ignore
export const TFoot = tFootComposed(TFootComponent)
