import _assign from 'object-assign'
import invariant from 'fbjs/lib/invariant'
import isPlainObject from 'lodash/isPlainObject'

const hasOwnProperty = {}.hasOwnProperty

function shallowCopy(x) {
  if (Array.isArray(x)) {
    return x.concat()
  } if (x && typeof x === 'object') {
    return _assign(new x.constructor(), x)
  }
  return x
}

const COMMAND_PUSH = '$push'
const COMMAND_UNSHIFT = '$unshift'
const COMMAND_SPLICE = '$splice'
const COMMAND_SET = '$set'
const COMMAND_MERGE = '$merge'
const COMMAND_APPLY = '$apply'

const ALL_COMMANDS_LIST = [
  COMMAND_PUSH,
  COMMAND_UNSHIFT,
  COMMAND_SPLICE,
  COMMAND_SET,
  COMMAND_MERGE,
  COMMAND_APPLY,
]

const ALL_COMMANDS_SET = {}

ALL_COMMANDS_LIST.forEach((command) => {
  ALL_COMMANDS_SET[command] = true
})

function invariantArrayCase(value, spec, command) {
  invariant(
    Array.isArray(value),
    'update(): expected target of %s to be an array got %s.',
    command,
    value,
  )
  const specValue = spec[command]
  invariant(
    Array.isArray(specValue),
    'update(): expected spec of %s to be an array got %s. '
      + 'Did you forget to wrap your parameter in an array?',
    command,
    specValue,
  )
}

/**
 * Returns a updated shallow copy of an object without mutating the original.
 * See https://facebook.github.io/react/docs/update.html for details.
 */
function update(value, spec) {
  invariant(
    isPlainObject(spec),
    'update(): You provided a key path to update() that did not contain one '
      + 'of %s. Did you forget to include {%s: ...}?',
    ALL_COMMANDS_LIST.join(', '),
    COMMAND_SET,
  )

  if (hasOwnProperty.call(spec, COMMAND_SET)) {
    invariant(
      Object.keys(spec).length === 1,
      'Cannot have more than one key in an object with %s',
      COMMAND_SET,
    )

    return spec[COMMAND_SET]
  }

  let nextValue = shallowCopy(value)

  if (hasOwnProperty.call(spec, COMMAND_MERGE)) {
    const mergeObj = spec[COMMAND_MERGE]
    invariant(
      mergeObj && typeof mergeObj === 'object',
      "update(): %s expects a spec of type 'object' got %s",
      COMMAND_MERGE,
      mergeObj,
    )
    invariant(
      nextValue && typeof nextValue === 'object',
      "update(): %s expects a target of type 'object' got %s",
      COMMAND_MERGE,
      nextValue,
    )
    _assign(nextValue, spec[COMMAND_MERGE])
  }

  if (hasOwnProperty.call(spec, COMMAND_PUSH)) {
    invariantArrayCase(value, spec, COMMAND_PUSH)
    spec[COMMAND_PUSH].forEach((item) => {
      nextValue.push(item)
    })
  }

  if (hasOwnProperty.call(spec, COMMAND_UNSHIFT)) {
    invariantArrayCase(value, spec, COMMAND_UNSHIFT)
    spec[COMMAND_UNSHIFT].forEach((item) => {
      nextValue.unshift(item)
    })
  }

  if (hasOwnProperty.call(spec, COMMAND_SPLICE)) {
    invariant(
      Array.isArray(value),
      'Expected %s target to be an array got %s',
      COMMAND_SPLICE,
      value,
    )
    invariant(
      Array.isArray(spec[COMMAND_SPLICE]),
      'update(): expected spec of %s to be an array of arrays got %s. '
        + 'Did you forget to wrap your parameters in an array?',
      COMMAND_SPLICE,
      spec[COMMAND_SPLICE],
    )
    spec[COMMAND_SPLICE].forEach((args) => {
      invariant(
        Array.isArray(args),
        'update(): expected spec of %s to be an array of arrays got %s. '
          + 'Did you forget to wrap your parameters in an array?',
        COMMAND_SPLICE,
        spec[COMMAND_SPLICE],
      )
      nextValue.splice(...args)
    })
  }

  if (hasOwnProperty.call(spec, COMMAND_APPLY)) {
    invariant(
      typeof spec[COMMAND_APPLY] === 'function',
      'update(): expected spec of %s to be a function got %s.',
      COMMAND_APPLY,
      spec[COMMAND_APPLY],
    )
    nextValue = spec[COMMAND_APPLY](nextValue)
  }

  for (const k in spec) {
    if (!(ALL_COMMANDS_SET.hasOwnProperty(k) && ALL_COMMANDS_SET[k])) {
      nextValue[k] = update(value[k], spec[k])
    }
  }

  return nextValue
}

export default update
