import { inject, observer } from 'mobx-react'

import {
  compose,
  withHandlers,
  lifecycle,
} from 'recompose'
import get from 'lodash/get'

import { indexLexic } from 'config/lexics'

import { rangeSettingsHoc } from 'hocs/rangeSettingsHoc'
import {
  TComposeInner,
  TWithHandlersOuter,
  TWithHandlersInner,
  TLifecycle,
  TPropsOuter,
} from './types'

export const gearPopupComposed = compose<TComposeInner, TPropsOuter>(
  inject(
    'lexics',
    'playerProfile',
    'gearPopupStore',
  ),
  withHandlers<TWithHandlersOuter, TWithHandlersInner>({
    /**
    * Функция для отображения названия для таблицы.
    * @param {object} table - объект таблицы с параметрами
    * @param {boolean} newPopup - флаг новое ли окно popup.
      Если да, то отображаем название по-умолчанию Название таблицы
  */
    getTable: () => (table, newPopup) => {
      if (newPopup) {
        const newTable = {
          position: 0,
          tableName: indexLexic.new_table,
        }
        return newTable
      }
      return table
    },
    /**
    * Запись в стор выбранных параметров в попапе шестеренки
    * @param {bool} newPopup - если шестеренка для создания новой таблицы
    * @param {func} handleNewPopupAction - функция закрытия попапа для новой таблцы
    * @param {func} saveTempParams - функция сохранения параметров
    * @param {func} handlePopupAction - функция закрытия попапа
    * @param {number} position - номер таблицы
    */
    onApply: ({
      currentStore: {
        addTable,
        saveTempParams,
        storeName,
      },
      gearPopupStore: {
        editTempTableName,
        eraseTempSelected,
        tempSelected,
        tempTableName,
      },
      handleNewPopupAction,
      handlePopupAction,
      newPopup,
      playerProfile: { profile },
    }) => position => () => {
      const ampluaId = get(profile, 'amplua.id')
      if (newPopup) {
        addTable(tempTableName, storeName, tempSelected)
        editTempTableName(indexLexic.new_table, true)
      } else {
        saveTempParams({
          ampluaId,
          localStorageName: storeName,
          position,
          selectedParams: tempSelected,
        })
      }

      if (newPopup) {
        handleNewPopupAction()
      } else {
        handlePopupAction()
      }
      eraseTempSelected()
    },
    /**
    * Закрытие попапа шестеренки
    * @param {func} editTempSelected - функция очищения выбранных параметров
    * @param {func} handlePopupAction - функция закрытия попапа
    *
    */
    onClose: ({
      gearPopupStore: { eraseTempSelected },
      handleNewPopupAction,
      handlePopupAction,
      newPopup,
    }) => () => {
      eraseTempSelected()
      if (newPopup) {
        handleNewPopupAction()
      } else {
        handlePopupAction()
      }
    },
  }),
  lifecycle<TLifecycle, {}>({
    componentDidMount() {
      const {
        gearPopupStore: { fillTempSelected },
        newPopup,
        table: { selectedParams },
      } = this.props
      if (!newPopup) {
        fillTempSelected(selectedParams)
      }
    },
  }),
  rangeSettingsHoc,
  observer,
)
