import React from 'react'
import { observer } from 'mobx-react'

import { compose } from 'recompose'

import { RangeSettings } from 'features/components/common/map/RangeSettings'

import { rangeSettingsHoc } from 'hocs/rangeSettingsHoc'
import { TRangeSettingsHocInner } from 'hocs/rangeSettingsHoc/types'

export const RangeSelectorComponent = ({
  onBlurHandler,
  onChangeHandler,
  rangeAfter,
  rangeBefore,
}: TRangeSettingsHocInner) => (
  <RangeSettings
    onBlurHandler={onBlurHandler}
    onChangeHandler={onChangeHandler}
    rangeAfter={rangeAfter}
    rangeBefore={rangeBefore}
  />
)

const rangeSelectorEnhanced = compose<TRangeSettingsHocInner, {}>(
  rangeSettingsHoc,
  observer,
)

export const RangeSelector = rangeSelectorEnhanced(RangeSelectorComponent)
