import get from 'lodash/get'
import reduce from 'lodash/reduce'
import size from 'lodash/size'
import omit from 'lodash/omit'
import keys from 'lodash/keys'

import { PlayerPageMatchesBaseActions } from './baseActions'

const moveTypes = {
  DOWN: 'down',
  UP: 'up',
}
/* Действия с таблицами
(добавление, удаление, перемещение, возврат к значению по умолчанию)
*/
export const PlayerPageMatchesActions = PlayerPageMatchesBaseActions
  .actions(self => ({
    addTable(
      name: string,
      localStorageName: string,
      selectedParams: Array<{}>,
    ) {
      const position: number = size(self.tables) + 1
      self.tables = {
        ...self.tables,
        [position]: {
          position,
          selectedParams,
          summaryType: 'average_for_match',
          tableName: name,
        },
      }
      self.saveToStorage(localStorageName)
    },
    deleteTable(position: number, localStorageName: string) {
      const tablesList = keys(self.tables)
      // Удаляем текущую таблицу
      const omitedTables = omit(self.tables, position)
      // Перезаписываем все таблицы, позиция которых после удаленной. Передвигаем весь список
      const updatedTables = reduce<{}, {[key: string]: {}}>(
        omitedTables,
        (acc, curr, key) => {
          const keyNum = Number(key)
          if (keyNum > position) {
            acc[keyNum - 1] = {
              ...get(self.tables, key),
              position: keyNum - 1,
            }
          }

          acc[key] = get(self.tables, key)
          return acc
        },
        {},
      )

      // Удаляем последнюю таблицу, дубль при перемещении всех наверх
      self.tables = omit(updatedTables, size(tablesList))
      self.saveToStorage(localStorageName)
    },

    editTable(
      position: number,
      name: string,
      localStorageName: string,
    ) {
      self.tables = {
        ...self.tables,
        [position]: {
          ...get(self.tables, position),
          tableName: name,
        },
      }
      self.saveToStorage(localStorageName)
    },
    moveTable(
      position: number,
      type: string,
      localStorageName: string,
    ) {
      const tableCount: number = size(self.tables)
      const currentTable = get(self.tables, position)
      const prevTable = get(self.tables, [position - 1])
      const nextTable = get(self.tables, [position + 1])

      if (
        (type === moveTypes.UP && position === 1)
        || (type === moveTypes.DOWN && position === tableCount)
      ) return null

      if (type === moveTypes.UP) {
        self.tables = {
          ...self.tables,
          [position - 1]: {
            ...currentTable,
            position: position - 1,
          },
          [position]: {
            ...prevTable,
            position,
          },
        }
      }

      if (type === moveTypes.DOWN) {
        self.tables = {
          ...self.tables,
          [position]: {
            ...nextTable,
            position,
          },
          [position + 1]: {
            ...currentTable,
            position: position + 1,
          },
        }
      }
      return self.saveToStorage(localStorageName)
    },
    resetTable(localStorageName: string) {
      self.tables = {}
      self.saveTables(localStorageName)
    },
  }))
