import React from 'react'
import PropTypes from 'prop-types'
import { observer, inject } from 'mobx-react'
import { T9n } from 'react-sport'

// @ts-ignore
const InfoItem = (props) => {
  const { infoItem, t, tooltips, variants } = props
  const { clickCnt, clickHandle } = infoItem
  return (
    <span
      onClick={e => clickHandle(e, tooltips)}
      className='player-label tooltip-trigger'
    >
      {variants[clickCnt]} <T9n t={t[clickCnt]} />
      <div className='hover-tooltip'>
        <T9n t={tooltips[clickCnt]} />
      </div>
    </span>
  )
}

InfoItem.propTypes = {
  variants: PropTypes.array,
  tooltips: PropTypes.array,
  t: PropTypes.array,
  infoItem: PropTypes.object,
}

export default inject('infoItem')(observer(InfoItem))
