// @ts-ignore
import { action } from 'mobx'
import { utils as remoteUtils } from 'react-sport'
import uniq from 'lodash/uniq'
import find from 'lodash/find'
import map from 'lodash/map'

// @ts-ignore
import routes from 'config/routes'
// @ts-ignore
import Marker from 'features/stores/Markers/Marker'
// @ts-ignore
import { actions } from 'config/tabsConfig'
// @ts-ignore
import { fetchProps } from 'config/markers'
// @ts-ignore
import { getToolTipResult } from 'features/stores/ToolTips/ToolTipActions'
// @ts-ignore
import { getToolTipName } from 'features/stores/PlayerInfo/FetchPlayerInfo'
// @ts-ignore
import { getToolTipOpponentTeam } from 'features/stores/MapShoot/FetchMapShoot'
// @ts-ignore
import { applyMarkersState } from 'module/utils/helpers'

// @ts-ignore
// @ts-ignore
// @ts-ignore
const getBody = (getStore, matches, id) => {
  const tab = getStore('tabs').active
  const { page } = getStore('lexics')
  const proc = page === 'player' ? 'scout_player_map_shoot' : 'scout_team_map_shoot'
  const pageId = page === 'player' ? '_p_player_id' : '_p_team_id'
  return {
    proc,
    params: {
      _p_match_arr: matches,
      _p_date_from: null,
      _p_date_to: null,
      //@ts-ignore
      _p_c_action_arr: [actions[tab]],
      [pageId]: id,
    },
  }
}

// @ts-ignore
// @ts-ignore
// @ts-ignore
export const getFilteredMarkers = (data, id, value) =>
  //@ts-ignore
  data.filter(({ prop }) =>
    find(prop, property => property[0] === id && property[2] === value))

// @ts-ignore
const FetchMarkers = superclass =>
  class extends superclass {
    //@ts-ignore
    //@ts-ignore
    //@ts-ignore
    @action fetch = (id, matches) => {
      const url = `${routes.API_ROOT}/data`
      const getStore = this.getStore.bind(this)
      getStore('shotTypes').fetch()

      const config = {
        method: 'POST',
        body: JSON.stringify(getBody(getStore, matches, id)),
      }

      remoteUtils.callApi({
        url,
        config,
        onRequest: this._onRequest.bind(this),
        onSuccess: this._onSuccess.bind(this, matches),
        onError: this._onError.bind(this),
      })
    };

    //@ts-ignore
    //@ts-ignore
    setMarkers = (data, matches) => {
      const tab = this.getStore('tabs').active
      let tooltips = this.getStore('tooltips').data
      switch (tab) {
        case 'attack':
          this.getStore('mapShoot').fetch(
            data,
            //@ts-ignore
            fetchProps[tab].mapShoot,
            matches,
          )
          //@ts-ignore
          this.getStore('playerInfo').fetch(data, fetchProps[tab].playerInfo)
          //@ts-ignore
          tooltips = tooltips.filter(({ prop_id }) => prop_id === 24)
          //@ts-ignore
          this.data = getToolTipResult(data, tooltips)
          break
        case 'block':
          if (this.getStore('lexics').page === 'player') {
            const { profile } = this.getStore('playerProfile')
            this.getStore('playerInfo').fetch(
              data,
              //@ts-ignore
              fetchProps[tab].playerInfo,
              profile.id,
            )
          }
          //@ts-ignore
          tooltips = tooltips.filter(({ prop_id }) => prop_id === 27)
          //@ts-ignore
          this.data = getToolTipResult(data, tooltips)
          break
        case 'pass':
          //@ts-ignore
          this.getStore('playerInfo').fetch(data, fetchProps[tab].playerInfo)
          //@ts-ignore
          tooltips = tooltips.filter(({ prop_id }) => prop_id === 21)
          //@ts-ignore
          this.data = getToolTipResult(data, tooltips)
          break
        case 'serve':
          this.getStore('mapShoot').fetch(
            data,
            //@ts-ignore
            fetchProps[tab].mapShoot,
            matches,
          )
          //@ts-ignore
          this.getStore('playerInfo').fetch(data, fetchProps[tab].playerInfo)
          //@ts-ignore
          tooltips = tooltips.filter(({ prop_id }) => prop_id === 15)
          //@ts-ignore
          this.data = getToolTipResult(data, tooltips)
          break
        case 'attack_reception':
          data = getFilteredMarkers(data, 70, 196)
          //@ts-ignore
          this.getStore('playerInfo').fetch(data, fetchProps[tab].playerInfo)
          this.getStore('mapShoot').fetch(
            data,
            //@ts-ignore
            fetchProps[tab].mapShoot,
            matches,
          )
          //@ts-ignore
          tooltips = tooltips.filter(({ prop_id }) => prop_id === 18)
          //@ts-ignore
          this.data = getToolTipResult(data, tooltips)
          break
        case 'serve_reception':
          data = getFilteredMarkers(data, 70, 195)
          //@ts-ignore
          this.getStore('playerInfo').fetch(data, fetchProps[tab].playerInfo)
          this.getStore('mapShoot').fetch(
            data,
            //@ts-ignore
            fetchProps[tab].mapShoot,
            matches,
          )
          //@ts-ignore
          tooltips = tooltips.filter(({ prop_id }) => prop_id === 18)
          //@ts-ignore
          this.data = getToolTipResult(data, tooltips)
          break
        default:
          //@ts-ignore
          this.data = data
      }
    };

    //@ts-ignore
    //@ts-ignore
    async _onSuccess(matches, { data }) {
      const store = this.getStore.bind(this)
      const markers = map(data, marker => new Marker(marker, store))
      //@ts-ignore
      this.data = markers
      await this.getStore('playerInfo').basicFetch()
      await this.getStore('mapShoot').basicFetch()
      const types = this.getStore('shotTypes').data
      const players = this.getStore('playerInfo').basicData
      const teams = this.getStore('mapShoot').basicData
      //@ts-ignore
      data = applyMarkersState(markers, types)
      data = getToolTipOpponentTeam(data, teams)
      if (this.getStore('tabs').active === 'block') {
        data = getToolTipName(data, players, 'block')
      } else {
        //@ts-ignore
        data = getToolTipName(data, players)
      }
      this.setMarkers(uniq(data), matches)
      //@ts-ignore
      this.loaded = true
      //@ts-ignore
      this.error = false
    }

    _onRequest() {
      //@ts-ignore
      this.loaded = false
      //@ts-ignore
      this.error = false
    }

    //@ts-ignore
    _onError(error) {
      //@ts-ignore
      this.error = error
      //@ts-ignore
      this.loaded = false
    }
  }

export default FetchMarkers
