import React from 'react'
import map from 'lodash/map'
import { TTeamStats } from '../types'

export const TeamsStats = ({
  getClassName,
  getExtLink,
  getStatValue,
  stats,
  team1,
  team2,
}: TTeamStats) => (
  <div className='t-compare__stats-container'>
    {map(stats, ({ param, param_name, param_name_short }, index) => (
      <div
        key={`${param}_${param_name_short}_${index}`}
        className='t-compare__stats-row'
      >
        <span
          className={getClassName(param, team1)}
            //@ts-ignore
          onClick={getExtLink(param, param_name, team1)}
        >
          {getStatValue(param, team1)}
        </span>
        <span className='t-compare__stats-name'>{param_name}</span>
        <span
          className={getClassName(param, team2)}
            //@ts-ignore
          onClick={getExtLink(param, param_name, team2)}
        >
          {getStatValue(param, team2)}
        </span>
      </div>
    ))}
  </div>
)

export default TeamsStats
