import { action } from 'mobx'

import { utils } from 'react-sport'

import get from 'lodash/get'

import routes from 'config/routes'
import BasicStore from 'features/stores/BasicStore'
import { amplua } from 'config/amplua'

export default class PlayerProfileApiStore extends BasicStore {
  @action fetch = (playerId: string | number) => {
    const url = `${routes.API_ROOT}/data`
    const config = {
      method: 'POST',
      body: JSON.stringify({
        proc: 'scout_player_inf',
        params: {
          player_id: playerId,
        },
      }),
    }
    utils.callApi({
      url,
      config,
      onRequest: () => {
        //@ts-ignore
        this.profile = null
        //@ts-ignore
        this.isFetching = true
      },
      onSuccess: json => this._onSuccess(json),
      onError: this._onError,
    })
  }

  _onSuccess = (json: {data: {}}) => {
    //@ts-ignore
    this.isFetching = false
    //@ts-ignore
    this.profile = get(json, 'data', null)
    const ampluaId = get(this, 'profile.amplua.id')
    if (ampluaId === amplua.libero) {
      this.getStore('tabs').changeActive('serve_reception')
      this.getStore('markers').setDefaultDirections()
    }

    //@ts-ignore
    this.error = null
  }

  //@ts-ignore
  _onError = (error: {}) => {
    //@ts-ignore
    this.isFetching = false
    //@ts-ignore
    this.error = error
  }
}
