
import { types } from 'mobx-state-tree'
import FiltersApiActions from './FiltersApiActions'
import FilterActions from './FilterActions'

// Модель группы с названием
const FilterElem = types.model('FilterElem', {
  c_action: types.maybeNull(types.number),
  c_match_event_prop: types.maybeNull(types.array(types.number)),
  c_match_event_prop2: types.maybeNull(types.array(types.number)),
  c_match_event_prop_value: types.maybeNull(types.array(types.number)),
  c_match_event_prop_value2: types.maybeNull(types.array(types.number)),
  filter_name: types.string,
  id: types.number,
  lexic_id: types.maybeNull(types.number),
})

export const Filters = types.model('Filters', {
  data: types.array(FilterElem),
  checked: types.frozen({}),
  opened: types.boolean,
  loaded: types.boolean,
  error: types.maybeNull(types.frozen({})),
})

const FiltersStore = types.compose(
  Filters,
  FiltersApiActions,
  FilterActions,
)

export default FiltersStore
