import filter from 'lodash/filter'
import get from 'lodash/get'
import reduce from 'lodash/reduce'
import set from 'lodash/set'
import isEmpty from 'lodash/isEmpty'

import stores from 'features/stores'

import { saveTablesToLocalStorage } from 'module/utils/helpers'

import { PlayerMatchesModel } from './PlayerPageMatchesStore'

type TTable = {
  position: number,
  selectedParams: Array<{ is_default: string | null }>,
  summaryType: string,
  tableName: string | number,
}

// Базовые действия с таблицами на профиле игрока
export const PlayerPageMatchesBaseActions = PlayerMatchesModel
  .actions(self => ({
    saveToStorage(localStorageName: string) {
      saveTablesToLocalStorage({
        id: get(stores, 'playerProfile.profile.amplua.id'),
        name: localStorageName,
        tables: self.tables,
      })
    },
  }))
  .actions(self => ({
    // Записываем в стор таблицы после получения параметров
    saveTables(localStorageName: string) {
      const filteredParams = filter(self.gearParams, ({ params }) => params.length)

      self.tables = reduce(filteredParams, (acc, curr, index) => {
        const position = index + 1
        const table: TTable = {
          position,
          selectedParams: filter(curr.params, { is_default: 'default' }),
          summaryType: 'average_for_match',
          tableName: String(curr.groupName),
        }

        if (!isEmpty(table.selectedParams)) {
          set(acc, position, table)
        }

        return acc
      }, {})

      self.saveToStorage(localStorageName)
    },
  }))
