
import { types } from 'mobx-state-tree'
import TableReportsApiActions from './TableReportsApiActions'

const TableReports = types.model('TableReports', {
  pdfIsFetching: types.boolean,
  xlsIsFetching: types.boolean,
  error: types.frozen({}),
})

const TableReportsStore = types.compose(
  TableReports,
  TableReportsApiActions,
)

export default TableReportsStore
