import { utils } from 'react-sport'
import { action } from 'mobx'
import routes from 'config/routes'
import map from 'lodash/map'
import get from 'lodash/get'

import BasicStore from 'features/stores/BasicStore'
import { headerPlayerParams } from 'config/params'

import { filterDBStats } from 'module/utils/helpers'

type TConfig = {
  method: string,
  body: string,
}

interface IPlayerStatsApiStore {
  fetch: (
    playerId: number | string,
    //@ts-ignore
    selectedMatches: Array<number> | string | null,
    playerTypeId: number,
  ) => void,
  eraseHeaderStats: () => void,
}

// Действия со стором статистики, получение от БД данных по статистике игрока.
export default class PlayerStatsApiStore extends BasicStore
  implements IPlayerStatsApiStore {
  @action
  //@ts-ignore
  fetch = (playerId, selectedMatches, playerTypeId) => {
    const url: string = `${routes.API_ROOT}/data`
    const playerParams: Array<number> = map(get(headerPlayerParams, playerTypeId), 'param_id')
    const config: TConfig = {
      method: 'POST',
      body: JSON.stringify({
        proc: 'ask_player_param_view',
        params: {
          _v_match_id: selectedMatches,
          _v_player_id: [playerId],
          _v_param_id: playerParams,
        },
      }),
    }
    utils.callApi({
      url,
      config,
      onRequest: () => {
        //@ts-ignore
        this.stats = []
        //@ts-ignore
        this.isFetching = true
      },
      onSuccess: json => this._onSuccess(json),
      onError: error => this._onError(error),
    })
  }

  //@ts-ignore
  _onSuccess = (json: { data?: Array<{}> }) => {
    //@ts-ignore
    this.isFetching = false
    //@ts-ignore
    this.stats = filterDBStats(json.data)
    //@ts-ignore
    this.error = null
  }

  //@ts-ignore
  _onError = (error: {}) => {
    //@ts-ignore
    this.isFetching = false
    //@ts-ignore
    this.error = error
  }

  @action eraseHeaderStats = () => {
    //@ts-ignore
    this.stats = []
  }
}
