import React, { Fragment } from 'react'
import { observer } from 'mobx-react'

import { compose, withHandlers } from 'recompose'
import cx from 'classnames'

import { OutsideClick } from 'react-sport'

import { rangeSettingsHoc } from 'hocs/rangeSettingsHoc'
import { RangeSettings } from 'features/components/common/map/RangeSettings'

import {
  TComposeInner,
  TWithHandlersOuter,
  TWithHandlersInner,
  TClickOutsideOuter,
  TClickOutsideInner,
} from './types'

export const RangeSettingsPopupComponent = ({
  handleShowPopup,
  onBlurHandler,
  onChangeHandler,
  rangeAfter,
  rangeBefore,
  shootsRange: { isOpened },
}: TComposeInner) => (
  <Fragment>
    <button className='map-block__controls-button map-block__controls-button_range'>
      <i className='icon-settings' onClick={handleShowPopup} />
    </button>
    <div
      className={cx('main-page__range-popup-settings', { hidden: !isOpened })}
    >
      {isOpened
      && (
        <RangeSettings
          onBlurHandler={onBlurHandler}
          onChangeHandler={onChangeHandler}
          rangeAfter={rangeAfter}
          rangeBefore={rangeBefore}
        />
      )}
    </div>
  </Fragment>
)

const rangeSettingsPopupComposed = compose<TComposeInner, {}>(
  rangeSettingsHoc,
  withHandlers<TWithHandlersOuter, TWithHandlersInner>({
    handleShowPopup: ({ shootsRange: { rangePopup } }) => () => rangePopup(),
  }),
  withHandlers<TClickOutsideOuter, TClickOutsideInner>({
    clickOutside: ({
      handleShowPopup,
      onBlurHandler,
      shootsRange: { isOpened },
    }) => () => {
      if (isOpened) {
        onBlurHandler()
        handleShowPopup()
      }
    },
  }),
  OutsideClick,
  observer,
)

export const RangeSettingsPopup = rangeSettingsPopupComposed(RangeSettingsPopupComponent)
