
import React from 'react'
import { inject, observer } from 'mobx-react'

import { compose } from 'recompose'

// @ts-ignore
import { getPlayerFullName } from 'module/utils/helpers/getPlayerFullName'

type TProps = {
  player: {},
  lexics: { lang: string },
};

const PlayerNameComponent = ({ lexics: { lang }, player }: TProps) => (
  <span>{getPlayerFullName({ player, lang, long: true })}</span>
)

const playerNameComposed = compose(
  inject('lexics'),
  observer,
)

// @ts-ignore
export const PlayerName = playerNameComposed(PlayerNameComponent)
