import filter from 'lodash/fp/filter'
import sortBy from 'lodash/fp/sortBy'
import flow from 'lodash/fp/flow'

import reduce from 'lodash/reduce'
import map from 'lodash/map'
import get from 'lodash/get'
import find from 'lodash/find'
import isEmpty from 'lodash/isEmpty'
import set from 'lodash/set'

import { saveTablesToLocalStorage } from 'module/utils/helpers'

import { MatchPagePlayersViews } from './views'

export const MatchPagePlayersActions = MatchPagePlayersViews
  .actions(self => ({
    setTables(tables:{}) {
      self.tables = tables
    },
  }))
  .actions(self => ({
    saveTables() {
      const filteredParams = filter(
        ({ groupName, params }) => !isEmpty(params),
      )(self.gearParams)
      const updatedTables = reduce(filteredParams, (acc, curr, index) => {
        const position = index + 1
        const table = {
          position,
          selectedParams: flow(
            filter('is_default'),
            sortBy(['gear_group_id', 'sort_order']),
          )(curr.params),
          summaryType: 'average_for_match',
          tableName: String(curr.groupName),
        }

        if (!isEmpty(table.selectedParams)) {
          set(
            table,
            'team',
            ['showHomeTeam', 'showOppTeam'],
          )
          set(
            acc,
            position,
            table,
          )
        }

        return acc
      }, {})

      self.setTables(updatedTables)

      saveTablesToLocalStorage({
        name: 'matchPagePlayers',
        tables: self.tables,
      })
    },

    toggleCheckTeam(team: Array<number>, table: { position: number }) {
      const { position } = table
      const updatedTables = {
        ...self.tables,
        [position]: {
          ...table,
          team,
        },
      }

      self.setTables(updatedTables)
    },

    // Дополняем профиль игрока именем и фамилией
    updatePlayerProfile() {
      const teams = self.teamsId

      self.playerList = reduce(
        teams,
        (acc, curr) => {
          const currentTeamPlayers: Array<{ f_player: number }> = get(self.playerList, curr, [])
          const updatedPlayers = map(currentTeamPlayers, (player) => {
            const {
              firstname_eng,
              firstname_rus,
              lastname_eng,
              lastname_rus,
            } = get(
              find(self.playersInfo, { f_player: player.f_player }),
              'prop_player[0]',
              {},
            )

            return {
              ...player,
              firstname_eng,
              firstname_rus,
              lastname_eng,
              lastname_rus,
            }
          })
          set(acc, curr, updatedPlayers)
          return acc
        },
        {},
      )
    },
  }))
