
import React from 'react'
import { inject, observer } from 'mobx-react'

import { compose, withHandlers } from 'recompose'
import isEqual from 'lodash/isEqual'

import { SaveScreenShot } from 'features/components/common/map/SaveScreenShot'
import { RangeSettingsPopup } from 'features/components/common/map/RangeSettingsPopup'
import { PlayVideoBtn } from 'features/components/common/video-player/PlayVideoBtn'

import { TeamsPopup } from './TeamsPopup'
import { Directions } from './Directions'
import { Zones } from './Zones'

import {
  TProps,
  TWithHandlersOuter,
} from './types'

export const MapFieldControlsComponent = ({
  lexics: { page },
  selectArea,
  shootsRange: { after, before },
  tabs: { active },
}: TProps) => (
  <div className='map-block__controls'>
    <div className='map-block__controls-col'>
      <Directions />
      <Zones />
      {page === 'match' && <TeamsPopup />}
      <button className='map-block__controls-button' onClick={selectArea}>
        <i className='icon-area' />
      </button>
    </div>
    {/*
      //@ts-ignore */}
    <PlayVideoBtn tab={active} />
    <div className='map-block__controls-col'>
      <SaveScreenShot />
      {/* TODO MapShare пока не делаем */}
      {/* <MapShare /> */}
      <RangeSettingsPopup key={`${after}_${before}`} />
    </div>
  </div>
)

const mapFieldControlsComposed = compose<TProps, {}>(
  inject(
    'tabs',
    'lexics',
    'markers',
    'zones',
    'shootsRange',
  ),
  withHandlers<TWithHandlersOuter, {}>({
    selectArea: ({
      markers: {
        directions,
        select,
        selectToggle,
        setAllVisible,
      },
      zones: {
        resetToDefaultDirections,
        setZone,
      },
    }) => () => {
      if (!select) {
        setAllVisible()
      }
      if (isEqual(directions, { from: true, to: true })) {
        resetToDefaultDirections()
      }
      setZone('without_zone')
      selectToggle(!select)
    },
  }),
  observer,
)
export const MapFieldControls = mapFieldControlsComposed(MapFieldControlsComponent)
