import m from 'moment'

/**
 * Функция получения текущего года или любого, для теста
 * @param {any} test - флаг, если нужен год, отличный от текущего в бОльшую сторону
 * @param {number} changeYear - число, прибавляемое к текущему году
 */
function getYear(test: any, changeYear: number): number {
  return test ? m().year() + changeYear : m().year()
}

/**
 * Функция получения текущей даты
 * @param {string} format - формат выводимой даты, если требуется
 */
export function getNow(format?: string): string {
  return m().format(format)
}

/**
 * Функция получения начала года
 * @param {string} format - формат выводимой даты, если требуется
 */
export function getStartYear(format?: string): string {
  return m([m().year(), 0, 1]).format(format)
}

/**
 * Функция получения начала сезона
 * @param {string} format - формат выводимой даты, если требуется
 */
export function getStartSeason(format?: string): string {
  /** Сезон начинается с 1 июля **/
  return m([getYear(m().month() < 6, -1), 6, 1]).format(format)
}

/**
 * Функция получения конца сезона
 * @param {string} format - формат выводимой даты, если требуется
 */
export function getEndOfSeason(format?: string): string {
  /** Сезон кончается 30 июня **/
  return m([getYear(m().month() > 6, 1), 5, 30]).format(format)
}

/**
 * Функция форматирования любой даты (прим. 5:15 или 5:03) с секундами и минутами
 * @param {number} minutes - минуты
 * @param {number} seconds - секунды
 */
export function formatTime({
  minutes,
  seconds,
}: {
  minutes: number,
  seconds: number,
}): string {
  return `${minutes}:${seconds > 10 ? seconds : `0${seconds}`}`
}
