import { action } from 'mobx'
import { utils } from 'react-sport'

// @ts-ignore
import routes from 'config/routes'
// @ts-ignore
import BasicStore from 'features/stores/BasicStore'

type TParams = {
  proc: string,
  match_id: number,
  team_id?: number,
  player_id?: number,
};

type TConfig = {
  method: string,
  body: string,
};

interface IMatchLinksApi {
  fetch: (match_id: number, id: number, type: string) => void,
}
export default class MatchLinksApiStore extends BasicStore
  implements IMatchLinksApi {
  @action fetch = (
    //@ts-ignore
    match_id,
    //@ts-ignore
    pageId,
    //@ts-ignore
    type,
  ) => {
    const url = `${routes.API_ROOT}/video/match`
    const id_type = type === 'team' ? 'team_id' : 'player_id'
    const proc = type === 'team' ? 'scout_team_match_pop_up' : 'scout_match_pop_up'
    const params: TParams = {
      proc,
      match_id,
    }
    params[id_type] = pageId

    const config: TConfig = {
      method: 'POST',
      body: JSON.stringify(params),
    }

    utils.callApi({
      url,
      config,
      onRequest: () => {
        //@ts-ignore
        this.links = []
        //@ts-ignore
        this.isFetching = true
      },
      onSuccess: (json: { links: {} | Array<any> | void | null }) =>
        this._onSuccess(json),
      onError: (error: any) => this._onError(error),
    })
  };

  _onSuccess = ({ links }: { links: {} | Array<any> | null | void }) => {
    //@ts-ignore
    this.links = links
    //@ts-ignore
    this.isFetching = false
    //@ts-ignore
    this.error = null
  };

  _onError = (error: {}) => {
    //@ts-ignore
    this.error = error
    //@ts-ignore
    this.isFetching = false
  };
}
