
import React from 'react'
import { inject, observer } from 'mobx-react'

import { lifecycle, compose } from 'recompose'

// @ts-ignore
import { Tables } from 'features/Table'
// @ts-ignore
import Loader from 'features/components/common/loader/Loader'
// @ts-ignore
import { tableTypes } from 'config/tables'

type TProps = {
  lexics: { page: string },
  tournamentId: number,
  tournamentPlayersTableStore: { isLoaded: boolean, tables: {} },
};

const TournamentPlayersTableComponent = ({
  tournamentId,
  tournamentPlayersTableStore: { isLoaded, tables },
}: TProps) =>
  (isLoaded ? (
    <Tables
      tableType={tableTypes.tournamentPlayers}
      tables={tables}
      pageId={String(tournamentId)}
    />
  ) : (
    <Loader />
  ))

export const TournamentPlayersTableComposed = compose(
  inject('lexics', 'tournamentPlayersTableStore', 'tournamentSeasonSelect'),
  lifecycle({
    componentDidMount() {
      const {
        //@ts-ignore
        tournamentId,
        //@ts-ignore
        tournamentPlayersTableStore: { fetchTableParams },
        //@ts-ignore
        tournamentSeasonSelect: { selectedSeason },
      } = this.props
      fetchTableParams(tournamentId, selectedSeason.season_id)
    },
  }),
  observer,
)

// @ts-ignore
export const TournamentPlayersTable = TournamentPlayersTableComposed(
  //@ts-ignore
  TournamentPlayersTableComponent,
)
