import { inject, observer } from 'mobx-react'

import {
  compose,
  withHandlers,
} from 'recompose'
import get from 'lodash/get'

import { clickableClassNameHOC } from 'hocs/getClickableTdClassNameHoc'

import { TFoot as TFootComponent } from 'features/Table'

const TfootComposed = compose(
  inject(
    'selectedMatches',
    'matches',
    'shootsRange',
    'lexics',
    'videoLink',
    'teamTable',
    'playerTable',
  ),
  //@ts-ignore
  withHandlers({
    /**
    * Функция для открытия попап окна с видео повторами
    * @param {number} param_id - id параметра
    * @param {string} lexical_formula - название параметра
    * @param {array} tableStats - массив всех параметров
    * @param {number} id - id игрока или команды
    * @param {boolean} forOpp - флаг для фильтрации. Ищем для своей команды или для команд соперника
    */
    getExtLink: ({
      lexics: { lang, page },
      shootsRange,
      tableStats,
      videoLink: { request },
    }) => (
      {
        //@ts-ignore
        lexical_formula,
        //@ts-ignore
        param_id,
      },
      //@ts-ignore
      id,
      //@ts-ignore
      forOpp,
    ) => {
      //@ts-ignore
      const checkId = (teamId) => {
        if (page === 'player') {
          return true
        }
        if (forOpp) {
          return teamId !== id
        }
        return teamId === id
      }

      // Фильтруем сначала по команде
      const events = tableStats.filter(
        //@ts-ignore
        ({ f_param, f_team }) => (
          f_param === param_id && checkId(f_team)),
      )
      const filteredEvents = events.map(
        (
          { f_match_event_ids_json }
          //@ts-ignore
          : { f_match_event_ids_json: Array<number | null> },
        ) => f_match_event_ids_json,
      )
        .flat()
      // удаляем все null из массива эвентов
        .filter(Boolean)

      if (events.length) {
        request({
          events: filteredEvents,
          lang,
          lexical_formula,
          range: shootsRange,
          type: 'tablesFooter',
        })
      }
    },
    /**
    * Функция установки выбранного типа итогового значения в таблице
    * @param {string} type - типа итогового значения
    */
    setType: ({
      lexics: { page },
      playerTable,
      teamTable,
    }) => (type: string, position: number) => {
      if (page === 'team') {
        return teamTable.changeValueType(type, position)
      }
      return playerTable.changeValueType(type, position)
    },
    /**
    * Функция фильтрации статистики по всем матчам только по выбранным матчам.
    И для команды соперника или нашей
    * @param {string} page - игрок или команда
    * @param {string} pageId - id игрока/команды
    * @param {array} tableStats - массив параметров
    */
    getActualMatchesList: ({
      lexics: { page },
      selectedMatches,
      tableStats,
    }) => ({
      forOpps,
      pageId,
    }: {
      pageId: string,
      forOpps: boolean,
    }) => {
      const selectedMatchesList: Array<number> = get(selectedMatches, `${page}.${pageId}`, [])
      // Для команды соперника
      if (forOpps) {
        //@ts-ignore
        return tableStats.filter(({ f_match, f_team }) => (
          f_team !== +pageId && selectedMatchesList.includes(f_match)))
      }
      // для страницы игрока возвращем просто весь массив со значением параметров
      // (нам нужны все для среднего значения)
      if (page === 'player') {
        //@ts-ignore

        return tableStats.filter(({ f_match }) => (
          selectedMatchesList.includes(f_match)))
      }
      // Для текущей команды
      //@ts-ignore
      return tableStats.filter(({ f_match, f_team }) => (
        f_team === +pageId && selectedMatchesList.includes(f_match)))
    },
  }),
  clickableClassNameHOC,
  observer,
)

export const TFoot = TfootComposed(TFootComponent)
