import { observable, action, computed } from 'mobx'
import {
  tabs,
  tabsForAmplua5,
  tabsForAmplua6,
  tabsForAmplua7,
  tabsForAmplua8,
  //@ts-ignore
} from 'config/tabsConfig'
// @ts-ignore
import stores from 'features/stores'
import BasicStore from './BasicStore'

export default class Tabs extends BasicStore {
  @observable active = 'serve';

  @observable tabs = tabs;

  @computed get list() {
    const { page } = this.getStore('lexics')
    const { isFetching, profile } = this.getStore('playerProfile')

    if (page === 'player') {
      if (!isFetching && profile) {
        switch (profile.amplua.id) {
          case 4:
          case 5:
            return tabsForAmplua5
          case 8:
            return tabsForAmplua8
          case 6:
            return tabsForAmplua6
          case 7:
            return tabsForAmplua7
          default:
            return this.tabs
        }
      }
    }

    return this.tabs
  }

  //@ts-ignore
  @action changeActive = (tab) => {
    this.active = tab
    // Очищаем выбранные фильтры
    //@ts-ignore
    stores.filters.clear()
  };
}
