import { types } from 'mobx-state-tree'
import get from 'lodash/get'
import values from 'lodash/values'
import reduce from 'lodash/reduce'
import filter from 'lodash/filter'


import { callApi } from 'react-sport'
import routes from 'config/routes'

const MatchProfileApiActions = types.model({}).actions(self => ({
  //@ts-ignore
  fetch(matchId) {
    const url = `${routes.API_ROOT}/data`
    const config = {
      method: 'POST',
      body: JSON.stringify({
        proc: 'scout_match_inf',
        params: {
          _p_match_id: matchId,
        },
      }),
    }

    callApi({
      url,
      config,
      onRequest: () => {
        //@ts-ignore
        self.data = {}
        //@ts-ignore
        self.isFetching = true
      },
      //@ts-ignore
      onSuccess: json => self._onSuccess(json, matchId),
      //@ts-ignore
      onError: error => self._onError(error),
    })
  },

  _onSuccess({ data }: { data: {} }, matchId: string) {
    //@ts-ignore
    self.data = get(data, '[0].scout_match_inf', {})
    //@ts-ignore
    self.fetchPeriods(matchId)
    //@ts-ignore
    self.fetchVideoLinks(matchId)
    //@ts-ignore
    self.error = null
    //@ts-ignore
    self.matchId = matchId
  },

  _onError(error: any) {
    //@ts-ignore
    self.error = error
    //@ts-ignore
    self.isFetching = false
  },

  fetchPeriods(matchId: string) {
    const url = `${routes.API_ROOT}/data`
    const config = {
      method: 'POST',
      body: JSON.stringify({
        proc: 'ask_team_param_view',
        params: {
          _v_match_id: [matchId],
          //@ts-ignore
          //@ts-ignore
          _v_team_id: [self.data.team1_id, self.data.team2_id],
          _v_param_id: [239],
        },
      }),
    }

    callApi({
      url,
      config,
      onRequest: () => {},
      //@ts-ignore
      onSuccess: json => self._onSuccessPeriods(json),
      //@ts-ignore
      onError: error => self._onError(error),
    })
  },

  _onSuccessPeriods(json: {
    data: Array<{
      value: number,
      f_option_0: number,
      f_team: number,
    }>,
  }) {
    const data = json.data.map(({ f_option_0, f_team, value }) => ({
      value,
      period: f_option_0,
      team: f_team,
    }))
    //@ts-ignore
    self.periods = values(
      reduce(
        data,
        (res, { period }) => {
          if (period !== 0) {
            //@ts-ignore
            res[period] = filter(data, item => item.period === period)
          }
          return res
        },
        {},
      ),
    )
  },

  fetchVideoLinks(matchId: string) {
    const url = `${routes.API_ROOT}/video/match`
    const config = {
      method: 'POST',
      body: JSON.stringify({
        proc: 'scout_team_match_pop_up',
        //@ts-ignore
        team_id: `${get(self.data, 'team1_id', '')}`,
        match_id: +matchId,
      }),
    }

    callApi({
      url,
      config,
      onRequest: () => {},
      //@ts-ignore
      onSuccess: json => self._onSuccessVideoLinks(json),
      //@ts-ignore
      onError: error => self._onError(error),
    })
  },

  _onSuccessVideoLinks(json: { links: {} }) {
    //@ts-ignore
    self.videoLinks = json.links
    //@ts-ignore
    self.isFetching = false
  },
}))

export default MatchProfileApiActions
