import React from 'react'
import { observer, inject } from 'mobx-react'

import { compose, lifecycle, withProps } from 'recompose'

import { PlayersSelect } from './PlayersSelect'
import { PlayersInfo } from './PlayersInfo'
import PlayerCompareHeader from './PlayerCompareHeader'

import { defaultParams } from './config'

type TProps = {
  playersCompareWidget: {
    isLoaded: boolean,
    selectedPlayers: {},
  },
  matchProfile: {
    data: {
      team1_id: number,
      team2_id: number,
    },
  },
}

export const PlayersCompareComponent = ({
  matchProfile: {
    data: { team1_id, team2_id },
  },
  playersCompareWidget: { isLoaded, selectedPlayers },
}: TProps) => (
  <div className='t-compare'>
    <PlayerCompareHeader />
    <div className='t-compare__container'>
      {/*
      //@ts-ignore */}
      <PlayersSelect team1={team1_id} team2={team2_id} />
      {isLoaded ? (
        <PlayersInfo
          team1={team1_id}
          team2={team2_id}
          selectedPlayers={selectedPlayers}
        />
      ) : (
        <div className='loading' />
      )}
    </div>
  </div>
)

const PlayersCompareComposed = compose<TProps, {}>(
  inject('lexics', 'playersCompareWidget', 'matchProfile'),
  observer,
  //@ts-ignore
  withProps(({ lexics: { lang } }) => ({
    lang,
  })),
  lifecycle({
    componentDidMount() {
      const {
        //@ts-ignore
        lang,
        //@ts-ignore
        matchProfile: { matchId },
        //@ts-ignore
        playersCompareWidget: { basicFetch },
      } = this.props

      basicFetch({
        lang,
        matchId,
        params: defaultParams,
      })
    },
    componentDidUpdate(prevProps) {
      const {
        //@ts-ignore
        lang,
        //@ts-ignore
        matchProfile: { matchId },
        //@ts-ignore
        playersCompareWidget: { fetch, getParamsIds },
      } = this.props

      //@ts-ignore
      if (prevProps.lang !== lang) {
        fetch({
          lang,
          matchId,
          params: getParamsIds,
        })
      }
    },
  }),
  observer,
)

export const PlayersCompare = PlayersCompareComposed(PlayersCompareComponent)
