// @ts-ignore
import { action } from 'mobx'
import { utils as remoteUtils } from 'react-sport'
import remove from 'lodash/remove'

// @ts-ignore
import { getPlayers, getLongSuffix } from 'module/utils/helpers'
// @ts-ignore
import { getFilteredMarkers } from 'features/stores/Markers/FetchMarkerActions'
// @ts-ignore
import routes from 'config/routes'

const setMarkerPlayer = ({
  //@ts-ignore
  isBlock,
  //@ts-ignore
  marker,
  //@ts-ignore
  markersWithName,
  //@ts-ignore
  players,
  //@ts-ignore
  playerSource,
}) => {
  //@ts-ignore
  const player = players.find(
    //@ts-ignore
    ({ f_player }) => f_player === (isBlock ? playerSource[2] : playerSource),
  )
  if (!player) {
    return markersWithName.push(marker)
  }

  const currentPlayerName = player.prop_player[0]

  if (marker.playerName) {
    marker.playerName.push(currentPlayerName)
  } else {
    marker.playerName = [currentPlayerName]
  }
  return markersWithName.push(marker)
}

// @ts-ignore
// @ts-ignore
// @ts-ignore
const getPlayersIdList = (data, playerIdList, players) =>
  //@ts-ignore
  data.forEach(({ prop }) =>
    // из маркеров достаем игроков по ключу
    //@ts-ignore
    playerIdList.forEach((id) => {
      //@ts-ignore
      const player = prop.find(property => property[0] === id)
      if (player) {
        players.push(player[2])
      }
    }))

// @ts-ignore
// @ts-ignore
// @ts-ignore
// @ts-ignore
export const getToolTipName = (markers, players, tab) => {
  //@ts-ignore
  const markersWithName = []
  const playerIdList = [39, 40, 41]

  if (tab === 'block') {
    //@ts-ignore
    markers.forEach((marker) => {
      const { prop } = marker
      // из маркеров достаем игроков по ключу
      playerIdList.forEach((id) => {
        //@ts-ignore
        const player = prop.find(property => property[0] === id)
        if (player) {
          setMarkerPlayer({
            players,
            playerSource: player,
            marker,
            //@ts-ignore
            markersWithName,
            isBlock: true,
          })
        }
      })
    })
  } else {
    //@ts-ignore
    markers.forEach((marker) => {
      const { f_player } = marker

      //@ts-ignore
      setMarkerPlayer({
        players,
        playerSource: f_player,
        marker,
        //@ts-ignore
        markersWithName,
      })
    })
  }
  //@ts-ignore
  return markersWithName
}

// @ts-ignore
const FetchPlayerInfo = superclass =>
  class extends superclass {
    //@ts-ignore
    @action basicFetch = () => {
      const promise = new Promise((resolve, reject) => {
        const url = `${routes.API_ROOT}/data`
        let { data } = this.getStore('markers')
        //@ts-ignore
        const players = []
        if (this.getStore('tabs').active === 'block') {
          const playerIdList = [39, 40, 41]
          //@ts-ignore
          getPlayersIdList(data, playerIdList, players)
        } else {
          if (this.getStore('tabs').active === 'serve_reception') {
            data = getFilteredMarkers(data, 70, 195)
          } else if (this.getStore('tabs').active === 'attack_reception') {
            data = getFilteredMarkers(data, 70, 196)
          }
          //@ts-ignore
          data.forEach(({ f_player }) => players.push(f_player))
        }
        const config = {
          method: 'POST',
          body: JSON.stringify({
            proc: 'scout_players_info',
            params: {
              //@ts-ignore
              _p_player_id: players,
            },
          }),
        }

        remoteUtils.callApi({
          url,
          config,
          onRequest: this._basicRequest.bind(this),
          onError: error => this._basicError(error, reject),
          onSuccess: json => this._basicSuccess(json, resolve),
        })
      })

      return promise
    };

    //@ts-ignore
    //@ts-ignore
    //@ts-ignore
    @action fetch = (markers, propsList, excludePlayer = false) => {
      const url = `${routes.API_ROOT}/data`
      const players = getPlayers(markers, propsList)
      if (excludePlayer) {
        remove(players, player => player === excludePlayer)
      }
      const config = {
        method: 'POST',
        body: JSON.stringify({
          proc: 'scout_players_info',
          params: {
            _p_player_id: players,
          },
        }),
      }

      remoteUtils.callApi({
        url,
        config,
        onRequest: this._onRequest.bind(this),
        onSuccess: this._onSuccess.bind(this),
        onError: this._onError.bind(this),
      })
    };

    _basicRequest() {
      //@ts-ignore
      this.basicLoaded = false
    }

    //@ts-ignore
    //@ts-ignore
    _basicSuccess({ data }, resolve) {
      resolve(data)
      //@ts-ignore
      this.basicData = data
      //@ts-ignore
      this.basicLoaded = true
    }

    //@ts-ignore
    //@ts-ignore
    _basicError(error, reject) {
      reject(error)
      //@ts-ignore
      this.basicLoaded = false
    }

    //@ts-ignore
    _onSuccess({ data }) {
      //@ts-ignore
      this.data = data
      //@ts-ignore
      this.loaded = true
      //@ts-ignore
      this.error = false
    }

    _onRequest() {
      //@ts-ignore
      this.loaded = false
      //@ts-ignore
      this.error = false
    }

    //@ts-ignore
    _onError(error) {
      //@ts-ignore
      this.error = error
      //@ts-ignore
      this.loaded = false
    }
  }

export default FetchPlayerInfo
