import { toJS } from 'mobx'
import { types } from 'mobx-state-tree'

import filter from 'lodash/filter'
import omit from 'lodash/omit'
import get from 'lodash/get'
import reduce from 'lodash/reduce'
import isEqual from 'lodash/isEqual'

import { indexLexic } from 'config/lexics'
import { saveTablesToLocalStorage } from 'module/utils/helpers'

type TSetActiveParam = {
  param: {
    param_id: number,
  },
  sortAsc: boolean,
  table: {
    position: number,
  },
}

//TODO переписать и декомпозировать
const TeamPlayersActions = types.model({}).actions(self => ({
  // Записываем в стор таблицы после получения параметров
  saveTables() {
    //@ts-ignore
    self.gearParams.forEach(({ groupName, params }, index) => {
      const position: number = index + 1
      const table: {
        position: number,
        selectedParams: Array<{ is_default: string }>,
        tableName: string | number,
      } = {
        position,
        //@ts-ignore
        selectedParams: params.filter(({ is_default }) => is_default === 'default'),
        tableName: String(groupName),
        //@ts-ignore
        summaryType: 'average_for_match',
        avatarIsVisible: false,
      }
      //@ts-ignore
      if (table.selectedParams.length) {
        //@ts-ignore
        self.tables = {
          //@ts-ignore
          ...self.tables,
          [position]: {
            ...table,
            sortAsc: false,
            activeId: null,
          },
        }
      }
      //@ts-ignore
      saveTablesToLocalStorage({ name: 'teamPlayersTable', tables: self.tables })
    })
  },
  /**
   * Действия с выбранными в шестеренке параметрами
   * (добавление, удаление, очищение выбранных параметров, перемещение карточек в правом меню)
   * @param {string} action - название действия
   * @param {object} param - параметр, с которым выполняем действие или список параметров
   */
  //@ts-ignore
  editTempSelected(action = null, param) {
    switch (action) {
      //@ts-ignore
      case 'add':
        //@ts-ignore
        self.tempSelected = [...self.tempSelected, toJS(param)]
        break
      //@ts-ignore
      case 'delete':
        //@ts-ignore
        self.tempSelected = filter(
          //@ts-ignore
          self.tempSelected,
          item => !isEqual(param, item),
        )
        break
      //@ts-ignore
      case 'erase':
        //@ts-ignore
        self.tempSelected = []
        break
      default:
        //@ts-ignore
        self.tempSelected = param.map(param => toJS(param))
        break
    }
  },
  /**
   * Выбранные параметры (чекбоксы) сохраняем в стор
   * @param {number} position - порядковый номер таблицы
   */
  saveTempParams({
    localStorageName = 'teamPlayersTable',
    position,
    selectedParams,
  }: {
    localStorageName: string,
    position: number,
    selectedParams: Array<{}>,
  }) {
    //@ts-ignore
    self.tables = {
      //@ts-ignore
      ...self.tables,
      [position]: {
        //@ts-ignore
        ...self.tables[position],
        selectedParams,
      },
    }
    // Сохраняем выбранные параметры в localStorage
    //@ts-ignore
    saveTablesToLocalStorage({ name: localStorageName, tables: self.tables })
    // Очищаем временно выбранные параметры из popup
    //@ts-ignore
    self.editTempSelected('erase')
  },
  /**
   * Добавляем новую таблицу
   * @param {string} name - название таблицы
   */
  addTable(
    name: string,
    localStorageName: string = 'teamPlayersTable',
    selectedParams: Array<{}>,
  ) {
    //@ts-ignore
    const position: number = Object.keys(self.tables).length + 1
    //@ts-ignore
    self.tables = {
      //@ts-ignore
      ...self.tables,
      [position]: {
        avatarIsVisible: false,
        position,
        selectedParams,
        summaryType: 'average_for_match',
        tableName: name,
      },
    }
    // Сохраняем таблицы в localStorage
    //@ts-ignore
    saveTablesToLocalStorage({ name: localStorageName, tables: self.tables })
  },

  /** удаления таблицы
* @param {number} position - порядковый номер таблицы
*/
  deleteTable(position: number) {
    //@ts-ignore
    const tablesList: Array<any> = Object.keys(self.tables)
    // Удаляем текущую таблицу
    //@ts-ignore
    const omitedTables = omit(self.tables, position)
    // Перезаписываем все таблицы, позиция которых после удаленной. Передвигаем весь список
    const updatedTables = reduce(omitedTables, (acc, curr, key) => {
      //@ts-ignore
      if (key > position) {
        //@ts-ignore
        acc[key - 1] = {
          //@ts-ignore
          ...self.tables[key],
          //@ts-ignore
          position: key - 1,
        }
      }
      //@ts-ignore
      acc[key] = self.tables[key]
      return acc
    }, {})
    //@ts-ignore
    self.tables = omit(updatedTables, tablesList.length)
    // Удаляем и из localStorage
    //@ts-ignore
    saveTablesToLocalStorage({ name: 'teamPlayersTable', tables: self.tables })
  },

  /** Редактирование таблицы
* @param {number} position - порядковый номер таблицы
* @param {string | number} name - новое название таблицы
*/
  editTable(position: number, name: string | number) {
    //@ts-ignore
    self.tables = {
      //@ts-ignore
      ...self.tables,
      [position]: {
        //@ts-ignore
        ...self.tables[position],
        tableName: name,
      },
    }
    //@ts-ignore
    saveTablesToLocalStorage({ name: 'teamPlayersTable', tables: self.tables })
  },

  /** Сброс всех таблиц по-умолчанию*/
  resetTable() {
    // Сначала удаляем таблицы уже сформированные
    window.localStorage.removeItem('teamPlayersTable')
    //@ts-ignore
    self.tables = {}
    //@ts-ignore
    self.saveTables()
  },

  /** Перемещение таблицы вверх/вниз
  * @param {number} position - порядковый номер таблицы
  * @param {string} type - тип перемещения (вверх или вниз)
  */
  moveTable(position: number, type: string) {
    //@ts-ignore
    const tableCount: number = Object.keys(self.tables).length
    //@ts-ignore
    const current: {} = self.tables[position]
    //@ts-ignore
    const prev: {} = self.tables[position - 1]
    //@ts-ignore
    const next: {} = self.tables[position + 1]
    if (type === 'up') {
      //@ts-ignore
      self.tables = {
        //@ts-ignore
        ...self.tables,
        [position - 1]: {
          ...current,
          position: position - 1,
        },
        [position]: {
          ...prev,
          position,
        },
      }
    }
    if (type === 'down') {
      // Если мы опускаем последнюю таблицу вниз, то ничего не происходит
      if (position === tableCount) return null
      //@ts-ignore
      self.tables = {
        //@ts-ignore
        ...self.tables,
        [position]: {
          ...next,
          position,
        },
        [position + 1]: {
          ...current,
          position: position + 1,
        },
      }
    }
    //@ts-ignore
    saveTablesToLocalStorage({ name: 'teamPlayersTable', tables: self.tables })
  },

  /** Редактирование названия таблицы
  * @param {string} name - название таблицы
  * @param {bool} erase - возврат к значению по-умолчанию
  */
  editTempTableName(name: string, erase: boolean = false) {
    if (erase) {
      //@ts-ignore
      self.tempTableName = indexLexic.new_table
    } else {
      //@ts-ignore
      self.tempTableName = name
    }
  },

  /** Меняем стиль отображения ячеек (итоговое по всем матчам или среднее значение)
  * @param {string} type - тип отображения
  */
  changeValueType(type: string, position: number) {
    // Если тип не меняется, то не используем формулу
    //@ts-ignore
    if (get(self.tables, `${position}.summaryType`) === type) return null
    //@ts-ignore
    self.tables = {
      //@ts-ignore
      ...self.tables,
      [position]: {
        //@ts-ignore
        ...self.tables[position],
        summaryType: type,
      },
    }
  },

  setActiveParam({
    param,
    sortAsc,
    table,
  }: TSetActiveParam) {
    const { position } = table
    //@ts-ignore
    self.tables = {
      //@ts-ignore
      ...self.tables,
      [position]: {
        ...table,
        sortAsc,
        activeId: param.param_id,
      },
    }
  },

  showAvatarToggle(position: number) {
    //@ts-ignore
    self.tables = {
      //@ts-ignore
      ...self.tables,
      [position]: {
        //@ts-ignore
        ...self.tables[position],
        //@ts-ignore
        avatarIsVisible: !self.tables[position].avatarIsVisible,
      },
    }
  },
}))

export default TeamPlayersActions
