import { types } from 'mobx-state-tree'
import TeamProfileApiActions from './TeamProfileApiActions'

export const TeamProfile = types.model('TeamProfile', {
  profile: types.maybeNull(types.frozen({})),
  isLoaded: types.boolean,
  error: types.maybeNull(types.frozen({})),
})

const TeamProfileStore = types.compose(
  TeamProfile,
  TeamProfileApiActions,
)

export default TeamProfileStore
