import { inject, observer } from 'mobx-react'

import { compose, withHandlers, withProps } from 'recompose'
import get from 'lodash/get'
import find from 'lodash/find'
import size from 'lodash/size'
import filter from 'lodash/filter'

import { lastElemsInGear } from 'config/teamPopupParams'
import { amplua } from 'config/amplua'

import {
  TComposeInner,
  TPropsOuter,
  TWithPropsInner,
  TWithPropsOuter,
  TWithHandlersOuter1,
  TWithHandlersInner1,
  TWithHandlersOuter2,
  TWithHandlersInner2,
  TParam,
} from './types'

export const gearTableComposed = compose<TComposeInner, TPropsOuter>(
  inject('gearPopupStore'),
  withProps<TWithPropsInner, TWithPropsOuter>(({ ampluaId }) => {
    // Id последних параметров в подгруппе
    const lastElemsIds = ampluaId === amplua.team
      ? lastElemsInGear.team
      : lastElemsInGear.rest

    return {
      lastElemsInGear: lastElemsIds,
    }
  }),
  withHandlers<TWithHandlersOuter1, TWithHandlersInner1>({
    getCurrentGroupInfo: ({ currentStore: { gearParams } }) => num =>
      get(gearParams, num, {}),

    getGroupTempSelected: ({ gearPopupStore: { tempSelected } }) => (
      groupParams: Array<{}>,
    ) =>
      filter(
        tempSelected,
        ({ gear_group_id }) =>
          gear_group_id === get(groupParams, '[0].gear_group_id'),
      ),
  }),
  withHandlers<TWithHandlersOuter2, TWithHandlersInner2>({
    /**
     * Проверка активного класса для выбранного чекбокса заголовка групп параметров
     * @param {array} tempSelected - массив выбранных параметров (не сохраненные в сторе)
     */
    isActive: ({
      getCurrentGroupInfo,
      getGroupTempSelected,
    }) => (num) => {
      const groupParams = get(getCurrentGroupInfo(num), 'params')

      return size(getGroupTempSelected(groupParams)) === size(groupParams)
    },
    massCheck: ({
      gearPopupStore: {
        addToTempSelected,
        removeFromTempSelected,
        tempSelected,
      },
      getCurrentGroupInfo,
      getGroupTempSelected,
    }) => num => () => {
      const groupParams: Array<TParam> = get(getCurrentGroupInfo(num), 'params')
      // Если выбраны все чекбоксы в группе, то снимаем с каждого галочку
      if (size(getGroupTempSelected(groupParams)) === size(groupParams)) {
        groupParams.forEach((param) => {
          removeFromTempSelected(param)
        })
        // Если нет, то выбираем все чекбоксы в группе
      } else {
        groupParams.forEach((param) => {
          const {
            gear_group_id,
            param_id,
            short_lexical_formula,
          } = param

          if (
            find(tempSelected, {
              gear_group_id,
              param_id,
              short_lexical_formula,
            })
          ) {
            // Удаляем из временно выбранных, чтобы не было дублей
            removeFromTempSelected(param)
          }
          addToTempSelected(param)
        })
      }
    },
  }),
  observer,
)
