
import { types } from 'mobx-state-tree'
import MatchProfileApiActions from './MatchProfileApiActions'

export const MatchProfile = types.model('MatchProfile', {
  data: types.frozen({}),
  periods: types.array(types.array(types.frozen({}))),
  isFetching: types.boolean,
  matchId: types.maybeNull(types.string),
  videoLinks: types.frozen({}),
  error: types.maybeNull(types.frozen({})),
})

const MatchProfileStore = types.compose(
  MatchProfile,
  MatchProfileApiActions,
)

export default MatchProfileStore
