import React from 'react'

import {
  compose,
  withHandlers,
  withState,
} from 'recompose'
import cx from 'classnames'
import map from 'lodash/map'

import { T9n, OutsideClick } from 'react-sport'

import { profilePaths } from 'config/common'
import { beta } from 'config/routes'

import {
  TComposedInner,
  TPropsOuter,
  TWithHandlersOuter,
  TWithHandlersInner,
} from './types'

const ProfilePopupComponent = ({
  isOpened,
  logoutClick,
  toggleOpened,
}: TComposedInner) => (
  <div className='link-iconed-wrapper'>
    <a
      onClick={() => toggleOpened(!isOpened)}
      className={
        cx('link-iconed link-iconed-primary header-profile-link',
          'js-open-dropdown tooltip-trigger')}
    >
      <div className='hover-tooltip'>
        <T9n t='header_my_profile' />
      </div>
      <i className='icon-person' />
      <T9n t='header_my_profile' className='link-iconed-text' />
    </a>
    {
      isOpened && (
        <div className='dropdown-menu-container js-dropdown-profile open'>
          <ul className='list-unstyled'>
            {map(profilePaths, (path, name) => (
              <li>
                <a
                  href={`${beta}/${path}`}
                >
                  <T9n t={name} />
                </a>
              </li>
            ))}
            <li>
              <a className='js-sign-out' onClick={logoutClick}>
                <T9n t='header_quit' />
              </a>
            </li>
          </ul>
        </div>
      )
    }
  </div>
)

const composed = compose<TComposedInner, TPropsOuter >(
  withState(
    'isOpened',
    'toggleOpened',
    false,
  ),
  withState(
    'componentName',
    'changeComponentName',
    'profilePopup',
  ),

  withHandlers<TWithHandlersOuter, TWithHandlersInner>({
    clickOutside: ({ toggleOpened }) => () => toggleOpened(false),

    logoutClick: ({ logout }) => (e) => {
      e.preventDefault()
      logout()
    },
  }),
  OutsideClick,
)

export const ProfilePopup = composed(ProfilePopupComponent)
