import { inject, observer } from 'mobx-react'

import {
  compose,
  withHandlers,
  withProps,
} from 'recompose'

import get from 'lodash/get'
import find from 'lodash/find'

import { utils } from 'react-sport'

import { clickableClassNameHOC } from 'hocs/getClickableTdClassNameHoc'

const PlayerTrComposed = compose(
  inject(
    'videoLink',
    'lexics',
    'shootsRange',
    'matchLinks',
    'teamPlayers',
    'teamProfile',
    'selectedMatches',
    'shootsRange',
  ),
  clickableClassNameHOC,
  //@ts-ignore
  withProps(({ player: { f_player } }) => {
    const {
      helpers: { getLogo },
    } = utils
    const avatarUrl = getLogo({
      type: 'players_volleyball',
      id: f_player,
    })
    return { avatarUrl }
  }),
  //@ts-ignore
  withHandlers({
    handleClick: ({
      lexics: { lang, page },
      selectedMatches,
      shootsRange,
      teamPlayers: { fetchEvents },
      teamProfile: {
        profile: { id },
      },
      videoLink: { request },
    }) => (
      //@ts-ignore
      paramId,
      //@ts-ignore
      lexical_formula,
      //@ts-ignore
      playerId,
    ) => fetchEvents(
      lang,
      lexical_formula,
      playerId,
      paramId,
      request,
      selectedMatches[page][id],
      shootsRange,
    ),
    getParamValue: ({
      player: { params },
      teamPlayers: { tables },
      //@ts-ignore
    }) => ({ param_id }, position) => {
      const playerParam = find(params, { param: param_id })
      const value = get(tables, `${position}.summaryType`) === 'average_for_match'
        ? get(playerParam, 'averageValue')
        : get(playerParam, 'value')

      if (value) return value.toFixed()
      return '—'
    },
  }),
  observer,
)

export default PlayerTrComposed
