import { compose } from 'recompose'

// @ts-ignore
import VideoCuts from 'features/stores/VideoCuts/VideoCuts'
// @ts-ignore
import VideoCutsMixin from 'features/stores/VideoCuts/VideoCutsMixin'

// @ts-ignore
// @ts-ignore
const ComposeVideoCuts = (superclass, markers) =>
  compose(
    VideoCuts(markers),
    VideoCutsMixin,
  )(superclass)

export default ComposeVideoCuts
