type TArgs = {
  matchId: number,
  param: {
    param_id: number,
  },
  playerId: number,
  tableType: string,
}

export const getStatsFilterOptions = ({
  matchId,
  param,
  playerId,
  tableType,
}: TArgs) => {
  const options: {[key: string]: {}} = {
    matchPlayersTable: {
      f_param: param.param_id,
      f_player: playerId,
    },
    playerMatches: {
      f_match: matchId,
      f_param: param.param_id,
      f_player: playerId,
    },
    teamMatches: {
      f_match: matchId,
      f_param: param.param_id,
      f_team: playerId,
    },
    tournamentMatches: {
      f_match: matchId,
      f_param: param.param_id,
      f_team: playerId,
    },
    tournamentMatchesTotal: {
      f_match: matchId,
      f_param: param.param_id,
    },
  }

  return options[tableType]
}
