import { compose, lifecycle } from 'recompose'
import { inject, observer } from 'mobx-react'
// @ts-ignore
import tournamentLexic from 'config/lexics/tournamentPage'

export const tournamentPageComposed = compose(
  inject(
    'lexics',
    'tournamentsProfile',
    'tournamentSeasonSelect',
    'tournamentMatches',
  ),
  lifecycle({
    componentDidMount() {
      const {
        //@ts-ignore
        lexics,
        //@ts-ignore
        match: { params },
        //@ts-ignore
        tournamentsProfile: { fetchTournamentProfile },
      } = this.props
      lexics.appPage = {
        page: 'tournaments',
        phrases: tournamentLexic,
      }
      fetchTournamentProfile(Number(params.tournamentId))
    },
  }),
  observer,
)
