import { utils } from 'react-sport'
// @ts-ignore
import { action } from 'mobx'

import forIn from 'lodash/forIn'
import includes from 'lodash/includes'
import get from 'lodash/get'
import map from 'lodash/map'
import filter from 'lodash/filter'

import routes from 'config/routes'
import { zone_params } from 'config/params'
import { defaultDirections } from 'config/tabsConfig'

type TCommonObjOptions = {
  [key: string]: string,
}

const procedures: TCommonObjOptions = {
  player: 'ask_player_param_view',
  team: 'ask_team_param_view',
}

const fields: TCommonObjOptions = {
  player: '_v_player_id',
  team: '_v_team_id',
}

// @ts-ignore
const ZoneActionsMixin = superclass =>
  class extends superclass {
    // Ставим активную зону и меняем направления маркеров для этой зоны
    //@ts-ignore
    @action setChecked = (name) => {
      //@ts-ignore
      this.checked = name
      const { active } = this.getStore('tabs')
      const { handleDirection } = this.getStore('markers')
      // Случай, если пользователь выбирает показывать зоны
      // Для АТАК, БЛОК, ПЕРЕДАЧА показываем точки откуда, в остальных случаях точки куда
      if (includes(['attack', 'pass'], active)) {
        return forIn({ from: false, to: true }, (value, key) =>
          handleDirection(key, value))
      }
      return forIn({ from: true, to: false }, (value, key) =>
        handleDirection(key, value))
    }

    //@ts-ignore
    @action resetToDefaultDirections = () => {
      const { active } = this.getStore('tabs')
      const { handleDirection } = this.getStore('markers')

      const defaultDirectionsActive = get(defaultDirections, active)
      return forIn(defaultDirectionsActive, (value, key) =>
        handleDirection(key, value))
    }

    //@ts-ignore
    @action setZone = (value) => {
      //@ts-ignore
      this.checked = value
    };

    //@ts-ignore
    @action setTotal = (value) => {
      //@ts-ignore
      this.total = value
    };

    //@ts-ignore
    @action fetch = (pageId, matches) => {
      const url = `${routes.API_ROOT}/data`
      const tab = this.getStore('tabs').active
      const page: string = get(this.getStore('lexics'), 'page')
      const params = map(zone_params, [tab, 'param_id'])
      const config = {
        body: JSON.stringify({
          proc: procedures[page],
          params: {
            _v_match_id: matches,
            [fields[page]]: [pageId],
            _v_param_id: [params],
          },
        }),
        method: 'POST',
      }
      utils.callApi({
        config,
        onError: error => this._onError(error),
        onRequest: () => {
          //@ts-ignore
          this.stats = []
          //@ts-ignore
          this.loaded = null
        },
        onSuccess: json => this._onSuccess(json),
        url,
      })
    };

    //@ts-ignore
    _onSuccess = ({ data }) => {
      //@ts-ignore
      this.loaded = true
      // Нужна статистика только по целому матчу f_option_0 = 0
      //@ts-ignore
      this.stats = filter(data, ({ f_option_0 }) => f_option_0 === 0) || null

      //@ts-ignore
      this.error = null
    };

    //@ts-ignore
    _onError = (error) => {
      //@ts-ignore
      this.loaded = true
      //@ts-ignore
      this.error = error
    };
  }

export default ZoneActionsMixin
