
import React from 'react'
import { inject, observer } from 'mobx-react'
import { compose, withProps } from 'recompose'
import get from 'lodash/get'

// @ts-ignore
import { getSuffix } from 'module/utils/helpers'
import VideoLinks from './VideoLinks/VideoLinks'
import MatchInfo from './MatchInfo/MatchInfo'

type TProps = {
  headerVideoButtons: Array<{
    link: string,
    id: string,
    t: string,
  }>,
  suffix: string,
  lexics: { lang: string },
  matchProfile: {
    periods: Array<{}>,
    //@ts-ignore
    data: {},
    //@ts-ignore
    //@ts-ignore
    data: {
      match_date: string,
      score_team1: number,
      score_team2: number,
      team1_id: number,
      team2_id: number,
    },
  },
};

export const HeaderComponent = ({
  headerVideoButtons,
  matchProfile,
  suffix,
}: TProps) => (
  <div className='match-header'>
    <div className='match-header__container'>
      <MatchInfo matchProfile={matchProfile} suffix={suffix} />
      <div className='match-header__info'>
        <div className='match-header__play'>
          <VideoLinks headerVideoButtons={headerVideoButtons} />
        </div>
      </div>
    </div>
  </div>
)

const HeaderComposed = compose(
  inject('lexics', 'matchProfile'),
  observer,
  //@ts-ignore
  withProps(({ lexics: { lang }, matchProfile: { videoLinks } }) => ({
    suffix: getSuffix(lang),
    headerVideoButtons: [
      {
        id: 'full_game',
        link: get(videoLinks, 'full_game.link', ''),
        t: 'match_popup_full_game',
      },
      {
        id: 'ball_in_play',
        link: get(videoLinks, 'ball_in_play.link', ''),
        t: 'match_popup_ball_in_play',
      },
    ],
  })),
)

// @ts-ignore
export const Header = HeaderComposed(HeaderComponent)
