import { types } from 'mobx-state-tree'

type TSetActiveParam = {
  param: {
    param_id: number,
  },
  sortAsc: boolean,
  table: {
    position: number,
  },
}

export const setActiveParam = types.model({}).actions(self => ({
  setActiveParam({
    param,
    sortAsc,
    table,
  }: TSetActiveParam) {
    const { position } = table
    //@ts-ignore
    self.tables = {
      //@ts-ignore
      ...self.tables,
      [position]: {
        ...table,
        sortAsc,
        activeId: param.param_id,
      },
    }
  },
}))
