
import React from 'react'
import { inject, observer } from 'mobx-react'

import {
  compose,
  withHandlers,
  withState,
  withProps,
} from 'recompose'
import cx from 'classnames'
import isEmpty from 'lodash/isEmpty'

import { T9n } from 'react-sport'

import ToolTip from './ToolTip'

import {
  TProps,
  TPropsOuter,
  TWithHandlersInner,
  TWithHandlersOuter,
  TWithPropsInner,
  TWithPropsOuter,
} from './types'
import './styles.scss'

export const HeaderStatsItemComponent = ({
  events,
  getExtLink,
  itemClassName,
  lexicId,
  statsIsFetching,
  statValue,
  toggleTooltip,
  toolTipIsVisible,
  valueBlockClassName,
}: TProps) => (
  <div className='header-stats-item-wrapper'>
    <div
      onClick={getExtLink(events, lexicId)}
      onMouseEnter={toggleTooltip}
      onMouseLeave={toggleTooltip}
      className={itemClassName}
    >
      <div className='header-stats-item__label'>
        <T9n t={lexicId} />
      </div>
      <div className={valueBlockClassName}>
        <span>{statValue}</span>
      </div>
      {statsIsFetching && <div className='header-stats-item__loader' />}
      {toolTipIsVisible && !statsIsFetching && <ToolTip />}
    </div>
  </div>
)

const headerStatsItemComposed = compose<TProps, TPropsOuter>(
  inject('lexics', 'videoLink', 'shootsRange'),
  withState('toolTipIsVisible', 'toggleVisible', false),
  withState('statsIsFetching', 'setLoader', false),
  observer,
  withProps<TWithPropsInner, TWithPropsOuter>(({
    percent,
    statsIsFetching,
    toolTipIsVisible,
  }) => ({
    itemClassName: cx('header-stats-item', {
      'header-stats-item_clickable': toolTipIsVisible && !statsIsFetching,
      'header-stats-item_loading': statsIsFetching,
    }),
    valueBlockClassName: cx('header-stats-item__value', {
      'header-stats-item__value_percentage': percent,
    }),
  })),
  withHandlers<TWithHandlersOuter, TWithHandlersInner>({
    getExtLink: ({
      lexics: { lang },
      setLoader,
      shootsRange,
      videoLink: { request },
    }) => (events, lexicId) => () => {
      if (!isEmpty(events)) {
        setLoader(true)
        request({
          events,
          lang,
          lexical_formula: lexicId,
          range: shootsRange,
          type: 'headerStats',
        }).then(() => setLoader(false))
      }
    },
    toggleTooltip: ({
      events,
      toggleVisible,
      toolTipIsVisible,
    }) => () => {
      if (events.length) toggleVisible(!toolTipIsVisible)
    },
  }),
)

export const HeaderStatsItem = headerStatsItemComposed(HeaderStatsItemComponent)
