
import React, { Fragment } from 'react'
import { inject, observer } from 'mobx-react'
import { compose, lifecycle } from 'recompose'
import { PlayersTable } from './PlayersTable'

type TProps = {
  teamPlayers: {
    tables: {},
  },
};

const TeamPlayers = ({ teamPlayers: { tables } }: TProps) => (
  <Fragment>
    {Object.values(tables)
      .sort((a: any, b: any) => a.position - b.position)
      .map((table: any) => (
        // @ts-ignore
        <PlayersTable key={table.position} table={table} />
      ))}
  </Fragment>
)

const TeamPlayersComposed = compose<any, TProps>(
  inject('teamPlayers', 'teamProfile', 'selectedMatches'),
  lifecycle({
    componentDidMount() {
      const {
        // @ts-ignore
        selectedMatches,
        // @ts-ignore
        teamPlayers: { fetchParams },
        // @ts-ignore
        teamProfile: {
          profile: { id },
        },
      } = this.props
      fetchParams(id, selectedMatches)
    },
  }),
  observer,
)(TeamPlayers)

export default TeamPlayersComposed
