import React from 'react'
import { inject, observer } from 'mobx-react'

import {
  compose,
  withProps,
  withState,
  withHandlers,
} from 'recompose'
import includes from 'lodash/includes'
import filter from 'lodash/filter'
import cx from 'classnames'

import { T9n } from 'react-sport'

import { getCheckedFiltersSubMenu } from 'module/utils/helpers'
import { FilterMenu } from './FilterMenu'
import { FilterSubMenu } from './FilterSubMenu'

import {
  TProps,
  TWithHandlersOuter,
  TWithPropsOuter,
  TComposeInner,
  TWithHandlersInner,
  TWithPropsInner,
} from './types'

const FilterItemComponent = ({
  checkedInGroup,
  clickOutside,
  data,
  filters: {
    handleUncheck,
  },
  getMenuClassName,
  menuOpened,
  style,
  title,
  toggleOpenClose,
  type,
}: TProps) => (
  <div className='map-block__filters-row'>
    <span className={getMenuClassName()} onClick={toggleOpenClose}>
      <T9n t={title} />
    </span>
    {menuOpened
        && (
          //@ts-ignore
          <FilterMenu
            type={type}
            data={data}
            style={style}
            clickOutside={clickOutside}
            toggleOpenClose={toggleOpenClose}
          />
        )
    }
    <FilterSubMenu
      checkedInGroup={checkedInGroup}
      handleUncheck={handleUncheck}
    />
  </div>
)

const filterItemComposed = compose<TComposeInner, TProps>(
  inject(
    'filters',
    'mapShoot',
    'playerInfo',
  ),
  withState('menuOpened', 'menuToggle', false),
  withHandlers<TWithHandlersOuter, TWithHandlersInner>({
    clickOutside: ({
      filters: { toggleOpenMenu },
      menuOpened,
      menuToggle,
    }) => () => {
      menuToggle(!menuOpened)
      toggleOpenMenu('reset')
    },
    getMenuClassName: ({ menuOpened }) => () => cx(
      'map-block__title',
      { 'map-block__title_active': menuOpened },
    ),
    toggleOpenClose: ({
      filters: { toggleOpenMenu },
      menuOpened,
      menuToggle,
    }) => () => {
      menuToggle(!menuOpened)
      toggleOpenMenu()
    },
  }),
  observer,
  withProps<TWithPropsInner, TWithPropsOuter>(({
    data: idsFromConfig,
    filters: { checked },
    mapShoot,
    playerInfo,
    type,
  }) => {
    const getIds = () => {
      switch (type) {
        case 'popupWithPlayers':
          return mapShoot.playersIds
        case 'popupWithHomePlayers':
          return playerInfo.playersIds
        default:
          return idsFromConfig
      }
    }
    const ids = getIds()
    const filtered = getCheckedFiltersSubMenu(ids, type)
    const keyNormalized = (key: string) => (
      includes(['popupWithPlayers', 'popupWithHomePlayers'], type)
        ? key
        : Number(key)
    )
    const checkedInGroup = filter(checked, (value, key) => (
      includes(filtered, keyNormalized(key))))

    return { checkedInGroup }
  }),
  observer,
)

export const FilterItem = filterItemComposed(FilterItemComponent)
