/**
 * Получение фильтров для маркеров
 * @param {array} filters - массив id фильтров, необходимых для отображения в блоке
 * @param {object || array || number} elem - возвращенное значение от сервера.
 * @param {string} name - если elem объект, то для ключа используем name.
 * Это будет слово для заголовка фильтра в компоненте.
 */
export function getFilters(
  elem: any,
  filters: Array<any>,
  name: string = 'items',
) {
  const rootObj = {}
  if (isPlainObject(elem)) {
    objFilters(rootObj, elem, filters, name)
  } else if (isArray(elem)) {
    //@ts-ignore
    rootObj[name] = arrFilters(elem, filters)
  } else {
    //@ts-ignore
    rootObj[name] = filters.find(({ id }) => id === elem) || elem
  }
  return rootObj
}

/**
 * Обработка значения elem. В случае если от серера пришел объект.
 * @param {object} rootObj - пустой объект по умолчанию, заполняем его парами ключ-значение rootObj[name] = ...
 * @param {object || array || number} elem - возвращенное значение от сервера.
 * @param {array} filters - массив id фильтров, необходимых для отображения в блоке
 * @param {string} name - имя для ключа для нового объекта rootObj
 */
const objFilters = (
  rootObj: {},
  //@ts-ignore
  elem: { id: number | string | null, items?: Array<any> },
  filters: Array<{ id: number }>,
  name: string,
) => {
  if ('id' in elem) {
    //@ts-ignore
    rootObj[name] = {
      info: filters.find(({ id }) => id === elem.id) || elem.id,
    }
    if ('items' in elem) {
      //@ts-ignore
      rootObj[name] = {
        //@ts-ignore
        ...rootObj[name],
        ...getFilters(elem.items, filters),
      }
    }
  } else {
    Object.entries(elem).forEach(([groupName, groupElements]) => {
      //@ts-ignore
      rootObj[name] = {
        //@ts-ignore
        ...rootObj[name],
        ...getFilters(groupElements, filters, groupName),
      }
    })
  }
  return rootObj
}
/**
 * Обработка значения elem. В случае если от серера пришел массив.
 * @param {array} elem - вовращенное значение от сервера.
 * @param {array} filters - массив id фильтров, необходимых для отображения в блоке.
 */
const arrFilters = (elem: any, filters: Array<{ id: number }>) => {
  //@ts-ignore
  let arr = []
  //@ts-ignore
  elem.forEach((item) => {
    if (isPlainObject(item)) {
      let list = {}
      Object.entries(item).forEach(([groupName, groupElements]) => {
        list = {
          ...list,
          ...getFilters(groupElements, filters, groupName),
        }
      })
      //@ts-ignore
      arr = [...arr, list]
    } else {
      //@ts-ignore
      arr = [...arr, filters.find(({ id }) => id === item) || item]
    }
  })
  //@ts-ignore
  return arr
}

/**
 * Показ даты с годом или без
 * @param {string} date - дата, которая приходит от сервера.
 */
export function dateWithYear(date: string) {
  if (m(date).year() !== m().year()) return m(date).format('DD/MM/YY')
  return m(date).format('DD/MM')
}

