/**
 * Перевод мер измерения в случае англоязычной версии
 * @param {number} cm - число, которе будем менять
 */
export function cmToFt(cm: number) {
  const resArr = (cm / 30.48).toFixed(2).toString().split('.')
  return `${resArr[0]}' ${resArr[1]}'' `
}

