/**
  * Функция, возвращающая количество выбранных чекбоксов в группе
  * @param {Array} checkboxes - список всех чекбоксов для выпадающего меню
  * @param {Object} checked - выбранные чекбоксы
  * @param {String} groupName - название группы
  */
export const getCheckedFiltersNumber = ({
  checkboxes,
  checked,
  groupName,
}: {
  checkboxes: Array<any> | {},
  checked: {},
  groupName?: string,
}) => {
  const filtersCount = size(
    filter(checkboxes, ({
      f_player,
      id,
      player_id,
    }) => {
      const key = id
        || f_player
        || player_id
      const checkedArray = keys(checked)
      const checkedFilters = groupName
        ? map(checkedArray, (id) => {
          const newId = id.match(/(\d+|\D+)/g)
          return newId
        })
        : checkedArray

      return groupName
        //@ts-ignore
        ? find(checkedFilters, ([id, group]) => (
          String(key) === id && groupName === group
        ))
        //@ts-ignore
        : includes(checkedFilters, String(key))
    }),
  )
  return filtersCount || null
}

/**
  * Оборачиваем любое число в круглые скобки
  * @param {number || null} number - число
  */
export const addParenthesesToNumber = (number: number | null) => (
  number ? `(${number})` : null
)

/**
  * Получаем число выбранных фильтров в группе и оборачиваем в круглые скобки
  * @param {Array} checkboxes - список всех чекбоксов для выпадающего меню
  * @param {Object} checked - выбранные чекбоксы
  * @param {String} groupName - название группы
  */
export const checkedFiltersCount = ({
  checkboxes,
  checked,
  groupName,
}: {
  checkboxes: Array<any> | {},
  checked: {},
  groupName?: string,
}) => {
  const number = getCheckedFiltersNumber({
    checkboxes,
    checked,
    groupName,
  })
  return addParenthesesToNumber(number)
}

/** Возвращаем список айди выбранных фильтров.
 * В зависимости от типа применяем нужную функцию преобразования
*/
export const getCheckedFiltersSubMenu = (data: any, type: string) => {
  const getFiltersWithHeader = () => flatten(
    map(data, ({ ids }) => ids),
  )
  const getFiltersWithZones = () => map(data, 'zoneId')
  const getData = () => data

  const types = {
    popupWithColumns: getFiltersWithHeader,
    popupWithZones: getFiltersWithZones,
    popupSimple: getData,
    popupWithPlayers: getData,
    popupWithHomePlayers: getData,
  }
  //@ts-ignore
  return types[type]()
}

