import React from 'react'
import { observer, inject } from 'mobx-react'
import PropTypes from 'prop-types'
import cx from 'classnames'
import { T9n } from 'react-sport'
import toString from 'lodash/toString'
import { compose, withProps, withHandlers, withState } from 'recompose'

const FilterCheckbox = ({
  //@ts-ignore
  clickHandler,
  //@ts-ignore
  filter: {
    //@ts-ignore
    checked,
    //@ts-ignore
    lexical_formula,
  },
  //@ts-ignore
  getClassName,
}) => (
  <li onClick={clickHandler} className={getClassName(checked)}>
    <T9n t={toString(lexical_formula)} />
  </li>
)

const FilterCheckboxComposed = compose(
  inject('playersVS', 'teamsVS'),
  withState('isOpened', 'onOutsideClick', false),
  //@ts-ignore
  withProps(({ pageType, ...props }) => ({
    videoSummaries: props[`${pageType}VS`],
  })),
  //@ts-ignore
  withHandlers({
    //@ts-ignore
    getClassName: () => checked => cx({ active: checked }),
    clickHandler: ({ filter, setActive }) => () => setActive(filter),
  }),
  observer,
  //@ts-ignore
)(FilterCheckbox)

FilterCheckbox.propTypes = {
  filter: PropTypes.shape({
    checked: PropTypes.bool,
    lexical_formula: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  }),
  getClassName: PropTypes.func.isRequired,
  clickHandler: PropTypes.func.isRequired,
}

export default FilterCheckboxComposed
