import React from 'react'
import { observer, inject } from 'mobx-react'

import { lifecycle, compose } from 'recompose'
import get from 'lodash/get'

import { Header } from 'features/components/common/player/Header'
import { MapField } from 'features/MapField/components/MapField'
import { MapFilters } from 'features/MapFilters/components'
import { Widgets } from 'features/components/common/widgets/Widgets'

import './PlayerField.scss'

type TProps = {
  selectedMatches: {},
  lexics: { page: string },
  tabs: { active: string },
};

const PlayerFieldComponent = ({
  lexics: { page },
  tabs: { active },
  ...props
}: TProps) => (
  <div className='main-page__left'>
    <div className='main-page__content'>
      {/*
      //@ts-ignore */}
      <Header {...props} />
      <div className='main-page__side'>
        <div className='main-page__filters-wrapper'>
          <MapFilters />
        </div>
        {/*
        //@ts-ignore */}
        <MapField tab={active} {...props} />
      </div>
    </div>
    <Widgets page={page} />
  </div>
)

const playerFieldComposed = compose(
  inject('lexics', 'markers', 'selectedMatches', 'tabs', 'tooltips', 'zones'),
  lifecycle({
    componentDidMount() {
      const {
        //@ts-ignore
        lexics: { page },
        //@ts-ignore
        markers,
        //@ts-ignore
        matchId,
        //@ts-ignore
        pageId,
        //@ts-ignore
        selectedMatches,
        //@ts-ignore
        tooltips,
        //@ts-ignore
        zones,
      } = this.props
      const matchesList = matchId
        ? [matchId]
        : get(selectedMatches, [page, pageId])
      markers.fetch(pageId, matchesList)
      zones.fetch(pageId, matchesList)
      tooltips.fetch()
    },
  }),
  observer,
)

// @ts-ignore
export const PlayerField = playerFieldComposed(PlayerFieldComponent)
