
import { inject, observer } from 'mobx-react'

import { compose, withProps, lifecycle } from 'recompose'

import get from 'lodash/get'

export const mainContentComposed = compose(
  inject('lexics', 'matchProfile', 'matches', 'matchTable'),
  observer,
  //@ts-ignore
  withProps(({ matches: { active }, matchProfile }) => {
    const pageId = get(matchProfile, `data.${active}_id`)
    return {
      pageId,
    }
  }),
  lifecycle({
    componentDidMount() {
      const {
        //@ts-ignore
        matchProfile: { matchId },
        //@ts-ignore
        matchTable: { fetchMatchesInfo },
      } = this.props
      fetchMatchesInfo(+matchId)
    },
  }),
  observer,
)
