import React from 'react'
import { inject, observer } from 'mobx-react'

import forIn from 'lodash/forIn'
import filter from 'lodash/filter'
import includes from 'lodash/includes'
import {
  withHandlers,
  withProps,
  compose,
} from 'recompose'

import { T9n } from 'react-sport'

import directionsDropDownButtons from '../config/directionsDropdown'

import { ControlDropDown } from '../ControlsDropDown'

import {
  TProps,
  TWithPropsInner,
  TWithPropsOuter,
  TWithHandlersOuter,
  TWithHandlersInner,
} from './types'

export const DirectionsComponent = ({
  chooseDirection,
  directionsBtns,
}: TProps) => (
  <ControlDropDown menuType='action'>
    {directionsBtns.map(({ endPoint, name }) => (
      <div
        className='custom-select__option'
        onClick={chooseDirection(endPoint)}
        key={name}
      >
        <T9n t={name} />
      </div>
    ))}
  </ControlDropDown>
)

const directionsComposed = compose<TProps, {}>(
  inject(
    'tabs',
    'markers',
    'zones',
  ),
  observer,
  withProps<TWithPropsInner, TWithPropsOuter>(({ tabs: { active } }) => ({
    directionsBtns: filter(directionsDropDownButtons, ({ tabs }) =>
      includes(tabs, active)),
  })),
  withHandlers<TWithHandlersOuter, TWithHandlersInner>({
    chooseDirection: ({
      markers: { handleDirection, selectToggle },
      zones: { resetToDefaultDirections },
    }) => (endPoint: {}) => () => {
      // При смене режима отображения точек, сбрасываем показ зон и выделение
      resetToDefaultDirections()
      forIn(endPoint, (value, key) => handleDirection(key, value))
      selectToggle(false)
    },
  }),
  observer,
)

export const Directions = directionsComposed(DirectionsComponent)
