import React, { Fragment } from 'react'

import { TableUI } from 'react-sport'

import { TdLoader } from 'features/TdLoader'

import { tdComposed } from './composed'

type Props = {
  handleClick: () => void,
  linkIsFetching: boolean,
  value: string,
}
export const TdComponent = ({
  handleClick,
  linkIsFetching,
  value,
}: Props) => (
  <Fragment>
    {linkIsFetching && <TdLoader />}
    <TableUI.Td
      onClick={handleClick}
      value={value}
      className=''
    />
  </Fragment>
)

export const Td = tdComposed(TdComponent)
