
import { types } from 'mobx-state-tree'

import { callApi } from 'react-sport'

import get from 'lodash/get'

// @ts-ignore
import routes from 'config/routes'

type TConfig = {
  method: string,
  body: string,
};

const url: string = `${routes.API_ROOT}/data`

export const TournamentPagePlayersApiActions = types
  .model({})
  .actions(self => ({
    fetchTableParams(tournamentId: number, seasonId: number) {
      const config: TConfig = {
        method: 'POST',
        body: JSON.stringify({
          proc: 'scout_tournament_players_stat',
          params: {
            _p_tournament_id: tournamentId,
            _p_season_id: seasonId,
          },
        }),
      }

      // Делаем также запрос для параметров шестеренки
      //@ts-ignore
      self.getGearParamsList(0)

      return callApi({
        url,
        config,
        onRequest: () => {
          //@ts-ignore
          self.error = null
          //@ts-ignore
          self.isLoaded = false
        },
        //@ts-ignore
        onSuccess: self._onSuccesPlayers,
        //@ts-ignore
        onError: self._onError,
      })
    },

    //@ts-ignore
    _onSuccesPlayers({ data }: {}) {
      //@ts-ignore
      self.players = get(data, '[0].scout_tournament_players_stat') || []

      if (window.localStorage.getItem('tournamentPlayersTableStore') === null) {
        //@ts-ignore
        self.saveTables()
      } else {
        //@ts-ignore
        //@ts-ignore
        self.tables = JSON.parse(
          //@ts-ignore
          window.localStorage.getItem('tournamentPlayersTableStore'),
        )
      }

      //@ts-ignore
      self.isLoaded = true
    },

    _onError(error: any) {
      //@ts-ignore
      self.error = error
    },
  }))
