import { observable, computed } from 'mobx'
import map from 'lodash/map'
// @ts-ignore
import BasicStore from 'features/stores/BasicStore'
import FetchPlayerInfo from './FetchPlayerInfo'

class PlayerInfoStore extends FetchPlayerInfo(BasicStore) {
  @observable error = null;

  @observable loaded = false;

  @observable data = [];

  @observable basicLoaded = false;

  @observable basicData = [];

  @computed get playersIds() {
    //@ts-ignore
    return map(this.basicData, ({ f_player }) => `${f_player}_player`)
  }
}

export default PlayerInfoStore
