import { types } from 'mobx-state-tree'

export const resetTable = types.model({}).actions(self => ({
  /** Сброс всех таблиц по-умолчанию
   * @param {string} localStorageName - ключ для записи в localStorage
   */
  resetTable(localStorageName: string) {
    // Сначала удаляем таблицы уже сформированные
    window.localStorage.removeItem(localStorageName)
    //@ts-ignore
    self.tables = {}
    //@ts-ignore
    self.saveTables(localStorageName)
  },
}))
