import React from 'react'
import { inject, observer } from 'mobx-react'

import {
  withHandlers,
  withProps,
  compose,
} from 'recompose'

import { T9n } from 'react-sport'

import { ControlDropDown } from '../ControlsDropDown'

import {
  TProps,
  TWithHandlersOuter,
  TComposeInner,
  TWithHandlersInner,
} from './types'

export const ZonesComponent = ({ checkZone, zoneTitles }: TProps) => (
  <ControlDropDown menuType='zone'>
    {zoneTitles.map(zone => (
      <div
        className='custom-select__option'
        onClick={checkZone(zone)}
        key={zone}
      >
        <T9n t={zone} />
      </div>
    ))}
  </ControlDropDown>
)

const zonesComposed = compose<TComposeInner, {}>(
  inject(
    'tabs',
    'markers',
    'zones',
  ),
  observer,
  withProps(() => {
    // зоны поля
    const zoneTitles = [
      'without_zone',
      'basic_zones',
      'advanced_zones',
    ]
    return { zoneTitles }
  }),
  withHandlers<TWithHandlersOuter, TWithHandlersInner>({
    checkZone: ({
      markers: { selectToggle },
      zones: { resetToDefaultDirections, setChecked },
    }) => (checkedZone: string) => () => {
      if (checkedZone === 'without_zone') {
        resetToDefaultDirections()
      }
      setChecked(checkedZone)
      selectToggle(false)
    },
  }),
  observer,
)

export const Zones = zonesComposed(ZonesComponent)
