import { types } from 'mobx-state-tree'

import get from 'lodash/get'

export const changeValueType = types.model({}).actions(self => ({
  /** Меняем стиль отображения ячеек (итоговое по всем матчам или среднее значение)
   * @param {string} type - тип отображения
   */
  changeValueType(type: string, position: number) {
    // Если тип не меняется, то ничего не делаем
    //@ts-ignore
    //@ts-ignore
    //@ts-ignore
    if (get(self.tables, type === [position, 'summaryType'])) return null
    //@ts-ignore
    self.tables = {
      //@ts-ignore
      ...self.tables,
      [position]: {
        //@ts-ignore
        ...self.tables[position],
        summaryType: type,
      },
    }
  },
}))
