//@ts-ignore
import { action } from 'mobx'
import map from 'lodash/map'
import uniqBy from 'lodash/uniqBy'

import { utils as remoteUtils } from 'react-sport'

import { getPlayers } from 'module/utils/helpers'

import routes from 'config/routes'

export const getToolTipOpponentTeam = (
  markers: Array<{
    f_team_opponent: number,
    opponentTeam: string,
  }>,
  teams: Array<{
    f_player: number,
    prop_player: Array<string>,
  }>,
) => {
  const markersWithOpponentTeam: Array<{}> = []
  markers.forEach((marker) => {
    const { f_team_opponent } = marker
    const team = teams.find(({ f_player }) => f_player === f_team_opponent)
    if (team) {
      const name = team.prop_player[0]
      marker.opponentTeam = name
      markersWithOpponentTeam.push(marker)
    } else {
      markersWithOpponentTeam.push(marker)
    }
  })

  return markersWithOpponentTeam
}

// @ts-ignore
const FetchMapShoot = superclass =>
  class extends superclass {
    //@ts-ignore
    @action basicFetch = () => {
      const promise = new Promise((resolve, reject) => {
        const url = `${routes.API_ROOT}/data`
        //@ts-ignore
        const teams = []
        //@ts-ignore
        this.getStore('markers').data.forEach(({ f_team_opponent }) => {
          //@ts-ignore
          if (teams.includes(f_team_opponent)) return null
          return teams.push(f_team_opponent)
        })
        const config = {
          method: 'POST',
          body: JSON.stringify({
            proc: 'scout_teams_info',
            params: {
              //@ts-ignore
              _p_team_id: teams,
            },
          }),
        }

        remoteUtils.callApi({
          url,
          config,
          onRequest: this._basicRequest.bind(this),
          onError: error => this._basicError(error, reject),
          onSuccess: json => this._basicSuccess(json, resolve),
        })
      })

      return promise
    };

    //@ts-ignore
    //@ts-ignore
    //@ts-ignore
    //@ts-ignore
    @action fetch = (markers, propsList, matches) => {
      const url = `${routes.API_ROOT}/data`
      const players = getPlayers(markers, propsList)
      const config = {
        method: 'POST',
        body: JSON.stringify({
          proc: 'scout_teams_players_info',
          params: {
            _p_match_id: matches,
            _p_player_id: players,
          },
        }),
      }

      remoteUtils.callApi({
        url,
        config,
        onRequest: this._onRequest.bind(this),
        onSuccess: this._onSuccess.bind(this),
        onError: this._onError.bind(this),
      })
    };

    _basicRequest() {
      //@ts-ignore
      this.basicLoaded = false
    }

    //@ts-ignore
    //@ts-ignore
    _basicSuccess({ data }, resolve) {
      resolve(data)
      //@ts-ignore
      this.basicData = data
      //@ts-ignore
      this.basicLoaded = true
    }

    //@ts-ignore
    //@ts-ignore
    _basicError(error, reject) {
      reject(error)
      //@ts-ignore
      this.basicLoaded = false
    }

    //@ts-ignore
    _onSuccess({ data }) {
      //@ts-ignore
      //@ts-ignore
      this.data = map(data, ({ prop_player, ...item }) => ({
        ...item,
        //@ts-ignore
        //@ts-ignore
        prop_player: uniqBy(
          //@ts-ignore
          map(prop_player, player => ({
            ...player,
            // добавляем поля для полученных игроков
            groupName: 'who_served',
            fetchProps: 'mapShoot',
          })),
          'player_id',
        ),
      }))
      //@ts-ignore
      this.loaded = true
      //@ts-ignore
      this.error = false
    }

    _onRequest() {
      //@ts-ignore
      this.loaded = false
      //@ts-ignore
      this.error = false
    }

    //@ts-ignore
    _onError(error) {
      //@ts-ignore
      this.error = error
      //@ts-ignore
      this.loaded = false
    }
  }

export default FetchMapShoot
