import { types } from 'mobx-state-tree'

import filter from 'lodash/filter'
import includes from 'lodash/includes'
import map from 'lodash/map'

export const TeamsCompareActions = types
  .model({})
  .volatile(() => ({
    teamsInfoLoaded: false,
  }))
  .actions(self => ({
    // Сохранение временно выбранных параметров (при закрытии шестеренки)
    saveTempParams(
      lang: string,
      matchId: string,
      userConfirmedParams: Array<{}>,
    ) {
      //@ts-ignore
      const paramsIdList = map(userConfirmedParams, 'param_id', [])

      //@ts-ignore
      self.selectedParams = userConfirmedParams
      //@ts-ignore
      self.fetchStats(lang, matchId, paramsIdList)
    },

    //Сохраняем все параметры в шестеренке и предвыбираем дефолтные (при первом открытии)
    setDefaultSelectedParams() {
      // По умолчанию предвыбираем дефолтные параметры
      //@ts-ignore
      const defaultIds = map(self.stats, 'param')
      //@ts-ignore
      self.selectedParams = filter(
        //@ts-ignore
        self.totalParams,
        ({ param_id }) => includes(defaultIds, param_id),
      )
    },
  }))
