
import React, { Fragment } from 'react'

import { compose, withHandlers } from 'recompose'

import { TableHeaderSettingsButton, T9n } from 'react-sport'

// @ts-ignore
import { tableHeaderButtons } from 'module/utils/helpers'

type TProps = {
  handleNewPopupAction: () => void,
  handlePopupAction: () => void,
  deleteTable: () => void,
  moveTableUp: () => void,
  moveTableDown: () => void,
  resetTable: () => void,
  currentStore: {
    tables: {},
  },
  table: {},
}

const TableActionsComponent = ({
  currentStore,
  deleteTable,
  handleNewPopupAction,
  handlePopupAction,
  moveTableDown,
  moveTableUp,
  resetTable,
}: TProps) => (
  <Fragment>
    {tableHeaderButtons(
      handleNewPopupAction,
      deleteTable,
      moveTableUp,
      moveTableDown,
      resetTable,
      currentStore.tables,
    ).map(({ action, buttonType, t }) => (
      <TableHeaderSettingsButton
        onClick={action}
        buttonType={buttonType}
        key={t}
      >
        <T9n t={t} />
      </TableHeaderSettingsButton>
    ))}
    <a className='stats-table__action-link' onClick={handlePopupAction}>
      <i className='icon-settings' />
    </a>
  </Fragment>
)

const TableActionsComposed = compose(
  //@ts-ignore
  withHandlers({
    // Функция удаления таблицы
    deleteTable: ({
      currentStore: { deleteTable, storeName },
      table: { position },
    }) => () => deleteTable(position, storeName),

    // Поднятие таблицы в списке
    moveTableUp: ({
      currentStore: { moveTable, storeName },
      table: { position },
    }) => () => moveTable(position, 'up', storeName),

    // Спуск таблицы вниз по списку
    moveTableDown: ({
      currentStore: { moveTable, storeName },
      table: { position },
    }) => () => moveTable(position, 'down', storeName),

    // Функция возврата таблице значений по-умолчанию
    resetTable: ({ currentStore: { resetTable, storeName } }) => () =>
      resetTable(storeName),
  }),
)

// @ts-ignore
export const TableActions = TableActionsComposed(TableActionsComponent)
