import { types } from 'mobx-state-tree'
import includes from 'lodash/includes'
import get from 'lodash/get'
import map from 'lodash/map'
import sortBy from 'lodash/sortBy'
import filter from 'lodash/filter'
import reduce from 'lodash/reduce'

import { toJS } from 'mobx'
import { callApi } from 'react-sport'
import { percentageParams } from 'config/teamPopupParams'
import routes from 'config/routes'

type TConfig = {
  method: string,
  body: string,
}

const TeamPlayersApiActions = types.model({}).actions(self => ({
  // Получение всех параметров для игроков в команде
  //@ts-ignore
  fetchParams(id, selectedMatches) {
    const url: string = `${routes.API_ROOT}/data`
    const config: TConfig = {
      method: 'POST',
      body: JSON.stringify({
        proc: 'ask_gear_params',
        params: {
          _v_amplua_id: 0,
        },
      }),
    }
    callApi({
      url,
      config,
      //@ts-ignore
      onRequest: () => self._onRequest(),
      onSuccess: (
        //@ts-ignore
        json: { data: Array<{}> },
        //@ts-ignore
      ) => self._onSuccessParams(json, id, selectedMatches),
      //@ts-ignore
      onError: (error: any) => self._onError(error),
    })
  },

  _onRequest() {
    //@ts-ignore
    self.isFetching = true
    //@ts-ignore
    self.error = null
  },

  _onSuccessParams(
    //@ts-ignore
    json: { data: Array<{}> },
    //@ts-ignore
    id,
    //@ts-ignore
    selectedMatches,
  ) {
    //@ts-ignore
    self.totalParams = get(json, 'data', [])
    //@ts-ignore
    self.error = null
    //@ts-ignore
    self.fetchGroups()
    //@ts-ignore
    self.fetchPlayers(id, selectedMatches)
  },

  //@ts-ignore
  _onError(error: {}) {
    //@ts-ignore
    self.isFetching = false
    //@ts-ignore
    self.error = error
  },

  // Получение общего списка игроков
  //@ts-ignore
  fetchPlayers(id, selectedMatches) {
    const url: string = `${routes.API_ROOT}/data`
    const config: TConfig = {
      method: 'POST',
      body: JSON.stringify({
        proc: 'scout_team_players_stat',
        params: {
          _p_team_id: id,
          _p_match_arr: selectedMatches.team[id],
          //@ts-ignore
          _p_params_arr: map(self.totalParams, 'param_id'),
        },
      }),
    }
    callApi({
      url,
      config,
      //@ts-ignore
      onRequest: () => self._onRequest(),
      //@ts-ignore
      onSuccess: (json: { data: Array<{}> }) => self._onSuccessPlayers(json),
      //@ts-ignore
      onError: (error: any) => self._onError(error),
    })
  },
  //@ts-ignore
  _onSuccessPlayers(json: { data: Array<{}> }) {
    //@ts-ignore
    self.isFetching = false
    //@ts-ignore
    self.players = map(
      get(json.data, '[0].scout_team_players_stat', []),
      player => ({
        ...player,
        params: get(player, 'params') && map(player.params, param => ({
          ...param,
          // для % параметров не используем формулу, не меняем значение
          // % параметры берем из конфига
          value: includes(percentageParams, param.param)
            ? param.value / param.matches_count
            : param.value,
          averageValue: param.value / param.matches_count,
        })),
      }),
    )
    //@ts-ignore
    self.error = null
  },

  // Получение списка групп для шестеренки
  fetchGroups() {
    const url: string = `${routes.API_ROOT}/data`
    const config: TConfig = {
      method: 'POST',
      body: JSON.stringify({
        proc: 'ask_gear_group_view',
      }),
    }
    callApi({
      url,
      config,
      //@ts-ignore
      onRequest: () => self._onRequest(),
      //@ts-ignore
      onSuccess: (json: { data: Array<any> }) => self._onSuccessGroups(json),
      //@ts-ignore
      onError: (error: any) => self._onError(error),
    })
  },

  //@ts-ignore
  _onSuccessGroups(json: { data: Array<any> }) {
    //@ts-ignore
    self.paramGroups = get(json, 'data', [])
    //@ts-ignore
    self.paramGroupsError = null
    //@ts-ignore
    const serializedParams = map(self.totalParams, item => toJS(item))
    //@ts-ignore
    self.gearParams = reduce(self.paramGroups, (prev, curr) => {
      const current = {
        params: sortBy(
          filter(
            serializedParams, ({ gear_group_id }) => gear_group_id === curr.group_id,
          ), 'sort_order',
        ),
        groupName: String(curr.group_lexical_formula),
      }
      return [...prev, current]
    }, [])
    if (window.localStorage.getItem('teamPlayersTable') === null) {
      //@ts-ignore
      self.saveTables()
    } else {
      //@ts-ignore
      self.tables = JSON.parse(window.localStorage.getItem('teamPlayersTable'))
    }
  },

  // Получение списка ивентов для игрока
  fetchEvents(
    //@ts-ignore
    lang,
    //@ts-ignore
    lexical_formula,
    //@ts-ignore
    playerId,
    //@ts-ignore
    paramId,
    //@ts-ignore
    request,
    //@ts-ignore
    selectedMatches,
    //@ts-ignore
    shootsRange,
  ) {
    const url: string = `${routes.API_ROOT}/data`
    const config: TConfig = {
      method: 'POST',
      body: JSON.stringify({
        proc: 'scout_events_sec_by_param',
        params: {
          _p_matches: selectedMatches,
          _p_param_id: paramId,
          _p_player_id: playerId,
        },
      }),
    }
    return callApi({
      url,
      config,
      onRequest: () => { },
      //@ts-ignore
      onSuccess: (json: { data: Array<any> }) => (
        //@ts-ignore
        self._onSuccessEvents(
          json,
          lang,
          lexical_formula,
          request,
          shootsRange,
          playerId,
        )
      ),
      //@ts-ignore
      onError: (error: any) => self._onError(error),
    })
  },

  _onSuccessEvents(
    //@ts-ignore
    json: { data: Array<any> },
    lang: string,
    lexical_formula: string,
    request: (arg: { }) => void,
    shootsRange: {},
    playerId: number,
  ) {
    //@ts-ignore
    self.events = map(
      get(json.data, '[0].scout_events_sec_by_param', []), 'id',
    )
    //@ts-ignore
    if (self.events.length) {
      request({
        //@ts-ignore
        events: self.events,
        range: shootsRange,
        lang,
        lexical_formula,
        playerInfo: playerId,
        type: 'teamPlayers',
      })
    }
  },
}))

export default TeamPlayersApiActions
