import { types } from 'mobx-state-tree'
import { callApi } from 'react-sport'
// @ts-ignore
import routes from 'config/routes'

const FiltersApiActions = types.model({}).actions(self => ({
  fetch() {
    const url = `${routes.API_ROOT}/data`
    const config = {
      method: 'POST',
      body: JSON.stringify({
        proc: 'ask_filters_manual_view',
      }),
    }

    callApi({
      url,
      config,
      onRequest: () => {
        //@ts-ignore
        self.data = []
        //@ts-ignore
        self.error = null
        //@ts-ignore
        self.loaded = false
      },
      //@ts-ignore
      onSuccess: json => self._onSuccess(json),
      //@ts-ignore
      onError: error => self._onError(error),
    })
  },

  _onSuccess({ data }: { data: {} }) {
    //@ts-ignore
    self.data = data
    //@ts-ignore
    self.loaded = true
    //@ts-ignore
    self.error = null
  },

  _onError(error: any) {
    //@ts-ignore
    self.error = error
    //@ts-ignore
    self.loaded = true
  },
}))

export default FiltersApiActions
