import { inject, observer } from 'mobx-react'

import { compose, withHandlers } from 'recompose'

import get from 'lodash/get'
import find from 'lodash/find'

import { withMatchLinksGetter } from 'hocs'
// @ts-ignore
import { getExtLinkHOC } from 'hocs/getExtLinkHOC'
// @ts-ignore
import { clickableClassNameHOC } from 'hocs/getClickableTdClassNameHoc'

import { TPropsTrComposed } from './types'

export const TrComposed = compose<any, TPropsTrComposed>(
  inject('videoLink', 'lexics', 'shootsRange', 'matchLinks'),
  withMatchLinksGetter,
  getExtLinkHOC,
  clickableClassNameHOC,
  //@ts-ignore
  withHandlers({
    getStat: ({ rowInfo: { params } }) =>
      //@ts-ignore
      paramId => String(get(find(params, { p: paramId }), 'v', 0)),
  }),
  observer,
)
