
import React from 'react'
import { inject, observer } from 'mobx-react'

import {
  compose,
  withHandlers,
  withProps,
} from 'recompose'
import map from 'lodash/map'
import some from 'lodash/some'
import get from 'lodash/get'
import find from 'lodash/find'
import includes from 'lodash/includes'
import filter from 'lodash/filter'
import inRange from 'lodash/inRange'
import size from 'lodash/size'
import isEmpty from 'lodash/isEmpty'

import {
  zoneAction,
  zoneBorderConfig,
} from 'config/zones'
import {
  TEventToEpisodeOuter,
  TEvent,
  THandleShootClickOuter,
  TProps,
  TPropsInner,
  TPropsOuter,
  TGetParamsOuter,
  TGetParamsInner,
  TWithPropsInner,
  TWithPropsOuter,
} from './types'

const GAME_PART_LEXIC = 8684

const ZonesStatisticComponent = ({
  handleShootClick,
  percentage,
  succesZoneMarkers,
  totalZoneMarkers,
  zone,
  zoneEvents,
  zones: { total },
}: TProps) => (
  <a
    className={`map-block__zones-episode zone_${zone}`}
    onClick={handleShootClick(zoneEvents)}
  >
    <p>{percentage}%</p>
    <span>{!total && `${succesZoneMarkers} — ${totalZoneMarkers}`}</span>
  </a>
)

const zonesStatisticComposed = compose<TPropsInner, TPropsOuter>(
  inject(
    'tabs',
    'markers',
    'zones',
    'videoPlayer',
    'shootsRange',
    'lexics',
  ),
  withHandlers<TGetParamsOuter, TGetParamsInner>({
    /*
     * фильтрация параметров.
     * data - список маркеров
     * key - ключ, по которому получаем нужный нам список
     * zone - текущая зона
     */
    getParams: ({
      tabs: { active },
      zones: { checked },
    }) => ({
      data,
      key,
      zone,
    }) => {
      // Фильтруем "Уcпешные" действия (маркеры)
      if (key === 'successActions') {
        const firstConditionId = get(zoneAction, `${active}.[0]`)
        const secondConditionId = get(zoneAction, `${active}.[1]`)
        data = filter(data, ({ prop }) =>
          some(
            prop,
            propItem =>
              includes(propItem, firstConditionId)
              && some(propItem, propItemValue =>
                includes(secondConditionId, propItemValue)),
          ))
      }
      // Получаем границы зоны из конфига в зависимости от выбранного типа поля
      const side = active === 'serve' ? 'top' : 'bottom' //наша часть поля или соперника
      const fieldType = checked === 'basic_zones' ? 'basic' : 'advanced'
      // достаем из конфига границы для выбранного типа и стороны поля
      const zoneTypeX = get(zoneBorderConfig, `x.${fieldType}.${side}`)
      const zoneTypeY = get(zoneBorderConfig, `y.${fieldType}.${side}`)
      // получаем координаты границы по Х и по Y
      const xCoords = get(
        find(zoneTypeX, ({ zonesList }) => includes(zonesList, zone)),
        'coords',
      )
      const yCoords = get(
        find(zoneTypeY, ({ zonesList }) => includes(zonesList, zone)),
        'coords',
      )
      // Для подачи (serve) или передачи (pass) нам нужны зоны соперника,
      // т.е. куда мяч улетел ('to')
      const coordsType = includes(['serve', 'pass'], active) ? 'to' : 'from'

      // Возвращаем отфильтрованные маркеры по текущей зоне
      return filter(
        data,
        marker =>
          inRange(get(marker, `${coordsType}.pos_x`), xCoords[0], xCoords[1])
          && inRange(get(marker, `${coordsType}.pos_y`), yCoords[0], yCoords[1]),
      )
    },
  }),
  observer,
  withProps<TWithPropsInner, TWithPropsOuter>((
    {
      getParams,
      markers: { list },
      zone,
    } : TWithPropsOuter,
  ) => {
    const getMarkers = (key: string) =>
      getParams({
        data: list,
        key,
        zone,
      })
    // количество всех маркеров в данной зоне
    const allMarkers = getMarkers('totalActions')
    const totalZoneMarkers = size(allMarkers)
    // количество "успешных" маркеров в данной зоне
    const succesZoneMarkers = size(getMarkers('successActions'))
    const percentage = Math.round((succesZoneMarkers / totalZoneMarkers) * 100) || 0
    return {
      percentage,
      succesZoneMarkers,
      totalZoneMarkers,
      zoneEvents: allMarkers,
    }
  }),
  withHandlers<TEventToEpisodeOuter, {}>({
    eventToEpisode: ({
      lexics: { getLexics },
    }) => (event: TEvent) => {
      const {
        f_match,
        half,
        id,
        result_lexic,
        second,
      } = event
      const set = `${half} ${getLexics(GAME_PART_LEXIC)}`

      return {
        action: {
          name: getLexics(result_lexic),
          set,
        },
        eventId: Number(id),
        half,
        matchId: f_match,
        period: set,
        second: Math.round(second / 1000),
      }
    },
  }),
  withHandlers<THandleShootClickOuter, {}>({
    handleShootClick: ({
      eventToEpisode,
      videoPlayer: {
        fetchVideo,
        setModalOpen,
      },
    }) => (events: Array<number>) => () => {
      if (!isEmpty(events)) {
        const data = map(events, event => eventToEpisode(event))

        fetchVideo(data)
        setModalOpen(true)
      }
    },
  }),
)

export const ZonesStatistic = zonesStatisticComposed(ZonesStatisticComponent)
