import React, { Fragment } from 'react'
import map from 'lodash/map'
import { compose, withHandlers } from 'recompose'

import { T9n } from 'react-sport'

type TParam = {
  param_id: number,
  gear_group_id: number,
  full_lexical_formula: string,
  short_lexical_formula: string,
}

type TProps = {
  table: { selectedParams: Array<TParam> },
  onClick: (param: TParam) => void,
}

// Шапка с выбранными параметрами в таблице статистики
export const THeadComponent = ({
  onClick,
  table: { selectedParams },
}: TProps) => (
  <Fragment>
    <th />
    {map(selectedParams, param => (
      <th
        key={`${param.param_id}_${param.gear_group_id}`}
          //@ts-ignore
        onClick={onClick(param)}
      >
        <div>
          <span className='th-tooltip'>
            <T9n t={param.full_lexical_formula} />
          </span>
          <T9n t={param.short_lexical_formula} />
        </div>
      </th>
    ))}
  </Fragment>
)

const THeadComposed = compose(
  //@ts-ignore
  withHandlers({
    //@ts-ignore
    onClick: ({ setSortedId }) => param => () => setSortedId(param),
  }),
)

// @ts-ignore
export const THead = THeadComposed(THeadComponent)
