import React, { Fragment } from 'react'

import { inject, observer } from 'mobx-react'

import { withProps, compose } from 'recompose'
import map from 'lodash/map'
import get from 'lodash/get'
import reduce from 'lodash/reduce'

import { getLongSuffix } from 'module/utils/helpers'

import {
  TPropsInner,
  TPropsOuter,
  TWithPropsInner,
  TWithPropsOuter,
} from './types'

export const PlayerNameComponent = ({ tooltipNames }: TPropsInner) => (
  <Fragment>
    {map(tooltipNames, name => (
      <span key={name} className='shot__player-name'>
        {name}
      </span>
    ))}
  </Fragment>
)

