const playerNameComposed = compose<TPropsInner, TPropsOuter>(
  inject('lexics'),
  observer,
  withProps<TWithPropsInner, TWithPropsOuter>(({
    lexics: { lang },
    point: {
      playerName,
    },
  }) => {
    const suffix = getLongSuffix(lang)
    const tooltipNames = reduce(
      playerName,
      (acc: Array<string>, curr) => {
        const firstName = get(curr, `firstname_${suffix}`)
        const lastName = get(curr, `lastname_${suffix}`)
        const fullName = `${firstName} ${lastName}`
        acc.push(fullName)

        return acc
      }, [],
    )
    // TODO Пока не готово пробрасывание второго и третьего имени
    // || secondPlayerName
    // || thirdPlayerName
    return {
      tooltipNames,
    }
  }),
)

export const PlayerName = playerNameComposed(PlayerNameComponent)
