
import { types } from 'mobx-state-tree'

import sortBy from 'lodash/sortBy'
import findIndex from 'lodash/findIndex'
import map from 'lodash/map'

export const PlayersCompareViews = types.model({}).views(self => ({
  get sortedStats() {
    //@ts-ignore
    const ids = self.getParamsIds
    return sortBy(
      //@ts-ignore
      self.stats,
      ({ param }) => findIndex(ids, o => o === Number(param)),
    )
  },

  get getParamsIds(): Array<number> {
    //@ts-ignore
    return map(self.selectedParams, 'param_id')
  },
}))
