import React from 'react'
import { inject, observer } from 'mobx-react'

import {
  compose,
  withProps,
  withHandlers,
} from 'recompose'
import get from 'lodash/get'
import without from 'lodash/without'
import includes from 'lodash/includes'
import map from 'lodash/map'
import values from 'lodash/values'

import { getSuffix } from 'module/utils/helpers'
import { TeamCheckbox } from './TeamCheckbox'

import {
  TComposeInner,
  TWithPropsInner,
  TInject,
  TWithHandlersOuter,
  TWithHandlersInner,
} from './types'

// Компонент шапки таблицы, где есть выбор у пользователя какую команду показывать
const HeaderWithTeamsComponent = ({
  onClick,
  table: { team },
  teamsInfo,
}: TComposeInner) => (
  <div className='stats-table-checkbox-opp'>
    {map(values(teamsInfo), ({ name, status }) => (
      <TeamCheckbox
        key={status}
        status={status}
        onClick={onClick}
        teamName={name}
        team={team}
      />
    ))}
  </div>
)

export const headerWithTeamsComposed = compose<TComposeInner, {}>(
  inject(
    'matchProfile',
    'lexics',
    'matchTable',
  ),
  observer,
  withProps<TWithPropsInner, TInject>(
    ({ lexics: { lang }, matchProfile: { data } }) => {
      const suffix = getSuffix(lang)
      return {
        teamsInfo: {
          home: {
            name: get(data, `team1_name_${suffix}`, 'home'),
            status: 'showHomeTeam',
          },
          opp: {
            name: get(data, `team2_name_${suffix}`, 'opp'),
            status: 'showOppTeam',
          },
        },
      }
    },
  ),
  withHandlers<TWithHandlersOuter, TWithHandlersInner>({
    onClick: ({
      matchTable: { toggleCheckTeam },
      table,
    }) => teamName => () => {
      const { team } = table

      const updatedTeamsId = includes(team, teamName)
        ? without(team, teamName)
        : [...team, teamName]

      toggleCheckTeam(updatedTeamsId, table)
    },
  }),
  observer,
)

export const HeaderWithTeams = headerWithTeamsComposed(HeaderWithTeamsComponent)
