import { observable, computed } from 'mobx'

// @ts-ignore
import BasicStore from 'features/stores/BasicStore'
// @ts-ignore
import ZoneActions from 'features/stores/Zones/ZoneActionsMixin'
// @ts-ignore
import { zone_params } from 'config/params'

// @ts-ignore
// @ts-ignore
// @ts-ignore
const setParam = (data, tab, key) =>
  //@ts-ignore
  data.filter(({ f_param }) => f_param === zone_params[tab][key].param_id)

class Zones extends ZoneActions(BasicStore) {
  @observable checked = 'without_zone';

  @observable total = false;

  @observable stats = null;

  @observable loaded = false;

  //@ts-ignore
  @observable error;

  @observable basic = {
    left: {},
    right: {},
  };

  @observable advanced = {
    left: {},
    right: {},
  };

  @computed get activeStats() {
    //@ts-ignore
    const { active } = this.getStore('tabs')
    const zoneStats = this.checked === 'advanced_zones'
    //@ts-ignore
      ? this.stats.filter(({ f_variant_zone }) => f_variant_zone === 124)
    //@ts-ignore
      : this.stats.filter(({ f_variant_zone }) => f_variant_zone !== 124)
    return {
      percent: setParam(zoneStats, active, 0),
      successActions: setParam(zoneStats, active, 1),
      totalActions: setParam(zoneStats, active, 2),
    }
  }
}

export default Zones
