import { observer } from 'mobx-react'

import {
  compose,
  branch,
  renderComponent,
} from 'recompose'

import Loader from 'features/components/common/loader/Loader'

type TIsLoading = (args: any) => boolean

/*
  Хок для добавления лоадера для оборачиваемого компонента
  Работает так: используем функцию проверки isLoading
  Прим. props => props.someModel.isFetching
  Если возвращает true, то рендерится лоадер.
  Если false, то рендерится компонент, или нижестоящий хок (если в compose)
*/
export const withSpinnerWhileLoading = (isLoading: TIsLoading) =>
  compose(
    observer,
    branch(
      isLoading,
      renderComponent(Loader),
    ),
  )
