import includes from 'lodash/includes'

import {
  attackZone,
  attackZoneSpecial,
  frontLineParams,
  backLineParams,
  allBlocks,
  fieldLines,
  zones,
  blockTypes,
} from 'config/FieldZones'

const { BACK_LINE, FRONT_LINE } = fieldLines
const { ATTACK_ZONE, ATTACK_ZONE_SPECIAL } = zones
const { ALL_BLOCKS } = blockTypes

// Формирование объекта с параметров для запроса отчета
export const getParamObj = (paramId: number) => {
  const params: {
    [key: string]: number | null,
  } = {
    param_id: paramId,
  }
  // Для некоторых параметров считается не вся площадка, добавляем f_option_3
  if (includes(frontLineParams, paramId)) {
    params.f_option_3 = FRONT_LINE
  }
  // Для некоторых параметров считается не вся площадка, добавляем f_option_3
  if (includes(backLineParams, paramId)) {
    params.f_option_3 = BACK_LINE
  }
  // Для блоков считается общее количество (без разновидностей), добавляем f_option_5
  if (includes(allBlocks, paramId)) {
    params.f_option_5 = ALL_BLOCKS
  }
  // для параметров атак, добавляем variant_zone
  if (includes(attackZone, paramId)) {
    params.f_variant_zone = ATTACK_ZONE
  }
  // Если параметр относится к специальному атакующему типу,
  // меняем зону на зону специальную атаки
  if (includes(attackZoneSpecial, paramId)) {
    params.f_variant_zone = ATTACK_ZONE_SPECIAL
  }

  return params
}
