
import { types } from 'mobx-state-tree'
import orderBy from 'lodash/orderBy'
import get from 'lodash/get'

import { callApi } from 'react-sport'

// @ts-ignore
import routes from 'config/routes'

type TConfig = {
  method: string,
  body: string,
};

const url: string = `${routes.API_ROOT}/data`

export const TournamentMatchesApiActions = types.model({}).actions(self => ({
  fetchMatchesInfo(tournamentId: number, seasonId: number) {
    const config: TConfig = {
      method: 'POST',
      body: JSON.stringify({
        proc: 'scout_tournament_matches_stat',
        params: {
          _p_tournament_id: tournamentId,
          _p_season_id: seasonId,
        },
      }),
    }

    return callApi({
      url,
      config,
      onRequest: () => {
        //@ts-ignore
        self.isLoaded = false
        //@ts-ignore
        self.error = null
        //@ts-ignore
        self.matches = []
      },
      //@ts-ignore
      onSuccess: self._onSuccessMatchesInfo,
      //@ts-ignore
      onError: self._onError,
    })
  },

  _onSuccessMatchesInfo(json: {
    data: Array<{
      scout_tournament_matches_stat: Array<{}>,
    }>,
  }) {
    //@ts-ignore
    self.matches = orderBy(
      get(json, 'data[0].scout_tournament_matches_stat', []),
      'match_date',
      'desc',
    )
    //@ts-ignore
    self.error = null
    //@ts-ignore
    self.isLoaded = true
  },

  _onError(error: any) {
    //@ts-ignore
    self.error = error
  },
}))
