import React from 'react'
import { observer, inject } from 'mobx-react'

import { withHandlers, compose, withProps } from 'recompose'
import map from 'lodash/map'
import flatten from 'lodash/flatten'
import values from 'lodash/values'
import get from 'lodash/get'
import find from 'lodash/find'
import head from 'lodash/head'
import includes from 'lodash/includes'
import pipe from 'lodash/fp/pipe'

import { SimpleDropdown } from 'react-sport'

// @ts-ignore
import { getPlayerFullName } from 'module/utils/helpers/getPlayerFullName'

import { PlayerName } from './PlayerName'

type TProps = {
  matchTable: {
    playerList: {},
  },
  team1: number,
  team2: number,
  playersCompareWidget: {
    selectedPlayers: {},
  },
  getPlayerNumber: (playerId: number) => number,
  selectPlayer: (player: {}) => void,
  getPlaceholder: (arg: Array<{ f_player: number }>) => string,
  getTeamPlayers: (value: number) => Array<{ f_player: number }>,
}

// Компонент с двумя выпадающими меню, игроки команд в профиле матча
const PlayersSelectComponent = ({
  getPlaceholder,
  getPlayerNumber,
  getTeamPlayers,
  selectPlayer,
  team1,
  team2,
}: TProps) => (
  <div className='t-compare__teams'>
    {map([team1, team2], team => (
      <div key={team} className='t-compare__player'>
        {/*
        //@ts-ignore */}
        <SimpleDropdown placeholder={getPlaceholder(getTeamPlayers(team))}>
          {map(getTeamPlayers(team), player => (
            //@ts-ignore
            <div key={player.f_player} onClick={selectPlayer(player)}>
              <span className='t-compare__player-num'>
                {getPlayerNumber(player.f_player)}
              </span>
              <span className='t-compare__player-name'>
                {/*
                //@ts-ignore */}
                <PlayerName player={player} />
              </span>
            </div>
          ))}
        </SimpleDropdown>
      </div>
    ))}
  </div>
)

const playersSelectComposed = compose(
  inject('lexics', 'playersCompareWidget', 'matchTable', 'matchProfile'),
  observer,
  //@ts-ignore
  withProps(({ lexics: { lang } }) => ({ lang })),
  //@ts-ignore
  withHandlers({
    getPlayerNumber: ({
      matchTable: { playerList },
      //@ts-ignore
    }) => (playerId) => {
      const players = pipe(
        values,
        flatten,
      )(playerList)

      //@ts-ignore
      return get(find(players, { f_player: playerId }, {}), 'num', '')
    },
    selectPlayer: ({
      lexics: { lang },
      matchProfile: { matchId },
      playersCompareWidget: { selectedPlayers, updateSelectedPlayers },
      //@ts-ignore
    }) => player => () => {
      if (!includes(values(selectedPlayers), player.f_player)) {
        updateSelectedPlayers({
          lang,
          matchId,
          players: [player],
        })
      }
    },
  }),
  //@ts-ignore
  withHandlers({
    getTeamPlayers: ({ matchTable: { playerList } }) =>
      //@ts-ignore
      team => get(playerList, `${team}`, []),
    getPlaceholder: ({
      getPlayerNumber,
      lexics: { lang },
      //@ts-ignore
    }) => (players) => {
      const defaultPlayer = head(players) || {}
      const fullName = getPlayerFullName({
        //@ts-ignore
        player: defaultPlayer,
        lang,
        long: true,
      })
      const number = getPlayerNumber(get(defaultPlayer, 'f_player'))
      return (
        <div key={number}>
          <span className='t-compare__player-num'>{number}</span>
          <span className='t-compare__player-name'>{fullName}</span>
        </div>
      )
    },
  }),
  observer,
)

// @ts-ignore
export const PlayersSelect = playersSelectComposed(PlayersSelectComponent)
