import React from 'react'

import { tBodyComposed } from './composed'

import { TPropsInner } from './types'

// Основной блок таблицы со статистикой
export const TBodyComponent = ({
  activeId,
  pageId,
  rowsList,
  selectedParams,
  showOpponentStats,
  tableStats,
  Tr,
}: TPropsInner) => (
  <tbody>
    {rowsList
      .map((trItem: { f_match?: number, f_player?: number }) => (
        <Tr
          key={trItem.f_player || trItem.f_match}
          rowInfo={trItem}
          pageId={pageId}
          selectedParams={selectedParams}
          tableStats={tableStats}
          activeId={activeId}
          showOpponentStats={showOpponentStats}
        />
      ))
    }
  </tbody>
)

export const TBody = tBodyComposed(TBodyComponent)
