import React, { Fragment } from 'react'
import { inject, observer } from 'mobx-react'

import {
  compose,
  withProps,
  withHandlers,
} from 'recompose'
import cx from 'classnames'
import map from 'lodash/map'
import size from 'lodash/size'

import { getLongSuffix } from 'module/utils/helpers'
import {
  TProps,
  TWithPropsOuter,
  TWithPropsInner,
  TPlayer,
  TWithHandlersOuter,
  TWithHandlersInner,
  TPropsOuter,
} from './types'

import { PopupHOC } from '../PopupHOC'

// Попап со списком игроков разбитым по командам
const PopupWithPlayersComponent = ({
  filters: { handleAllTempCheck, handleTempCheck },
  getFullName,
  getItemClassName,
  getPlayersId,
  getTitleClassName,
  mapShoot: { data },
  suffix,
  toggleOpenClose,
  type,
}: TProps) => (
  <Fragment>
    <div
      className={cx(
        'popup-column__wrapper',
        { 'popup-column__wrapper_wide': size(data) >= 3 },
        { 'popup-column__wrapper_narrow': size(data) < 2 },
      )}
    >
      {map(data, ({
        f_team: teamId,
        prop_player: playersList,
        ...team
      }) => (
        <div className='popup-column__container' key={teamId}>
          <div
            className={getTitleClassName(getPlayersId(playersList))}
            onClick={() =>
              handleAllTempCheck(getPlayersId(playersList), type)
            }
          >
            <span>{team[`name_${suffix}`]}</span>
          </div>
          <ul className='popup-column__list'>
            {map(playersList, ({ player_id: playerId, ...player }) => (
              <li
                className={getItemClassName(`${playerId}_player`)}
                key={playerId}
                onClick={() => handleTempCheck(`${playerId}_player`, type)}
              >
                <span>{getFullName(player)}</span>
              </li>
            ))}
          </ul>
        </div>
      ))
      }
    </div>
    <button className='map-block__popup-button' onClick={toggleOpenClose}>
      OK
    </button>
  </Fragment>
)

const popupWithPlayersComposed = compose<TProps, TPropsOuter>(
  inject('mapShoot', 'lexics'),
  withProps<TWithPropsInner, TWithPropsOuter>(({ lexics: { lang } }) => ({
    suffix: getLongSuffix(lang),
  })),
  withHandlers<TWithHandlersOuter, TWithHandlersInner>({
    getFullName: ({ suffix }) => (player: TPlayer) =>
      `${player[`firstname_${suffix}`]} ${player[`lastname_${suffix}`]}`,

    getPlayersId: () => (players: Array<{player_id: number}>) =>
      map(players, ({ player_id }) => `${player_id}_player`),
  }),
  PopupHOC,
  observer,
)

export const PopupWithPlayers = popupWithPlayersComposed(PopupWithPlayersComponent)
