import { observer, inject } from 'mobx-react'

import get from 'lodash/get'
import { compose, withProps } from 'recompose'

import { utils } from 'react-sport'

// @ts-ignore
import { getLongSuffix } from 'module/utils/helpers'

export const leftSideTrComposed = compose(
  inject('lexics', 'tournamentPlayersTableStore'),
  observer,
  //@ts-ignore
  withProps(
    //@ts-ignore
    ({
      lexics: { lang },
      rowInfo: {
        f_player,
        ...rowInfo
      },
      tournamentPlayersTableStore: { avatarIsVisible },
    }) => {
      const suffix = getLongSuffix(lang)

      return {
        rowspan: '',
        avatarUrl: utils.helpers.getLogo({
          type: 'players_volleyball',
          id: f_player,
        }),
        avatarIsVisible,
        playerProfileLink: `/players/${f_player}`,
        shirtNumber: get(rowInfo, 'shirt_num'),
        playerName: get(rowInfo, `name_${suffix}`),
      }
    },
  ),
)
