import get from 'lodash/get'
import size from 'lodash/size'

import { getSuffix, getLongSuffix, num2str } from 'module/utils/helpers'
import stores from 'features/stores'

import getHeaderStatsTitle from './titles/getHeaderStatsTitle'
import getMapShootsTitle from './titles/getMapShootsTitle'
import getTableMatchesTitle from './titles/getTableMatchesTitle'
import getTablesFooterTitle from './titles/getTablesFooterTitle'
import getTeamPlayersTitle from './titles/getTeamPlayersTitle'
import getWidgetsTitle from './titles/getWidgetsTitle'

/** создаем объект с названием плейлиста для внешней видео ссылки
 * @param {string} lexical_formula - название параметра
 * @param {object} matchInfo - Информация о матче. Нужна для имен команд
 * @param {string} tab - активный таб на карте ударов
 * @param {string} lang - язык
 * @param {string} playerInfo - Информация об игроке, если доступна.
 * @param {string} type - тип запроса, для составления title (приходит из компонента)
 * @param {string} widgetType - тип виджета, если доступен

 * @returns {object} возвращаемое значение - объект с полями тайтлов
 */
const setTitle = ({
  lang,
  lexical_formula,
  matchInfo,
  playerInfo,
  tab = '',
  type,
  widgetType,
}: {
  lexical_formula: string | number,
  matchInfo: any,
  tab: string,
  lang: string,
  playerInfo: any,
  type: string,
  widgetType: string,
}) => {
  const {
    //@ts-ignore
    lexics: { getLexics, page },
    //@ts-ignore
    playerProfile,
    //@ts-ignore
    selectedMatches,
    //@ts-ignore
    teamPlayers,
    //@ts-ignore
    teamProfile,
  } = stores
  //@ts-ignore
  const activeTab: string | null = getLexics(tab)
  const suffix: string = getSuffix(lang)
  const longSuffix: string = getLongSuffix(lang)
  const paramName: string | number = getLexics(lexical_formula) || lexical_formula

  const pageId: string | number = get(stores, `${page}Profile.profile.id`, '')
  const matchesCount: number = size(get(selectedMatches, `${page}.${pageId}`))
  let matchName: string
  if (lang === 'ru') {
    matchName = num2str(matchesCount, ['матч', 'матча', 'матчей'])
  } else {
    //@ts-ignore
    matchName = matchesCount.toString().split('').pop() === 1
      ? 'match'
      : 'matches'
  }

  const simpleName: string = get(teamProfile, `profile.name_${longSuffix}`, '')
    || get(playerProfile, `profile.short_name_${longSuffix}`, '')

  const getMatchInfo = () => {
    const {
      //@ts-ignore
      matches: { active },
      //@ts-ignore
      matchProfile: { data },
    } = stores
    const {
      score_team1: teamScore,
      score_team2: oppTeamScore,
    } = data
    const teamName: string = get(data, `team1_name_${suffix}`, '')
    const oppTeamName: string = get(data, `team2_name_${suffix}`, '')
    const activeTeam: string = get(data, `${active}_name_${suffix}`)
    return {
      teamScore,
      oppTeamScore,
      teamName,
      oppTeamName,
      activeTeam,
    }
  }

  // Аргументы для передачи в функцию создания тайтлов
  const args = {
    matches: {
      matchInfo,
      suffix,
      longSuffix,
      paramName,
      stores,
      getMatchInfo,
      playerInfo,
      page,
    },
    tablesFooter: {
      simpleName,
      matchesCount,
      matchName,
      paramName,
    },
    widgets: {
      getMatchInfo,
      playerInfo,
      paramName,
    },
    mapShoots: {
      getMatchInfo,
      activeTab,
      page,
      simpleName,
      matchesCount,
      matchName,
    },
    headerStats: {
      simpleName,
      matchesCount,
      matchName,
      paramName,
    },
    teamPlayers: {
      teamPlayers,
      paramName,
      playerInfo,
      longSuffix,
    },
  }

  // Объект со ссылками на функции для составления названия плейлиста
  // В зависимости от type вызывается нужная функция
  const titleTypes = {
    //@ts-ignore
    matches: getTableMatchesTitle(args.matches),
    tablesFooter: getTablesFooterTitle(args.tablesFooter),
    widgets: getWidgetsTitle(args.widgets),
    mapShoots: getMapShootsTitle(args.mapShoots),
    headerStats: getHeaderStatsTitle(args.headerStats),
    teamPlayers: getTeamPlayersTitle(args.teamPlayers),
  }
  //@ts-ignore
  if (!titleTypes[type]) return null

  if (type === 'widgets') {
    return titleTypes[type](widgetType)
  }
  //@ts-ignore
  return titleTypes[type]()
}

export default setTitle
