import React from 'react'
import { compose, withHandlers } from 'recompose'
import { observer } from 'mobx-react'
import { T9n } from 'react-sport'
// @ts-ignore
import { tableLexics } from 'config/tables'

type TProps = {
  getClassName: (value: boolean) => string,
  currentStore: { showOpponentStats: boolean },
  setShowOppStat: () => void,
  tableType: string,
};

// Компонент шапки таблицы, где есть статистика соперника
const HeaderWithOppComponent = ({
  currentStore: { showOpponentStats },
  getClassName,
  setShowOppStat,
  tableType,
}: TProps) => (
  <a className={getClassName(showOpponentStats)} onClick={setShowOppStat}>
    {/*
    //@ts-ignore */}
    <T9n t={tableLexics[tableType]} />
  </a>
)

const HeaderWithOppComposed = compose(
  //@ts-ignore
  withHandlers({
    setShowOppStat: ({ currentStore: { toggleShowOpponent } }) => () =>
      toggleShowOpponent(),
  }),
  observer,
)

// @ts-ignore
export const HeaderWithOpp = HeaderWithOppComposed(HeaderWithOppComponent)
