import React from 'react'
import { inject, observer } from 'mobx-react'

import {
  compose,
  lifecycle,
} from 'recompose'
import get from 'lodash/get'

import { headerPlayerParams } from 'config/params'

import Loader from 'features/components/common/loader/Loader'
import { HeaderStatsList } from 'features/HeaderStatsList'

import {
  TPropsOuter,
  TPropsInner,
} from './types'
/**
 * Отображение блока со статистикой в шапке профиля
 */
export const PlayerStatsWrapperComponent = ({
  playerProfile: {
    profile,
  },
  playerStats: {
    isFetching,
    stats,
  },
}: TPropsInner) => (
  <div className='player-card-stats-list'>
    {isFetching ? (
      <Loader />
    ) : (
      <HeaderStatsList
        params={headerPlayerParams[profile.amplua.id]}
        stats={stats}
      />
    )}
  </div>
)

const playerStatsComposed = compose<TPropsInner, TPropsOuter>(
  inject(
    'playerProfile',
    'playerStats',
    'selectedMatches',
  ),
  lifecycle<TPropsInner>({
    componentDidMount() {
      const {
        page,
        pageId,
        playerProfile: {
          profile: {
            amplua: { id },
          },
        },
        playerStats: { fetch },
        selectedMatches,
      } = this.props
      fetch(
        pageId,
        get(selectedMatches, [page, pageId], []),
        id,
      )
    },
  }),
  observer,
)

export const PlayerStatsWrapper = playerStatsComposed(PlayerStatsWrapperComponent)
