import React from 'react'

import get from 'lodash/get'
import map from 'lodash/map'

import { utils } from 'react-sport'

import { getLongSuffix } from 'module/utils/helpers'

import PlayerTrComposed from './PlayerTrComposed'
import { Td } from './TdWrapper'

type TProps = {
  avatarIsVisible: boolean,
  avatarUrl: string,
  player: {
    shirt_num: string,
    f_player: number,
  },
  activeId: string,
  getClassName: (param: {}, activeId: string) => string,
  lexics: {
    lang: string,
  },
  selectedParams: Array<{
    param_id: number,
    gear_group_id: number,
    lexical_formula: string,
  }>,
  getParamValue: (param: {}, position: number) => string,
  handleClick: (
    paramId: number,
    lexical_formula: string,
    playerId: number
  ) => void,
  position: number,
};

/** Компонент строки в траблице со списком игроков в команде
 * @param {object} player - объект текущего игрока
 * @param {string} activeId - активный параметр, по которому идет сортировка внутри таблицы
 * @param {func} getClassName - функция получения класса (разные классы для параметров % и обычных)
 * @param {string} lang - выбранный язык пользователя
 * @param {array} selectedParams - выбранные параметры, которые отображаем в таблице
 * @param {func} getParamValue - функция получения значения для конкретного параметра в ячейке
 */

const {
  helpers: { onProfileImageError },
} = utils

export const PlayerTr = ({
  activeId,
  avatarIsVisible,
  avatarUrl,
  getClassName,
  getParamValue,
  handleClick,
  lexics: { lang },
  player,
  position,
  selectedParams,
}: TProps) => (
  <tr className='stats-table__table-row'>
    <td className='left-side-tr'>
      <div className='stats-cell'>
        <div className='stats-cell__player'>
          {avatarIsVisible && (
            <div className='results__avatar'>
              <img
                alt='logo'
                className='results__avatar-pic'
                src={avatarUrl}
                onError={onProfileImageError}
              />
            </div>
          )}
          <div className='match-table__player-number'>{player.shirt_num}</div>
          <div className='match-table__player-name'>
            <a
              target='_blank'
              rel='noopener noreferrer'
              href={`/players/${get(player, 'f_player', '')}`}
            >
              {get(player, `short_name_${getLongSuffix(lang)}`, '')}
            </a>
          </div>
        </div>
      </div>
    </td>
    {map(selectedParams, param => (
      <td
        key={`${param.param_id}_${param.gear_group_id}`}
        className={getClassName(param, activeId)}
      >
        <table>
          <tbody>
            <tr>
              <Td
                param={param}
                value={getParamValue(param, position)}
                handleClick={handleClick}
                player={player}
              />
            </tr>
          </tbody>
        </table>
      </td>
    ))}
  </tr>
)

// @ts-ignore
export default PlayerTrComposed(PlayerTr)
