import React, { Fragment } from 'react'
import { inject, observer } from 'mobx-react'

import { compose, withHandlers } from 'recompose'
import cx from 'classnames'
import find from 'lodash/find'

import { T9n } from 'react-sport'

import {
  TComposeInner,
  TPropsOuter,
  TWithHandlersOuter,
  TWithHandlersInner,
} from './types'

const PopupItemComponent = ({
  checkActive,
  isActiveParam,
  last,
  param,
  param: { lexical_formula },
}: TComposeInner) => (
  <Fragment>
    <div className='checkbox-wrapper'>
      <span
        className={cx(
          'checkbox-label',
          { 'checkbox-label_active': isActiveParam(param) },
        )}
        data-name='param'
        onClick={checkActive(param)}
      >
        <T9n t={lexical_formula} />
      </span>
    </div>
    {last && <br />}
  </Fragment>
)

export const popupItemComposed = compose<TComposeInner, TPropsOuter>(
  inject('gearPopupStore'),
  withHandlers <TWithHandlersOuter, TWithHandlersInner>({
    /**
     * Функция выбора чекбокса
     * @param {array} tempSelected - массив выбранных параметров (не сохраненные в сторе)
     * @param {number} editTempSelected - функция действия с чекбоксом ('add', 'delete')
     * @param {number} param_id - id параметра
     * @param {number} gear_group_id - id группы параметров
     * @param {string} short_lexical_formula - лексика параметра
     * @param {object} param - весь параметр
     */
    checkActive: ({
      gearPopupStore: {
        addToTempSelected,
        removeFromTempSelected,
        tempSelected,
      },
    }) => param => () => {
      const {
        gear_group_id,
        param_id,
        short_lexical_formula,
      } = param

      if (
        find(tempSelected, {
          gear_group_id,
          param_id,
          short_lexical_formula,
        })
      ) {
        removeFromTempSelected(param)
      } else {
        addToTempSelected(param)
      }
    },
    /**
     * Проверка активного класса для выбранного чекбокса
     * @param {array} tempSelected - массив выбранных параметров (не сохраненные в сторе)
     * @param {number} param_id - id параметра
     * @param {number} gear_group_id - id группы параметров
     * @param {string} short_lexical_formula - лексика параметра
     */
    isActiveParam: ({ gearPopupStore: { tempSelected } }) => ({
      gear_group_id,
      param_id,
      short_lexical_formula,
    }) => (
      !!find(tempSelected, {
        gear_group_id,
        param_id,
        short_lexical_formula,
      })
    ),
  }),
  observer,
)

export const PopupItem = popupItemComposed(PopupItemComponent)
