import filter from 'lodash/fp/filter'

import reduce from 'lodash/reduce'

import routes from 'config/routes'

import { callApi } from 'react-sport'

import { MatchPagePlayersFetchPlayersInfo } from './fetchPlayersInfo'

type TConfig = {
  body: string,
  method: string,
}

export const MatchPagePlayersFetchPlayers = MatchPagePlayersFetchPlayersInfo.actions(self => ({
  _onSuccessPlayers(json: { data: typeof self.playersInfo }) {
    const playersPlayedThisMatch = filter<Array<{}>>('played')(json.data)

    self.playerList = reduce(
      playersPlayedThisMatch,
      (
        acc: {[key: number]: Array<{}>},
        curr: any,
      ) => {
        const players = acc[curr.f_team] || []

        acc[curr.f_team] = [
          ...players,
          {
            ...curr,
            sortBy: 0,
          },
        ]

        return acc
      }, {},
    )

    self.setError(null)

    // Делаем запрос для получения данных по полученным игрокам
    self.fetchPlayersInfo()
  },
}))
  .actions(self => ({
    fetchPlayers(matchId: number | string) {
      const url: string = `${routes.API_ROOT}/data`
      const config: TConfig = {
        body: JSON.stringify({
          params: {
            _v_match_id: matchId,
            _v_played: true,
          },
          proc: 'ask_players_list_match',
        }),
        method: 'POST',
      }

      return callApi({
        config,
        onError: self._onError,
        onRequest: () => { self.setIsLoaded(false) },
        onSuccess: self._onSuccessPlayers,
        url,
      })
    },
  }))
