
// Получение тайтлов для шапки профиля команды/игрока

type TArgs = {
  simpleName: string,
  matchesCount: number,
  matchName: string,
  paramName: string | number,
};

const getHeaderStatsTitle = ({
  matchesCount,
  matchName,
  paramName,
  simpleName,
}: TArgs) => () => ({
  playlist_title: `${simpleName}`,
  playlist_subtitle: `${matchesCount} ${matchName} - ${paramName}`,
})

export default getHeaderStatsTitle
