import React from 'react'
import TeamCard from 'features/TeamPage/components/TeamCard'
import MatchSelect from 'features/components/common/MatchSelect'
import Loader from 'features/components/common/loader/Loader'
import { TeamStatsWrapper } from './TeamStatsWrapper'

type TProps = {
  firstLoad: boolean,
  profile: {},
  teamId: string,
}

export const TeamPageHeader = ({
  firstLoad,
  profile,
  teamId,
}: TProps) => (
  <section className='player-card'>
    <div className='container-fluid'>
      <div className='player-card-wrapper'>
        {profile && <TeamCard />}
        <div className='player-card-responsive'>
          {!firstLoad
            ? (
          //@ts-ignore
              <MatchSelect
                page='team'
                pageId={teamId}
              />
            )
            : <Loader />}
        </div>
        <div className='player-card-right'>
          {profile && !firstLoad
            ? (
              <TeamStatsWrapper
                page='team'
                pageId={teamId}
              />
            )
            : <Loader />
          }
        </div>
      </div>
    </div>
  </section>
)
