import React from 'react'
import { Router, Route, Redirect } from 'react-router-dom'
import PropTypes from 'prop-types'
import { Provider } from 'mobx-react'

import stores from 'features/stores'
import history from 'features/history'

import App from 'features/components/App/App'

// @ts-ignore
const onEnter = (props) => {
  if (
    window.localStorage.getItem('AuthUser') === null
    && props.location.pathname !== '/login'
  ) {
    localStorage.setItem('backLocation', props.location.pathname)
    return <Redirect to='/login' />
  }
  return <App {...props} />
}

onEnter.propTypes = {
  location: PropTypes.object,
}

const Features = () => (
  <Router history={history}>
    <Provider {...stores}>
      <div>
        <Route path='/' render={props => onEnter(props)} />
      </div>
    </Provider>
  </Router>
)

export default Features
