
import React from 'react'
import { compose, withHandlers, withProps, withState } from 'recompose'

type TProps = {
  child: any,
  listItemStyle: {},
  handleMouseHover: () => void,
};

const DropDownItem = ({ child, handleMouseHover, listItemStyle }: TProps) => (
  <div
    onMouseEnter={handleMouseHover}
    onMouseLeave={handleMouseHover}
    style={listItemStyle}
  >
    {child}
  </div>
)

const DropDownItemComposed = compose(
  withState('isHovered', 'toggleHover', false),
  //@ts-ignore
  withHandlers({
    //@ts-ignore
    handleMouseHover: ({ toggleHover }) => () => toggleHover(n => !n),
  }),
  //@ts-ignore
  withProps(({ isHovered, primaryColor }) => ({
    listItemStyle: {
      backgroundColor: isHovered ? primaryColor : '#fff',
      padding: '5px 10px',
    },
  })),
  //@ts-ignore
)(DropDownItem)

export default DropDownItemComposed
