import React from 'react'
import { inject, observer } from 'mobx-react'

import {
  compose,
  withHandlers,
  withStateHandlers,
} from 'recompose'

import { OutsideClick } from 'react-sport'

import {
  TPropsInner,
  TPropsOuter,
  TWithStateInner,
  TWithStateHandlers,
  TWithStateOuter,
  TWithHandlersOuter,
  TWithHandlersInner,
  TWithHandlersFixationOuter,
  TWithHandlersFixationInner,
} from './types'

// Input для редактирования названия таблицы
const EditableInputComponent = ({
  changeTableName,
  enterPress,
  handleFocus,
  newName,
}: TPropsInner) => (
  <input
    type='text'
    onFocus={handleFocus}
    onKeyPress={enterPress}
    tabIndex={0}
    value={newName}
    onChange={changeTableName}
  />
)

const editableInputComposed = compose<TPropsInner, TPropsOuter>(
  inject('gearPopupStore'),
  withStateHandlers<TWithStateInner, TWithStateHandlers, TWithStateOuter>(
    ({ tableName }) => ({
      newName: tableName,
    }),
    {
      changeTableName: () => e => ({
        newName: e.target.value,
      }),
    },
  ),
  withHandlers<TWithHandlersOuter, TWithHandlersInner>({
    /**
     * Функция сохранения нового имени таблицы
     * @param {func} handleShowInput - показываем ли инпут вместо блока
     * @param {func} editTableName - функция редактирования названия таблицы
     * @param {string} newName - название таблицы, новое
     * @param {number} position - номер таблицы
     * @param {string} tableName - название таблицы, изначальное
     * @param {string} tableType - тип таблицы внутри основной страницы (прим. team/573/players)
     */
    handleFixName: ({
      editTableName,
      gearPopupStore: { editTempTableName },
      handleShowInput,
      newName,
      newPopup,
      position,
      tableName,
    }) => () => {
      // Изменяем название таблицы только если название поменялось на новое
      if (tableName !== newName && !newPopup) {
        editTableName(position, newName)
      }
      if (newPopup) {
        editTempTableName(newName)
      }
      return handleShowInput()
    },
  }),
  withHandlers<TWithHandlersFixationOuter, TWithHandlersFixationInner>({
    clickOutside: ({
      handleFixName,
    }) => () => handleFixName(),

    enterPress: ({
      handleFixName,
    }) => (e) => {
      if (e.key === 'Enter') {
        handleFixName()
      }
    },
  }),
  observer,
  OutsideClick,
)

export const EditableInput = editableInputComposed(EditableInputComponent)
