
import React from 'react'
import get from 'lodash/get'
import find from 'lodash/find'

import { compose, withHandlers } from 'recompose'

type TProps = {
  periods: Array<any>,
  getScoreByPeriods: (period: {}, team1_id: number, team2_id: number) => string,
  team1_id: number,
  team2_id: number,
};

export const MatchScore = ({
  getScoreByPeriods,
  periods,
  team1_id,
  team2_id,
}: TProps) => (
  <div className='match-header__periods-wrapper'>
    {periods.map(period => (
      <span key={period[0].period} className='match-header__periods'>
        {getScoreByPeriods(period, team1_id, team2_id)}
      </span>
    ))}
  </div>
)

const MatchScoreComposed = compose(
  withHandlers({
    //@ts-ignore
    //@ts-ignore
    //@ts-ignore
    getScoreByPeriods: () => (period, team1_id, team2_id) => {
      const scoreTeam = get(find(period, { team: team1_id }), 'value')
      const scoreOppTeam = get(find(period, { team: team2_id }), 'value')
      return `${scoreTeam} : ${scoreOppTeam}`
    },
  }),
)

// @ts-ignore
export default MatchScoreComposed(MatchScore)
