import { observer, inject } from 'mobx-react'

import {
  compose,
  withHandlers,
  withState,
} from 'recompose'
import isEmpty from 'lodash/isEmpty'
import reduce from 'lodash/reduce'

import {
  TWithHandlersOuter,
  TWithHandlersInner,
} from './types'

export const getExtLinkHOC = compose(
  inject(
    'lexics',
    'videoLink',
    'shootsRange',
  ),
  withState(
    'linkIsFetching',
    'toggleLinkIsFetching',
    false,
  ),
  withHandlers<TWithHandlersOuter, TWithHandlersInner>({
    getExtLink: ({
      lexics: { lang },
      shootsRange,
      toggleLinkIsFetching,
      videoLink: { request },
    }) => ({
      match,
      param: { lexical_formula },
      playerInfo,
      playListType,
      stats,
    }) => () => {
      const events = reduce(
        stats,
        (acc: Array<number>, { f_match_event_ids_json }) => {
          if (f_match_event_ids_json) {
            f_match_event_ids_json.forEach(event => acc.push(event))
          }
          return acc
        },
        [],
      )

      if (!isEmpty(events)) {
        toggleLinkIsFetching(true)
        request({
          events,
          lang,
          lexical_formula,
          matchInfo: match,
          playerInfo,
          range: shootsRange,
          type: playListType,
        }).then(() => toggleLinkIsFetching(false))
      }
    },
  }),
  observer,
)
