import { types } from 'mobx-state-tree'

import { saveTablesToLocalStorage } from 'module/utils/helpers'

export const saveTempParams = types.model({}).actions(self => ({
  /**
   * Выбранные параметры (чекбоксы) сохраняем в стор
   * @param {number} position - порядковый номер таблицы
   * @param {string} localStorageName - ключ для записи в localStorage
   * @param {array} selectedParams - выбранные параметры, которые мы записываем в постоянные
   */
  saveTempParams({
    ampluaId,
    localStorageName,
    position,
    selectedParams,
  }: {
    ampluaId?: number,
    localStorageName: string,
    position: number,
    selectedParams: Array<{}>,
  }) {
    //@ts-ignore
    self.tables = {
      //@ts-ignore
      ...self.tables,
      [position]: {
        //@ts-ignore
        ...self.tables[position],
        selectedParams,
      },
    }
    // Сохраняем выбранные параметры в localStorage
    saveTablesToLocalStorage({
      id: ampluaId,
      name: localStorageName,
      //@ts-ignore
      tables: self.tables,
    })
  },
}))
