import React, { Fragment } from 'react'

import { TPropsInner } from './types'

import TeamsCompareStats from '../TeamsCompareStats'
import TeamsCompareLogos from '../TeamsCompareLogos'
import { teamsCompareInfoComposed } from './composed'

export const TeamsCompareInfoComponent = ({
  getClassName,
  getExtLink,
  getImage,
  getSortedStats,
  getStatValue,
  team1,
  team2,
}: TPropsInner) => (
  <Fragment>
    <TeamsCompareLogos
      getImage={getImage}
      team1={team1}
      team2={team2}
    />
    <TeamsCompareStats
      stats={getSortedStats()}
      team1={team1}
      team2={team2}
      getStatValue={getStatValue}
      getClassName={getClassName}
      getExtLink={getExtLink}
    />
  </Fragment>
)

export const TeamsCompareInfo = teamsCompareInfoComposed(TeamsCompareInfoComponent)
