import React from 'react'
import {
  compose,
  withHandlers,
} from 'recompose'

import { TournamentTable } from 'features/TournamentPageMatchesTable'
import { TournamentPlayers } from 'features/TournamentPagePlayersTable'
import { TableMatchWrapper } from 'features/MatchProfileTable'
import { CommonTable } from 'features/CommonTable'

import {
  TPropsInner,
  TPropsOuter,
  TWithHandlersInner,
  TTables,
} from './types'

const TableContentComponent = ({
  getTable,
  tableType,
}: TPropsInner) => getTable(tableType) || null

const tableContentComposed = compose<TPropsInner, TPropsOuter>(
  withHandlers<TPropsOuter, TWithHandlersInner>({
    getTable: ({ ...props }) => (tableType) => {
      const tables: TTables = {
        //@ts-ignore
        matchPlayersTable: <TableMatchWrapper {...props} />,
        playerMatches: <CommonTable {...props} />,
        teamMatches: <CommonTable {...props} />,
        tournamentMatches: <TournamentTable {...props} />,
        tournamentPlayers: <TournamentPlayers {...props} />,
      }

      if (!tableType) return null

      return tables[tableType]
    },
  }),
)

export const TableContent = tableContentComposed(TableContentComponent)
