import get from 'lodash/get'

// @ts-ignore
import { getLongSuffix, getSuffix } from 'module/utils/helpers'

/** Получение полного имени игрока (англ/русский)
 * @param {object} player - объект игрока
 * @param {string} lang - язык (ru, rus, en, eng)
 * @param {bool} long - форма языка (прим. ru или rus)
 */
export function getPlayerFullName({
  lang,
  long,
  player,
}: {
  player: {},
  lang: string,
  //@ts-ignore
  long: bool,
}): string {
  const suffix = long ? getLongSuffix(lang) : getSuffix(lang)
  const firstName = get(player, `firstname_${suffix}`, '')
  const lastName = get(player, `lastname_${suffix}`, '')

  return `${firstName} ${lastName}`
}
