import React from 'react'
import { T9n, utils } from 'react-sport'
import { TTeamsCompareLogos } from '../types'

const {
  helpers: { onProfileImageError },
} = utils

export const TeamsCompareLogos = ({
  getImage,
  team1,
  team2,
}: TTeamsCompareLogos) => (
  <div className='t-compare__control-row'>
    <div className='t-compare__logo-container t-compare__logo-container_teams'>
      <img
        className='t-compare__logo t-compare__logo_team'
        //@ts-ignore
        src={getImage(team1)}
        alt='logoTeam'
        onError={e => onProfileImageError(e, 'team')}
      />
    </div>

    <div className='t-compare__controls t-compare__controls_player'>
      <T9n className='t-compare__toggler' t='match' />
    </div>
    <div className='t-compare__logo-container t-compare__logo-container_teams'>
      <img
        className='t-compare__logo t-compare__logo_team t-compare__logo_team-right'
        //@ts-ignore
        src={getImage(team2)}
        alt='logoTeam'
        onError={e => onProfileImageError(e, 'team')}
      />
    </div>
  </div>
)

export default TeamsCompareLogos
