import React from 'react'
import PropTypes from 'prop-types'
// @ts-ignore
import { noImages } from 'module/utils/helpers'

// @ts-ignore
// @ts-ignore
const Avatar = ({ image, name }) => {
  //@ts-ignore
  const noImage = name ? noImages[name] : noImages.player
  return (
    <img
      src={image}
      onError={(e) => {
        //@ts-ignore
        e.target.src = noImage
      }}
      alt={name}
    />
  )
}

Avatar.propTypes = {
  name: PropTypes.string,
  image: PropTypes.string,
}

export default Avatar
