
import { types } from 'mobx-state-tree'

// @ts-ignore
import { tableApiActions } from 'features/Table'

import { PlayersCompareApiActions } from './apiActions'
import { PlayersCompareActions } from './actions'
import { PlayersCompareViews } from './views'

const Stat = types.model('Stat', {
  param: types.number,
  param_name: types.maybeNull(types.string),
  param_name_short: types.maybeNull(types.string),
})

const GearParam = types.model('SelectedGearParam', {
  clickable: types.number,
  f_option_3: types.number,
  full_lexical_formula: types.string,
  gear_group_id: types.number,
  gear_subgroup_id: types.number,
  is_default: types.maybeNull(types.string),
  lexical_formula: types.string,
  name: types.string,
  param1_id: types.maybeNull(types.array(types.string)),
  param2_id: types.maybeNull(types.array(types.string)),
  param_id: types.number,
  short_lexical_formula: types.string,
  sort_order: types.number,
})

export const PlayersCompare = types.model('PlayersCompareWidget', {
  stats: types.maybeNull(types.array(Stat)),
  playersInfo: types.frozen({}),
  isLoaded: types.boolean,
  error: types.frozen({}),
  selectedPlayers: types.frozen({}),
  selectedParams: types.maybeNull(types.array(GearParam)),
  tempSelected: types.array(GearParam),
})

export const PlayersCompareStore = types.compose(
  // Общие методы для получения параметров для шестеренки
  tableApiActions,
  // Собственные методы
  PlayersCompare,
  PlayersCompareApiActions,
  PlayersCompareActions,
  PlayersCompareViews,
)
