import React from 'react'
import PropTypes from 'prop-types'
import isArray from 'lodash/isArray'
import { T9n } from 'react-sport'
import names from 'config/matchesSelection'

const SelectLabel = ({ selected = names[3] }) =>
  (isArray(selected) ? (
    <span>
      <T9n t='match_selected' />: {selected.length}
    </span>
  ) : (
  //@ts-ignore
    <T9n t={names[selected]} />
  ))

SelectLabel.propTypes = {
  selected: PropTypes.any,
}

export default SelectLabel
