import React from 'react'
import { inject, observer } from 'mobx-react'

import get from 'lodash/get'
import {
  compose,
  withHandlers,
  withProps,
  withState,
} from 'recompose'
import { OutsideClick, T9n } from 'react-sport'

import { getSuffix, getAbbr } from 'module/utils/helpers'

import { ControlDropDown } from '../ControlsDropDown'

import {
  TPropsInner,
  TWithState,
  TWithPropsInner,
  TWithHandlersInner,
  TWithPropsOuter,
  TWithHandlersOuter,
  TWithHandlersCheckOuter,
  TWithHandlersCheckInner,
} from './types'

export const TeamsPopupComponent = ({
  activeTeamShortName,
  checkTeam,
  teams,
}: TPropsInner) => (
  <ControlDropDown
    menuType='teamSelect'
    title={activeTeamShortName}
  >
    {teams.map(({ id, teamName }) => (
      <div
        onClick={checkTeam(id)}
        className='custom-select__option'
        key={id}
      >
        <T9n t={teamName} />
      </div>
    ))}
  </ControlDropDown>
)

const teamsPopupComposed = compose<TPropsInner, {}>(
  inject(
    'filters',
    'lexics',
    'matchProfile',
    'markers',
    'matches',
  ),
  withState<TWithState, {}>('isOpened', 'setIsOpened', false),
  observer,
  withProps<TWithPropsInner, TWithPropsOuter>(({
    lexics: { lang },
    matches: { active },
    matchProfile: { data },
  }) => {
    const suffix = getSuffix(lang)
    const team1Name = get(data, `team1_name_${suffix}`)
    const team2Name = get(data, `team2_name_${suffix}`)
    return {
      activeTeamShortName: getAbbr(get(data, `${active}_name_${suffix}`)),
      teams: [
        {
          id: 'team1',
          teamName: team1Name,
        },
        {
          id: 'team2',
          teamName: team2Name,
        },
      ],
    }
  }),
  withHandlers<TWithHandlersOuter, TWithHandlersInner>({
    clickOutside: ({ setIsOpened }) => () => setIsOpened(false),
    handleActive: ({
      matches: {
        changeActive,
      },
    }) => (active) => {
      changeActive(active)
    },
    setIsOpenedHandler: ({ isOpened, setIsOpened }) => () => () => setIsOpened(!isOpened),
  }),
  withHandlers<TWithHandlersCheckOuter, TWithHandlersCheckInner>({
    checkTeam: ({
      filters: { clear },
      handleActive,
      markers: { fetch },
      matches: { active },
      matchProfile,
      setIsOpenedHandler,
    }) => id => () => {
      if (active !== id) {
        handleActive(id)
        const teamId = get(matchProfile, `data.${id}_id`, '')
        const matches = [get(matchProfile, 'matchId')]
        fetch(teamId, matches)
        // Очищаем все выбранные фильтры при смене команды
        clear()
      }
      return setIsOpenedHandler()
    },
  }),
  OutsideClick,
  observer,
)

export const TeamsPopup = teamsPopupComposed(TeamsPopupComponent)
