
import { types, flow } from 'mobx-state-tree'
import isEmpty from 'lodash/isEmpty'
import flatten from 'lodash/flatten'
import uniqBy from 'lodash/uniqBy'
import size from 'lodash/size'

import { callApi } from 'react-sport'

// @ts-ignore
import routes from 'config/routes'
// @ts-ignore
import { filterDBStats } from 'module/utils/helpers'
import { matchesToShow } from '../config'

type TConfig = {
  method: string,
  body: string,
};

const url: string = `${routes.API_ROOT}/data`

export const TournamentMatchesTableApiActions = types
  .model({})
  .actions(self => ({
    fetchTableInfo: flow(function* fetchTableInfo() {
      //@ts-ignore
      yield self.getGearParamsList(0)
      //@ts-ignore
      yield self.fetchTableParams()
    }),

    fetchTableParams() {
      //@ts-ignore
      const paramsList: Array<number> = isEmpty(
        //@ts-ignore
        self.summarySelectedParamsFromTables,
      )
        //@ts-ignore
        ? self.defaultParams
        //@ts-ignore
        : self.summarySelectedParamsFromTables
      const config: TConfig = {
        method: 'POST',
        body: JSON.stringify({
          proc: 'ask_tournament_param_view',
          params: {
            //@ts-ignore
            _v_match_id: self.matchesToFetch,
            _v_param_id: paramsList,
          },
        }),
      }

      return callApi({
        url,
        config,
        onRequest: () => {
          //@ts-ignore
          self.partFetching = true
          //@ts-ignore
          self.error = null
        },
        //@ts-ignore
        onSuccess: self._onSuccesTableParams,
        //@ts-ignore
        onError: self._onError,
      })
    },

    _onSuccesTableParams(json: { data: Array<{}> }) {
      //@ts-ignore
      //@ts-ignore
      self.stats = uniqBy(
        //@ts-ignore
        flatten([...self.stats, filterDBStats(json.data)]),
        'id',
      )
      //@ts-ignore
      if (size(self.matchesToFetch) < matchesToShow) {
        //@ts-ignore
        self.canFetch = false
      }
      if (window.localStorage.getItem('tournamentMatchesTableStore') === null) {
        //@ts-ignore
        self.saveTables()
      } else {
        //@ts-ignore
        //@ts-ignore
        self.tables = JSON.parse(
          //@ts-ignore
          window.localStorage.getItem('tournamentMatchesTableStore'),
        )
      }
      //@ts-ignore
      self.partFetching = false
      //@ts-ignore
      self.isLoaded = true
    },

    _onError(error: any) {
      //@ts-ignore
      self.error = error
    },
  }))
