import * as React from 'react'
import { PlayButton, MatchName, TableUI } from 'react-sport'
// @ts-ignore
import { MatchPopup } from 'features/Table'
import { leftSideTrComposed } from './composed'

type TProps = {
  showOpponent?: boolean,
  tdClassName: string,
  rowspan: string,
  match: {
    has_video: boolean,
    home_match: string,
    f_match: number,
  },
  //@ts-ignore
  handleClick: (value: SyntheticEvent<HTMLButtonElement>) => void,
  titleScore: string,
  titleClassName: string,
  matchDate: string,
  shortNameOpponent: string,
  ownerTeamName: string,
  oppTeamName: string,
  matchPopupOpened: boolean,
  isFetching: boolean,
  links: {},
  toggleClose: () => {},
}

// @ts-ignore
export const LeftSideTr = leftSideTrComposed(
  //@ts-ignore
  ({
    handleClick,
    isFetching,
    links,
    match: { f_match, has_video, home_match },
    matchDate,
    matchPopupOpened,
    oppTeamName,
    ownerTeamName,
    rowspan,
    shortNameOpponent,
    showOpponent,
    tdClassName,
    titleClassName,
    titleScore,
    toggleClose,
  }: TProps) => (
    //@ts-ignore
    <TableUI.LeftSideTrWrapper rowSpan={rowspan} className='left-side-tr'>
      {matchPopupOpened && (
      //@ts-ignore
        <MatchPopup
          isFetching={isFetching}
          links={links}
          f_match={f_match}
          toggleClose={toggleClose}
        />
      )}
      <TableUI.PlayButtonWrapper>
        {/*
        //@ts-ignore */}
        {has_video && <PlayButton handleClick={handleClick} />}
      </TableUI.PlayButtonWrapper>

      <TableUI.MatchDate>{matchDate}</TableUI.MatchDate>
      {/*
        //@ts-ignore */}
      <TableUI.MatchNameWrapper home_match={home_match}>
        {/*
        //@ts-ignore */}
        <TableUI.MatchSide className={tdClassName}>
          {home_match}
        </TableUI.MatchSide>
        {/*
        //@ts-ignore */}
        <MatchName handleClick={handleClick} teamName={shortNameOpponent} />
      </TableUI.MatchNameWrapper>
      {/*
        //@ts-ignore */}
      <TableUI.MatchScoreWrapper className={titleClassName}>
        {titleScore}
      </TableUI.MatchScoreWrapper>

      {showOpponent && (
        <TableUI.MatchScoreForOppWrapper>
          <TableUI.MatchAbbrName side='home'>
            {ownerTeamName}
          </TableUI.MatchAbbrName>
          <TableUI.MatchAbbrName side='opp'>
            {oppTeamName}
          </TableUI.MatchAbbrName>
        </TableUI.MatchScoreForOppWrapper>
      )}
    </TableUI.LeftSideTrWrapper>
  ),
)
