import React from 'react'
import { inject, observer } from 'mobx-react'

import { compose, withProps } from 'recompose'
import map from 'lodash/map'
import values from 'lodash/values'
import pickBy from 'lodash/pickBy'
import get from 'lodash/get'
import filter from 'lodash/filter'
import includes from 'lodash/includes'

import { Pointer } from './Pointer'
import { ToolTip } from './ToolTip'

type TProps = {
  //@ts-ignore
  marker?: {
    //@ts-ignore
    point?: {},
  },
  toolTips: Array<{}>,
}

const MarkerComponent = ({
  marker: positionsInfo,
  toolTips,
}: TProps) => (
  <div className='marker__wrapper'>
    {/*
      //@ts-ignore */}
    <Pointer positionsInfo={positionsInfo} />
    {map(toolTips, ({
      //@ts-ignore
      pos_x,
      //@ts-ignore
      pos_y,
      //@ts-ignore
      type,
    }) => (
      <ToolTip
        key={`${pos_x}_${pos_y}`}
            //@ts-ignore
        point={positionsInfo.point}
        type={type}
        y={pos_y}
        x={pos_x}
      />
    ))}
  </div>
)

const markerComposed = compose(
  inject('markers'),
  observer,
  //@ts-ignore
  withProps(({
    marker,
    markers: {
      directions: {
        from,
        to,
      },
    },
  }) => {
    const toolTips = values(
      pickBy(marker, value => get(value, 'pos_x') && get(value, 'pos_y')),
    )
    let toolTipsWithVectors = null
    if (from && to) {
      toolTipsWithVectors = filter(
        toolTips, ({ type }) => includes(['out', 'point'], type),
      )
    }
    // Если у нас показываюся векторы (когда есть точки куда и откуда),
    // то используем отфильтрованные точки
    return {
      toolTips: toolTipsWithVectors || toolTips,
    }
  }),
  observer,
)
//@ts-ignore
export const Marker = markerComposed(MarkerComponent)
