import React, { Fragment } from 'react'
import { inject, observer } from 'mobx-react'

import cx from 'classnames'
import get from 'lodash/get'
import map from 'lodash/map'

import { withHandlers, withProps, compose } from 'recompose'

import { ZonesStatistic } from 'features/components/common/map/ZoneStatistic'

import { zones } from 'config/zones'
import {
  TProps,
  TComposeInner,
  TPRopsInner,
  TPropsOuter,
  TWithHandlersOuter,
  TWithHandlersInner,
} from './types/fieldZonesTypes'
import { MapFieldSVG } from './MapFieldSVG'

const FieldZonesComponent = ({
  getZonesClassName,
  side,
  zones: { checked },
  zonesList,
}: TProps) => (
  <Fragment>
    <div className={getZonesClassName()}>
      {checked !== 'without_zone'
        && map(zonesList, (zone: string) => (
          <ZonesStatistic key={zone} zone={zone} />
        ))}
    </div>
    <MapFieldSVG side={side} checked={checked} />
  </Fragment>
)

const fieldZonesComposed = compose<TComposeInner, {}>(
  inject('zones', 'tabs'),
  observer,
  withProps<TPRopsInner, TPropsOuter>(({
    tabs: { active },
    zones: { checked },
  }) => {
    const side = active === 'serve' ? 'top' : 'bottom'

    return {
      side,
      zonesList: get(zones, [side, checked], []),
    }
  }),
  withHandlers<TWithHandlersOuter, TWithHandlersInner>({
    getZonesClassName: ({ side, zones: { checked } }) => () =>
      cx(
        'map-block__zones',
        `map-block__zones_${checked.split('_')[0]}-${side}`,
      ),
  }),
)

export const FieldZones = fieldZonesComposed(FieldZonesComponent)
