import { inject, observer } from 'mobx-react'

import { withHandlers, compose } from 'recompose'
import cx from 'classnames'
import uniqBy from 'lodash/uniqBy'
import sortBy from 'lodash/sortBy'
import map from 'lodash/map'
import get from 'lodash/get'
import find from 'lodash/find'

import {
  TPropsInner,
  TPropsOuter,
  TWithHandlersOuter1,
  TWithHandlersInner1,
  TWithHandlersOuter2,
  TWithHandlersInner2,
} from './types'

export const teamsCompareInfoComposed = compose<TPropsInner, TPropsOuter>(
  inject(
    'lexics',
    'videoLink',
    'shootsRange',
    'matchTable',
    'teamsCompareWidget',
  ),
  withHandlers<TWithHandlersOuter1, TWithHandlersInner1>({
    getTeamParams: ({
      teamsCompareWidget: { teamsInfo },
    }) => teamId => get(teamsInfo, [teamId, 'params'], []),
  }),
  withHandlers<TWithHandlersOuter2, TWithHandlersInner2>({
    getClassName: ({ getTeamParams }) => (param, teamId) => {
      const hasVideo = get(
        find(getTeamParams(teamId), { param }),
        'f_match_event_ids',
      )
      return cx(
        't-compare__stats-num',
        { 't-compare__clickable-cell': hasVideo },
      )
    },

    getExtLink: ({
      getTeamParams,
      lexics: { lang },
      shootsRange,
      videoLink: { request },
    }) => (param, param_name, teamId) => () => {
      const events = get(
        find(getTeamParams(teamId), { param }), 'f_match_event_ids',
      )
      if (events) {
        request({
          events,
          lang,
          lexical_formula: param_name,
          range: shootsRange,
          type: 'widgets',
          widgetType: 'teamsCompare',
        })
      }
    },

    getImage: ({ teamsCompareWidget: { teamsInfo } }) => teamId => (
      get(teamsInfo, `${teamId}.photo_team`, '')
    ),

    getSortedStats: ({
      teamsCompareWidget: {
        selectedParams,
        stats,
      },
    }) => () => {
      if (stats) {
        const uniqStats = uniqBy(stats, 'param')
        return sortBy(uniqStats, ({ param }) => {
          const selectedParamsIds = map(selectedParams, 'param_id')
          return selectedParamsIds.indexOf(param)
        })
      }
      return []
    },

    getStatValue: ({ getTeamParams }) => (param, teamId) => {
      const value = get(
        find(getTeamParams(teamId), { param }),
        'value',
      )
      return value ? value.toFixed() : '-'
    },
  }),
  observer,
)
