import React, { Fragment } from 'react'

import cx from 'classnames'
import { getSuffix } from 'module/utils/helpers'

type TProps = {
  lang: string,
  match: {
    [key: string]: string | number,
    home_match: string,
    opponent_score: number,
    score: number,
  },
}

export const MatchCheckBox = ({ lang, match }:TProps) => (
  <Fragment>
    <span className='title'>
      {match.home_match} {match[`name_opponent_${getSuffix(lang)}`]}
    </span>
    <span className={cx(
      'title__score',
      { title__score_lose: match.opponent_score > match.score },
      { title__score_win: match.opponent_score < match.score },
    )}
    >{match.score}{' '} - {match.opponent_score}
    </span>
  </Fragment>
)
